<template>
  <canvas :id="id" :width="width" :height="height"></canvas>
</template>
<script>
  export default {
    props: ['totle', 'value', 'id', 'color'],
    data() {
      return {
        width: 80,
        height: 80
      }
    },
    components: {},
    methods: {
      createdPage() {
        let data = [
          {
            "value": this.value / this.totle,
            "color": this.color,
          },
          {
            "value": 1 - (this.value / this.totle),
            "color": '#eee',
          }
        ]
        let canvas = document.getElementById(this.id);
        canvas.width = this.width;//设置canvas宽
        canvas.height = this.height;
        let tempAngle = -90
        let context = canvas.getContext("2d");
        let x0  = this.width / 2, y0 = this.height /2;
        data.forEach(item => {
          let startAngle = tempAngle*Math.PI/180
          let angle = item.value*360
          let endAngle = (tempAngle+angle)*Math.PI/180
          context.beginPath();
//          context.moveTo(x0,y0);
          context.lineWidth = 10
          context.strokeStyle = item.color;
          context.arc(x0,y0, 30,startAngle,endAngle, false);
//          context.fill();
          context.stroke();
          context.closePath()
          tempAngle += angle;
        })
      }
    }
  }
</script>



