<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item label="分组：" prop="apiGroupId" label-width="60px">
        <el-select class="w9000" clearable filterable v-model="searchForm.apiGroupId" placeholder="请选择" @change="getApiAry">
          <el-option
            v-for="item in groupAry"
            :key="item.id"
            :label="item.groupName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="接口名称：" prop="apiId" label-width="90px">
        <el-select class="w9000" clearable filterable v-model="searchForm.apiId" placeholder="请选择">
          <el-option
            v-for="item in apiAry"
            :key="item.id"
            :label="item.apiName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item  label="下线日期: ">
        <el-date-picker class="w200" v-model="rangeDate" @change="dateChange" rangeSeparator="/" type="datetimerange"
                        placeholder="选择时间范围">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table ref="multipleTable" :data="tableList" tooltip-effect="dark"
              style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
      <el-table-column prop="id" label="ID" v-if="false"></el-table-column>
      <el-table-column prop="" type="index" label="序号" width="80"></el-table-column>
      <el-table-column prop="apiGroupName" label="数据源"></el-table-column>
      <el-table-column prop="apiName" label="接口"></el-table-column>
      <el-table-column prop="remarks" label="备注"></el-table-column>
      <el-table-column prop="createTime" label="开始时间" :formatter="dateFormat"></el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png"/></p>
      <span>暂无数据</span>
    </div>
    <div class="page" style="padding-bottom:30px;" v-if="this.tableList.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="this.searchForm.pageNum"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="this.searchForm.pageSize" layout=" sizes, prev, pager, next, jumper"
                     :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  import moment from "moment";

  export default {
    name: "apiOfflineRecord",
    components: {},
    data() {
      return {
        jurisdiction: {
          principal: {},
          loginUrl: '',
          logoutUrl: '',
          roles: [],
          perms: []
        },
        totalCount: 1,
        groupAry: [],
        apiAry: [],
        rangeDate: [moment(moment(new Date()).format('YYYY-MM-DD')), moment(new Date())],
        tableList: [],
        searchForm: {
          pageNum: 1,
          pageSize: 10,
          startTime: Number(moment(moment(new Date()).format('YYYY-MM-DD')).format('x')),
          endTime: Number(moment(new Date()).format('x')),
          apiGroupId: '',
          apiId: ''
        },
        formLabelWidth: "120px",
        description: "",

        delData: "",
        rowFlowData: {},


        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      };
    },

    created() {
      this.initData();
      this.utils.getUserPermissions(this);
      this.utils.getGroupAry(this);
    },

    methods: {
      getApiAry(groupId) {
        this.utils.getApiAry(this, groupId)
      },
      // 日期改变
      dateChange(date) {
        let newDate = date.split("/")
        this.searchForm.startTime = Number(moment(newDate[0]).format('x'))
        this.searchForm.endTime = Number(moment(newDate[1]).format('x'))
      },

      //初始化列表
      initData() {
        axios({
          method: "post",
          url: "/ApiOffline/findForPage",
          data: this.searchForm
        }).then(res => {
          if (res.data.code === 2000) {
            this.tableList = res.data.data.list;
            this.totalCount = res.data.data.total;
          } else if (res.data.code === 403) {
            location.href = res.data.data;
          }
        }).catch(error => {
          console.log("网络错误，不能访问" + error);
        });
      },

      //翻页事件
      handleCurrentChange(val) {
        this.searchForm.pageNum = val;
        this.initData();
      },
      //改变分页大小事件
      handleSizeChange(val) {
        this.searchForm.pageSize = val;
        this.initData();
      }

    }
  };
</script>

<style scoped>

</style>



