<template>
  <div class="TestApi" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入APIID进行查询" class="w200" v-model='searchApiId'></el-input>
        <el-input placeholder="请输入API分组进行查询" class="w200" v-model='searchGroup'></el-input>
        <el-input placeholder="请输入开始时间进行查询" class="w200" v-model='createTime'></el-input>
        <el-input placeholder="请输入结束时间进行查询" class="w200" v-model='endTime'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="apiId" label="apiId"></el-table-column>
      <el-table-column prop="apiName" label="apiName"></el-table-column>
      <el-table-column prop="apiGroupId" label="分组"></el-table-column>
      <el-table-column prop="testDate" label="创建时间" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
              <el-button type="text" class="radiusBtn pubColor" size="small" @click="lookJson(scope.row)">查看</el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 发布API弹框 start -->
    <el-dialog title="查看：" :visible.sync="jsonDialog" size="tiny" style="top:-5%">
        <pre><span class="writePlaces" style="white-space: pre-line;">{{callBack}}</span></pre>
        <span slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="jsonDialog = false">关闭</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'findApi',
  components: {
  },
  data() {
    return {
      jurisdiction:{},
      outObj:{},
      jsonDialog:false,
      callBack:{},
      pageNum: 1,
      pageSize: 10,
      total: 0,
      env: 1,
      page: 1,
      searchApiId: '',
      searchGroup:'',
      createTime:'',
      endTime:'',
      listLoading: false,
      tablegroup: [],
      newPage:'',

      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  },

  created() {
    this.getUserPermissions();
    this.initData();
  },

  methods: {

    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化与搜索框查询
    initData() {
      axios({
        method: 'post',
        url: '/api/page/apiTestConfig',
        data: {
          apiId: this.searchApiId == '' ? '' : this.searchApiId,
          apiGroupId:this.searchGroup,
          startDate:this.createTime,
          endDate:this.endTime,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        this.tablegroup = res.data.data.list;

        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
   //查看
   lookJson(row){
    this.jsonDialog = true;
    this.callBack =  JSON.parse(row.testResult);
   },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();


    },
    handleSizeChange(val) {
      this.newPage = val;
      this.initData();
    },
    setJson(data) {
     console.log(data)
      return JSON.parse(data)
    },
  }

}
</script>

<style scoped>

</style>



