<template>
  <div class="errorText">

  <div class="w1660fr">
    <div class="detailBtn">
      <div class="fl">
        <span class="detailNav">错误代码说明</span>
        <router-link  :to="{path:'portApiManage',query:{id:this.$route.query.id,apiGroupId:this.$route.query.apiGroupId,productEnvVersion:this.$route.query.productEnvVersion}}"><el-button size="small">返回API详情</el-button></router-link>
      </div>
      <p style="clear:both"></p>
    </div>


    <div class="panel panel-default">
      <div class="console-panel-header">
      <el-form>
      <div class="panel panel-default" style="position:relative">
        <div class="console-panel-header">
          <span class="panel-header">错误代码对照表</span>
          <table class="table table-hover table-bordered">
            <thead>
              <tr>
                <th><b>错误代码</b></th>
                <th><b>错误信息</b></th>
                <th><b>详细描述</b></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in errorTabelData">
                <td><span>{{item.errorCode}}</span></td>
                <td><span>{{item.errorDescription}}</span></td>
                <td><span>{{item.errorMsg}}</span></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      </el-form>

      </div>
    </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'errorText',
  components:{
  },
  data () {
    return {
      formLabelWidth:"120px",
      errorTabelData:[],
    }
  },
  created(){
    this.errorData();
  },
  methods:{
    errorData(){
      axios({
        method:'post',
        url:'/api/systemErrorCodeList',
        data:{}
      }).then(res => {
        this.errorTabelData = res.data.data;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    }
  }
}
</script>

<style scoped>
</style>



