<template>
  <div>
    <div class="bgm_div">
      <div @mouseup="subm = false" @click="subm = true">
        <el-menu :default-active="activeIndex1" mode="vertical" class="menu_1" theme="dark" @select="changeMenu1">
          <div v-for="(data, index) in menuList">
            <router-link :to="menuList[index].subMenu[0].path">
              <el-menu-item :index="`${index}`">
                <img class="menuImg imgNHover" style="width:20px; height: 20px" src="../../assets/data.svg"/>
                <img class="menuImg din imgHover" style="width:20px; height: 20px" src="../../assets/data.svg"/>
                <p class="navTextP navTextDefault">{{menuList[index].groupName}}</p>
                <p class="navTextP din navTextHover">{{menuList[index].groupName}}</p>
              </el-menu-item>
            </router-link>
          </div>
        </el-menu>
      </div>
    </div>
    <div v-if="subm">
      <el-menu :default-active="activeIndex2" mode="vertical" class="menu_2" theme="light" @select="changeMenu2">
        <div v-for="(data,index) in menuListAct" style="position:relative">
          <router-link :to="data.path">
            <el-menu-item :index="activeIndex1+`-${index}`">{{data.name}}</el-menu-item>
          </router-link>
        </div>
      </el-menu>
    </div>
  </div>
</template>
<script>

  export default {
    name: 'Lmenus',
    components: {},
    props: [],
    data() {
      return {
        activeIndex1: '0',
        activeIndex2: '0',
        subm: true,
        menuList: [
          {
            groupName: ' API 管理',
            subMenu:
              [
                {path: '/apiStatistical', name: '接口上游'},
                {path: '/apiDownstream', name: '接口下游'},
                {path: '/portManage', name: 'API列表'},
                {path: '/Port', name: 'API分组'},
                {path: '/findApi', name: '批量API'},
                {path: '/TestConfig', name: '配置测试'},
                {path: '/TestApi', name: '测试结果'},
                {path: '/combinationApi', name: 'API组合'}
              ]
          },
          {
            groupName: '流量管理',
            subMenu:
              [
                {path: '/flowControl', name: '接口流量控制'},
                {path: '/flowDistribution', name: '流量分发'},
                {path: '/flowMonitoring', name: '策略管理'},
                {path: '/ipList', name: 'ip白名单'},
                {path: '/trafficMonitoring', name: '流量监控'},
                {path: '/networkRelationship', name: '路由策略'},
                {path: '/flowRateLimit', name: '用户流量控制'},
                {path: '/flowRateLimitInterface', name: '用户接口控制'},
                {path: '/serviceRoute', name: '服务路由控制'}
              ]
          },
          {
            groupName: '码值管理',
            subMenu:
              [
                {path: '/codeMap', name: '码值映射'},
                {path: '/codeValue', name: '码值管理'}
              ]
          },
          {
            groupName: '帐号管理',
            subMenu:
              [
                {path: '/authorityMsg', name: '村镇银行'}
              ]
          },
          {
            groupName: '运维管理',
            subMenu:
              [
                {path: '/healthCheck', name: '健康检查'},
                {path: '/apiOfflineRecord', name: '接口下线记录'}
              ]
          },
          {
            groupName: '计费管理',
            subMenu:
              [
                {path: '/billingRule', name: '计费规则'},
                {path: '/authList', name: '接口权限'},
                {path: '/datasourceCharge', name: '数据源计费'},
                {path: '/datasourceBill', name: '数据源账单'},
                {path: '/appManage', name: '客户列表'},
                {path: '/customerBill', name: '客户账单'},
                {
                  path: '/profit', name: '利润'
                }
              ]
          }
        ],
        menuListAct: []
      }
    },
    created() {
      this.menuListAct = this.menuList[Number(this.activeIndex1)].subMenu;
    },
    methods: {
      changeMenu1(key, keyPath) {
        this.menuListAct = this.menuList[key].subMenu;
        this.activeIndex1 = key;
        this.activeIndex2 = '0';
        let menu = $(".el-menu-item")
        console.log('main-index: ' + key)
        for (let i = 0; i < this.menuList.length; i++) {
          menu.eq(i).removeClass('is-active');
        }
        menu.eq(key).addClass('is-active');

        for (let i = 0; i < this.menuListAct.length; i++) {
          let subM = key + '-' + i
          if (i === 0) {
            menu.eq(subM).addClass('is-active')
          } else {
            menu.eq(subM).removeClass('is-active')
          }
        }
      },

      changeMenu2(key) {
        console.log('sub-index:' + key);
        let menu = $(".el-menu-item")
        for (let i = 0; i < this.menuListAct.length; i++) {
          let subM = this.activeIndex1 + '-' + i
          menu.eq(subM).removeClass('is-active')
        }
        menu.eq(key).addClass('is-active');
      }
    }
  }
</script>

<style scoped>
  p {
    line-height: 0 !important;
  }

  .menuImg {
    width: 16px;
    height: 16px;
  }

  .navTextP {
    position: absolute !important;
    left: 16px !important;
    font-size: 12px;
    top: 55px;
    color: #fff
  }

  .bgm_div {
    height: calc(100vh - 70px);
    width: 80px;
    float: left;
    background: #6C6C6C;
  }

  .menu_1 {
    width: 80px;
    min-height: 400px;
    position: fixed;
    background: none;
  }

  .menu_1 .el-menu-item {
    height: 80px;
    line-height: 60px;
    color: #bfcbd9;
    text-align: center;
    position: relative;
  }

  .menu_1 > a :hover {
    /* background: green; */
  }

  .menu_1 .el-menu-item.is-active {
    background: #393D40;
  }

  .menu_2 {
    width: 150px;
    min-height: calc(100vh - 70px);
    float: left;
    background: rgb(255, 255, 255);
  }

  .menu_2 .el-menu-item {
    height: 50px;
    line-height: 50px;
    color: #8792A3;
    border-bottom: 1px solid #F0F0F0;
  }

  .menu_2 > div :hover {
    background: #F0f0f0;
  }

  .menu_2 .el-menu-item.is-active {
    background: #4E4F4F;
    color: #FEBD49;
    border-right: 2px solid #FEBD49;
    font-weight: bold;
  }

  .menu_2 .el-menu-item.is-active:after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 5px;
    height: 5px;
    border-top: 3px solid #FEBD49;
    border-left: 3px solid #FEBD49;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    right: -3px;
    top: 22px;
  }

  a:focus,
  a:hover {
    text-decoration: none;
  }

  .el-menu--dark .el-menu-item:hover {
    background-color: #4E4F4F;
  }

  /*.el-menu--dark .el-menu-item:hover .imgNHover{
    display: none;
  } */
  .menu_1 .el-menu-item.is-active .imgHover {
    display: inline-block;
  }

  .menu_1 .el-menu-item.is-active .navTextHover {
    display: inline-block;
  }

  .menu_1 .el-menu-item.is-active .imgNHover {
    display: none;
  }

  .menu_1 .el-menu-item.is-active .navTextDefault {
    display: none;
  }

  .navTextHover {
    color: #FEBD49;
  }
</style>



