<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="键值管理" name="first">

        <el-form :inline="true" class="search_form">
          <el-form-item>
            <el-select v-model="dictType" clearable placeholder="请选择">
              <el-option v-for="item in typeNameOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>

            <el-input placeholder="请输入键值进行查询" class="w350" v-model="dictCodes"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
          </el-form-item>
        </el-form>

        <el-button type="primary addOne ml20" v-if="addJBtn" @click="addCode">添加键值</el-button>
        <el-button type="primary addOne" v-if="typeBtn" @click="addTypeData();typeData()">类别管理</el-button>
        <el-table ref="multipleTable" :data="codeData" tooltip-effect="dark" style="width:100%;margin-top:20px;" :row-class-name="tableRowClassName">
          <el-table-column prop="dictValue" label="键值"></el-table-column>
          <el-table-column prop="typeName" label="类别"></el-table-column>
          <el-table-column prop="dictName" label="名称"></el-table-column>
          <el-table-column prop="" label="操作" width="80">
            <template slot-scope="scope">
              <el-button type="text" v-if="delBtnO" class="radiusBtn delColor" size="small" @click="delCode(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="teCen mt60" v-if="this.codeData.length < 1">
          <p><img src="../../assets/Shape.png" /></p>
          <span>暂无数据</span>
        </div>

        <div class="page" style="padding-bottom:30px;" v-if="this.codeData.length > 0">
          <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
            :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
          </el-pagination>
        </div>
      </el-tab-pane>

      <el-tab-pane label="码值管理" name="second">
        <el-form :inline="true" class="search_form">
          <el-form-item>

            <el-select v-model="valueKey" clearable filterable placeholder="请选择" @visible-change="slideList">
              <el-option v-for="item in optionsKey" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>

            <el-input placeholder="请输入码值进行查询" class="w350" v-model="dictCode"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="info" class="searchOne" @click="codeValMapTwo()">搜 索</el-button>
          </el-form-item>
        </el-form>

        <el-button type="primary addOne ml20" v-if="addMBtn" @click="codeValMsg = true;">添加码值</el-button>
        <el-table ref="multipleTable" :data="codeValMap" tooltip-effect="dark" style="width:100%;margin-top:20px;" :row-class-name="tableRowClassName2">
          <el-table-column prop="dictValue" label="key"></el-table-column>
          <el-table-column prop="dictItemValue" label="码值"></el-table-column>
          <el-table-column prop="dictItemName" label="码值描述"></el-table-column>
          <el-table-column prop="" label="操作" width="80">
            <template slot-scope="scope">
              <el-button type="text" v-if="delBtnT" class="radiusBtn delColor" size="small" @click="delCodeMap(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="teCen mt60" v-if="this.codeValMap.length < 1">
          <p><img src="../../assets/Shape.png" /></p>
          <span>暂无数据</span>
        </div>

        <div class="page" style="padding-bottom:30px;" v-if="this.codeValMap.length > 0">
          <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange2" @current-change="handleCurrentChange2" :current-page="pageNum2"
            :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize2" layout=" sizes, prev, pager, next, jumper" :total="total2">
          </el-pagination>
        </div>


      </el-tab-pane>
    </el-tabs>

    <!-- 添加键值对话框 start -->
    <el-dialog title="添加键值" :visible.sync="codeVal" size="tiny" style="top:-5%">
      <el-form :model="addCodeMes" ref="addCodeMes" class="demo-ruleForm">
        <el-form-item label="键值" prop="dictValueOne" :label-width="formLabelWidth" :rules="[
        { required: true, message: '键值不能为空'},
        { min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="addCodeMes.dictValueOne"></el-input>
        </el-form-item>

        <el-form-item label="类别" :label-width="formLabelWidth" :rules="[
        { required: true, message: '类别不能为空'}]">
          <el-select v-model="typeNameOptions2Res" placeholder="请选择">
            <el-option v-for="item in typeNameOptions2" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
          <el-button type="text" class="ml35 addWcolor" style="color:#FEBD49" @click="addtypeBtn();">新建分类</el-button>
        </el-form-item>

        <el-form-item label="名称" prop="dictNamesDic" :label-width="formLabelWidth" :rules="[
        { required: true, message: '名称不能为空'},
        { min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="addCodeMes.dictNamesDic"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOfCode('addCodeMes')">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addCodeVal('addCodeMes')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加键值对话框  end -->

    <!-- 新建分类对话框 start -->
    <el-dialog title="类别管理" :visible.sync="otherVal" size="tiny" style="top:-5%">
      <el-input auto-complete="off" placeholder="添加类别" v-model="addType" @change="validateT"></el-input>
      <el-table :data="typeDia" border style="width: 100%;height:250px;overflow:auto;">
        <el-table-column prop="name" label="类别" width="180">
        </el-table-column>
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button type="text" class="radiusBtn amendColor" size="small" @click="amendCode(scope.row)">编辑</el-button>
            <el-button type="text" class="radiusBtn delColor" size="small" @click="delType(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="otherVal = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addTypeBtn()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 新建分类对话框  end -->
    <!-- 编辑类别 start -->
    <el-dialog title="编辑类别" :visible.sync="amendType" size="tiny" style="top:-5%">
      <el-input v-model="typeName" placeholder="请输入内容"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="amendType = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="amendTypeBtn()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑类别  end  -->

    <!-- 添加码值对话框 start -->
    <el-dialog title="添加码值" :visible.sync="codeValMsg" size="tiny" style="top:-5%">
      <el-form :model="codeMapMes" ref="codeMapMes" class="demo-ruleForm">
        <el-form-item label="key" :label-width="formLabelWidth" :rules="[
        { required: true, message: 'key不能为空'}]">
          <el-select v-model="valueKey2" filterable placeholder="请选择">
              <el-option v-for="item in keyOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="码值" prop="dictValueTwo" :label-width="formLabelWidth" :rules="[
        { required: true, message: '码值不能为空'},
        { min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="codeMapMes.dictValueTwo"></el-input>
        </el-form-item>

        <el-form-item label="码值描述" :label-width="formLabelWidth" prop="dictItemNameTwo" :rules="[
        { required: true, message: '码值描述不能为空'},
        { min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur' }]">
          <el-input auto-complete="off" v-model="codeMapMes.dictItemNameTwo"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('codeMapMes')">取 消</el-button>
        <el-button type="primary" class="addOne" @click="codeValMsgRes('codeMapMes')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加值码对话框  end -->

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "flowMonitoring",
  components: {},
  data() {
    return {
      jurisdiction:{},
      addJBtn:false,
      typeBtn:false,
      delBtnO:false,
      delBtnT:false,
      addMBtn:false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      pageNum2: 1,
      pageSize2: 10,
      total2: 0,
      dictType: "", //码值搜索类别
      dictCodes: "", //码值搜索值
      dictKey: "", //值码搜索key
      dictCode: "", //值码搜索值
      codeData: [],
      codeValMap: [],
      typeNameOptions: [], // 类别
      typeNameOptions2:[],
      keyOptions: [], //key
      keyOptionsId:'',
      typeNameOptionsArr: [],
      searchStrategy: "",
      searchType: "",
      searchCode: "",
      formLabelWidth: "120px",
      activeName: "first",
      codeVal: false,
      otherVal: false,
      codeValMsg: false,
      amendType: false,
      dictionaryId: "",
      value: "",
      form: {
        region: ""
      },
      addCodeMes: {},
      codeMapMes: {},
      addType: "", //添加类别
      typeDia: [],
      typeName: "",
      typeObj:{},
      typeNameOptions2Res:'',
      valueKey:'',
      valueKey2:'',
      optionsKey:[],
      arr:[],
      newPage:'',
      newPage2:'',

    };
  },

  created() {
    this.initData();
    this.typeData();
    this.dataList();
    this.getUserPermissions();
  },

  methods: {
    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //==================================== 码值111数据展示 ====================================
    initData() {
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {
          typeId: this.dictType,
          dictValue: this.dictCodes,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }
        if(!this.jurisdiction.roles.includes('admin')){
          this.delBtnO = false;
          this.addJBtn = false; //添加键值Btn
          this.typeBtn = false; //类别管理Btn
        }else{
          this.delBtnO = true;
          this.addJBtn = true; //添加键值Btn
          this.typeBtn = true; //类别管理Btn
        }

        this.codeData = res.data.data.list;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },
    dataList(){
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {
          pageSize: 10000,
        }
      }).then(res => {
          var list = res.data.data.list;
          this.keyOptions = [];
          list.map(data => {
            this.keyOptions.push({ label: data.dictValue, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    //添加键值
    addCode() {
      this.codeVal = true;
    },
    //新建分类
    addtypeBtn() {
      this.codeVal=false;
      this.otherVal=true;
    },
    addCodeVal(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.typeNameOptions.map(data => {
          this.typeNameOptionsArr = data;
        });
        this.addCodeMes.typeId = this.typeNameOptionsArr.id;
        axios({
          method: "post",
          url: "/dict/save/dictionary",
          data: {
            dictValue: this.addCodeMes.dictValueOne, //码值
            dictName: this.addCodeMes.dictNamesDic, //  描述（中）
            typeId: this.typeNameOptions2Res
          }
        }).then(res => {
            if (res.data.code == 2000) {
              this.$refs[formName].resetFields();
              this.addCodeMes.dictValueOne = "";
              this.addCodeMes.dictNamesDic = "";
              this.typeNameOptions2Res = "";
              this.codeVal = false;
              this.initData();
              this.$message({
                message: "添加成功",
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          return false;
        }
      });

    },
    //添加类别
    addTypeBtn() {
      var flag = true;
      if(this.addType.length > 200){
        this.$message.info('输入长度不能大于200！');
        flag = false;
      }else{
        axios({
          method: "post",
          url: "/dict/save/type",
          data: {name: this.addType}
        }).then(res => {
          if(res.data.code == 2000){
            this.addType = "";
            this.initData();
            this.typeData();
          }else{
            this.$message({
            message: res.data.message,
            type: 'warning'
            });
          }
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      }
    },
    //添加类别
    addTypeData(){
      this.otherVal=true;
    },
    //展示类别
    typeData() {
      axios({
        method: "post",
        url: "/dict/query/type",
        data: {
          pageSize:100,
        }
      }).then(res => {
          this.typeDia = res.data.data.list;
          var list = res.data.data.list;
          this.typeNameOptions = [];
          this.typeNameOptions2 = [];
          this.typeNameOptions.push({ label:'全部', id: '' });
          list.map(data => {
            this.typeNameOptions.push({ label: data.name, id: data.id });
            this.typeNameOptions2.push({ label: data.name, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    //删除键值
    delCode(row) {
    this.$confirm(`此操作将永久删除该键值"${row.dictValue}", 是否继续?`, "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
        this.delCodeRes(row);
        this.$message({
          type: "success",
          message: "删除成功!"
        });
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },
    //删除确认
    delCodeRes(row) {
      axios({
        method: "post",
        url: "/dict/delete/dictionary",
        data: {
          id: row.id
        }
      }).then(res => {
        this.initData();
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },
    //编辑类别
    amendCode(row) {
      this.amendType = true;
      this.typeName = row.name;
      this.typeObj = row;
    },
    amendTypeBtn() {
      axios({
        method: "post",
        url: "/dict/save/type",
        data: {
          name: this.typeName,
          id: this.typeObj.id
        }
      }).then(res => {
        if (res.data.code == 2000) {
          this.amendType = false;
          this.typeData();
          this.initData();
        } else {
          this.$message({
            message: res.data.message,
            type: 'warning'
          });
        }
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    //删除类别
    delType(row) {
          this.$confirm(`此操作将永久删除该类别"${row.name}", 是否继续?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
            this.delTypeRes(row);
            this.typeData();
            this.$message({
              type: "success",
              message: "删除成功!"
            });
          }).catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });

    },
    //删除类别确认
    delTypeRes(row) {
      axios({
        method: "post",
        url: "/dict/delete/type",
        data: {
          id: row.id
        }
      }).then(res => {
          console.log(res, "删除类别");
          this.typeData();
          // this.initData();
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    //==================================== 值码222数据展示 ====================================
    slideList(){
      this.codeOptions();
    },
    handleClick(tab, index) {
      if (tab.index == 1) {
        this.codeValMapTwo();
        this.dataList();
        this.getUserPermissions2();
      }
    },
    //权限配置
    getUserPermissions2(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        if(this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },

    codeValMapTwo() {
      axios({
        method: "post",
        url: "/dict/query/item",
        data: {
          dictItemValue: this.dictCode,
          dictionaryId:this.valueKey,
          pageSize: this.newPage2 == '' ? 10 : this.newPage2,
          pageNum: this.pageNum2
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }

        if(!this.jurisdiction.roles.includes('admin')){
          this.delBtnT = false;
          this.addMBtn = false;
        }else{
          this.delBtnT = true;
          this.addJBtn = true; //添加键值Btn
          this.addMBtn = true; //类别管理Btn
        }

        this.codeValMap = res.data.data.list;
        this.pageSize2 = res.data.data.pageSize;
        this.total2 = res.data.data.total;
        this.optionsKey = [];
        this.optionsKey.push({ label:'全部', id: '' });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    codeOptions(){
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {pageSize: 6000}
      }).then(res => {
        var list = res.data.data.list;
        this.optionsKey = [];
        this.optionsKey.push({ label:'全部', id: '' });
        list.map(data =>{
          this.optionsKey.push({ label:data.dictValue, id: data.id });
        })
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },


    codeValMsgRes(formName) {
      this.$refs[formName].validate(valid => {
      if (valid) {
      axios({
        method: "post",
        url: "/dict/save/item",
        data: {
          dictItemValue: this.codeMapMes.dictValueTwo,
          dictItemName: this.codeMapMes.dictItemNameTwo,
          dictionaryId: this.valueKey2
        }
      }).then(res => {
        if (res.data.code == 2000) {
          this.$refs[formName].resetFields();
          this.codeValMsg = false;
          this.valueKey2 = '';
          this.codeMapMes.dictValueTwo = "";
          this.codeMapMes.dictItemNameTwo = "";
          this.codeValMapTwo();
          this.$message({
            type: "success",
            message: "添加成功!"
          });
        } else {
          this.$message({
            type: "info",
            message: res.data.message
          });
        }
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
      } else {
        console.log("error submit!!");
        return false;
      }
      });
    },
    //删除
    delCodeMap(row) {
      this.$confirm(`是否删除码值 "${row.dictItemValue}" ,如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$message({
          type: "success",
          message: "删除成功!"
        });
        this.delStrategy(row);
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },

    delStrategy(row) {
      axios({
        method: "post",
        url: "/dict/delete/item",
        data: {
          id: row.id
        }
      }).then(res => {
          console.log(res, "删除222成功");
          this.codeValMapTwo();
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    //类别管理验证输入长度
    validateT(){
      if(this.addType.length > 200){
        this.$message.info('输入长度不能大于200！');
      }
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    },
    handleSizeChange(val){
      this.newPage = val;
      this.initData();
    },
    handleCurrentChange2(val) {
      this.pageNum2 = val;
      this.codeValMapTwo();
    },
    handleSizeChange2(val){
      this.newPage2 = val;
      this.codeValMapTwo();
    },
    callOf(formName) {
      this.codeValMsg = false;
      this.$refs[formName].resetFields();
    },
    callOfCode(formName) {
      this.codeVal = false;
      this.$refs[formName].resetFields();
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
    tableRowClassName2(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
};
</script>

