<template>
  <div class="networkRelationship" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入名称进行查询" class="w200" v-model='searcName'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>

      <div style="margin-right:100px;" class="fr">
        <el-button type="primary addOne" class="" @click="addName()">添 加</el-button>
        <el-button type="primary addOne" class="" @click="renovate()">刷 新</el-button>
      </div>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="name" label="名称"></el-table-column>
      <el-table-column prop="path" label="path"></el-table-column>
      <el-table-column prop="aUrl" label="a中心地址" ></el-table-column>
      <el-table-column prop="bUrl" label="b中心地址" ></el-table-column>
      <el-table-column prop="description" label="描述" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn amendColor" size="small" @click="amendNet(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn delColor" size="small" @click="delNet(scope.$index,scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <el-dialog :title="titleTab" :visible.sync="creatname" size="tiny" style="top:-5%">
      <el-form :model="addJson" ref="addJson" class="demo-ruleForm">
         <el-form-item label="名称：" prop="name" :label-width="formLabelWidth" :rules="[{ required:true, message: '名称不能为空'}]">
          <el-input class="w9000" v-model="addJson.name"></el-input>
        </el-form-item>

        <el-form-item label="path：" prop="path" :label-width="formLabelWidth" :rules="[{ required:true, message: 'path不能为空'}]">
          <el-input class="w9000" v-model="addJson.path"></el-input>
        </el-form-item>

        <el-form-item label="a中心地址：" prop="aUrl" :label-width="formLabelWidth" :rules="[{ required:true, message: 'a中心地址不能为空'}]">
          <el-input class="w9000" v-model="addJson.aUrl"></el-input>
        </el-form-item>

        <el-form-item label="b中心地址：" prop="bUrl" :label-width="formLabelWidth">
            <el-input class="w9000" v-model="addJson.bUrl"></el-input>
          </el-form-item>

        <el-form-item label="描述：" prop="description" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="addJson.description" class="w9000" :rows="4" auto-complete="off"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="creatname = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addList('addJson')">确 定</el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'findApi',
  components: {

  },
  data() {
    return {
      formLabelWidth: "120px",
      titleTab:'创建',
      searcName:'',
      jurisdiction:{},
      creatname:false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      listLoading: false,
      tablegroup: [],
      newPage:'',
      addJson:{
        name:'',
        path:'',
        aUrl:'',
        bUrl:'',
        description:'',
      },
      delData:{},

      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  },

  created() {
    this.getUserPermissions();
    this.initData();
  },

  methods: {

    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化与搜索框查询
    initData() {
      axios({
        method: 'post',
        url: '/routing/index',
        data: {
          name:this.searcName,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        this.tablegroup = res.data.data.elements;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //添加
    addName(){
      this.titleTab = '创建';
      this.creatname = true;
    },
   addList(formName){
    this.$refs[formName].validate(valid => {
    if (valid) {
    axios({
        method: 'post',
        url: this.titleTab == '创建'? '/routing/add' : '/routing/update',
        data: {
          name:this.addJson.name,
          path: this.addJson.path,
          aUrl: this.addJson.aUrl,
          bUrl: this.addJson.bUrl,
          description: this.addJson.description == null ? '' : this.addJson.description,
          id:this.titleTab == '编辑'?this.addJson.id:'',
        }
      }).then(res => {
        if(res.data.code == 2000){
          this.$refs[formName].resetFields();
          this.creatname = false;
          this.addJson = {};
          this.initData();
        }else{
          this.creatname = true;
          this.$message({
            type:"info",
            message:res.data.message
          });
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
      } else {
        console.log("error submit!!");
          return false;
        }
      });
   },
   renovate(){
    axios({
        method: 'post',
        url: '/gateway/routeFresh',
        data: {}
      }).then(res => {
        if(res.data.code == 2000){
          this.$message({
            type: "success",
            message: "刷新成功"
          });
        }else{
          this.$message({
          type: "info",
          message: "刷新失败"
        });
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
    });
   },
   //编辑
   amendNet(row){
    this.titleTab = '编辑';
    this.creatname = true;
    this.addJson = row;
   },

   //删除
   delNet(index, row){
    this.delData = row;
    console.log(row);
    this.$confirm(`是否删除 "${row.name}",如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          this.delMsg();
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
   },
   delMsg(){
    axios({
        method: 'post',
        url: '/routing/delete',
        data: {
          id:this.delData.id,
        }
      }).then(res => {
        if(res.data.code == 2000){
          this.searcName = '';
          this.initData();
          this.$message({
            type: "success",
            message: "删除成功"
          });
        }else{
          this.$message({
            type: "info",
            message: "删除失败"
          });
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
    });
   },

    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();


    },
    handleSizeChange(val) {
      this.newPage = val;
      this.initData();
    },
  }

}
</script>

<style scoped>

</style>



