<template>
  <div class="cy-fast-menu" :style="{height: bodyHeight + 'px', width: isCollapse ? '65px' : '180px'}">
    <el-menu class="menu-left" :default-active="activeName" :collapse="isCollapse" :default-openeds="openNames" @select="selectMenu">
      <template  v-for="firstItem in leftColumnItems">
        <el-submenu :index="firstItem.id + ''"  :key="firstItem.id" v-if="firstItem.children && firstItem.children.length > 0">
          <!--<i class="el-icon-setting"></i>-->
          <template slot="title">
            <!--<i v-if="firstItem.iconCode !== '' && firstItem.iconCode != null" :class="firstItem.iconCode"></i>-->
            <span v-if="firstItem.iconCode !== ''" :class="firstItem.iconCode" aria-hidden="true"></span>
            <span>{{firstItem.name}}</span>
          </template>
          <template v-for="secodItem in firstItem.children">
            <el-submenu :class="{'is-active': activeName === secodItem.id + '' }"   :index="secodItem.id + ''" :key="secodItem.id" v-if="secodItem.children && secodItem.children.length > 0">
              <template slot="title">{{secodItem.name}}</template>
              <el-menu-item v-for="thirdItem in secodItem.children" :key="secodItem.id" :id="secodItem.id" :path="secodItem.urls" :index="thirdItem.id + ''">{{thirdItem.name}}</el-menu-item>
            </el-submenu>
            <el-menu-item :index="secodItem.id + ''"  :id="secodItem.id" :path="secodItem.urls" v-else>
              <span>{{secodItem.name}}</span>
            </el-menu-item>
          </template>
        </el-submenu>
        <el-menu-item :class="{'is-active': activeName === firstItem.id + '' }" :index="firstItem.id + ''" :id="firstItem.id" :path="firstItem.urls" v-else>
          <!--<i v-if="firstItem.iconCode !== '' && firstItem.iconCode != null" :class="firstItem.iconCode"></i>-->
          <span v-if="firstItem.iconCode !== ''" :class="firstItem.iconCode" aria-hidden="true"></span>
          <span slot="title">{{firstItem.name}}</span>
        </el-menu-item>
      </template>
    </el-menu>
    <div style="display: none">{{leftColumnItems.length + activeColumn.id}}</div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        openNames: [],
        columnItems: [],
        activeName: '12'
      }
    },
    computed: {
      isCollapse() {
        return this.$store.getters.isCollapse
      },
      bodyHeight() {
        return this.$store.getters.bodyHeight
      },
      leftColumnItems() {
        //设置openNames ，展开所有，但不包含没有子item的column
        let items = this.$store.getters.commonCacheData('leftColumnItems')
        //检查是否是同一个items 因为openNames在计算属性中增加的，iview修改展开项是会触发事件，必须比对一下
        if (this.columnItems === items) {
          return this.columnItems;
        }
        this.columnItems = items
        this.openNames = []
        let column = this.$store.getters.commonCacheData('activeColumn')
        console.log(column)
        if (column && column.parentIds.split('/').length === 4) { //展开二级菜单
          items.forEach((value) => {
            if (value.children && value.children.length > 0 && column.parentId === value.id) {
              this.openNames = [value.id+ '']
              console.log(this.openNames)
            }
          })
        } else if (column && column.parentIds.split('/').length === 5) { //展开三级菜单
          items.forEach((value, index) => {
            if (value.children && value.children.length > 0) {
              value.children.forEach(list => {
                if (list.children && list.children.length > 0 && column.parentId === list.id) {
                  this.openNames = [items[index].id+ '', list.id+ '']
                }
              })
            }
          })
        } else {
          this.openNames = []
        }
//        //设置下个tick更新视图
//        this.$nextTick(() => {
//          this.$refs.menu.updateOpened()
//          this.$refs.menu.updateActiveName()
//        });
        return items
      },
      activeColumn() {
        let column = this.$store.getters.commonCacheData('activeColumn')
        console.log(column)
        if (column) {
          this.activeName = column.id + ''
        }
        if (!column) column = {}
        return column
      }
    },
    watch: {
      $route(to, from) {
        let items = this.$store.getters.commonCacheData('leftColumnItems')
        this.openNames = []
        let column = this.$store.getters.commonCacheData('activeColumn')
        console.log(column)
        if (column && column.parentIds.split('/').length === 4) { //展开二级菜单
          items.forEach((value) => {
            if (value.children && value.children.length > 0 && column.parentId === value.id) {
              this.openNames = [value.id + '']
              console.log(this.openNames)
            }
          })
        } else if (column && column.parentIds.split('/').length === 5) { //展开三级菜单
          items.forEach((value, index) => {
            if (value.children && value.children.length > 0) {
              value.children.forEach(list => {
                if (list.children && list.children.length > 0 && column.parentId === list.id) {
                  this.openNames = [items[index].id+ '', list.id+ '']
                }
              })
            }
          })
        }
      }
    },
    methods: {
      selectMenu(id) {
        let jsDom = document.getElementById(id)
        let path = jsDom && jsDom.getAttribute('path')
        !!path && this.$router.push(path)
        this.activeName = id + ''
      }
    }
  }
</script>
<style>
  .cy-fast-menu{
    height: calc(100vh - 60px);
    width: 180px;
    background: #6C6C6C;
  }
  .menu-left{
    background: #6C6C6C;
  }
  .menu-left .el-submenu .el-menu-item{
    min-width: 180px;
  }
  .menu-left .el-menu-item, .el-submenu__title {
    color: #fff;
  }
  .menu-left .el-menu-item.is-active{
    color: #febd49;
    background: #323232;
    border-right: 3px solid #febd49;
    position: relative;
  }
  .menu-left li.el-menu-item.is-active:after {
    content: "";
    display: inline-block;
    position: absolute;
    width: 5px;
    height: 5px;
    border-top: #febd49;
    border-left: #febd49;
    background: #febd49;
    right: -3px;
    top: 22px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }
  .menu-left .el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item:hover,
  .menu-left .el-menu--horizontal.el-menu--dark .el-submenu .el-submenu-title:hover,
  .menu-left .el-menu-item:hover{
    color: #FEBD49;
    background: #393D40;
  }
  .menu-left .el-submenu div{
    color: #fff;
  }
  .menu-left .el-submenu .el-menu{
    background: #646464;
  }
  .menu-left .el-submenu__title:hover {
    background: #393D40;
    color: #FEBD49;
  }
  .menu-left .el-submenu .el-menu-item:hover, .el-submenu__title:hover{
    background: #393D40;
    color: #FEBD49;
  }
 .menu-left.el-menu--collapse > .el-menu-item span.glyphicon,
 .menu-left.el-menu--collapse > .el-submenu > .el-submenu__title span.glyphicon{
    height: auto;
   width: auto;
   visibility: visible;
  }
</style>



