<template>
  <div class="portName">
    <div class="addChild" :data="rowFlowData">
      <div v-for="item2 in subApisData">
        <b class="childSpan fl">{{item2.interfaceName}}</b>
        <el-button type="text" size="small" v-if="parentMsg[0] == 'admin'" :class="item2.id == null ? 'childDelBtn fr delColor mt15' : 'radiusBtn fr delColor mt15' " @click="delChildBtn(item2)">删除</el-button>
        <router-link :to="{path:'portApiManage/portApiManage',query:{num:1,item2,rowFlowData}}">
          <button type="text" v-if="parentMsg[0] == 'admin'" class="radiusBtn fr amendColor mt15" size="small" v-show="item2.id !==null">编辑</button>
          <button type="text" v-if="parentMsg[0] == 'admin'" class="radiusBtn fr amendColor mt15" size="small" v-show="item2.id ==null">编辑</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'portName',
  props: ["subApisData", "rowFlowData","apiFlowData","parentMsg"],
  data(){
    return{
      jurisdiction:{},
    }
  },
  created() {
    // this.getUserPermissions();
  },
  methods: {
    //权限配置
    // getUserPermissions(){
    //   axios({
    //     method: "post",
    //     url: "/getCasUser",
    //     data: {}
    //   }).then(res => {
    //     this.jurisdiction  = res.data;

    //   }).catch(error => {
    //     console.log("网络错误");
    //   });
    // },
    delChildBtn(data) {
      this.$confirm(`是否删除后端服务 "${data.interfaceName}",如删除则不可恢复!`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.delChild(data);
      }).catch((error) => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    delChild(data) {
      axios({
        method: 'post',
        url: '/api/back/sub/delete',
        data: {
          apiId: data.apiId,
          id: data.id
        }
      }).then(res => {
        console.log(data,111);
        this.$message({
          type: 'success',
          message: '删除分组成功'
        });
        this.apiFlowData();
      }).catch(error => {
        console.log('网络错误');
      })
    },
  }
}
</script>
<style scoped>
.addChild .childSpan {
  width: 79%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: normal;
}

.childDelBtn {
  display: none;
}
</style>



