<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入appKey进行查询" class="w350" v-model='searchForm.appKey'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="searchData()">搜 索</el-button>
        <el-button type="primary addOne" class="searchOne" @click="createCustomer = true,titleText = '新建客户'">新 建</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tableList" style="width: 100%;border-top:8px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
      <el-table-column prop="" type="index" label="序号"></el-table-column>
      <el-table-column prop="appKey" label="appKey"></el-table-column>
      <el-table-column prop="appName" label="客户名称"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="updateTime" label="更新时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="balance" label="余额" :formatter="dataFixed"></el-table-column>
      <el-table-column prop="strategyUuid" label="计费规则UUID" v-if="false"></el-table-column>
      <el-table-column prop="strategyName" label="计费规则"></el-table-column>
      <el-table-column prop="stock" label="调用余量"></el-table-column>
      <el-table-column prop="price" label="价格"></el-table-column>
      <el-table-column prop="description" label="备注" v-if="false"></el-table-column>
      <el-table-column label="充值操作">
        <template slot-scope="scope">
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="recharge(scope.row)">充值</el-button>
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="initDataChargeRecord(scope.row)">记录</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="150px">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn pubColor" size="small" @click="balanceCustomer(scope.row)">余额记录</el-button>
          <el-button type="text" class="radiusBtn pubColor" size="small" @click="updateCustomer(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn pubColor" size="small" @click="deleteCustomer(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>
    <div class="page" style="padding-bottom:30px;" v-if="this.tableList.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="searchForm.pageNum"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="searchForm.pageSize"
                     layout=" sizes, prev, pager, next, jumper"
                     :total="totalCount"></el-pagination>
    </div>
    <!-- 新建 start -->
    <el-dialog :title="titleText" :visible.sync="createCustomer" size="tiny" style="top:-5%" @close="closeDialog">
      <el-form :model="addGroupData" :rules="rules" ref="addGroupData" class="demo-ruleForm">
        <el-form-item label="appKey：" prop="appKey" :label-width="formLabelWidth" :rules="[{required:true, message: 'appKey不能为空'}]">
          <el-input class="w9000" v-model="addGroupData.appKey" :disabled="this.titleText !== '新建客户'"></el-input>
        </el-form-item>
        <el-form-item label="客户名称：" prop="appName" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.appName"></el-input>
        </el-form-item>
        <el-form-item label="描述：" prop="description" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.description"></el-input>
        </el-form-item>
        <el-form-item label="余额：" prop="balance" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.balance"></el-input>
        </el-form-item>
        <el-form-item label="计费规则：" prop="strategyUuid" :label-width="formLabelWidth">
          <el-select class="w9000" filterable v-model="addGroupData.strategyUuid" placeholder="请选择计费规则">
            <el-option :key="'1'" :label="'清除计费规则'" :value="''"></el-option>
            <el-option v-for="item in billingRules"
                       :key="item.uuid"
                       :label="item.name"
                       :value="item.uuid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="价格：" prop="price" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.price"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="createCustomer = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="createCustomerCommit()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- recharge start -->
    <el-dialog :title="titleText" :visible.sync="rechargeFlag" size="tiny" style="top:-5%">
      <el-form :model="rechargeData" ref="rechargeData" class="demo-ruleForm">
        <el-form-item label="appKey：" prop="appKey" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="rechargeData.appKey" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="金额：" prop="amount" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="rechargeData.amount"></el-input>
        </el-form-item>
        <el-form-item label="备注：" prop="remark" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="rechargeData.remark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="rechargeFlag = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="rechargeCommit()">确 定</el-button>
      </div>
    </el-dialog>

    <!--charge record-->
    <el-dialog :title="chargeRecordTitleText" :visible.sync="chargeRecordFlag" size="small" style="top:-5%" @close="chargeRecordFlag = false">
      <el-table :data="chargeRecordList" style="width: 100%;border-top:8px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
        <el-table-column prop="" type="index" label="序号"></el-table-column>
        <el-table-column prop="amount" label="金额"></el-table-column>
        <el-table-column prop="createTime" label="充值时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="remark" label="备注"></el-table-column>
      </el-table>
      <div class="teCen mt60" v-if="this.chargeRecordList.length < 1">
        <p><img src="../../assets/Shape.png" /></p>
        <span>暂无数据</span>
      </div>
      <div class="page" style="padding-bottom:30px;" v-if="this.chargeRecordList.length > 0">
        <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChangeCharge" @current-change="handleCurrentChangeCharge"
                       :current-page="pageNum_charge"
                       :page-sizes="[10, 20, 30, 40]"
                       :page-size="pageSize_charge" layout=" sizes, prev, pager, next, jumper"
                       :total="totalCount_charge">
        </el-pagination>
      </div>
    </el-dialog>
    <el-dialog title="详情" :visible.sync="showBalanceDialog" size="large" style="top:-5%;" @close="dialogBalanceClose">
      <el-table ref="multipleTable" :data="tableBalanceData" tooltip-effect="dark"
                style="width: 100%;border-bottom: 1px solid #e6e6e6; border-right: 1px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="appKey" label="客户"></el-table-column>
        <el-table-column prop="balance" label="余额"></el-table-column>
        <el-table-column prop="stock" label="余量"></el-table-column>
        <el-table-column prop="createTime" label="日期"  :formatter="dateFormat"></el-table-column>
      </el-table>
      <div class="page" style="padding-bottom:30px;" v-if="this.tableBalanceData.length > 0">
        <el-pagination style="text-align:right;margin-top:30px;" @size-change="handleSizeBalanceChange"
                       @current-change="handleCurrentBalanceChange"
                       :current-page="this.balanceSearch.pageNum"
                       :page-sizes="[10, 20, 30, 40]"
                       :page-size="this.balanceSearch.pageSize" layout=" sizes, prev, pager, next, jumper"
                       :total="tableBalanceTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'

  export default {
    name: 'findApi',
    components: {

    },
    data() {
      return {
        rules:{
          appKey: [{required:true, message: 'appKey不能为空'}],
          appName: [{required:true, message: '客户名称不能为空'}],
          description: [{required:true, message: '请输入备注信息'}],
          balance: [{required:true, type: 'number', message: '余额不能为空', trigger: 'change'}],
          price: [{required:true, message: '金额/单价不能为空'}]
        },
        jurisdiction:{},
        addGroupData:{},
        rechargeData:{},
        chargeRecordData:{},
        index:0,
        billingRules: [],
        titleText:'',
        chargeRecordTitleText:'充值记录',
        searchForm: {
          pageNum: 1,
          pageSize: 10,
          appKey: ''
        },
        createCustomer:false,
        rechargeFlag:false,
        chargeRecordFlag:false,

        totalCount: 1,

        pageNum_charge: 1,
        pageSize_charge: 10,
        totalCount_charge: 1,
        chargeRecordSearchForm: {
          appKey: '',
          appName: '',
          pageNum: 1,
          pageSize: 10
        },

        tableList: [],
        chargeRecordList: [],
        formLabelWidth: "120px",
        showBalanceDialog: false,
        tableBalanceData: [],
        balanceSearch: {
          appKey: '',
          pageSize: 10,
          pageNum: 1
        },
        tableBalanceTotal: 0,
        dateFormat(row, column) {
          const date = row[column.property];
          if (date === undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      }
    },

    created() {
      this.utils.getUserPermissions(this);
      this.utils.getBillingRules(this);
      this. initData();
    },

    methods: {
      //初始化
      initData() {
        axios({
          method: 'post',
          url: ' /gateway/charge/customer/list',
          data: this.searchForm
        }).then(res => {
          if (res.data.code === 2000) {
            this.tableList = res.data.data.list;
            this.totalCount = res.data.data.total;
          } else {
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问' + error);
        });
      },
      //搜索框查询
      searchData() {
        this.searchForm.pageNum = 1;
        this.initData();
      },

      //新建
      createCustomerCommit(){
        if (this.titleText === '新建客户') {
          delete this.addGroupData.id
        }
        axios({
          method: 'post',
          url: this.titleText === '新建客户' ? '/gateway/charge/customer/add' :'/gateway/charge/customer/update',
          data: this.addGroupData
        }).then(res => {
          if(res.data.code === 2000){
            this.createCustomer = false;
            this.searchData();
            this.addGroupData = {};
            this.titleText = '';
            this.$message({type: "success",message: this.titleText === '新建客户' ? '新建成功' : '编辑成功'});
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //编辑
      updateCustomer(row){
        this.createCustomer = true;
        this.titleText = '编辑客户';
        this.addGroupData = row;
        console.log(row)
        // this.addGroupData.id = row.id;
        // this.addGroupData.appKey = row.appKey;
        // this.addGroupData.appName = row.appName;
        // this.addGroupData.description = row.description;
        // this.addGroupData.balance = row.balance;
        // this.addGroupData.strategyName = row.strategyName;
        // this.addGroupData.strategyUuid = row.strategyUuid;
        // this.addGroupData.price = row.price;
      },
      //删除
      deleteCustomer(row){
        this.delIpData = row;
        this.$confirm(`是否删除 "${row.appName}",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          axios({
            method: 'post',
            url: '/gateway/charge/customer/delete',
            data: {
              id: row.id,
              appKey: row.appKey
            }
          }).then(res => {
            if(res.data.code === 2000){
              this.$message({type: "success",message: '删除appKey成功'});
              this.searchData();
            }else{
              this.$message({type: "info",message: res.data.message});
            }
          }).catch(error => {
            console.log('网络错误，不能访问' + error);
          });
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },

      recharge(row) {
        this.rechargeFlag = true;
        this.titleText = '客户充值';
        this.rechargeData.appKey = row.appKey;
      },
      rechargeCommit() {
        axios({
          method: 'post',
          url: '/gateway/charge/customer/recharge',
          data: this.rechargeData
        }).then(res => {
          if(res.data.code === 2000){
            this.rechargeFlag = false;
            this.$message({type: "success",message: '充值成功'});
            this.searchData();
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      searchChargeRecord() {
        axios({
          method: 'post',
          url: ' /gateway/charge/customer/chargeRecord',
          data: this.chargeRecordSearchForm
        }).then(res => {
          if (res.data.code === 2000) {
            this.chargeRecordList = res.data.data.list;
            this.totalCount_charge = res.data.data.total;
          } else {
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      initDataChargeRecord(row) {
        this.chargeRecordFlag = true;
        this.chargeRecordSearchForm.appName = row.appName;
        this.chargeRecordSearchForm.appKey = row.appKey;
        this.chargeRecordSearchForm.pageNum = this.pageNum_charge;
        this.chargeRecordSearchForm.pageSize = this.pageSize_charge;
        this.chargeRecordTitleText = '[' + this.chargeRecordSearchForm.appName + ']充值记录';

        this.searchChargeRecord();
      },

      closeDialog() {
        this.addGroupData = {};
      },

      //分页查询
      handleCurrentChange(val) {
        this.searchForm.pageNum = val;
        this.initData();
      },
      handleSizeChange(val) {
        this.searchForm.pageSize = val;
        this.searchData();
      },

      //充值分页查询
      handleCurrentChangeCharge(val) {
        this.chargeRecordSearchForm.pageNum = val;
        this.searchChargeRecord();
      },
      handleSizeChangeCharge(val) {
        this.chargeRecordSearchForm.pageSize = val;
        this.searchChargeRecord();
      },
      balanceCustomer(row) {
        this.showBalanceDialog = true
        this.balanceSearch.appKey = row.appKey
        this.handleCurrentBalanceChange(1)
      },
      dialogBalanceClose() {
        this.showBalanceDialog = false
      },
      balanceHandle() {
        axios({
          method: 'post',
          url: '/gateway/charge/customer/balance/history',
          data: this.balanceSearch
        }).then(res => {
          if(res.data.code === 2000){
            this.tableBalanceData = res.data.data.list
            this.tableBalanceTotal = res.data.data.total
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      handleSizeBalanceChange(size) {
        this.balanceSearch.pageSize = size
        this.handleCurrentBalanceChange(1)
      },
      handleCurrentBalanceChange(page) {
        this.balanceSearch.pageNum = page
        this.balanceHandle()
      },
      dataFixed(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return date.toFixed(2);
      }
    }

  }
</script>

<style scoped>

</style>



