<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <div class="box">
      <ul>
        <li><h3>{{freqLabel}}</h3><p>频率(笔/<span>{{uniTime}}</span>)</p></li>
        <li style="background-image: linear-gradient(90deg,#44b89f,#7ae0f9);
        background-color: #44b89f;"><h3>{{countLabel}}</h3><p>总量</p></li>
      </ul>
    </div>
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-date-picker v-model="startTime" style="width:200px" type="datetime" placeholder="请选择日期时间"></el-date-picker>
        <el-date-picker v-model="endTime" style="width:200px" type="datetime" placeholder="请选择日期时间" ></el-date-picker>
        <el-input placeholder="请输入apiId进行查询" class="w200" v-model='apiId'></el-input>
        <el-input placeholder="请输入appKey进行查询" class="w200" v-model='appKey'></el-input>

        <el-select v-model="cached" clearable  placeholder="是否缓存">
          <el-option v-for="item in cachedOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>

        <el-select v-model="resultStatus" clearable placeholder="是否成功" class="w200">
          <el-option v-for="item in resultStatusOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>

        <el-select v-model="timeUnit" clearable placeholder="频率单位" class="w200">
          <el-option v-for="item in timeUnitOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>

        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="apiId" label="APIID" width="80"></el-table-column>
      <el-table-column prop="apiName" label="API名称" width="280"></el-table-column>
      <el-table-column prop="" label="平均响应时间" :formatter="dateFormat">
        <template slot-scope="scope">
          <span>{{scope.row.responseTime}} ms</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalCount" label="总量"></el-table-column>
      <el-table-column prop="successData" label="成功量" ></el-table-column>
      <el-table-column prop="totalFail" label="失败量" ></el-table-column>
      <el-table-column prop="cachedData" label="缓存量"></el-table-column>
      <el-table-column prop="totalException" label="异常量"></el-table-column>
      <el-table-column prop="" label="成功率">
        <template slot-scope="scope">
          <el-progress :text-inside="true" :stroke-width="18" :percentage="scope.row.totalCount === 0 ? 0 : Math.round(scope.row.successData/scope.row.totalCount * 100)" :status="scope.row.totalCount === 0 ? 'exception' : scope.row.successData/scope.row.totalCount === 1 ? 'success' : 'exception' "></el-progress>
        </template>
      </el-table-column>
      <el-table-column prop="" label="频率">
        <template slot-scope="scope">
          <span>{{scope.row.frequency}} 笔/{{uniTime}}</span>
        </template>
      </el-table-column>
    </el-table>
    <!-- <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div> -->

  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'

  export default {
    name: 'findApi',
    components: {

    },
    data() {
      return {
        jurisdiction:{},
        startTime:new Date(),
        endTime:new Date(),
        apiId:'',
        appKey:'',
        cached:'',
        resultStatus:'',
        timeUnit:'',
        cachedOptions:[{ value: '', label: "空" },{ value: 1, label: "缓存" },{ value: 2, label: "未使用缓存" }],
        resultStatusOptions:[{ value: '', label: "空" },{value:1,label:'成功'},{value:2,label:'失败'}],
        timeUnitOptions:[{value:3,label:'时'},{value:2,label:'分'},{ value: 1, label: "秒" }],

        page: 1,
        listLoading: false,
        tablegroup: [],
        newPage:'',
        formLabelWidth: "120px",
        freqLabel:0,
        countLabel:0,
        uniTime:'s',


        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      }
    },

    created() {
      // this. initData();
    },
    filters:{
      rounding(value){
        return value.toFixed(1);
      }
    },
    methods: {
      //初始化
      initData() {
        var starTimeDate = this.startTime;
        var starTime = new Date(starTimeDate);
        var starTimestemp = starTime.getTime();

        var endTimeDate = this.endTime;
        var endTime = new Date(endTimeDate);
        var endTimestemp = endTime.getTime();
        axios({
          method: 'post',
          url: '/strategy/interfaceCount',
          data: {
            startTime:starTimestemp,
            endTime:endTimestemp,
            apiId:this.apiId,
            appKey:this.appKey,
            cached:this.cached,
            resultStatus:this.resultStatus,
            timeUnit:this.timeUnit
          }

        }).then(res => {
          if(res.data.code !== 2000){
            this.$message({type: "info",message: res.data.message});
          }else{
            this.tablegroup = res.data.data.dataList;
            this.currPage = res.data.data.currPage;
            this.pageSize = res.data.data.pageSize;
            this.total = res.data.data.totalCount;
            this.freqLabel = res.data.data.freq;
            this.countLabel = res.data.data.count;
            if(this.timeUnit == 1 || this.timeUnit == ''){
              this.uniTime = 's';
            }else if(this.timeUnit == 2){
              this.uniTime = 'm';
            }else{
              this.uniTime = 'h';
            }
          }

        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },


      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      handleCurrentChange(val) {
        this.currPage = val;
        this.initData();


      },
      handleSizeChange(val) {
        this.newPage = val;
        this.initData();
      }

    }

  }
</script>

<style scoped>
  .box{
    padding: 20px;
    left: 50%;
    position: relative;
    margin-left: -370px;
    width: 745px;
  }
  .box ul li{
    width:250px;
    height: 120px;
    border-radius: 6px;
    padding: 20px;
    color: #fff;
    background-image: linear-gradient(90deg,#3c95d3,#7ac2f9);
    background-color: #3c95d3;
    float: left;
    margin-right: 30px;
    text-align: center;
  }
</style>



