<template>
  <div>
    <el-form :model="groupHerfId" ref="groupHerfId" class="demo-ruleForm">
      <span style="font-weight:700;">您将要发布以下API：</span>
      <div class="issueAPI">{{groupHerfId.apiName}}</div>

      <!--<p style="font-weight:700;">请选择要发布的环境：</p>-->

      <!--<el-radio-group v-model="env">-->
        <!--<el-radio-button label=1>线上</el-radio-button>-->
        <!--<el-radio-button label=2>预发</el-radio-button>-->
      <!--</el-radio-group>-->

      <el-form-item prop="pubDescription" label="请填写变更备注：" :rules="[{required:true,message:'备注不能为空'},{validator:issueEmpty, trigger: 'blur' },{ min:1, max:180, message:'长度在 1 到 180 个字符',trigger:'blur'}]">
        <el-input type="textarea" v-model="groupHerfId.pubDescription" :rows="4" placeholder="必填，不超过180个字符" auto-complete="off"></el-input>
      </el-form-item>
      <p class="textRed">如果您设置了使用Mock，实际请求则不会调用到后端服务， 请仔细确认</p>
      <p>该操作将导致线上环境的该API被覆盖，请仔细确认</p>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button class="cancelBtn" @click="$emit('cancle', '')">取 消</el-button>
      <el-button type="primary" class="addOne" @click="publishApi('groupHerfId')">发布</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'publishApi',
  props: ["groupHerfId", "issue"],

  data() {

    return {
      env: 1,
      //发布限制不能输入空格
      issueEmpty: (rule, value, callback) => {
        var reg = /\s+/;
        if (reg.test(value)) {
          callback(new Error('不能输入空格'));
        } else {
          callback();
        }
      },

    }
  },
  created() {

  },
  methods: {

    publishApi(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const apiGroupId = this.groupHerfId.apiGroupId;
          const id = this.groupHerfId.id;
          const pubDescription = this.groupHerfId.pubDescription;

          axios({
            method: 'post',
            url: '/api/publish',
            data: {
              apiGroupId,
              id,
              env: this.env,
              pubDescription: pubDescription,
            }
          }).then(res => {
            this.$emit('issueOK', '')
            this.$message({
              type: 'success',
              message: '发布成功'
            });
            this.env = 1;
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },

  }
}
</script>

<style scoped>

</style>



