<template>
  <div>
    <el-form v-if="addApiData.requestParamsList.length > 0">
      <el-form-item v-if="addApiData.requestParamsList[dataStrs].paramsType == 1" label="最大长度" :label-width="formLabelWidth">
        <el-input v-model="addApiData.requestParamsList[dataStrs].maxLength" class="w2000" type="number" @change="maxLine(addApiData.requestParamsList[dataStrs].maxLength)"></el-input>
        <p style="color:red;" v-if="isMinGreater">最大长度不能小于等于最小长度</p>
      </el-form-item>

      <el-form-item v-if="addApiData.requestParamsList[dataStrs].paramsType == 1" label="最小长度" :label-width="formLabelWidth">
        <el-input v-model="addApiData.requestParamsList[dataStrs].minLength" type="number" class="w2000"></el-input>
      </el-form-item>

      <el-form-item label="参数验证" :label-width="formLabelWidth">
        <el-input type="textarea" v-model="addApiData.requestParamsList[dataStrs].regularExpress" class="w350" :rows="4"></el-input>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer fr">
      <el-button class="cancelBtn" @click="$emit('cancleAmendApi', '')">取 消</el-button>
      <el-button type="primary" @click="lineValue">确 定</el-button>
      <p style="clear:both"></p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'amendApi',
  props: ["addApiData","dataStrs"],

  data() {

    return {
      formLabelWidth: '125px',
      amendApiIndex: 0,
      isMinGreater: false,
    }
  },
  created() {
  },
  methods: {
    

    maxLine(value) {
      for (var i = 0; i < this.addApiData.requestParamsList.length; i++) {
        if (parseInt(this.addApiData.requestParamsList[i].maxLength) < parseInt(this.addApiData.requestParamsList[i].minLength))
          this.isMinGreater = true;
        else
          this.isMinGreater = false;
      }
    },

    lineValue() {
      let flag = true;
      for (let i = 0; i < this.addApiData.requestParamsList.length; i++) {
        if (parseInt(this.addApiData.requestParamsList[i].maxLength) < parseInt(this.addApiData.requestParamsList[i].minLength)) {
          this.isMinGreater = true;
          flag = false;
        }
      }
      if (flag) {
        this.isMinGreater = false;
        this.$emit('confirm', "");
      }
    },

  }
}
</script>


<style scoped>

</style>



