import axios from 'axios'


export default {
  /**
   * 获取权限信息
   * @param component
   */
  getUserPermissions: (component) => {
    axios({
      method: "post",
      url: "/getCasUser",
      data: {}
    }).then(res => {
      component.jurisdiction = res.data;
      if (component.jurisdiction.perms.length === 0 || component.jurisdiction.perms == null) {
        component.$confirm('权限不足，请联系管理员', '提示', {
          confirmButtonText: '退出',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          location.href = res.data.logoutUrl;
        }).catch(() => {
          component.$message({
            type: 'info',
            message: '权限不足，请联系管理员'
          });
        });
      }
    }).catch(error => {
      console.log("网络错误:" + error);
    });
  },

  /**
   * 获取计费规则
   * @param component
   */
  getBillingRules: (component) => {
    axios({
      method: 'post',
      url: ' /gateway/charge/billingRules/chargeNameList',
      data: {}
    }).then(res => {
      if (res.data.code === 2000) {
        component.billingRules = res.data.data;
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log('获取收费规则失败: ' + error);
    });
  },
  /**
   * 表格样式
   * @param row
   * @param index
   * @returns {string}
   */
  tableRowClassName(row, index) {
    if (index % 2 !== 0) {
      return 'info-row';
    }
    return '';
  },

  /**
   * 获取API分组下拉列表
   * @param component
   */
  getGroupAry: (component) => {
    axios({
      method: 'post',
      url: '/apiGroup/listOfNoLowerLevelGroup',
      data: {}
    }).then(res => {
      if (res.data.code === 2000) {
        component.groupAry = res.data.data
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log('获取API分组下拉列表出错: ' + error);
    });
  },

  /**
   * 获取groupId分组下的所有API
   * @param component
   * @param groupId
   */
  getApiAry: (component, groupId) => {
    if (!groupId) return;
    axios({
      method: 'post',
      url: '/api/findApiByGroupId',
      data: {id: groupId}
    }).then(res => {
      if (res.data.code === 2000) {
        component.apiAry = res.data.data
      } else {
        component.$message({type: "info", message: res.data.message});
      }
    }).catch(error => {
      console.log('获取groupId分组下的API失败: ' + error);
    });
  },
}



