<template>
  <div class="TestApi" style="background:#ffffff;height:100%;">
  <el-tabs v-model="activeName" type="card" @tab-click="handleTabClick" style="background:#ffffff;height:100%;">
    <el-tab-pane label="日账单" name="day">
      <customer-bill-day></customer-bill-day>
    </el-tab-pane>
    <el-tab-pane label="月账单" name="month">
      <customer-bill-month></customer-bill-month>
    </el-tab-pane>
    <el-tab-pane label="年账单" name="year">
      <customer-bill-year></customer-bill-year>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>

<script>
  import axios from 'axios'
  import customerBillDay from '@/components/page/customerBillDay'
  import customerBillMonth from '@/components/page/customerBillMonth'
  import customerBillYear from '@/components/page/customerBillYear'

  export default {
    name: 'customerBill',
    components: {
      customerBillDay, customerBillMonth, customerBillYear
    },
    data() {
      return {
        activeName: 'day',
        jurisdiction:{
          principal: {},
          loginUrl: '',
          logoutUrl: '',
          roles: [],
          perms: []
        },
      }
    },

    created() {
      this.$nextTick(() => {
        this.getUserPermissions();
      })
    },
    mounted() {

    },
    methods: {
      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length === 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误: " + error);
        });
      },

      handleTabClick(tab) {
        console.log(tab.name)
      }
    }

  }
</script>

<style scoped>
  .card_statistical{
    padding: 5px 15px;
  }
  .grid-content{
    border: 1px solid #eaeefb;
    background: #fff;
    border-radius: 4px;
    min-height: 100px;
    display: flex;
    font-size: 0;
  }
  .grid-content:hover{
    box-shadow: 0 0  2px rgba(0, 0, 0, 0.2);
  }
  .grid-content>div{
    font-size: 18px;
    vertical-align: middle;
  }
  .canvasBox{
    text-align: right;
    flex: 0 0 50px;
  }
  .grid-content>div.canvasBoxTotal{
    flex: 1;
    padding: 0;
    margin-top: 10px;
  }
  .canvasBoxText{
    flex: 0 0 102px;
  }
  .grid-content>div.text{
    flex: 1;
    text-align: left;
    padding: 20px 10px;
    font-weight: bolder;
  }
  .text>p:first-child{
    font-weight: normal;
  }
</style>



