<template>
  <div class="flowControl" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入API名称进行查询" class="w350" v-model="searchStrategy"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="handleCurrentChange(1)">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table ref="multipleTable" :data="flowData" tooltip-effect="dark" style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="tableRowClassName">
      <el-table-column prop="apiName" label="API名称"></el-table-column>
      <el-table-column prop="strategyName" label="绑定策略"></el-table-column>
      <el-table-column prop="limitStrategyTotal" label="接口总量"></el-table-column>
      <el-table-column prop="updateTime" label="操作时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="" label="操作" width="140">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn bindColor" size="small" v-if="scope.row.limitStrategyuuid == null && bindBtn" @click="bindFlow(scope.row)">绑定策略</el-button>
          <el-button type="text" class="radiusBtn onBindColor" size="small" v-if="scope.row.limitStrategyuuid !== null && ovBindBtn" @click="removeBindFlow(scope.row)">解绑</el-button>
          <el-button type="text" class="radiusBtn amendColor" size="small" v-if="scope.row.limitStrategyuuid !== null && amendBtn" @click="amendCon(scope.row)">&#12288编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="flowData.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <el-dialog title="绑定策略" :visible.sync="bindFlowDialog" size="small" id="accreditBox" style="top:-5%">
      <div class="accreditLine"></div>
      <el-form>
        <div>
          <span>接口总量：
             <el-input style="width: 200px" placeholder="请输入接口总量" v-model='limitStrategyTotal'></el-input>
           </span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px;width: 100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="请输入策略名称" v-model='oderr'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="info searchOne" @click="getAppList()">搜索</el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="appList" tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto" @selection-change="handleSelectionChange">
              <el-table-column label="" width="40">
                <template slot-scope="scope">
                  <el-radio class="radio" v-model="radioVal" :label="scope.$index" @change.native="getCurrentRow(scope.row,scope.$index)"></el-radio>
                </template>
              </el-table-column>

              <el-table-column label="策略名称" prop="name"></el-table-column>
              <el-table-column prop="unit" label="限流单位" :formatter="unitFormat"></el-table-column>
              <el-table-column prop="no" label="限流次数" ></el-table-column>
            </el-table>
          </div>

        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="bindFlowDialog = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="setAuth()">确 定</el-button>
      </div>
    </el-dialog>

    <div class="page" style="padding-bottom:30px;" v-if="flowData.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                     :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
      <!-- <el-pagination style="text-align: right;margin-top:80px;" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination> -->
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  import menuListDetail from '@/components/common/menuListDetail'

  export default {
    name: 'flowControl',
    components: {
      menuListDetail
    },
    data() {
      //------------------必须以英文与汉字开头的正则------------------//
      let sNameRule1 = (rule, value, callback) => {
        let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
        if (regExp.test(value) === false) {
          callback(new Error('必须以英文字母或汉字开头'));
        } else {
          callback();
        }
      };
      //------------------必须是汉字、英文字母、数字、英文格式或下划线的正则------------------//
      let sNameRule2 = (rule, value, callback) => {
        let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
        if (regExp.test(value) === false) {
          callback(new Error('格式错误'));
        } else {
          callback();
        }
      };
      //------------------API流量限制自定义验证------------------//
      let sNo = (rule, value, callback) => {
        let regExp = /^[0-9]*[1-9][0-9]*$/;
        if (regExp.test(value) === false) {
          callback(new Error('必须是正整数'));
        } else {
          callback();
        }
      };
      //------------------API流量最大为10000000次------------------//
      let sNum = (rule, value, callback) => {
        if (value > 10000000) {
          callback(new Error('最大为10000000(一千万)次'));
        } else {
          callback();
        }
      };
      return {
        limitStrategyTotal: 1,
        jurisdiction:{},
        bindBtn:false,
        ovBindBtn:false,
        amendBtn:false,
        pageNum: 1,
        pageSize: 10,
        total: 5,
        pageNumA: 1,
        pageSizeA: 5,
        totalA: 5,
        appList: [],
        oderr: '',
        flowData: [],
        tableData3: [],
        group: '',
        value: '',
        searchStrategy: '',
        formLabelWidth: '120px',
        description: '',
        currentPage4: 4,
        delData: '',
        multipleSelection: [],
        bindFlowDialog: false,
        amendStrategyDialog: false,
        strategyUuid: '',
        radioVal: '',
        apiFlowArr: [],
        flowStr: {},
        flowObj:{},
        newPage:'',

        unitFormat(row, column) {
          const unit = row[column.property];
          if (unit == undefined) {
            return "";
          }
          return unit == 1 ? "分钟" : (unit == 2 ? "小时" : "天");
        },
        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        },
        ruleForm: {
          sName: '',
          sNo: '',
          sUnit: 1
        },
        rules: {
          sName: [
            { required: true, message: '请输入策略名称', trigger: 'blur' },
            { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
            { validator: sNameRule2, trigger: 'blur' }
          ],
          sNo: [
            { required: true, message: '请输入API流量限制', trigger: 'blur' },
            { validator: sNo, trigger: 'blur' },
            { validator: sNum, trigger: 'blur' }
          ],
          sUnit: [
            // { required: true, message: '请选择单位', trigger: 'change' }
          ]
        }

      };
    },

    created() {
      this.handleCurrentChange(1);
      this.getAppList();
      this.getUserPermissions();
//     this.menuData();
    },

    methods: {
      menuData(){
//      $(".el-menu-item").eq(5).removeClass('is-active');
//      $(".el-menu-item").eq(4).removeClass('is-active');
//      $(".el-menu-item").eq(3).addClass('is-active');
        $(".el-menu-item:eq(5),.el-menu-item:eq(6),.el-menu-item:eq(7),.el-menu-item:eq(8),.el-menu-item:eq(10),.el-menu-item:eq(9),.el-menu-item:eq(11)").removeClass('is-active');
        console.log($(".el-menu-item"));
      },
      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },

      bindFlow(row) {
        for(var i = 0; i<this.jurisdiction.perms.length; i++){
          if(this.jurisdiction.perms[i] == '08b354ee63474fb4a68aad7910c17823'){
            this.radioVal = '';
            this.bindFlowDialog = true;
            this.apiFlowArr = row;
            this.limitStrategyTotal = row.limitStrategyTotal
          }
        }
      },
      getCurrentRow(row, index) {
        console.log(row.uuid);
        this.flowObj = row;
      },
      //编辑策略
      amendCon(row) {
        this.radioVal = '';
        if(row.limitStrategyuuid !== null){
          for(var i = 0; i < this.appList.length; i++){
            // this.flowObj.uuid = row.limitStrategyuuid;
            if(row.limitStrategyuuid == this.appList[i].uuid){
              this.radioVal = i;
              break;
            }
          }
          this.flowObj.uuid = row.limitStrategyuuid
        }
        this.bindFlowDialog = true;
        this.apiFlowArr = row;
        this.limitStrategyTotal = row.limitStrategyTotal
      },

      //绑定策略
      setAuth() {
        let regExp = /^[0-9]*[1-9][0-9]*$/;
        if(regExp.test(this.limitStrategyTotal) === false) {
          this.$message.warning('接口流量必须为大于0的整数');
          return
        }
        if(this.radioVal === ''){
          axios({
            method: 'post',
            url: '/api/apiBindingStrategy',
            data: {
              id: this.apiFlowArr.id,
              limitStrategyuuid: this.flowObj.uuid==undefined ? '111' : this.flowObj.uuid,
              limitStrategyTotal: this.limitStrategyTotal
            }
          }).then(res => {
            if(res.data.code == 2000){
              this.bindFlowDialog = false;
              console.log(this.flowObj.uuid);
              this.handleCurrentChange(1);
            }else{
              this.$message.info('请选择限流策略');
            }
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        }else{
          axios({
            method: 'post',
            url: '/api/apiBindingStrategy',
            data: {
              id: this.apiFlowArr.id,
              limitStrategyuuid: this.flowObj.uuid==undefined ? this.flowObj.uuid : this.flowObj.uuid,
              limitStrategyTotal: this.limitStrategyTotal
            }
          }).then(res => {
            if(res.data.code == 2000){
              this.bindFlowDialog = false;
              this.handleCurrentChange(1);
            }else{
              this.$message.info('请选择限流策略');
            }
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        }

      },
      //解绑
      removeBindFlow(row) {
        this.apiFlowArr = row;
        this.$confirm('此操作将解绑该策略, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios({
            method: 'post',
            url: '/api/apiBindingStrategy',
            data: {
              id: this.apiFlowArr.id,
              limitStrategyuuid: null,
              limitStrategyTotal: -1
            }
          }).then(res => {
            this.$message({
              type: 'success',
              message: '解绑成功!'
            });
            this.flowData = [];
            this.bindFlowDialog = false;
            this.handleCurrentChange(1);
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消解绑'
          });
        });
      },

      //获取appList
      getAppList() {
        axios({
          method: 'post',
          url: '/strategy/getAllStrategy',
          data: {
            strategyName: this.oderr == '' ? null : this.oderr
          }
        }).then(res => {
          this.appList = res.data.data;
          this.flowStr.uuid = res.data.data[0].uuid;
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      //初始化列表
      dataInit() {
        axios({
          method: 'post',
          url: '/api/ApiInfoList',
          data: {
            apiName:this.searchStrategy == '' ? null : this.searchStrategy,
            pageSize: this.newPage == ''? 10 : this.newPage,
            pageNum: this.pageNum
          }
        }).then(res => {
          if(res.data.code == 403){
            location.href = res.data.data;
          }


          if(!this.jurisdiction.roles.includes('admin')){
            this.bindBtn = false;
            this.ovBindBtn = false;
            this.amendBtn = false;
          }else{
            this.bindBtn = true;
            this.ovBindBtn = true;
            this.amendBtn = true;
          }
          this.flowData = res.data.data.list;
          this.pageNum = res.data.data.pageNum;
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.total;

        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //分页查询
      handleCurrentChange(val) {
        this.pageNum = val;
        this.dataInit();
      },
      handleSizeChange(val){
        this.newPage = val;
        this.handleCurrentChange(1);
      },

      //复选框初始化时候是否可勾选
      checkboxInit(row, index) {
        const uuid = row.limitStrategyuuid;
        if (uuid != null && uuid != '')
          return 0;
        else
          return 1;
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .accreditLine {
    width: 100%;
    height: 1px;
    background: #ebebeb;
    position: relative;
    top: -15px;
  }

  .active {
    color: white;
    background: #546478
  }

  #accreditBox {
    min-width: 1200px;
  }

  .flBox {
    width: 73%;
  }

  .addBtn {
    width: 60px;
    height: 28px;
    color: white;
    background: #20a0ff;
    display: inline-block;
    text-align: center;
    outline: none;
  }

  .addedBtn {
    width: 60px;
    height: 28px;
    color: #bcbcbc;
    background: #f7f7f7;
    display: inline-block;
    text-align: center;
    outline: none;
    border: 1px solid #e4e4e4;
  }
</style>



