<template>
 <div class="env" style="line-height:24px;">
    <span>线上
      <a href="javascript:void(0)" @click="runTimeEnvFnc" :class="rowData.publishProductEnvStatus == 1 ?'':'runEnvironment'" style="margin-left:5px;color:#282828">(运行中)</a>
    </span>
   <!--</br>-->
    <!--<span>预发-->
      <!--<a href="javascript:void(0)" @click="testRunTimeEnvFnc" :class="rowData.publishTestEnvStatus == 1 ?'':'runEnvironment'" style="margin-left:15px;color:#282828" >(运行中)</a>-->
    <!--</span>-->
</div>


</template>

<script>
export default {
   name: 'env',
    props: {
      rowData:Object
    },

    methods: {
      runTimeEnvFnc(){
        this.$emit("envLookData", this.rowData, this.rowData.productEnvVersion);
      },
      testRunTimeEnvFnc(){
        this.$emit("envLookData", this.rowData, this.rowData.testEnvVersion);
      }
    },
    created(){
      //console.log(this.rowData);
    }
}
</script>
<style scoped>

.runEnvironment{
  display:none;
}
.env span a:hover{
  color:#1c72ee!important;
}
</style>



