<template>
  <div>
    <!--<menuListDetail></menuListDetail>-->
    <!--<div class="panel-body">-->
      <!--<router-view style="margin-top: 16px"></router-view>-->
    <!--</div>-->
    <el-tabs v-model="activeIndex" type="card" @tab-click="changeDetailIndex">
      <el-tab-pane label="API定义" name="1-1">
        <prot-api-manage v-if="activeIndex=== '1-1'"></prot-api-manage>
      </el-tab-pane>
      <el-tab-pane label="授权信息" name="1-2">
        <accredit-mes v-if="activeIndex=== '1-2'"></accredit-mes>
      </el-tab-pane>
      <el-tab-pane label="发布历史" name="1-3">
        <history v-if="activeIndex=== '1-3'"></history>
      </el-tab-pane>
      <el-tab-pane label="调试API" name="1-4">
        <debug-api v-if="activeIndex=== '1-4'"></debug-api>
      </el-tab-pane>
      <el-tab-pane label="接口查询" name="1-5">
        <api-detail-num v-if="activeIndex=== '1-5'"></api-detail-num>
      </el-tab-pane>
      <el-tab-pane label="接口文档" name="1-6">
        <port-excel v-if="activeIndex=== '1-6'"></port-excel>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import menuListDetail from '../common/menuListDetail'
import ProtApiManage from '../apiDetail/portApiManage.vue'
import AccreditMes from '../apiDetail/accreditMes.vue'
import History from '../apiDetail/history.vue'
import DebugApi from '../apiDetail/debugApi.vue'
import ApiDetailNum from '../apiDetail/apiDetailNum.vue'
import PortExcel from '../apiDetail/portExcel.vue'
import { COMMON_CACHE_DATA_UPDATE } from '../../store/mutation-types'

export default {
  name: 'apiDetail',
  components: {
    menuListDetail,
    ProtApiManage,
    AccreditMes,
    History,
    DebugApi,
    ApiDetailNum,
    PortExcel
  },
  data() {
    return {
      activeIndex: '1-1'
    }
  },
  mounted() {
    this.setBreadcrumb()
  },
  created() {

  },
  methods: {
    changeDetailIndex(tab) {
      console.log(tab)
    },
    setBreadcrumb() {
      const breadcrumb = JSON.parse(JSON.stringify(this.$store.getters.commonCacheData('breadcrumb')))
      breadcrumb.push({
        urls: this.$route.path,
        name: '详情'
      })
      this.$store.commit(COMMON_CACHE_DATA_UPDATE, {
        name: 'breadcrumb',
        value: breadcrumb
      })
    }
  },
}
</script>

<style scoped>
.main_con {
  float: right;
  width: calc(100% - 230px);
  height: calc(100vh - 70px);
  padding: 20px;
  background: #F3F3F3;
  overflow: auto;
}
</style>



