<template>
  <div class="accreditMes" style="background:#fff;">

    <div class="detailBtn">
      <span class="detailNav">授权信息</span>
      <router-link to="/portManage">
        <el-button  size="small">返回API列表</el-button>
      </router-link>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">基本信息</span>
        <table v-model="accreditMesDeatail" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>API名称：
                <span>{{accreditMesDeatail.apiName}}</span>
              </td>
              <td>ApiID：
                <span>{{accreditMesDeatail.id}}</span>
              </td>
            </tr>

            <tr>
              <td colspan="2">描述：
                <span>{{accreditMesDeatail.apiDescription}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <el-tabs type="border-card" style="margin-top:20px;">
        <el-tab-pane label="已授权的APP">
          <!-- <el-button style="margin-bottom:10px;" size="small" class="fr" type="primary" @click="NoAppDataDetail">添加</el-button> -->
          <el-table ref="multipleTable" :data="appKeyData2" border tooltip-effect="dark" style="width: 100%">
            <el-table-column resizable show-overflow-tooltip prop="appKey" label="APP名称">
            </el-table-column>
            <el-table-column resizable show-overflow-tooltip prop="createTime" label="授权时间" :formatter="dateFormatRang"></el-table-column>
            <el-table-column resizable show-overflow-tooltip prop="strategyName" label="计费策略"></el-table-column>
            <el-table-column resizable show-overflow-tooltip prop="price" label="单价"></el-table-column>
          </el-table>

          <el-pagination style="margin-top:20px;text-align:right;" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>

        </el-tab-pane>
      </el-tabs>

      <!-- 授权弹框 start -->
      <el-dialog title="授权" :visible.sync="accredit" size="small" id="accreditBox">
        <div class="accreditLine"></div>
        <el-form v-model="accreditMesDeatail">
          <span style="font-weight:700;">您将对下列API进行授权操作：</span>
          <div class="issueAPI">{{accreditMesDeatail.apiName}}</div>

          <span style="font-weight:700;">选择要绑定的APP</span>
          <p></p>
          <template>
            <el-radio-group v-model="radioEnv" @change='radioChange'>
              <el-radio-button label="线上"></el-radio-button>
              <el-radio-button label="预发"></el-radio-button>
            </el-radio-group>
          </template><br>

          <div>

            <p></p>
            <div class="fl flBox" style="margin-bottom:20px;width: 100%">
              <el-form :inline="true" class="demo-form-inline">
                <el-form-item>
                  <el-input placeholder="请输入应用名称" v-model="searchApp" class="w250"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="NoAppDataDetail()">搜索</el-button>
                </el-form-item>
              </el-form>
              <el-table ref="multipleTable" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto" :data="appKeyData" @selection-change="handleSelectionChangeApi">
                <el-table-column type="selection" width="55" class="selection" prop='' @selection-change="handleSelectionChangeApi"></el-table-column>
                <el-table-column prop="appName" label="应用名称"></el-table-column>
                <el-table-column prop="appKey" label="appKey"></el-table-column>
                <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
                <el-table-column prop="description" label="描述"></el-table-column>

              </el-table>
            </div>
          </div>
        </el-form>
        <p style="clear:both"></p>
        <div slot="footer" class="dialog-footer">
          <el-button @click="accredit = false">取 消</el-button>
          <el-button type="primary" @click="NoAppDataDetailBtn">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 授权弹框 end -->
      <!-- 删除授权 start-->
      <el-dialog title="删除授权" size="tiny" :visible.sync="relieveDialog">
        <el-form :model="delAccreditData">
          <el-form-item label="授权名称：" :label-width="formLabelWidth">
            <span>{{delAccreditData.appName}}</span>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="relieveDialog = false">取 消</el-button>
          <el-button type="primary" @click="relieveBtn">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 删除授权 end-->
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
import menuListDetail from '@/components/common/menuListDetail'
export default {
  name: 'accreditMes',
  components: {
    menuListDetail
  },
  data() {
    return {
      jurisdiction:{},
      pageNum: 1,
      pageSize: 8,
      total: 2,
      value: '',
      accreditMesDeatail: {},                  //  基本信息展示对象
      envData: {},         //线上预发保存对象
      searchApp: '', //搜索

      amendApp: false, // 修改应用
      formLabelWidth: '120px',
      activeName: 'second',

      value: '',
      //API列表 end
      appKeyData: [], //未授权
      appKeyData2: [], //已授权
      radioEnv: '线上',
      env: 1,
      multipleSelection: [],

      dialogTableVisible: false,
      dialogFormVisible: false,
      dialogFormVisible2: false,
      issue: false,//发布API
      accredit: false,//授权API
      relieveDialog: false,//删除授权
      delAccreditData: {},

    }
  },
  created() {
    this.accreditMesData();
    this.appDataDetail();
    this.getUserPermissions();
  },
  methods: {
    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //================================    已授权APP展示   start   ===============================

    appDataDetail() {
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/gateway/charge/auth/search',
        data: {
          apiId: id,
          appKey: '',
          pageNum: this.pageNum,
          pageSize: 8
        }
      }).then(res => {
        for(var i = 0; i<this.jurisdiction.perms.length; i++){
          if(this.jurisdiction.perms[i] == 'ffdf01a7b3624eebb2e4d5dff1f95ec5'){
            if (res.data.code !== 2000) {
              alert(res.data.message);
            } else {
              this.appKeyData2 = res.data.data.list;
//              this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
//              this.pageSize = res.data.data.pageSize;
              this.total = res.data.data.total;
            }
          }
        }
      }).catch(error => {
        console.log(error.data.message);
      })
    },
    //================================    已授权APP展示    end    ===============================
    //================================       解除授权    start    ===============================
    relieve(index, row) {
      this.delAccreditData = row;
      this.relieveDialog = true;
      this.delAccreditData.index = index;
    },
    relieveBtn() {
      const delData = this.delAccreditData;
      const index = this.delAccreditData.index;
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/app/unAccredit',
        data: {
          apiId: id,
          appCertificationId: delData.id,
          env: delData.env
        }
      }).then(res => {
        if (res.data.code !== 2000) {
          alert(res.data.message);
        } else {
          this.relieveDialog = false;
          this.appDataDetail();
          this.$message({
            type: 'success',
            message: '解除授权成功'
          });
        }
      }).catch(error => {
        console.log('网络错误');
      })
    },
    //================================       解除授权    end    ===============================

    //================================    未授权APP弹框 start     ===============================
    NoAppDataDetail() {
      this.accredit = true;
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/api/findNoAccreditApps',
        data: {
          appName: this.searchApp == '' ? null : this.searchApp,
          apiId: id,
          env: this.env
        }
      }).then(res => {
        if (res.data.code !== 2000) {
          alert(res.data.message);
        } else {
          this.appKeyData = res.data.data;
        }
      }).catch(error => {
        console.log(error.message);
      })
    },

    //确认提交绑定api
    NoAppDataDetailBtn() {
      const id = this.$route.query.id;
      var flag = 0;
      var appIds = '';
      if (this.multipleSelection.length != 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            appIds += row.id;
          } else {
            appIds += (',' + row.id);
          }
          flag++;
        });
        axios({
          method: 'post',
          url: '/api/apiToApp',
          data: {
            apiId: id,
            appIds: appIds,
            env: this.env
          }
        }).then(res => {
          if (res.data.code !== 2000) {
            alert(res.data.message);
          } else {
            this.accredit = false;
            this.appDataDetail();
            this.$message({
              type: 'success',
              message: '添加授权成功'
            });
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }
    },



    //线上/预发单选按钮改变事件
    radioChange(val) {
      if (val == '线上') {
        this.env = 1;
        this.NoAppDataDetail();
      } else if (val == '预发') {
        this.env = 2;
        this.NoAppDataDetail();
      }
    },
    //授权API复选框状态改变
    handleSelectionChangeApi(val) {
      this.multipleSelection = val;
    },

    //分页bar
    handleCurrentChange(val) {
      this.pageNum = val;
      this.appDataDetail();
    },

    //================================    未授权APP弹框   end     ===============================

    //================================            基本信息展示    ================================
    accreditMesData() {
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/api/details',
        data: {
          id: id
        }
      }).then(res => {
        for(var i = 0; i<this.jurisdiction.perms.length; i++){
          if(this.jurisdiction.perms[i] == '0a17c1e02a4b475ba99ba362a3024cba'){
            this.accreditMesDeatail = res.data.data;
          }
        }
      }).catch(error => {
        console.log(res.message);
      })
    },

    onLine() {
      $(".onLine").addClass("active");
      $(".pretest").removeClass("active");
    },
    pretest() {
      $(".onLine").removeClass("active");
      $(".pretest").addClass("active");
    },
    //时间戳
    dateFormat(row, column) {
      const date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    dateFormatRang(row, column) {
      const date1 = row.startTime
      const date2 = row.endTime;
      if (!date1 || !date2) {
        return "";
      }
      return moment(date1).format("YYYY/MM/DD HH:mm:ss") + '-' + moment(date2).format("YYYY/MM/DD HH:mm:ss");
    },
    envFormat(row, column) {
      const env = row[column.property];
      if (env == undefined) {
        return "";
      }
      return env == 1 ? "线上" : "预发";
    },
  }




}
</script>

<style scoped>
.apiListNav {
  width: 100%;
  text-align: center;
  color: #fff;
  margin-bottom: 60px;
}

.apiListNav ul li {
  float: left;
  width: 418px;
  height: 30px;
  line-height: 30px;
  background: #ccc;
}

.apiListNav ul li.active {
  background: #00a2ca;
}

.issueAPI {
  height: 40px;
  line-height: 40px;
  background: #f1f6f9;
  padding-left: 10px;
  margin-bottom: 20px;
  margin-top: 10px;
}

.issue-environment {
  width: 100%;
  height: 40px;
  margin-top: 10px;
}

.issue-environment li {
  float: left;
  width: 55px;
  height: 30px;
  background: #D9DEE4;
  color: #000;
  text-align: center;
  line-height: 30px;
  margin-right: 10px;
  cursor: pointer;
}

.checkList {
  width: 35%;
  height: 180px;
}

.checkList span {
  background: #eef1f6;
  height: 30px;
  display: block;
  line-height: 30px;
  padding-left: 5px;
}

.checkMes {
  padding-left: 5px;
  height: 231px;
  overflow: auto;
}




/*guozhangqiang新加css====start*/

.accreditLine {
  width: 100%;
  height: 1px;
  background: #ebebeb;
  position: relative;
  top: -15px;
}

.issue-environment .active {
  color: white;
  background: #546478
}

#accreditBox {
  min-width: 1200px;
}

.flBox {
  width: 73%;
}

#accreditSelect {
  width: 30%;
}

#checkList {
  border: none;
}

#checkList table tr {
  height: 35px;
  margin-bottom: 3px;
}

#checkList table tr button {
  display: inline-block;
  margin-top: 4px;
}

.checkMes {
  border: 1px solid #dfe6ec;
}

.addBtn {
  width: 60px;
  height: 28px;
  color: white;
  background: #20a0ff;
  display: inline-block;
  text-align: center;
  outline: none;
}

.addedBtn {
  width: 60px;
  height: 28px;
  color: #bcbcbc;
  background: #f7f7f7;
  display: inline-block;
  text-align: center;
  outline: none;
  border: 1px solid #e4e4e4;
}

.deleteBtn {
  width: 60px;
  height: 25px;
  color: black;
  display: inline-block;
  text-align: center;
  outline: none;
  background: #f7f7f7;
  border: 1px solid #e4e4e4;
}





/*guozhangqiang新加css====end*/
</style>



