<template>
    <div class="codeMap" style="background:#ffffff;position:relative;height:100%;">
      <div class="w1660fr">
        <div class="detailBtn">
            <span class="detailNav">接口查询</span>
            <!--<router-link :to="{path:'portManage/portApiManage',query:{id:this.$route.query.id,apiGroupId:this.$route.query.apiGroupId,productEnvVersion:this.$route.query.productEnvVersion}}" v-if="this.$route.query.productEnvVersion">-->
              <!--<el-button  size="mini">返回API列表</el-button>-->
            <!--</router-link>-->
        </div>

        <div class="dataTime" style="height:645px;">
          <div class="monitoring-detail fl" >
          <div class="monitoringMes mr20 mb20 fl" style="width:49%">
            <span>请求参数</span>
            <table class="table table-bordered">
              <tbody>
                <tr>
                  <td class="petitionWay">Http Method:
                    <b class="petition">{{method}}</b>
                  </td>
                  <td class="petitionWay">Path:
                    <b class="petition">{{uri}}</b>
                  </td>
                </tr>

                <tr id="headers">
                  <td colspan="2">
                    <b>Header</b>
                    <p></p>
                    <ul>
                      <li v-for="(header,index) in headers" class="headers">
                        <div v-if='header.paramMust==1' style="display: inline-block;"><b class="mustTip">*</b></div>
                        <div v-else='header.paramMust==2' style="display: inline-block;">&#12288</div>

                        <el-tooltip class="item" effect="dark" :content="header.paramDescription" placement="right">
                          <label style="width:40%;color:#1f2d3d">{{header.paramName}}</label>
                        </el-tooltip>

                        <i style="margin:0 20px;">=</i>
                        <el-input style="width:40%;" v-model='header.model'></el-input>
                      </li>
                    </ul>
                  </td>
                </tr>

                <tr id="querys">
                  <td colspan="2">
                    <b>Query</b>
                    <p></p>
                    <ul>
                      <li v-for="(query,index) in querys" class="headers">
                        <div v-if='query.paramMust==1' style="display: inline-block;"><b class="mustTip">*</b></div>
                        <div v-else='query.paramMust==2' style="display: inline-block;">&#12288</div>

                        <el-tooltip class="item" effect="dark" :content="query.paramDescription" placement="right">
                          <label style="width:40%;color:#1f2d3d">{{query.paramName}}</label>
                        </el-tooltip>

                        <i style="margin:0 20px;">=</i>
                        <el-input style="width:40%;" v-model='query.model'></el-input>
                      </li>
                    </ul>
                  </td>
                </tr>

                <tr id="bodys">
                  <td colspan="2">
                    <b>Body</b>
                    <p></p>
                    <ul>
                      <li v-for="(body,index) in bodys" class="headers">
                         <div v-if='body.paramMust==1' style="display: inline-block;"><b class="mustTip">*</b></div>
                         <div v-else='body.paramMust==2' style="display: inline-block;">&#12288</div>

                         <el-tooltip class="item" effect="dark" :content="body.paramDescription" placement="top">
                            <label style="width:40%;color:#1f2d3d">{{body.paramName}}</label>
                          </el-tooltip>

                        <i style="margin:0 20px;">=</i>
                        <el-input style="width:40%;" v-model='body.model'></el-input>
                      </li>
                    </ul>
                  </td>
                </tr>

                <tr>
                  <td colspan="2">
                    <el-button type="primary" style="text-align:center;" class="sendBtn" @click="sendPost()">发送请求</el-button>
                    <small>
                      <router-link :to="{path:'errorText',query:{id:this.$route.query.id,apiGroupId:this.$route.query.apiGroupId,productEnvVersion:this.$route.query.productEnvVersion}}">错误码查询表</router-link> （X-Ca-Error-Message字段为错误码字段）</small>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          <div class="returnRes fr">
            <span>返回结果</span>
            <!--<div v-model='result' style="background-color:#393d40;height: 585px;width: 100%;color: white;overflow: auto" readonly="readonly">
            <pre style="color:#7fe2ab;border:1px solid white;font-size: 16px;height: auto;line-height: 40px">{{result.statusCode}}</pre>
            <pre style="color:#febd4b;border:1px solid white;font-size: 16px;height: auto;line-height: 40px">{{result.responseHeader}}</pre>
            <br>
            <pre style="color:white;border:1px solid white;font-size: 16px;line-height: 40px">{{result.resultString}}</pre>
          </div>-->
            <zk-table
            ref="table"
            sum-text="sum"
            index-text="#"
            :data="result.resultList"
            :columns="columns"
            :stripe="props.stripe"
            :border="props.border"
            :show-header="props.showHeader"
            :show-summary="props.showSummary"
            :show-row-hover="props.showRowHover"
            :show-index="props.showIndex"
            :tree-type="props.treeType"
            :is-fold="props.isFold"
            :expand-type="props.expandType"
            :selection-type="props.selectionType">
          </zk-table>
          </div>

      </div>
      </div>
      </div>
    </div>
  </template>

  <script>
  import axios from "axios";

  export default {
    name: "codeMap",
    data() {
      return {
        method: '',
        uri: '',
        headers: [],
        bodys: [],
        querys: [],
        isSubmit: '',
        arrData:[],
        ObjData:'',
        objKey:'',
        objVal:'',
        objNew:'',
        ss:{},
        result: {
          statusCode:'',
          responseHeader:'',
          resultString:'',
          resultList:[],
        },

        pageNum: 1,
        pageSize: 10,
        total: 0,
        codeData: [],
        searchStrategy: "",
        formLabelWidth: "120px",
        subApisArr: [],
        mappingArr: [],
        lookData: false,
        callBack: "",
        newPage:'',

        props: {
          stripe: false,
          border: false,
          showHeader: true,
          showSummary: false,
          showRowHover: true,
          showIndex: false,
          treeType: true,
          isFold: false,
          expandType: false,
          selectionType: false,
        },
        columns: [
          {
            label: '名称',
            prop: 'paramName',
          },
          {
            label: '中文名称',
            prop: 'description',
            width: '100px'
          },
          {
            label: '值',
            prop: 'value',
          },
          {
            label: '说明',
            prop: 'remark',
          },
          /*{
            label: '类型',
            prop: 'paramType',
          },*/
          /*{
            label: '层级',
            prop: 'position',
          },*/


        ]
      };
    },

    created() {
      this.initData();
    },

    methods: {
      //初始化列表
      initData() {
        let id = this.$route.query.id;
        let groupId = this.$route.query.apiGroupId;
        this.apiId = id;
        axios({
          method: 'post',
          url: '/api/debugInfo',
          data: {
            apiId: id,
            groupId: groupId
          }
        }).then(res => {
              var data = res.data.data;
              if (data == null) {
                this.isSubmit = '不能提交';
                this.$message({
                  message: '该API尚未有发布版本',
                  type: 'warning'
                });
              } else {

                this.uri = data.uri;
                this.method = data.method;
                if (data.headers == null || data.headers == '') {
                  $("#headers").hide();
                }
                if (data.querys == null || data.querys == '') {
                  $("#querys").hide();
                }
                if (data.bodys == null || data.bodys == '') {
                  $("#bodys").hide();
                }
                this.headers = data.headers;
                this.querys = data.querys;
                this.bodys = data.bodys;
              }

        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      //发送请求
    sendPost() {
      var headerList = null;
      var queryList = null;
      var bodyList = null;
      if (this.headers.length != 0) {
        var array = [];
        this.headers.forEach(row => {
          array.push({ "paramName": row.paramName, "paramValue": row.model });
        });
        headerList = array;
      }
      if (this.querys.length != 0) {
        var array = [];
        this.querys.forEach(row => {
          array.push({ "paramName": row.paramName, "paramValue": row.model });
        });
        queryList = array;
      }
      if (this.bodys.length != 0) {
        var array = [];
        this.bodys.forEach(row => {
          array.push({ "paramName": row.paramName, "paramValue": row.model });
        });
        bodyList = array;
      }
      if (this.isSubmit != '') {
        this.$message({
          message: '不能提交',
          type: 'warning'
        });
      } else {
        this.result.statusCode = '';
        this.result.resultString = '';
        let id = this.$route.query.id;
        axios({
          method: 'post',
          url: '/api/debugging',
          data: {
            idInfo: { "apiId": this.apiId, "apiGroupId": this.groupId },
            headerList: headerList,
            queryList: queryList,
            bodyList: bodyList
          }
        }).then(res => {
          this.result.statusCode =res.data.code;
          /*var jsonParse =JSON.parse(res.data.data);
          if(jsonParse.data){
            this.result.resultString =jsonParse.data;
          }else{*/
            this.result.resultString =res.data.data;
          // }
          this.result.resultList = res.data.resultList;
        }).catch(error => {
          console.log('网络错误，不能访问', error.toString());
        });
      }
    },

      //分页查询
      handleCurrentChange(val) {
        this.pageNum = val;
        this.initData();
      },
      handleSizeChange(val){
        this.newPage = val;
        this.initData();
      },
    },

  };
  </script>

  <style scoped>

  .dataTime {
  line-height: 50px;
}

.monitoring-detail {
  width: 100%;
}

.monitoring-detail .monitoringMes {
  width: 49%;
  height: 620px;
  min-height: 600px;
  float: left;
  overflow: auto;
}

.monitoring-detail .monitoringMes span {
  background: #F4F5F9;
  border: 1px solid #E1E6EB;
  padding-left: 15px;
  display: block;
  border-left: 3px solid #778;
  border-top: none;
  border-right: none;
}

.returnRes {
  width: 49%;
  min-height: 700px;
  border: 1px solid #bbb;
  /*background: #000;*/
  color: #fff;
}

.returnRes span {
  display: block;
  height: 35px;
  line-height: 35px;
  background: #fff;
  /*color: #000;*/
  padding-left: 10px;
}


/*guozhangqiang新加css====start*/

.petitionWay {
  color: #999999;
}

.petition {
  font-weight: 100;
  color: black;
}

.mustTip {
  color: red;
  font-weight: 100;
  display: inline-block;
  margin-right: 5px;
  position: relative;
  top: 3px;
}

#query {
  display: block;
  margin-bottom: 15px;
}

.sendBtn {
  display: block;
  margin: 0 auto;
}

small {
  display: block;
  width: 320px;
  text-align: center;
  margin: 5px auto 0;
}

small a {
  cursor: pointer;
}

.headers {
  margin-bottom: 10px;
}

  </style>



