  <script>
    import axios from 'axios'
    import apiListNav from './apiListNav';

    export default {
      name: 'thirdDialog',
      props: ["addApiData", "backEndParam", "numData", "numDataJson", "numDataXml"],
      components: {
        apiListNav,
      },

      data() {
        return {
          activeBack:'backValTab',
          requestWay:1,
          formLabelWidth: '125px',
          tcpHeaderIdOptions:[],
          httpMethodOptions: [{ value: 1, label: 'GET' }, { value: 2, label: 'POST' }],
          backendProtocolTypeOptions: [{ value: 1, label: 'HTTP' }, { value: 2, label: 'HTTPS' },{ value: 3, label: "Socket" }],
          paramsLocationOptions: [{ value: 2, label: 'query' },{ value: 1, label: 'header' },  { value: 3, label: 'body' }],
          paramsTypeOptions: [{ value: 1, label: 'String' }, { value: 2, label: 'Integer' }, { value: 3, label: 'Long' }, { value: 4, label: 'Float' }, { value: 5, label: 'Double' }, { value: 6, label: 'Boolean' }],
          chargeOptions: [{ value: 1, label: '是' }, { value: 2, label: '否' }],
          //后端名称验证重复

          interfaceNameVal: (rule, value, callback) => {
            if ((value = this.addApiData.apiInfo.interfaceName)) {
              const interfaceName = this.addApiData.apiInfo.interfaceName;
              var reg = /\s+/;
              axios({
                method: "post",
                url: "/api/checkUniqueInterfaceName",
                data: {
                  interfaceName: interfaceName
                }
              }).then(res => {
                if (res.data.data === false) {
                  callback(new Error("后端服务名称重复"));
                } else {
                  callback();
                }
              }).catch(error => {
                alert("网络错误，不能访问");
              });
            } else if (value === '') {
              callback(new Error("请输入后端服务名称"));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          },

          //后端服务地址正则验证：
          checkBackAddress: (rule, value, callback) => {
            var res = /^[a-zA-z]+:\/\//;
            var reg = /^[^\u4e00-\u9fa5]{0,}$/;
            if (value === '') {
              callback(new Error('请输入后端服务地址'));
            } else if (reg.test(value) === false) {
              callback(new Error('请输入正确的格式,例如http://www.baidu.com'));
            } else if (res.test(value) === false) {
              callback(new Error('请输入正确的格式,例如http://www.baidu.com'));
            } else {
              callback();
            }
          },
          checkBackAddress2: (rule, value, callback) => {
            var reg = /^(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9]):\d{0,5}/;
            if (value === '') {
              callback(new Error('请输入后端服务地址'));
            }
            else if(reg.test(value) === false){
              callback(new Error("请输入正确的格式,例如172.32.3.176:7302"));
            }
           else {
              callback();
            }
          },

          checkReqPath: (rule, value, callback) => {
          var reg = /^[^\u4e00-\u9fa5]{0,}$/;
          var res = /^(\/[A-Za-z0-9_.])+./;
          if (value === "") {
            callback(new Error("请输入请求Path"));
          }else if(reg.test(value) === false){
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else if (res.test(value) === false) {
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else {
            callback();
          }
          },

          checkReqPathBackend: (rule, value, callback) => {
          var reg = /^[^\u4e00-\u9fa5]{0,}$/;
          var res = /^(\/[A-Za-z0-9_.])*./;
            if (!value) {
              callback(new Error('请输入请求Path'));
            }else if(!reg.test(value)){
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else if (!res.test(value)) {
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else {
            callback();
          }
          },


          checkReqPath2: (rule, value, callback) => {
            var reg = /\s+/;
            if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else if (res.test(value) === false) {
              callback(new Error('请输入正确的格式,例如/getUserInfo/[userId]'));
            } else {
              callback();
            }
          },

          //后端超时
          backTimeCheckUnEmpty: (rule, value, callback) => {
            var res = /^(?!0)(?:[0-9]{1,5}|60000)$/;
            if (value === '') {
              callback(new Error('请输入请求后端超时，必须为数字'));
            } else if (res.test(value) === false) {
              callback(new Error('后端超时必须大于等于1ms，小于等于60000ms，并且必须为数字'));
            } else if(value >60000){
              callback(new Error('后端超时必须大于等于1ms，小于等于60000ms，并且必须为数字'));
            }else{
              callback();
            }
          },

          weightCheckUnEmpty: (rule, value, callback) => {
            var res = /^(?:10|[0-9])$/;
            if (value === '' || res.test(value) == false) {
              callback(new Error('权重只能是0-10之前的整数！'));
            } else {
              callback();
            }
          },

        }
      },
      created() {
        this.addApiData.apiInfo.backEndHttpMethod = 1;
        // this.tcpList();
      },
      mounted(){
        if(this.addApiData.apiInfo.backEndHttpMethod == 1){
          this.paramsLocationOptions = [{ value: 2, label: 'query' },{ value: 1, label: 'header' }]
        }else if(this.addApiData.apiInfo.backEndHttpMethod == 2){
          this.paramsLocationOptions = [{ value: 2, label: 'query' },{ value: 1, label: 'header' }, { value: 3, label: 'body' }]
        }
      },
      methods: {
        tcpSelect(val){
          if(val == null){
            this.addApiData.apiInfo.tcpBodyLenBytes = 0;
            this.addApiData.apiInfo.tcpReadBytes = 8;
          }
        },
      //   tcpList(){
      //   axios({
      //     method: 'post',
      //     url: '/api/queryTcpHeader',
      //     data: {}
      //   }).then(res => {
      //     let list = res.data.data;
      //     list.push({headerName:'',id:null});
      //     list.map(data =>{
      //       this.tcpHeaderIdOptions.push({ label: data.headerName, id: data.id });
      //     });
      //   }).catch(error => {
      //     console.log('网络错误，不能访问');
      //   });
      // },
        configFous(){
          axios({
            method: "post",
            url: "/api/"+ this.$route.query.id +"/pretty",
            data: {
              responseTransParam:this.addApiData.apiInfo.jsonConfig
            }
          }).then(res => {
            this.addApiData.apiInfo.jsonConfig = res.data.data;
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        },



        slideList(val){
          if(val !==3){
            this.addApiData.apiInfo.backEndHttpMethod = 2;
            this.paramsLocationOptions = [];
            this.paramsLocationOptions.push(
              { value: 2, label: "query" },
              { value: 1, label: "header" },
              { value: 3, label: "body" }
            );
          }
        },
        handleClick(tab, index) {
          if(this.addApiData.apiInfo.backendProtocolType ==3){
            if(this.numDataXml.length < 1){
                this.numDataXml.push({
                  paramName: "",
                  paramValue: "",
                  paramsLocation: 3,
                  paramDescription: "",
                  paramsType: 4
                });
              }
          }else{
            if (tab.index == 0) {
              this.numData.splice(index, 1);
              this.numDataJson.splice(index, 1);
              this.numDataXml.splice(index, 1);
            } else if (tab.index == 1) {
              this.numData.splice(index, 1);
              this.numDataXml.splice(index, 1);
              if(this.numDataJson.length < 1){
                this.numDataJson.push({
                  paramName: "",
                  paramValue: "",
                  paramsLocation: 3,
                  paramDescription: "",
                  paramsType: 3
                });
              }
            } else if (tab.index == 2) {
              this.numData.splice(index, 1);
              this.numDataJson.splice(index, 1);
              if(this.numDataXml.length < 1){
                this.numDataXml.push({
                  paramName: "",
                  paramValue: "",
                  paramsLocation: 3,
                  paramDescription: "",
                  paramsType: 4
                });
              }
            }
          }




        },
        //后端服务参数数组验证
        paraNameVal2(value, index) {
          let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
          let reg = /\s+/;
          if (value === ''  || reg.test(value)) {
            $(".jqueryValidate2").eq(index).show();
            $(".jqueryValidate8").eq(index).hide();
          } else if(regExp.test(value) == false){
            $(".jqueryValidate2").eq(index).hide();
            $(".jqueryValidate8").eq(index).show();
          }else{
            $(".jqueryValidate2").eq(index).hide();
            $(".jqueryValidate8").eq(index).hide();
          }
        },

        addTable2() {
          if(this.addApiData.apiInfo.backEndHttpMethod == 1){
            this.numData.push({
              paramName: "",
              paramValue: "",
              paramsLocation: 2,
              paramDescription: "",
              paramsType: 2
            });
          }else{
            this.numData.push({
              paramName: "",
              paramValue: "",
              paramsLocation: 3,
              paramDescription: "",
              paramsType: 2
            });
          }

        },
        delTable2: function (index) {
          this.numData.splice(index, 1);
        },

        nextStep: function () {
          let flag = true;
          let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
          let reg = /\s+/;
          if(this.addApiData.apiInfo.backendProtocolType !==3){
            this.backEndParam.map((data, index) => {
              if (data.paramName == "") {
                $(".jqueryValidate2").eq(index).show();
                flag = false;
              }
            });

            this.numData.map((data, index) => {
              if (data.paramName == "") {
                $(".jqueryValidate4").eq(index).show();
                flag = false;
              }else if (regExp.test(data.paramName) == false) {
                $(".jqueryValidate4").eq(index).show();
                flag = false;
              } else if (reg.test(data.paramName)) {
                $(".jqueryValidate4").eq(index).show();
                flag = false;
              } else if (data.paramValue == "") {
                $(".jqueryValidate5").eq(index).show();
                flag = false;
              } else if (reg.test(data.paramValue)) {
                $(".jqueryValidate5").eq(index).show();
                flag = false;
              }
            });
          }else{
            var regBank = /^(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9]):\d{0,5}/;
            if(regBank.test(this.addApiData.apiInfo.backEndAddress) == false){
              flag = false;
            }
          }


          if (flag) {
            this.$refs["addApiData3"].validate((valid) => {
              if (valid) {
                this.$emit('nextStep', "");
              } else {
                console.log('error submit!!');
                return false;
              }
            });
          }
        },

        preStep: function () {
          this.$emit('preStep', "");
        },

        //常量参数数组验证
        paraNameVal4(value, index) {
          let regExp = /^[a-zA-Z0-9_\u4e00-\u9fa5]{1,200}$/;
          if (value === '') {
            $(".jqueryValidate4").eq(index).show();
            $(".jqueryValidate7").eq(index).hide();
          } else if(regExp.test(value) == false){
            $(".jqueryValidate4").eq(index).hide();
            $(".jqueryValidate7").eq(index).show();
          }else{
            $(".jqueryValidate4").eq(index).hide();
            $(".jqueryValidate7").eq(index).hide();
          }
        },

        paraNameVal5(value, index) {
        let regExp = /^[A-Za-z|\u4e00-\u9fa5]{0}[a-zA-Z0-9_\u4e00-\u9fa5]{1,200}$/;
        if (value === "") {
          $(".jqueryValidate5").eq(index).show();
          $(".jqueryValidate6").eq(index).hide();
        } else if(regExp.test(value) == false){
          $(".jqueryValidate5").eq(index).hide();
          $(".jqueryValidate6").eq(index).show();
        }else{
          $(".jqueryValidate5").eq(index).hide();
          $(".jqueryValidate6").eq(index).hide();
        }
      },
        changeHTTP(val){
          if (this.addApiData.apiInfo.backEndHttpMethod == 2) {
          this.paramsLocationOptions = [];
          this.paramsLocationOptions.push(
            { value: 2, label: "query" },
            { value: 1, label: "header" },
            { value: 3, label: "body" }
          );
          this.backEndParam.map(data =>{
            data.paramsLocation = 3;
          });
          this.numData.map(data =>{
            data.paramsLocation = 3;
          });
        } else {
          this.backEndParam.map(data =>{
            data.paramsLocation = 2;
          });
          this.numData.map(data =>{
            data.paramsLocation = 2;
          });
          if (this.backEndParam || this.numData) {
            let list = [];
            if (this.backEndParam) {
              for (let i = 0; i < this.backEndParam.length; i++) {
                list.push(this.backEndParam[i]);
              }
            }
            if (this.numData) {
              for (let i = 0; i < this.numData.length; i++) {
                list.push(this.numData[i]);
              }
            }

            for (let i = 0; i < list.length; i++) {
              if (list[i].paramsLocation == 3) {
                this.$message({
                  message: "参数列表中含有body参数,不能选择GET",
                  type: "warning"
                });
                this.addApiData.apiInfo.backEndHttpMethod = 2;
                return;
              }
            }
          }
          this.paramsLocationOptions = [];
          this.paramsLocationOptions.push(
            { value: 2, label: "query" },
            { value: 1, label: "header" }
          );
          }
        }
      }
    }

  </script>


  <style scoped>
  </style>



