<template>
  <div class="authorityMsg" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input v-model="apiIdSearch" placeholder="请输入APIID进行查询" class="w350 apiIdClass" @change="apiIdChange()"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="请输入接口名称进行查询" class="w250" v-model="port"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" v-if="searchBtn" class="searchOne" @click="setDefaultSearch">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup"  @filter-change="tableChange" :row-class-name="tableRowClassName">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="apiName" label="API名称" width="250px">
        <template slot-scope="scope">
          <!-- <el-tooltip class="item" effect="dark" :content="scope.row.apiName" placement="right">
            <span  v-if="!lookMagBtn">{{scope.row.apiName}}</span>
          </el-tooltip> -->
          <el-tooltip class="item" effect="dark" :content="scope.row.apiName" placement="right">
            <router-link :to="{path:'combinationApi/combinationIdList',query:{combinationId:scope.row.id}}">{{scope.row.apiName}}</router-link>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="groupName" label="分组"></el-table-column>
      <el-table-column prop="apiDescription" label="描述" >
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" :content="scope.row.apiDescription" placement="right">
            <span>{{scope.row.apiDescription}}</span>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="最后修改" :formatter="dateFormat" width="170px"></el-table-column>
      <el-table-column class="upEnv" prop="productEnvVersion" label="运行环境"  column-key="runTimeEnv">
        <template slot-scope="scope">
          <env :rowData="scope.row" v-on:envLookData="lookData"></env>
        </template>
      </el-table-column>
      <el-table-column prop="" label="操作" width="240">
        <template slot-scope="scope">
          <router-link class="" :to="{path:'combinationApi/combinationIdList',query:{combinationId:scope.row.id}}">
            <el-button type="text" class="radiusBtn mr10 pubColor" v-if="amendBtn" size="small">查看</el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <p style="clear:both"></p>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;">
      <el-pagination id="pageOne" style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
                     :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>

    </div>
    <el-dialog title="API定义详情" :visible.sync="look" style="overflow:auto;top:-5%">
      <queryApiDetail :apiDefineDetail="apiDefineDetail" :requestParamsVersionsList="requestParamsVersionsList" :backEndParam2="backEndParam2" :resultSettingsVersionsList="resultSettingsVersionsList" :detialVersionFlag="detialVersionFlag" :numData2="numData2" :groupHerfId="groupHerfId" @closeLook="look=false"></queryApiDetail>
    </el-dialog>
  </div>
</template>
<script>
  import axios from "axios";
  import env from "@/components/common/env";
  import moment from "moment";
  import queryApiDetail from "@/components/portManage/queryApiDetail";
  import { formatJson } from "@/assets/json";
  export default {
    name: "authorityMsg",
    data() {
      return {
        apiIdSearch: '',
        port:'',
        env: '',
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchBtn:false,
        tablegroup:[],
        runArr: [{ text: "线上", value: 1 }, { text: "预发", value: 2 }], //  运行环境（全部）
        currentPage: 1,
        amendBtn: false,
        newPage:'',
        groupHerfId: { pubDescription: "" }, //分组管理传递id
        numData2: [],
        detialVersionFlag: null,
        resultSettingsVersionsList: [],
        backEndParam2: [],
        requestParamsVersionsList: [],
        apiDefineDetail: {},
        backendVersionsList: [],
        look: false

      };
    },

    created() {
      this.initData();
      this.getUserPermissions();
    },

    methods: {
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      initData() {
        axios({
          method: 'post',
          url: '/api/combinationApiList',
          data: {
            env: this.env == undefined ? '' :this.env,
            apiName:this.port === '' ? null : this.port,
            apiId: this.apiIdSearch === '' ? null : this.apiIdSearch,
            pageSize: this.newPage == '' ? 10 : this.newPage,
            pageNum: this.pageNum
          }
        }).then(res => {
          if(res.data.code == 2000){
            if(!this.jurisdiction.roles.includes('admin')){
              this.searchBtn = false;
              this.amendBtn = false
            }else{
              this.searchBtn = true;
              this.amendBtn = true
              this.tablegroup =  res.data.data.list;
              this.currentPage = res.data.data.pageNum;
              this.pageSize = res.data.data.pageSize;
              this.total = res.data.data.total;
            }
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //筛选
      tableChange(filters) {
        if (filters.hasOwnProperty("runTimeEnv")) {
          this.env = filters["runTimeEnv"][0];
          this.setDefaultSearch()
        }
      },
      setDefaultSearch() {
        this.pageNum = 1
        this.currentPage = 1;
        this.initData()
      },
      //时间戳
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      //表格 分组（全部）筛选
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },
      //分页查询
      handleCurrentChange(val) {
        this.pageNum = val;
        this.initData();
      },
      handleSizeChange(val) {
        this.newPage = val;
        this.initData();
      },
      //======================================  运行中查看 API定义详情 start ===================================
      lookData(row, versionFlag) {
        this.apiDefineDetail = {};
        this.backendVersionsList = [];
        this.requestParamsVersionsList = [];
        this.resultSettingsVersionsList = [];
        this.backEndParam2 = [];
        this.numData2 = [];
        this.detialVersionFlag = versionFlag;
        this.groupHerfId = row;
        const apiGroupId = this.groupHerfId.apiGroupId;
        const groupHerf = this.groupHerfId.id;
        this.look = true;

        axios({
          method: "post",
          url: "/api/findApiInfoVersion",
          data: {
            apiGroupId: apiGroupId,
            id: groupHerf,
            versionId: versionFlag
          }
        }).then(res => {
          var resultJson = formatJson(this.groupHerfId.callBackSuccessExample);
          $(".successWritePlaceDialog").html("<pre>" + resultJson + "<pre/>");
          var resultErrorJson = formatJson(
            this.groupHerfId.callBackFailExample
          );
          $(".errorWritePlaceDialog").html(
            "<pre>" + resultErrorJson + "<pre/>"
          );
          this.apiDefineDetail = res.data.data.apiInfoVersion;
          this.backendVersionsList = res.data.data.backendVersionsList;
          res.data.data.backendVersionsList.forEach((obj, index) => {
            if (obj.paramsType == 1) {
              this.backEndParam2.push(obj);
            } else if (obj.paramsType == 2) {
              this.numData2.push(obj);
            }
          });
          this.requestParamsVersionsList = res.data.data.requestParamsVersionsList;
          this.resultSettingsVersionsList = res.data.data.resultSettingsVersionsList;
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //========================================   api表格数据展示  end     ========================================
      //========================================  分组（全部）分组管理接口 start ===================================
      menuGroup() {
        const groupHerf = this.groupHerfId;
        axios({
          method: "post",
          url: "/apiGroup/allList",//加载所有的分组
          data: {}
        }).then(resGroup => {
          this.jurisdiction.perms.map(data =>{
            if(data == '8e0d4a7bd5a7467ebe33dca8c4b9c22c'){
              var groupArrLen = resGroup.data.data;
              for (var i = 0; i < groupArrLen.length; i++) {
                this.groupArr[i] = {
                  text: groupArrLen[i].groupName,
                  value: groupArrLen[i].id
                };
              }
            }
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //========================================  分组（全部）分组管理接口 end =====================================
      //========================================        发布API     start    =======================================
      publish(index, row) {
        this.groupHerfId = row;
        this.rowIndex = index;
        this.issue = true;
      },
    },

    components: {env, queryApiDetail}

  };
</script>

<style scoped>

</style>



