<template>
  <div>
    <apiListNav :activeIndex=1></apiListNav>

    <p style="clear:both"></p>
    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">名称及描述</span>
      </div>
    </div>
    <el-form class="ml100 demo-ruleForm" :model="data" ref="addApiData1" :rules="rulesGroup">
      <el-form-item label="分组：" :label-width="formLabelWidth">

        <el-select v-model="data.apiGroupId" class="w550">
          <!-- <el-tree style="border:none;" default-expand-all :data="groupOptions" :props="defaultProps" accordion @node-click="handleNodeClick"></el-tree> -->
          <el-option v-for="item in groupOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>

        <!-- <el-button type="text" style="margin-left:20px;" @click="$emit('creatGroup', '')">新建分组</el-button> -->
      </el-form-item>

      <el-form-item label="API名称：" prop="apiName" :label-width="formLabelWidth">
        <el-input class="w550" v-model="data.apiName" auto-complete="off"></el-input>
      </el-form-item>

      <el-form-item label="描述：" prop="apiDescription" :label-width="formLabelWidth">
        <el-input type="textarea" class="w550" :rows="4" v-model="data.apiDescription" auto-complete="off"></el-input>
      </el-form-item>

      <el-form-item label="是否存储：" prop="saveMongoDB" :label-width="formLabelWidth">
        <el-select class="w550" v-model="data.saveMongoDB">
          <el-option label="否" value="2"></el-option>
          <el-option label="是" value="1"></el-option>
        </el-select>
      </el-form-item>

      <!-- 是否存储 -->
      <div v-if="data.saveMongoDB=='1' ? true : false">

        <el-form-item label="MongodbURL" prop="mongodbURI" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mongodbURI" placeholder="mongodb://admin:admin@172.16.16.70"></el-input>
        </el-form-item>

        <el-form-item label="Mongodb库名" prop="mongodbDBName" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mongodbDBName"></el-input>
        </el-form-item>

        <el-form-item label="Mongodb表名" prop="mongodbCollectionName" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mongodbCollectionName"></el-input>
        </el-form-item>

      </div>

      <el-form-item label="发送MQ：" prop="saveMQ" :label-width="formLabelWidth">
        <el-select class="w550" v-model="data.saveMQ">
          <el-option label="否" value="2"></el-option>
          <el-option label="是" value="1"></el-option>
        </el-select>
      </el-form-item>

      <!--是否发送MQ -->
      <div v-if="data.saveMQ=='1' ? true : false">

        <el-form-item label="MQ类型：" prop="mqType" :label-width="formLabelWidth">
          <el-select class="w480" v-model="data.mqType">
            <el-option value="1" label="rabbbitmq"></el-option>
            <el-option value="2" label="kafka"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="MQ地址" prop="mqAddress" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mqAddress" placeholder="172.16.16.72:5672"></el-input>
        </el-form-item>

        <el-form-item label="用户名" prop="mqUserName" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mqUserName"></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="mqPasswd" :label-width="formLabelWidth">
          <el-input type="password" class="w480" v-model="data.mqPasswd"></el-input>
        </el-form-item>

        <el-form-item label="队列名称" prop="mqTopicName" :label-width="formLabelWidth">
          <el-input class="w480" v-model="data.mqTopicName"></el-input>
        </el-form-item>
      </div>


      <el-form-item label="是否缓存：" :label-width="formLabelWidth">
        <el-select class="w550" v-model="cacheInput[0].value" @change="selectCache">
          <el-option v-for="item in cacheInputOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <div v-if="cacheInput[0].value == 1 ?true :false">
        <el-form-item label="缓存时间：" prop="cacheNo" :label-width="formLabelWidth" :rules="[{required:true,message:'缓存时间不能为空'},{ type: 'number', message: '缓存必须为数字值'}]">
          <el-input class="w350" v-model.number="data.cacheNo" v-if="data.cacheUnit == '1' || data.cacheUnit == '2' || data.cacheUnit == null || data.cacheUnit ==''"></el-input>
          <el-input class="w350" v-model.number="data.cacheNo" :disabled="true" v-if="data.cacheUnit == 3"></el-input>

          <el-radio-group v-model="data.cacheUnit" @change="moreTime()">
            <el-radio style="display:inline-block;margin-left:10px;" class="radio" v-model="data.cacheUnit" label="1">小时</el-radio>
            <el-radio style="display:inline-block" class="radio"  v-model="data.cacheUnit" label="2">天</el-radio>
            <el-radio style="display:inline-block" class="radio" v-model="data.cacheUnit" label="3">不限</el-radio>
          </el-radio-group>
          <div class="chacheTimeInput din"></div>
          </el-form-item>
      </div>

      <el-form-item label="Mock" :label-width="formLabelWidth">
        <el-select style="width:550px" v-model="data.isMock" @change="selectMock">
          <el-option v-for="item in isMockOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>

      <!-- 是否使用Mock -->
      <div v-if="data.isMock=='1' ? true : false">
        <el-form-item label="Mock返回结果" prop="mockData" :label-width="formLabelWidth" :rules="[{required: true, message: '不能为空', trigger: 'blur'},{validator:mockEmpty, trigger: 'blur'}]">
          <el-input type="textarea" style="width:550px" v-model="data.mockData"></el-input>
        </el-form-item>
      </div>

      <div class="dialog-footer teCen">
        <el-button class="cancelBtn" @click="cancle('addApiData1')">取 消</el-button>

        <el-button type="primary" @click="nextStep">下 一 步</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import apiListNav from './apiListNav';
  import axios from "axios";
  export default {
    name: 'firstDialog',
    props: ["data", "groupOptions","cacheInputOptions","cacheInput"],
    components: {
      apiListNav,
    },

    data() {
      let mqTopicNameRule = (rule, value, callback) => {
        let regExp = /\s+/;
        if (regExp.test(value)) {
          callback(new Error('输入格式错误'));
        } else {
          callback();
        }
      };

      let checkUnEmpty = (rule, value, callback) => {
        var reg = /\s+/;
        if (value === '') {
          callback(new Error('请输入内容'));
        }
        else if (reg.test(value)) {
          callback(new Error('不能输入空格'));
        } else {
          callback();
        }
      };

      let soleApi = (rule, value, callback) => {
        var reg = /\s+/;
        if ((value = this.data.apiName)) {
          const apiNames = this.data.apiName;
          const apiGroupIds = this.data.apiGroupId;

          axios({
            method: 'post',
            url: '/api/checkUnique',
            data: {
              apiGroupId: apiGroupIds,
              apiName: apiNames
            }
          }).then(res => {
            console.log(res);
            if (res.data.data === false) {
              callback(new Error('API名称重复'));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          }).catch(error => {
            alert('网络错误，不能访问');
          });

        } else {
          callback();
        }
      };

      let descriptionValidate = (rule, value, callback) => {
        let res = /.+/;
        var reg = /\s+/;
        if (res.test(value) === false) {
          callback(new Error('输入值不能为空和包含其他非法字符'));
        } else if (reg.test(value)) {
          callback(new Error('不能输入空格'));
        } else {
          callback();
        }
      };

      return {
        mockEmpty : (rule, value, callback) => {
          var reg = /\s+/;
          if (value === '') {
            callback(new Error('请输入描述'));
          }
          else if (reg.test(value)) {
            callback(new Error('不能输入空格'));
          } else {
            callback();
          }
        },
        formLabelWidth: '125px',
        radio: '1',
        // cacheInput:2,
        isMockOptions: [{ value: 1, label: '使用MOCK' }, { value: 2, label: '不使用MOCK' }],

        //基本信息验证
        rulesGroup: {
          apiName: [
            { required: true, message: 'API名称不能为空', trigger: 'blur' },
            { min: 4, max: 50, message: '长度在 4 到 50 个字符', trigger: 'blur' },
            { validator: soleApi, trigger: 'blur' },
            { validator: descriptionValidate, trigger: 'blur' }

          ],
          apiDescription: [
            { required: true, message: '描述不能为空', trigger: 'blur' },
            { min: 1, max: 2000, message: '长度在 1 到 2000 个字符', trigger: 'blur' },
            { validator: descriptionValidate, trigger: 'blur' }
          ],
          mongodbURI: [
            { required: "this.data.saveMongoDB == '1' ? true : false", message: 'MongodbURI不能为空', trigger: 'blur' },
            { validator: checkUnEmpty, trigger: 'blur' }
          ],
          mongodbDBName: [
            { required: "this.data.saveMongoDB == '1' ? true : false", message: 'Mongodb库名不能为空', trigger: 'blur' },
            { validator: checkUnEmpty, trigger: 'blur' }
          ],
          mongodbCollectionName: [
            { required: "this.data.saveMongoDB == '1' ? true : false", message: 'Mongodb表名不能为空', trigger: 'blur' },
            { validator: checkUnEmpty, trigger: 'blur' }
          ],
          mqType: [
            { required: "this.data.saveMQ == '1' ? true : false", message: '请选择MQ类型', trigger: 'blur' },
          ],
          mqAddress: [
            { required: "this.data.saveMQ == '1' ? true : false", message: '请选择MQ地址', trigger: 'blur' },
            { validator: checkUnEmpty, trigger: 'blur' }
          ],
          mqUserName: [
            { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' },
          ],
          mqPasswd: [
            { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' },
          ],
          mqTopicName: [
            { required: "this.data.saveMQ == '1' ? true : false", message: '请输入队列名称', trigger: 'blur' },
            { validator: mqTopicNameRule, trigger: 'blur' }
          ],
        },
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    },
    created() {
      console.log(this.cacheInput[0].value);
    },
    methods: {
      //不限时间
      moreTime(){
        if(this.data.cacheUnit == '3'){
          this.data.cacheNo = 0;
          $(".chacheTimeInput").show();
        }else{
          $(".chacheTimeInput").hide();
          // this.data.cacheNo = '';
        }
      },
      selectCache(){
        if(this.cacheInput[0].value == 2){
          this.data.cacheNo = '';
          this.data.cacheUnit = '';
        }
      },
      selectMock(){
        if(this.data.isMock == 2){
          this.data.mockData = '';
        }
      },

      nextStep: function () {
        this.$refs["addApiData1"].validate((valid) => {
          if (valid) {
            this.$emit('nextStep', this.data);
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      cancle: function (formName) {
        this.$refs[formName].resetFields();
        this.$emit('cancle', "");
      },
      handleNodeClick(data) {
        this.data.apiGroupId = data.label;
      }
    }
  }

</script>


<style scoped>
.chacheTimeInput{
  color:red;position:absolute;top:2px;left:2px;
  background:#eef1f6;width:347px;color:#eef1f6;
  height:34px;
}
</style>



