<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <page-header>
      <div slot="searchContent">
        <!--<el-button type="primary" icon="search" size="mini" @click="searchByCond">搜索</el-button>-->
        <el-button type="primary" icon="search" size="mini"  @click="searchData()">搜 索</el-button>
        <el-button type="success" icon="plus" size="mini" @click="openCreate('only')">创 建</el-button>
        <el-button type="success" icon="plus" size="mini" @click="openCreate('more')">批量创建</el-button>
      </div>
      <div slot="searchBody">
        <div class="search-body-item">
          <label>appKey:</label>
          <el-input placeholder="请输入appKey进行查询" class="w200" size="small" v-model='searchParams.appKey'></el-input>
        </div>
        <div class="search-body-item">
          <label>apiId:</label>
          <el-input placeholder="请输入apiId进行查询" class="w200" size="small" v-model='searchParams.apiId'></el-input>
        </div>
      </div>
    </page-header>
    <!--<el-form :inline="true" class="search_form">-->
      <!--<el-form-item>-->
        <!--<el-input placeholder="请输入appKey进行查询" class="w350" v-model='searchParams.appKey'></el-input>-->
        <!--<el-input placeholder="请输入apiId进行查询" class="w350" v-model='searchParams.apiId'></el-input>-->
      <!--</el-form-item>-->
      <!--<el-form-item>-->
        <!--<el-button type="info" class="searchOne" @click="searchData()">搜 索</el-button>-->
        <!--<el-button type="primary addOne" class="searchOne" @click="openCreate">新 建</el-button>-->
      <!--</el-form-item>-->
    <!--</el-form>-->

    <el-table :data="tableList" border style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column resizable show-overflow-tooltip prop="" type="index" label="序号" width="80"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="appKey" label="appKey"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="apiId" label="接口id"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="apiName" label="接口名称"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="startTime" label="起始时间" :formatter="dateFormat"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="endTime" label="终止时间" :formatter="dateFormat"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="strategyUuid" label="计费规则UUID" v-if="false"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="strategyName" label="计费规则"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="stock" label="调用余量"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="price" label="价格"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="createTime" label="创建时间" :formatter="dateFormatTime"></el-table-column>
      <el-table-column resizable show-overflow-tooltip prop="updateTime" label="更新时间" :formatter="dateFormatTime"></el-table-column>
      <el-table-column label="操作" width="190px">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="stockHistory(scope.row)">余额历史</el-button>
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="updateAuth(scope.row)">编辑</el-button>
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="delIp(scope.row)">删除</el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="searchParams.pageNum"
                     :page-sizes="[10, 20, 30, 40]" :page-size="searchParams.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 新建弹框 start -->
    <el-dialog :title="titleText"
               :visible.sync="createAuthFlag"
               size="tiny" :before-close="handleClose" style="top:-5%">
      <el-form :model="addGroupData" v-if="createAuthFlag" ref="addGroupDataRef" class="demo-ruleForm">
        <el-form-item label="分组：" prop="apiName" :label-width="formLabelWidth" v-if="titleText === '创建' || titleText === '批量创建'">
          <!--<el-input class="w9000" v-model="addGroupData.apiName" v-if="titleText !== '新建'" :disabled="titleText !== '新建'"></el-input>-->
          <el-select  class="w9000" filterable v-model="createGroupName" placeholder="请选择" @change="getApiIdAry" >
            <el-option
              v-for="item in apiNameAry"
              :key="item.id"
              :label="item.groupName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接口名称：" prop="apiId" v-show="titleText !== '批量创建'" :label-width="formLabelWidth" :rules="[{required:true, message: 'apiId不能为空'}]">
          <el-input class="w9000" v-model="addGroupData.apiName" v-if="titleText !== '创建'" :disabled="titleText !== '创建'"></el-input>
          <el-select class="w9000" filterable v-model="addGroupData.apiId" @change="getAppName" placeholder="请选择" v-if="titleText === '创建'">
            <el-option
              v-for="item in apiAry"
              :key="item.id"
              :label="item.apiName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接口名称：" prop="apiIds" v-show="titleText === '批量创建'" :label-width="formLabelWidth" :rules="[{required:true, message: 'apiIds不能为空'}]">
          <el-select class="w9000" multiple filterable v-model="addGroupData.apiIds" placeholder="请选择">
            <el-option
              v-for="item in apiAry"
              :key="item.id"
              :label="item.apiName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="appKey：" prop="appKey" :label-width="formLabelWidth" :rules="[{required:true, message: 'appKey不能为空'}]">
          <el-input class="w9000" v-model="addGroupData.appKey" v-if="titleText !== '创建' && titleText !== '批量创建'" :disabled="titleText !== '创建' && titleText !== '批量创建'"></el-input>
          <el-select class="w9000" filterable v-model="addGroupData.appKey" @change="selectChange" placeholder="请选择"  v-if="titleText === '创建' || titleText === '批量创建'">
            <el-option
              v-for="item in appKeyAry"
              :key="item.id"
              :label="item.appKey"
              :value="item.appKey">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="计费规则：" prop="strategyUuid" :label-width="formLabelWidth">
          <el-select class="w9000" @change="selectChange" filterable v-model="addGroupData.strategyUuid" placeholder="请选择计费规则">
            <el-option v-for="item in billingRules" :key="item.uuid" :label="item.name" :value="item.uuid">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="价格：" prop="price" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.price"></el-input>
        </el-form-item>

        <el-form-item label="起始日期：" prop="startTime" :label-width="formLabelWidth" :rules="[{required:true, message: '起始日期不能为空'}]">
          <el-date-picker
            v-model="addGroupData.startTime"
            type="date"
            style="width: 90%"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="终止时间：" prop="endTime" :label-width="formLabelWidth" :rules="[{required:true, message: '终止时间不能为空'}]">
          <el-date-picker
            style="width: 90%"
            v-model="addGroupData.endTime"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="closeDialog">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addAuth()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="详情" :visible.sync="showStockDialog" size="large" style="top:-5%;" @close="dialogStockClose">
      <el-table ref="multipleTable" :data="tableStockData" tooltip-effect="dark"
                style="width: 100%;border-bottom: 1px solid #e6e6e6; border-right: 1px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="apiId" label="接口Id"></el-table-column>
        <el-table-column prop="appKey" label="客户"></el-table-column>
        <el-table-column prop="stock" label="余量"></el-table-column>
        <el-table-column prop="createTime" label="日期"  :formatter="dateFormat"></el-table-column>
      </el-table>
      <div class="page" style="padding-bottom:30px;" v-if="this.tableStockData.length > 0">
        <el-pagination style="text-align:right;margin-top:30px;" @size-change="handleSizeStockChange"
                       @current-change="handleCurrentStockChange"
                       :current-page="this.stockSearch.pageNum"
                       :page-sizes="[10, 20, 30, 40]"
                       :page-size="this.stockSearch.pageSize" layout=" sizes, prev, pager, next, jumper"
                       :total="tableStockTotal">
        </el-pagination>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  import PageHeader from '@/components/common/pageHeader.vue'

  export default {
    name: 'findApi',
    components: {
      PageHeader
    },
    data() {
      return {
        jurisdiction:{},
        addGroupData:{},
        amendObj:{},
        index:0,
        apiNameAry: [],
        apiAry: [],
        appKeyAry: [],
        billingRules: [],
        titleText:'',
        searchParams: {
          pageNum: 1,
          pageSize: 10,
          appKey: '',
          apiId: ''
        },
        createAuthFlag:false,
        total: 0,
        page: 1,
        searchIp:'',
        listLoading: false,
        tableList: [],
        newPage:'',
        formLabelWidth: "120px",
        companyId:'',
        createGroupName: '',
        showStockDialog: false,
        tableStockData: [],
        stockSearch: {
          appKey: '',
          apiId: null,
          pageSize: 10,
          pageNum: 1
        },
        addType: '',
        tableStockTotal: 0,
        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined || date == 0) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD");
        },
        dateFormatTime(row, column) {
          const date = row[column.property];
          if (date == undefined || date == 0) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      }
    },

    created() {
      this.utils.getUserPermissions(this);
      this. initData();
      this.getApiNameAry();
      this.getAppKeyAry();
      this.utils.getBillingRules(this);
    },

    methods: {
      //初始化
      initData() {
        this.listLoading = true
        axios({
          method: 'post',
          url: '/gateway/charge/auth/search',
          data: this.searchParams
        }).then(res => {
          this.listLoading = false;
          if (res.data.code === 2000) {
            this.tableList = res.data.data.list;
            this.total = res.data.data.total;
          } else {
            this.$message({type: "info",message: res.data.message});
          }

        }).catch(error => {
          this.listLoading = false
          console.log('网络错误，不能访问: ' + error);
        });
      },
      //搜索框查询
      searchData() {
        this.searchParams.pageNum = 1
        this.initData()
      },
      //新建
      addAuth(){
        const httpData = JSON.parse(JSON.stringify(this.addGroupData))
        httpData.startTime = httpData.startTime ?  Number(moment(httpData.startTime).format('x')) : ''
        httpData.endTime = httpData.endTime ? Number(moment(httpData.endTime).format('x')) : ''
        let url = ' '
        if (this.titleText === '创建' || this.titleText === '批量创建') {
          delete httpData.id
          if (this.addType === 'more')  {
            delete httpData.apiId
            url = '/gateway/charge/auth/listSave'
          } else {
            delete httpData.apiIds
            url = '/gateway/charge/auth/save'
          }
        } else {
          url = '/gateway/charge/auth/edit'
        }

        axios({
          method: 'post',
          url: url,
          data: httpData
        }).then(res => {
          if(res.data.code === 2000){
            this.$message({type: "success",message: this.titleText === '编辑' ? '编辑成功' : '创建成功'});
//            this.createAuthFlag = false;
            this.searchData();
            this.amendObj = {};
//            this.addGroupData = {};
            this.titleText = '';
            this.closeDialog()
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //编辑
      updateAuth(row){
        this.amendObj = row;
        this.createAuthFlag = true;
        this.titleText = '编辑';
        // this.addGroupData.appKey = row.appKey;
        // this.addGroupData.strategyUuid = row.strategyUuid;
        // this.addGroupData.price = row.price;
        // this.addGroupData.apiId = row.apiId;
        // this.addGroupData.apiName = row.apiName;
        // this.addGroupData.id = row.id;
        this.addGroupData = row;
        this.addGroupData.startTime = moment(row.startTime).format("YYYY-MM-DD");
        this.addGroupData.endTime = moment(row.endTime).format("YYYY-MM-DD");
      },

      //删除
      delIp(row){
        this.delIpData = row;
        this.$confirm(`是否删除",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.delIPList(row);
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },

      delIPList(row){
        axios({
          method: 'post',
          url: '/gateway/charge/auth/delete',
          data: {
            id: row.id
            // appKey: row.appKey,
            // apiId: row.apiId
          }

        }).then(res => {
          if(res.data.code === 2000){
            this.$message({type: "success",message: '删除成功'});
            this.searchData();
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },


      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      handleCurrentChange(val) {
        this.searchParams.pageNum = val;
        this.initData();


      },
      handleSizeChange(val) {
        this.searchParams.pageSize = val;
        this.searchData();
      },
      getApiNameAry() {
        axios({
          method: 'post',
          url: '/apiGroup/listOfNoLowerLevelGroup',
          data: {}
        }).then(res => {
          if(res.data.code === 2000){
              this.apiNameAry = res.data.data
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      getApiIdAry(groupId) {
        this.utils.getApiAry(this, groupId)
        if (this.titleText === '批量创建') {
          this.$set(this.addGroupData, 'apiIds', [])
        } else  if (this.titleText === '创建') {
          this.$set(this.addGroupData, 'apiId', null)
        }
//        if (!name)  return
//        let id = this.apiNameAry[this.apiNameAry.findIndex(item => {
//          return item.groupName === name
//        })].id
//        axios({
//          method: 'post',
//          url: '/api/findApiByGroupId',
//          data: {id: id}
//        }).then(res => {
//          if(res.data.code === 2000){
//            this.apiAry = res.data.data
//            if (this.titleText === '批量创建') {
//              this.addGroupData.apiIds = []
//            } else if (this.titleText === '创建') {
//              this.addGroupData.apiId = null
//            }
//          }else{
//            this.$message({type: "info",message: res.data.message});
//          }
//        }).catch(error => {
//          console.log('网络错误，不能访问');
//        });
      },

      getAppKeyAry() {
        axios({
          method: 'post',
          url: '/app/findApp',
          data: {}
        }).then(res => {
          if(res.data.code === 2000){
            this.appKeyAry = res.data.data
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      openCreate(type) {
        this.createAuthFlag = true
        this.titleText = type === 'more' ? '批量创建' : '创建'
        this.addType = type
//        this.addGroupData = {
//          startTime: moment(new Date()).format('YYYY-MM-DD'),
//          endTime:  moment(new Date()).add(1, 'years').format('YYYY-MM-DD'),
//          appKey: '',
//          apiId: '',
//          apiName: ''
//        }
      },
      handleClose(done) {
        this.$refs.addGroupDataRef.resetFields()
        this.createAuthFlag = false
        this.createGroupName = ''
        done();
      },
      closeDialog() {
        this.$refs.addGroupDataRef.resetFields()
        this.createAuthFlag = false
        this.createGroupName = ''
      },
      getAppName(apiId) {
        this.apiAry.forEach(item => {
          if (item.id === apiId) {
            this.addGroupData.apiName = item.apiName
          }
        })
      },
      stockHistory(row) {
        this.stockSearch.appKey = row.appKey
        this.stockSearch.apiId = row.apiId
        this.handleCurrentStockChange(1)
        this.showStockDialog = true

      },
      dialogStockClose() {
        this.showStockDialog = false
      },
      handleSizeStockChange(size) {
        this.stockSearch.pageSize = size
        this.handleCurrentStockChange(1)
      },
      handleCurrentStockChange(page) {
        this.stockSearch.pageNum = page
        this.stockHandle()
      },
      stockHandle() {
        axios({
          method: 'post',
          url: '/gateway/charge/auth/stock/history',
          data: this.stockSearch
        }).then(res => {
          if(res.data.code === 2000){
            this.tableStockData = res.data.data.list
            this.tableStockTotal = res.data.data.total
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      selectChange() {
        this.$forceUpdate()
      }
    }

  }
</script>

<style scoped>
  .el-select-dropdown.is-multiple .el-select-dropdown__item.selected{
    color: #fff;
  }
</style>



