<template>
  <div>
    <apiListNav :activeIndex=2></apiListNav>

    <p style="clear:both"></p>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">请求基础定义</span>
      </div>
    </div>
    <el-form :model="addApiData" class="demo-ruleForm" ref="addApiData2">
      <el-form-item label="协议" :label-width="formLabelWidth">
        <span>HTTP</span>
      </el-form-item>

      <el-form-item label="请求Path" prop="apiInfo.httpPath" :label-width="formLabelWidth" :rules="[{validator: checkReqPath, required: true, trigger: 'blur' }]">
        <el-input type="httpPath" class="w350" v-model="addApiData.apiInfo.httpPath" auto-complete="off"></el-input>
      </el-form-item>

      <el-form-item label="HTTP Method" :label-width="formLabelWidth">
        <el-select class="w350" v-model="addApiData.apiInfo.httpMethod" @change="checkHTTP">
          <el-option v-for="item in httpMethodOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>

      <div class="panel panel-default">
        <div class="console-panel-header">
          <span class="panel-header">入参定义</span>
          <table class="table table-hover table-bordered" id="addtableData">
            <thead>
              <tr>
                <th>参数名</th>
                <th>参数位置</th>
                <th>类型</th>
                <th>必填</th>
                <th>默认值</th>
                <th>示例</th>
                <th>描述</th>
                <th>操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in addApiData.requestParamsList">
                <td>
                  <el-form-item label="" :prop="'requestParamsList.' + index + '.paramName'" :rules="[
                  {validator: parasNamesValidate, required: true, trigger: 'blur' },
                  { min: 1, max: 200, message: '长度在 1 到 200 个字符', trigger: 'blur' },
              ]">
                    <el-input v-model="item.paramName"></el-input>
                  </el-form-item>
                </td>
                <td>
                  <el-select v-model="item.paramsLocation">
                    <el-option v-for="item in paramsLocationOptions" :class="(item.value==3) ? 'noBody' : 'hasBody' " :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled"></el-option>
                  </el-select>
                </td>
                <td>
                  <el-select v-model="item.paramsType" @change="parTypeSix(item.paramsType)">
                    <el-option v-for="item in paramsTypeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </td>
                <td>
                  <el-checkbox :checked="item.paramsMust == 1 ? true : false" @change="paramsMusts(item.paramsMust,index)"></el-checkbox>
                </td>
                <td>
                  <el-input v-if="item.paramsType == 1" :disabled="item.paramsMust==1 ? true : false" v-model="item.paramsDefaultValue"></el-input>

                  <el-input v-if="item.paramsType == 2 || item.paramsType == 3 || item.paramsType == 4 || item.paramsType == 5" :disabled="item.paramsMust==1 ? true : false" v-model="item.paramsDefaultValue" @change="IntegerNumVal(item.paramsDefaultValue,index)"></el-input>

                  <el-select v-model="item.paramsDefaultValue" placeholder="请选择" :disabled="item.paramsMust==1 ? true : false" v-if="item.paramsType == 6">
                    <el-option v-for="item in optionsBool" :key="item.label" :label="item.label" :value="item.label"></el-option>
                  </el-select>
                  <p class="textRed din IntegerNum">请输入正确格式的参数！</p>
                </td>
                <!-- v-if="item.paramsMust == 2" -->
                <td>
                  <input type="text" class="form-control" v-model="item.paramsExample">
                </td>
                <td>
                  <el-form-item label="" :prop="'requestParamsList.' + index + '.paramsDescription'" :rules="{required: true, message: '描述不能为空', trigger: 'blur'}">
                    <el-input v-model="item.paramsDescription"></el-input>
                  </el-form-item>
                  <!--<input type="text" class="form-control" :prop="'requestParamsList.' + index + '.paramsDescription'" -->
                         <!--v-model="item.paramsDescription" >-->
                </td>
                <td>
                  <el-button type="text" class="radiusBtn" size="small" v-on:click="amendApiMore(index)">编辑</el-button>
                  <el-button type="text" class="radiusBtn" size="small" @click="delTable(index)">移除</el-button>
                </td>
              </tr>
            </tbody>
          </table>
          <div style="margin:20px 0">请求中的所有参数，包括Path中的动态参数、Headers参数、Query参数、Body参数（通过Form表单传输的参数），参数名称保证唯一。</div>
          <el-button type="primary" size="mini" style="padding:10px 15px;width:97px;" class="mt20" @click="addTable">
            <i class="el-icon-plus" style="margin-right:5px"></i>增加一条</el-button>
        </div>
      </div>

      <div class="dialog-footer teCen">
        <el-button type="info" class="searchOne" @click="preStep">上 一 步</el-button>
        <el-button type="primary" @click="nextStep">下 一 步</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import apiListNav from './apiListNav';
import axios from "axios";
export default {
  name: 'secondDialog',
  props: ['addApiData','backEndParam'],
  components: {
    apiListNav,
  },

  data() {

    return {
    	valueBool:"",
      requestWay:"",
      formLabelWidth: '125px',
      httpMethodOptions: [{ value: 1, label: 'GET' }, { value: 2, label: 'POST' }],
      paramsLocationOptions:[{ value: 2, label: 'query' }, { value: 1, label: 'header' }, { value: 3, label: 'body' }],
      paramsTypeOptions:[{ value: 1, label: 'String' }, { value: 2, label: 'Integer' }, { value: 3, label: 'Long' },
                        { value: 4, label: 'Float' }, { value: 5, label: 'Double' }, { value: 6, label: 'Boolean' }],
      optionsBool:[
        {value:1,label:"true"},
        {value:2,label:"false"}
      ],
      //入参效验
      parasNamesValidate: (rule, value, callback) => {
        let regExp = /^[^\u4e00-\u9fa5]{0,}$/;
        let reg = /\s+/;
        if (value === '') {
          callback(new Error('请输入参数'));
        } else if (regExp.test(value) == false) {
          callback(new Error('输入格式错误,不能输入中文字符'));
        } else if (reg.test(value)) {
          callback(new Error('不能输入空格'));
        } else {
          callback();
        }
      },

      //请求path
      checkReqPath: (rule, value, callback) => {
        var reg = /^[^\u4e00-\u9fa5]{0,}$/;
        var res = /^(\/[A-Za-z0-9_.])+./;
        if (value === "") {
          callback(new Error("请输入请求Path"));
        }else if(reg.test(value) === false){
          callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
        } else if (res.test(value) === false) {
          callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
        } else {
          const httpPathVal = this.addApiData.apiInfo.httpPath;
          axios({
            method: "post",
              url: "/api/httpPathUnique",
              data: {
                httpPath: httpPathVal
              }
          }).then(res =>{
            console.log(res);
            if(res.data.data !== null){
              callback(new Error('该路径与'+res.data.data+'的API请求path重复'));
            }else{
              callback();
            }
          }).catch(error =>{
            alert("网络错误，不能访问");
          });
        }

      },

    }
  },
  created() {

  },
  mounted(){
      if(this.addApiData.apiInfo.httpMethod == 1){
          this.paramsLocationOptions = [{ value: 2, label: 'query' },{ value: 1, label: 'header' }]
      }else if(this.addApiData.apiInfo.httpMethod == 2){
          this.paramsLocationOptions = [{ value: 2, label: 'query' }, { value: 1, label: 'header' },{ value: 3, label: 'body' }]
      }
  },
  methods: {
    parTypeSix(data){
      if(data == 6){
        this.addApiData.requestParamsList.map(data =>{
          data.paramsDefaultValue = '';
        })
      }
    },
    IntegerNumVal(value, index) {
      console.log(222);
      console.log(value);
      let numLong = /^[0-9]+([.]{1,200}[0-9]+){0,1}$/;
      let reg = /\s+/;
      if(value === ""){
        $(".IntegerNum").eq(index).hide();
      }else if(numLong.test(value)  == false || reg.test(value)){
        $(".IntegerNum").eq(index).show();
      }else{
        $(".IntegerNum").eq(index).hide();
      }
    },


    //必填
    paramsMusts(value,index) {
      console.log(value);
      if (!this.addApiData.requestParamsList[index].paramsMust)
        $(".IntegerNum").eq(index).hide();
        this.addApiData.requestParamsList[index].paramsDefaultValue = "";
        this.valueBool = '';
        this.addApiData.requestParamsList[index].paramsMust = !this.addApiData.requestParamsList[index].paramsMust;
    },
    //增加一条
    addTable() {
      console.log(this.addApiData.apiInfo.httpMethod);
      if(this.addApiData.apiInfo.httpMethod == 1){
        this.addApiData.requestParamsList.push({
          maxLength: "50",
          minLength: "1",
          paramName: "",
          paramsLocation: this.paramsLocationOptions[0].value || "",
          paramsType: this.paramsTypeOptions[0].value || "",
          paramsMust: 1,
          paramsDefaultValue: "",
          paramsExample: "",
          paramsDescription: "",
          regularExpress: "" //参数验证
        });
      }else{
        this.addApiData.requestParamsList.push({
          maxLength: "50",
          minLength: "1",
          paramName: "",
          paramsLocation: this.paramsLocationOptions[2].value || "",
          paramsType: this.paramsTypeOptions[0].value || "",
          paramsMust: 1,
          paramsDefaultValue: "",
          paramsExample: "",
          paramsDescription: "",
          regularExpress: "" //参数验证
        });
      }

      // if(this.addApiData.apiInfo.httpMethod == 1){
      //   this.paramsLocationOptions = [{ value: 1, label: 'header' }, { value: 2, label: 'query' }]
      // }else if(this.addApiData.apiInfo.httpMethod == 2){
      //   this.paramsLocationOptions = [{ value: 1, label: 'header' }, { value: 2, label: 'query' }, { value: 3, label: 'body' }]
      // }
    },

    //编辑更多
    amendApiMore(index) {
      this.amendApiIndex = index;
      this.$emit('amendApiMore', this.amendApiIndex);
    },

    //移除一条
    delTable: function(index) {
      console.log(index);
      this.addApiData.requestParamsList.splice(index, 1);
      this.backEndParam.splice(index, 1);
      this.amendApiIndex = 0;
    },

    nextStep: function() {
      let regExp = /^[0-9]+([.]{1,200}[0-9]+){0,1}$/;
      let reg = /\s+/;
      let flag = true;
      this.addApiData.apiInfo.backEndHttpMethod = this.addApiData.apiInfo.httpMethod;
      this.$refs["addApiData2"].validate((valid) => {
        if (valid) {
          let len = this.addApiData.requestParamsList.length;
          let len2 = this.backEndParam.length;

          if (len != len2) {
            for (let i = 0; i < len - len2; i++) {
              this.backEndParam.push({
                paramName: "",
                paramValue: "",
                paramsLocation: 2,
                paramDescription: "",
                paramsType: 1
              });
            }
          };

          this.addApiData.requestParamsList.map((data, index) =>{
            if(data.paramsType ==2 || data.paramsType ==3 || data.paramsType ==4 ||data.paramsType ==5 ){
              if(data.paramsDefaultValue === ''){
              $(".IntegerNum").eq(index).hide();
            }else if(regExp.test(data.paramsDefaultValue) == false || reg.test(data.paramsDefaultValue)){
              $(".IntegerNum").eq(index).show();
              flag = false;
            }else {
              $(".IntegerNum").eq(index).hide();
            }
          }
//            if(data.paramsDescription === '') {
//              $(".jqueryValidateDescription").eq(index).show();
//              flag = false;
//            }
          });

          if(flag){
            this.$emit('nextStep', this.addApiData);
          }

        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    preStep: function() {
      this.$emit('preStep', "");
    },
    checkHTTP(val){
        if (this.addApiData.apiInfo.httpMethod == 2) {
          this.paramsLocationOptions = [];
          this.paramsLocationOptions.push(
            { value: 2, label: "query" },
            { value: 1, label: "header" },
            { value: 3, label: "body" }
          );
          this.addApiData.requestParamsList.map(data =>{
            data.paramsLocation = 3;
          })
        } else {
          if (this.requestParamsList) {
            let list = this.requestParamsList;
            for (let i = 0; i < list.length; i++) {
              if (list[i].paramsLocation == 3) {
                this.$message({
                  message: "参数列表中含有body参数,不能选择GET",
                  type: "warning"
                });
                this.addApiData.apiInfo.httpMethod = 2;
                return;
              }
            }
          }
          this.paramsLocationOptions = [];
          this.paramsLocationOptions.push(
            { value: 2, label: "query" },
            { value: 1, label: "header" },
          );
          this.addApiData.requestParamsList.map(data =>{
            data.paramsLocation = 2;
          })
        }
    }
  }
}
</script>


<style scoped>
/*.noBody{
  display: none;
}*/
</style>



