<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">

    <el-button class="createApi_btn addOne" v-if="addSBtn" type="primary" @click="creatFluid=true">创建策略</el-button>
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入策略名称进行查询" class="w350" v-model="searchStrategy"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="createData()">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table ref="multipleTable" :data="flowData" tooltip-effect="dark" style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="tableRowClassName">
      <el-table-column prop="name" label="策略名称"></el-table-column>
      <el-table-column prop="unit" label="单位" :formatter="unitFormat"></el-table-column>
      <el-table-column prop="no" label="流量限制" show-overflow-tooltip></el-table-column>
      <el-table-column prop="" label="操作"  width="120">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn amendColor" v-if="amendSBtn" size="small" @click="amendStrategy(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn delColor" v-if="delSBtn" size="small" @click="showDel(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.flowData.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <!-- 修改策略 start -->
    <el-dialog title="修改策略" :visible.sync="amendStrategyDialog" size="tiny">
      <el-form :model="flowAmendData" :rules="rules2" ref="flowAmendData" label-width="100px" class="demo-ruleForm" >

        <el-form-item label="策略名称" :label-width="formLabelWidth" prop="name">
          <el-input v-model="flowAmendData.name" auto-complete="off" class="w9000"></el-input>
        </el-form-item>

        <el-form-item label="单位" :label-width="formLabelWidth" prop="unit">
          <el-select v-model="flowAmendData.unit" placeholder="请选择单位" class="w9000">
            <el-option :key="1" label="分钟" :value="1"></el-option>
            <el-option :key="2" label="小时" :value="2"></el-option>
            <el-option :key="3" label="天" :value="3"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="流量限制" :label-width="formLabelWidth" prop="no">
          <el-input v-model="flowAmendData.no" auto-complete="off" class="w9000"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="amendStrategyDialog = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="modifyStrategy('flowAmendData')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 修改策略 end -->

    <div class="page" style="padding-bottom:30px;" v-if="this.flowData.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 创建流控弹框 start -->
    <el-dialog title="创建策略" :visible.sync="creatFluid" size="tiny" @close='closeDialog1'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="名称：" :label-width="formLabelWidth" prop='name'>
          <el-input class="w9000" v-model="ruleForm.name"></el-input>
          <p style="line-height:20px;">支持汉字、英文字母、数字、英文格式和下划线，必须以英文字母或汉字开头，4~15个字符</p>
        </el-form-item>

        <el-form-item label="单位：" :label-width="formLabelWidth" prop='unit'>
          <el-select v-model="ruleForm.unit" placeholder="请选择单位" class="w9000">
            <el-option :key="1" label="分钟" :value="1"></el-option>
            <el-option :key="2" label="小时" :value="2"></el-option>
            <el-option :key="3" label="天" :value="3"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="API流量限制：" :label-width="formLabelWidth" prop='no'>
          <el-input class="w9000" v-model="ruleForm.no"></el-input>
          次 (不超过API流量限制值)
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="creatFluid  = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addStrategy('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 创建流控弹框 end -->

  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "flowMonitoring",
  components: {},
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let sNameRule1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须以英文字母或汉字开头"));
      } else {
        callback();
      }
    };
    //------------------必须是汉字、英文字母、数字、英文格式或下划线的正则------------------//
    let sNameRule2 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("格式错误"));
      } else {
        callback();
      }
    };
    //------------------API流量限制自定义验证------------------//
    let sNo = (rule, value, callback) => {
      let regExp = /^[0-9]*[1-9][0-9]*$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须是正整数"));
      } else {
        callback();
      }
    };
    //------------------API流量最大为10000000次------------------//
    let sNum = (rule, value, callback) => {
      if (value > 10000000) {
        callback(new Error("最大为10000000(一千万)次"));
      } else {
        callback();
      }
    };

    // 策略唯一性
    let soleFlow = (rule, value, callback) => {
        var reg = /\s+/;
        if ((value = this.rowFlowData.name)) {
          const name = this.rowFlowData.name;
          axios({
            method: 'post',
            url: '/strategy/checkStrategyUnique',
            data: {
              strategyName: this.flowAmendData.name
            }
          }).then(res => {
            if (res.data.data === true){
              callback(new Error('策略名称重复'));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          }).catch(error => {
            alert('网络错误，不能访问');
          });

        } else {
          callback();
        }
      };

      let soleFlow2 = (rule, value, callback) => {
        var reg = /\s+/;
        if ((value != this.rowFlowData.name)) {
          axios({
            method: 'post',
            url: '/strategy/checkStrategyUnique',
            data: {
              strategyName: this.flowAmendData.name
            }
          }).then(res => {
            if (res.data.data === true){
              callback(new Error('策略名称重复'));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          }).catch(error => {
            alert('网络错误，不能访问');
          });

        } else {
          callback();
        }
      };

    return {
      jurisdiction:{},
      addSBtn:false,
      amendSBtn:false,
      delSBtn:false,
      pageNum: 1,
      pageSize: 10,
      total: 5,
      pageNumA: 1,
      pageSizeA: 5,
      totalA: 5,
      amendStrategyDialog: false,
      appList: [],
      oderr: "",
      flowData: [],
      flwDatalin:'',
      tableData3: [],
      flowAmendData: {
        name: "",
        no: "",
        unit: 1
      },
      group: "",
      value: "",
      searchStrategy: "",
      formLabelWidth: "120px",
      description: "",
      currentPage4: 4,
      creatFluid: false, // 创建流控弹框
      delData: "",
      multipleSelection: [],
      strategyInfo: {},
      flowArr: {},
      rowFlowData:{},
      newPage:'',

      unitFormat(row, column) {
        const unit = row[column.property];
        if (unit == undefined) {
          return "";
        }
        return unit == 1 ? "分钟" : unit == 2 ? "小时" : "天";
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      ruleForm: {
        name: "",
        no: "",
        unit: 1
      },
      rules: {
        name: [
          { required: true, message: "请输入策略名称", trigger: "blur" },
          { min: 4, max: 15, message: "长度在 4 到 15 个字符", trigger: "blur" },
          { validator: sNameRule2, trigger: "blur" },
          { validator: soleFlow, trigger: 'blur' },
        ],
        no: [
          { required: true, message: "请输入API流量限制"},
          { validator: sNo, trigger: "blur" },
          { validator: sNum, trigger: "blur" }
        ]
      },
      rules2: {
        name: [
          { required: true, message: "请输入策略名称", trigger: "blur" },
          { min: 4, max: 15, message: "长度在 4 到 15 个字符", trigger: "blur" },
          { validator: sNameRule2, trigger: "blur" },
          { validator: soleFlow2, trigger: 'blur' },
        ],
        no: [
          { required: true, message: "请输入API流量限制"},
          { validator: sNo, trigger: "blur" },
          { validator: sNum, trigger: "blur" }
        ]
      },
    };
  },

  created() {
    this.createData();
    this.getUserPermissions();
  },

  methods: {
    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化列表
    createData() {
      axios({
        method: "post",
        url: "/strategy/index",
        data: {
          name: this.searchStrategy,
          pageSize: this.newPage ==''? 10 :this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }
        if(!this.jurisdiction.roles.includes('admin')){
          this.amendSBtn = false;
          this.delSBtn = false;
          this.addSBtn = false;
        }else{
          this.amendSBtn = true;
          this.delSBtn = true;
          this.addSBtn = true;
        }
        this.flowData = res.data.data.elements;
        this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;

      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },
    //修改策略
    amendStrategy(row) {
      this.rowFlowData = row;
      this.flwDatalin = this.rowFlowData.name;
      axios({
        method: "post",
        url: "/strategy/details",
        data: {
          id: row.id
        }
      }).then(res => {
        this.amendStrategyDialog = true;
        this.flowAmendData.id = res.data.data.id;
        this.flowAmendData.name = res.data.data.name;
        this.flowAmendData.no = res.data.data.no;
        this.flowAmendData.unit = res.data.data.unit;
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    modifyStrategy(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/strategy/update",
            data: {
              id: this.flowAmendData.id,
              name: this.flowAmendData.name,
              unit: this.flowAmendData.unit,
              no: this.flowAmendData.no
            }
          }).then(res => {
            this.amendStrategyDialog = false;
            this.createData();
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    // 创建分组
    addPort: function() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hour = date.getHours();
      var minute = date.getMinutes();
      var second = date.getSeconds();

      this.tableData.push({
        group: this.group,
        description: this.description,
        data:
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          hour +
          ":" +
          minute +
          ":" +
          second
      }),
        (this.group = "");
      this.description = "";
      if (this.group == "") {
        $(".group").show();
      } else {
        $(".group").hide();
        this.tableData.push({
          group: this.name,
          description: this.descript
        });
      }
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.createData();
    },
    handleSizeChange(val){
      this.newPage = val;
      this.createData();
    },
    //创建策略
    addStrategy(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/strategy/add",
            data: {
              name: this.ruleForm.name,
              unit: this.ruleForm.unit,
              no: this.ruleForm.no
            }
          }).then(res => {
              if (res.data.code != 2000) {
                this.$message({
                  message: res.data.message,
                  type: "warning"
                });
              } else {
                this.creatFluid = false;
                this.createData();
              }
            }).catch(error => {
              console.log("网络错误，不能访问");
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //删除策略显示弹框
    showDel(row) {
      for(var i = 0; i<this.jurisdiction.perms.length; i++){
        if(this.jurisdiction.perms[i] == 'dde36ab7732e4f87b46c65fe39589aec'){
          this.delData = row;
          this.$confirm(`是否删除策略 "${this.delData.name}" ,如删除则不可恢复!`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            this.delStrategy();
          }).catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
        }
      }
    },
    //删除策略确认
    delStrategy() {
      axios({
        method: "post",
        url: "/strategy/delete",
        data: {
          id: this.delData.id
        }
      }).then(res => {
          if(res.data.code == 2000){
            this.createData();
          }else{
            this.$message({
              message: res.data.message,
              type: 'warning'
            });
          }
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    //appList分页查询
    handleCurrentChangeA(val) {
      this.pageNumA = val;
      // this.getAppList();
    },
    //复选框初始化时候是否可勾选
    checkboxInit(row, index) {
      const uuid = row.limitStrategyuuid;
      if (uuid != null && uuid != "") return 0;
      else return 1;
    },
    //复选框状态改变
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //关闭弹框的事件
    closeDialog() {
      this.oderr = "";
    },
    //关闭弹框的事件
    closeDialog1() {
      this.ruleForm.name = "";
      this.ruleForm.no = "";
      this.ruleForm.unit = 1;
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.accreditLine {
  width: 100%;
  height: 1px;
  background: #ebebeb;
  position: relative;
  top: -15px;
}

.active {
  color: white;
  background: #546478;
}

#accreditBox {
  min-width: 1200px;
}

.flBox {
  width: 73%;
}

.addBtn {
  width: 60px;
  height: 28px;
  color: white;
  background: #20a0ff;
  display: inline-block;
  text-align: center;
  outline: none;
}

.addedBtn {
  width: 60px;
  height: 28px;
  color: #bcbcbc;
  background: #f7f7f7;
  display: inline-block;
  text-align: center;
  outline: none;
  border: 1px solid #e4e4e4;
}
</style>



