<template>
  <div class="flowDistribution" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入接口名称进行查询" v-model="searchFlowName" class="w350"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="apiFlowData">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="apiFlow" style="border-top:8px solid #e6e6e6;border-bottom:1px solid #e6e6e6" border class="flowBorder">
      <el-table-column prop="apiName" label="接口名称" ></el-table-column>
      <el-table-column prop="apiGroupName" label="分组"></el-table-column>
      <el-table-column label="后端服务名称"  width="500">
        <template slot-scope="scope">
          <portName :rowFlowData="scope.row" :subApis="apiFlow" :subApisData="scope.row.subApis" :apiFlowData="apiFlowData" :parentMsg="parentMsg"></portName>
        </template>
      </el-table-column>
      <el-table-column prop="weight" label="权重" width="120">
        <template slot-scope="scope">
          <weight :subApis="apiFlow" :subApisData="scope.row.subApis" style="border:none"></weight>
        </template>
      </el-table-column>
      <el-table-column prop="province" label="操作" width="150">
        <template slot-scope="scope">
          <router-link v-if="addwetBtn" :to="{path:'portManage',query: {num:2,id:scope.row.id,groupId:scope.row.groupId}}">
            <el-button type="text" class="radiusBtn addWcolor" size="small">添加</el-button>
          </router-link>
          <el-button type="text" class="radiusBtn onBindColor" v-if="wetBtn" size="small" @click="weightBtn(scope.row.subApis)">权重配置</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.apiFlow.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <el-dialog title="权重配置" :visible.sync="weightFormVisible" size="tiny">
      <div class="panel panel-default">
        <div class="console-panel-header">
          <table class="table table-hover table-bordered">
            <thead>
              <tr>
                <th>后端服务名称</th>
                <th>权重</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in amendWeight">
                <td>
                  <span>{{item.interfaceName}}</span>
                </td>
                <td>
                  <input v-model="item.weights" class="form-control weightVal"/>
                  <p class="textRed din weightNum">权重只能是0-10之间的整数！</p>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="weightFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="amendWeightData">确 定</el-button>
      </div>
    </el-dialog>

    <div class="page" style="padding-bottom:30px;" v-if="this.apiFlow.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import portName from "@/components/common/portName";
import weight from "@/components/common/weight";

export default {
  name: "flowDistribution",
  components: {
    portName,
    weight
  },
  data() {
    return {
      jurisdiction:{},
      parentMsg:[],
      wetBtn:false,
      addwetBtn:false,
      num: "",
      pageNum: 1,
      pageSize: 10,
      total: 5,
      apiFlow: [],
      weightObj: [],
      amendWeight: [],
      searchFlowName: "",
      formLabelWidth: "120px",
      weightFormVisible: false
    };
  },

  created() {
    this.apiFlowData();
    this.getUserPermissions();
  },

  methods: {
    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    // 添加
    addFlow(row) {
      axios({
        method: "post",
        url: "/api/back/sub/query",
        data: {
          apiId: row.groupId,
          id: row.id
        }
      })
        .then(res => {
          console.log(res, "添加");
        })
        .catch(error => {
          console.log("网络错误");
        });
    },
    apiFlowData() {
      axios({
        method: "post",
        url: "/api/back/sub/query",
        data: {
          apiName: this.searchFlowName,
          pageSize: this.newPage == ''? 10 :this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }
        this.searchFlowName = "";

        this.parentMsg = this.jurisdiction.roles;
        if(!this.jurisdiction.roles.includes('admin')){
          this.wetBtn = false;
          this.addwetBtn = false;
        }else{
          this.wetBtn = true;
          this.addwetBtn = true;
        }
        this.apiFlow = res.data.data.list;


          let newJson = res.data.data.list;
          for (var i = 0; i < newJson.length; i++) {
            let childText = "";
            for (var j = 0; j < newJson[i].subApis.length; j++) {
              childText += newJson[i].subApis[j].interfaceName;
              this.apiFlow[i].newtext = childText;
            }
          }
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.total;
          for (var i = 0; i < res.data.data.list.length; i++) {
            for (var j = 0; j < res.data.data.list[i].subApis.length; j++) {
              this.weightObj = res.data.data.list[i].subApis;
            }
          }
        }).catch(error => {
          console.log("网络错误");
        });
    },

    //权重配置
    amendWeightData() {
      var result = /^(?:10|[0-9])$/;
      var regex=/^\d+\.\d+$/;
      let weightConfigs = [];
      this.amendWeight.map((data,index) => {
        weightConfigs.push({
          apiId: data.apiId,
          intefaceName: data.intefaceName,
          subId: data.id,
          weight: data.weights
        });

        if(result.test(data.weights) || !regex.test(data.weights)){
          axios({
            method: "post",
            url: "/api/back/sub/weight/update/config",
            data: { weightConfigs }
          }).then(res => {
            if (res.data.code == 2000) {
              this.weightFormVisible = false;
              this.apiFlowData();
            } else {
              this.$message.error('权重只能是0-10之间的整数！');
            }
          }).catch(error => {
            console.log("网络错误");
          });
        }else{
          this.$message.error('权重只能是0-10之间的整数！');
        }
    });

    },
    weightBtn(row) {
      for(var i = 0; i<this.jurisdiction.perms.length; i++){
        if(this.jurisdiction.perms[i] == '1e6daa89858f42fb88277c3544b45064'){
          this.amendWeight = row;
          this.weightFormVisible = true;
          this.amendWeight.map(data =>{
            data.weights = data.weight;
          });
        }
      }
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.apiFlowData();
    },
    handleSizeChange(val){
      this.newPage = val;
      this.apiFlowData();
    },
  }
};
</script>

<style scoped>
.el-form-item {
  display: inline-block;
}
</style>



