<template>
  <div class="flowControl" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入名字进行查询" class="w200" v-model="searchParams.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="请输入请求路径进行查询" class="w200" v-model="searchParams.path"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="请输入服务ID进行查询" class="w200" v-model="searchParams.serviceId"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="请输入URL进行查询" class="w200" v-model="searchParams.url"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="searchInit()">搜 索</el-button>
      </el-form-item>
      <el-button class="createApi_btn" style="top: 0" type="primary addOne" v-if="createBtN" @click="addHandle">新增</el-button>
    </el-form>
    <el-table ref="multipleTable" :data="flowData" tooltip-effect="dark" style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="tableRowClassName">
      <el-table-column prop="name" label="名字"></el-table-column>
      <el-table-column prop="path" label="请求路径"></el-table-column>
      <el-table-column prop="serviceId" label="服务ID"></el-table-column>
      <el-table-column prop="url" label="URL"></el-table-column>
      <el-table-column prop="description" label="描述"></el-table-column>
      <el-table-column prop="stripPrefix" label="stripPrefix" :formatter="boolFormat"></el-table-column>
      <el-table-column prop="retryable" label="retryable" :formatter="boolFormat"></el-table-column>
      <el-table-column prop="enabled" label="enabled" :formatter="boolFormat"></el-table-column>
      <!--<el-table-column prop="updateTime" label="操作时间" :formatter="dateFormat"></el-table-column>-->
      <el-table-column prop="" label="操作" width="140">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn onBindColor" size="small" v-if="updateBtn" @click="updateHandle(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn amendColor" size="small" v-if="deleteBtn" @click="delApiBtn(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page" style="padding-bottom:30px;" v-if="flowData.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="current"
                     :page-sizes="[10, 20, 30, 40]" :page-size="searchParams.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
      <!-- <el-pagination style="text-align: right;margin-top:80px;" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination> -->
    </div>
    <el-dialog :title="titleTab" :visible.sync="creataut" size="tiny" @close=''>
      <el-form :model="addPortForm" :rules="addPortFormRules" ref="addPortForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="名称：" :label-width="formLabelWidth" prop='name'>
          <el-input class="w9000"   v-model="addPortForm.name"></el-input>
        </el-form-item>
        <el-form-item label="请求路径：" :label-width="formLabelWidth" prop='path'>
          <el-input v-model="addPortForm.path" class="w9000" ></el-input>
        </el-form-item>
        <el-form-item label="服务ID：" :label-width="formLabelWidth" prop='serviceId'>
          <el-input  v-model="addPortForm.serviceId" class="w9000" ></el-input>
        </el-form-item>
        <el-form-item label="URL：" :label-width="formLabelWidth" prop='url'>
          <el-input  v-model="addPortForm.url" class="w9000" ></el-input>
        </el-form-item>
        <el-form-item label="stripPrefix：" :label-width="formLabelWidth" prop='stripPrefix'>
          <el-select v-model.number="addPortForm.stripPrefix" placeholder="请选择stripPrefix" class="w9000">
            <el-option v-for="item in boolAry" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
          <el-form-item label="retryable：" :label-width="formLabelWidth" prop='retryable'>
            <el-select v-model.number="addPortForm.retryable" placeholder="请选择retryable" class="w9000">
              <el-option v-for="item in boolAry" :key="item.id" :label="item.label" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="enabled：" :label-width="formLabelWidth" prop='enabled'>
              <el-select v-model.number="addPortForm.enabled" placeholder="请选择enabled" class="w9000">
                <el-option v-for="item in boolAry" :key="item.id" :label="item.label" :value="item.id"></el-option>
              </el-select>
        </el-form-item>
          <el-form-item label="描述：" :label-width="formLabelWidth" prop='description'>
          <el-input  v-model.number="addPortForm.description" class="w9000" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('addPortForm')">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addaut('addPortForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        searchParams: {
          pageNum: 1,
          pageSize: 10,
          name: "",
          path: "",
          serviceId: "",
          url: "",
        },
        jurisdiction: {},
        total: 0,
        flowData: [],
        current: 1,
        updateBtn: false,
        deleteBtn: false,
        createBtN: false,
        creataut: false,
        titleTab: '',
        addPortForm: {},
        addPortFormRules: {
          name: [{ required:true, trigger: 'blur', message: '名称不能为空',}],
          path: [{ required:true, trigger: 'blur', message: '请求路径不能为空',}],
          serviceId: [{ required:true, trigger: 'blur', message: '服务ID不能为空',}],
          url: [{ required:true, trigger: 'blur', message: 'URL不能为空',}]
        },
        formLabelWidth: "120px",
        boolAry: [
          {
            id: 1,
            label: '是'
          },
          {
            id: 0,
            label: '否'
          }
        ],
        delApiData: {}, //删除API保存对象
      }
    },
    components: {},
    created() {
      this.searchInit();
      this.getUserPermissions();
      // this.menuData();
    },
    mounted() {
    },
    methods: {
      //初始化列表
      dataInit() {
        axios({
          method: 'post',
          url: '/serviceRoute/index',
          data: this.searchParams
        }).then(res => {
          if(res.data.code == 403){
            location.href = res.data.data;
          }
          if(!this.jurisdiction.roles.includes('admin')){
            this.deleteBtn = false;
            this.updateBtn = false;
            this.createBtN = false
          }else{
            this.updateBtn = true;
            this.deleteBtn = true;
            this.createBtN = true
          }
          this.flowData = res.data.data.list;
          this.total = res.data.data.total;

        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      searchInit() {
        this.searchParams.pageNum = 1
        this.current = 1
        this.dataInit()
      },
      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },
      handleCurrentChange(val) {
        this.searchParams.pageNum = val;
        this.dataInit();
      },
      handleSizeChange(val){
        this.searchParams.pageSize = val;
        this.dataInit();
      },
      updateHandle(row) {
        this.creataut = true
        this.titleTab = '编辑'
        this.addPortForm = row
      },
      delApiBtn(index, row){
        this.delApiData = row;
        this.delApiData.index = index;

        this.$confirm(`是否删除服务器路由 "${this.delApiData.name}",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.delApiMes(index);
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },
      delApiMes(index) {
        const delData = this.delApiData;
        axios({
          method: "post",
          url: "/serviceRoute/delete",
          data: {
            id: delData.id
          }
        })
          .then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: "error",
                message: res.data.message
              });
            } else {
              this.flowData.splice(index, 1);
              this.$message({
                type: "success",
                message: "删除API成功"
              });
            }
          }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      addHandle() {
        this.addPortForm = {
          retryable: 1,
          stripPrefix: 1,
          enabled: 1
        }
        this.titleTab = '创建'
        this.creataut = true
      },
      callOf(formName){
        this.creataut  = false;
        this.addPortForm = {};
        this.$refs[formName].resetFields();
      },
      addaut(formName){
        this.$refs[formName].validate(valid => {
          if (valid) {
            axios({
              method: 'post',
              url: this.titleTab == '创建'? '/serviceRoute/add':'/serviceRoute/update',
              data: {
                path:this.addPortForm.path,
                serviceId: this.addPortForm.serviceId,
                name: this.addPortForm.name,
                url: this.addPortForm.url,
                description:this.addPortForm.description,
                enabled:this.addPortForm.enabled,
                stripPrefix:this.addPortForm.stripPrefix,
                retryable:this.addPortForm.retryable,
                id:this.titleTab == '编辑' ? this.addPortForm.id : '',
              }
            }).then(res => {
              if(res.data.code == 2000){
                if(this.titleTab == '创建'){
                  this.$message({type: "success",message: '新建成功'});
                }else{
                  this.$message({type: "success",message: '编辑成功'});
                }
                this.creataut = false;
                this.searchInit();
                this.$refs[formName].resetFields();
              }else{
                this.$message({type: "info",message: res.data.message});
              }
            }).catch(error => {
              console.log('网络错误，不能访问');
              this.addPortForm = {};
            });
          }else {
            console.log("error submit!!");
            return false;
          }
        });
      },

      boolFormat(row, column, cellValue) {
        if (cellValue === 1) {
          return 'true'
        }
        else if (cellValue === 0){
          return 'false'
        } else {
          return ''
        }

      }
    }
  }
</script>
<style scoped>
  .accreditLine {
    width: 100%;
    height: 1px;
    background: #ebebeb;
    position: relative;
    top: -15px;
  }

  .active {
    color: white;
    background: #546478
  }

  #accreditBox {
    min-width: 1200px;
  }

  .flBox {
    width: 73%;
  }

  .addBtn {
    width: 60px;
    height: 28px;
    color: white;
    background: #20a0ff;
    display: inline-block;
    text-align: center;
    outline: none;
  }

  .addedBtn {
    width: 60px;
    height: 28px;
    color: #bcbcbc;
    background: #f7f7f7;
    display: inline-block;
    text-align: center;
    outline: none;
    border: 1px solid #e4e4e4;
  }
</style>



