<template>
  <div class="flowMonitoringDetail" style="background:white;">

    <div class="detailBtn" style="padding:8px 10px 0;">
      <div class="fl">
        <span class="detailNav">流量控制</span>
        <router-link to="/flowMonitoring">
          <el-button size="small" style="width:auto">返回流量控制列表</el-button>
        </router-link>
      </div>
      <p style="clear:both"></p>
    </div>

    <div class="panel panel-default">

      <el-tabs type="border-card">
        <el-tab-pane label="绑定策略">
          <div class="panel panel-default" style="position:relative">
            <el-button class="fr" type="primary" @click="bindApii()" size="small" style="margin-bottom:10px;">绑定APP</el-button>
            <el-table ref="multipleTable" :data="authedList" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
              <el-table-column label="分组" width="120">
                <template slot-scope="scope">{{scope.row.id}}</template>
              </el-table-column>
              <el-table-column prop="appName" label="名称"></el-table-column>
              <el-table-column prop="appKey" label="描述"></el-table-column>
              <el-table-column prop="appSecret" label="运行环境"></el-table-column>
              <el-table-column prop="updateTime" label="授权时间" :formatter='dateFormat'></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button type="text" class="radiusBtn" size="small" @click='removeApp(scope.row.id)'>解绑</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <!-- 绑定授权弹框 start -->
    <el-dialog title="为策略绑定应用" :visible.sync="bindApi" size="small" id="accreditBox" @close='closeDialog'>
      <div class="accreditLine"></div>
      <el-form>
        <div>
          <span style="font-weight:700;">选择要绑定的应用</span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px;width: 100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="请输入应用名称" v-model='oderr'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getAppList()">搜索</el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="appList" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto" @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="55" class="selection" prop='limitStrategyuuid' :selectable='checkboxInit' @selection-change="handleSelectionChange"></el-table-column>

              <el-table-column label="应用名称">
                <template slot-scope="scope">{{ scope.row.appName}}</template>
              </el-table-column>
              <el-table-column prop="appKey" label="appKey"></el-table-column>
              <el-table-column prop="createTime" label="创建时间" :formatter='dateFormat'></el-table-column>
            </el-table>
          </div>

        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bindApi = false">取 消</el-button>
        <el-button type="primary" @click="setAuth()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改策略弹框 start -->
    <el-dialog title="修改策略" :visible.sync="amendFluid" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="名称：" :label-width="formLabelWidth" prop='sName'>
          <el-input class="w9000" v-model="ruleForm.sName"></el-input>
          <p>支持汉字、英文字母、数字、英文格式的下划线，必须以英文字母或汉字开头，4~15个字符</p>
        </el-form-item>

        <el-form-item label="单位：" :label-width="formLabelWidth" prop='sUnit'>
          <el-select v-model="ruleForm.sUnit" placeholder="请选择单位" class="w9000">
            <el-option :key="1" label="分钟" :value="1"></el-option>
            <el-option :key="2" label="小时" :value="2"></el-option>
            <el-option :key="3" label="天" :value="3"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="API流量限制：" :label-width="formLabelWidth" prop='sNo'>
          <el-input class="w9000" v-model="ruleForm.sNo"></el-input>
          次 (不超过API流量限制值)
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendFluid  = false">取 消</el-button>
        <el-button type="primary" @click="modifyStrategy('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 修改策略弹框 end -->
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'flowMonitoringDetail',
  components: {
  },
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let sNameRule1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
      if (regExp.test(value) === false) {
        callback(new Error('必须以英文字母或汉字开头'));
      } else {
        callback();
      }
    };
    //------------------必须是汉字、英文字母、数字、英文格式或下划线的正则------------------//
    let sNameRule2 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error('格式错误'));
      } else {
        callback();
      }
    };
    //------------------API流量限制自定义验证------------------//
    let sNo = (rule, value, callback) => {
      let regExp = /^[0-9]*[1-9][0-9]*$/;
      if (value == null || value == '') {
        callback(new Error('请输入API流量限制'));
      } else if (regExp.test(value) === false) {
        callback(new Error('必须是正整数'));
      } else if (value > 10000000) {
        callback(new Error('最大为10000000(一千万)次'));
      } else {
        callback();
      }
    };
    const generateData = _ => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `备选项 ${i}`,
          disabled: i % 4 === 0
        });
      }
      return data;
    };
    return {
      amendFluid: false, // 修改策略弹框
      bindApi: false, //绑定API
      currentPage4: 4,
      formLabelWidth: '120px',
      value: '',
      createTime: '',
      updateTime: '',
      strategyInfo: {},
      updateTimeAuthed: '',
      authedList: [],
      oderr: '',
      appList: [],
      delData: '',
      multipleSelection: [],
      data: generateData(),
      value3: [1],
      renderFunc(h, option) {
        return `<span>{ option.key } - { option.label }</span>`;
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      ruleForm: {
        sName: '',
        sNo: '',
        sUnit: ''
      },
      rules: {
        sName: [
          { required: true, message: '请输入策略名称', trigger: 'blur' },
          { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
          { validator: sNameRule2, trigger: 'blur' }
        ],
        sUnit: [
          // { required: true, message: '请选择单位', trigger: 'change' }
        ],
        sNo: [
          { validator: sNo, trigger: 'blur' }
        ]
      }

    };
  },

  created() {
    this.created();
  },

  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleChange(value, direction, movedKeys) {
      console.log(value, direction, movedKeys);
    },
    //页面策略信息初始化
    created() {
      let id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/strategy/details',
        data: {
          id: id
        }
      }).then(res => {
        this.strategyInfo = res.data.data;
        this.createTime = moment(res.data.data.createTime).format("YYYY-MM-DD HH:mm:ss");
        this.updateTime = moment(res.data.data.updateTime).format("YYYY-MM-DD HH:mm:ss");
        this.ruleForm.sName = res.data.data.name;
        this.ruleForm.sNo = res.data.data.no;
        this.ruleForm.sUnit = res.data.data.unit;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
      axios({
        method: 'post',
        url: '/strategy/getAllAuthed',
        data: {
          strategyUuid: this.$route.query.uuid
        }
      }).then(res => {
        this.authedList = res.data.data;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //修改策略
    modifyStrategy(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/strategy/update',
            data: {
              id: this.strategyInfo.id,
              name: this.ruleForm.sName,
              unit: this.ruleForm.sUnit,
              no: this.ruleForm.sNo
            }
          }).then(res => {
            this.amendFluid = false;
            this.created();
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //删除单个App
    removeApp(id) {
      this.$confirm('此操作将解绑该app, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/strategy/removeApp',
          data: {
            id: id
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '解绑成功!'
          });
          this.created();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消解绑'
        });
      });
    },
    bindApii() {
      this.bindApi = true;
      this.getAppList();
    },
    //获取appList
    getAppList() {
      axios({
        method: 'post',
        url: '/strategy/getStrategyList',
        data: {
          order: this.oderr == '' ? null : this.oderr
        }
      }).then(res => {
        this.appList = res.data.data.list;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //复选框初始化时候是否可勾选
    checkboxInit(row, index) {
      const uuid = row.limitStrategyuuid;
      if (uuid != null && uuid != '')
        return 0;
      else
        return 1;
    },
    //复选框状态改变
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //绑定应用
    setAuth() {
      var flag = 0;
      var ids = '';
      if (this.multipleSelection.length != 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            ids += row.id;
          } else {
            ids += (',' + row.id);
          }
          flag++;
        });
        axios({
          method: 'post',
          url: '/strategy/strategyAuth',
          data: {
            strategyUuid: this.strategyInfo.uuid,
            appIds: ids
          }
        }).then(res => {
          this.created();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }
      this.bindApi = false;
    },
    //关闭弹框的事件
    closeDialog() {
      this.oderr = '';
    },
    //关闭弹框的事件
    closeModify() {
      this.ruleForm.sName = this.strategyInfo.name;
      this.ruleForm.sNo = this.strategyInfo.no;
      this.ruleForm.sUnit = this.strategyInfo.unit;
    }

  }
}

</script>

<style scoped>
.transfer-footer {
  margin-left: 20px;
  padding: 6px 5px;
}
</style>



