<template>
  <div class="port" style="background:#ffffff;position:relative;">

    <el-button class="createApi_btn" type="primary" @click="creatGroup=true">创建应用</el-button>
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input v-model="searchGroupName" placeholder="请输入应用名称进行查询" class="w350"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="created()">搜索</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="applyData" border style="width: 100%">
      <el-table-column prop="appName" label="应用名称">
        <template slot-scope="scope">
          <router-link :to="{path:'appListDetail',query:{id:scope.row.id}}">{{scope.row.appName}}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="appKey" label="appKey"></el-table-column>
      <el-table-column prop="description" label="描述"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter='dateFormat'></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="text" @click="openModify(scope.row)">修改</el-button>
          <el-button type="text" @click="removeApp(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page" style="padding-bottom:30px;">
      <el-pagination style="text-align: right;margin-top:80px;" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>
    </div>

    <!-- 创建应用弹框 start -->
    <el-dialog title="创建应用" :visible.sync="creatGroup" size="tiny" @close='closeCreate'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="应用名称：" :label-width="formLabelWidth" prop='appName'>
          <el-input class="w9000" v-model='ruleForm.appName'></el-input>
          <p style="line-height:20px;">全局唯一，支持汉字、英文字母、数字、英文格式的下划线，且必须以字母或汉字开始，4~15个字符</p>
        </el-form-item>

        <el-form-item label="APPkey：" :label-width="formLabelWidth" prop='description'>
          <el-input type="textarea" class="w9000" :rows="4" v-model='ruleForm.description'></el-input>
        </el-form-item>

        <el-form-item label="描述：" :label-width="formLabelWidth" prop='appKey'>
          <el-input type="textarea" class="w9000" :rows="4" placeholder="不超过180个字符" v-model='ruleForm.appKey'></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="creatGroup  = false">取 消</el-button>
        <el-button type="primary" @click="creatApp('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 创建应用弹框 end -->

    <!-- 编辑APP start -->
    <el-dialog title="修改应用" :visible.sync="amendApp" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="应用名称：" :label-width="formLabelWidth" prop='modifyAppName'>
          <el-input v-model='ruleForm.modifyAppName'></el-input>
          <p style="line-height:20px;">全局唯一，支持汉字、英文字母、数字、英文格式的下划线，且必须以字母或汉字开始，4~15个字符</p>
        </el-form-item>
        <el-form-item label="描述：" :label-width="formLabelWidth" prop='modifyDescription'>
          <el-input v-model='ruleForm.modifyDescription' type="textarea" :rows="4" placeholder="不超过180个字符"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendApp  = false">取 消</el-button>
        <el-button type="primary" @click="modifyApp('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑APP end -->

    <!-- 删除应用 start -->
    <el-dialog title="删除应用" :visible.sync="delApp" size="tiny">
      <el-form>
        <p>APP在阿里云是通用的，删除该APP可能会影响到云推送等系统，并且该APP可能已经被授权，删除后该APP将不能被恢复，请确认是否删除？</p>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="delApp  = false">取 消</el-button>
        <el-button type="primary" @click="delApp  = true">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 删除流控 end -->
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'port',
  components: {

  },
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let appName1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error('格式错误'));
      } else {
        callback();
      }
    };
    //------------------检查appName是否重复------------------//
    let appName2 = (rule, value, callback) => {
      if (value != this.modifyBasicAppName) {
        axios({
          method: 'post',
          url: '/app/checkName',
          data: {
            appName: value
          }
        }).then(res => {
          if (res.data.data === false) {
            callback(new Error('应用名称重复'));
          } else {
            callback();
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      } else {
        callback();
      }
    };
    return {
      pageNum: 1,
      pageSize: 10,
      total: 5,
      applyData: [],
      group: '',
      formLabelWidth: '120px',
      creatGroup: false, //创建分组
      delGroup: false,
      // appName:'',
      // description:'',
      modify: '',
      // modifyAppName:'',
      // modifyDescription:'',
      modifyBasicAppName: '',
      amendApp: false, //编辑App
      delApp: false, // 删除App
      searchGroupName: "",//搜索
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      ruleForm: {
        appName: '',
        description: '',
        modifyAppName: '',
        modifyDescription: ''
      },
      rules: {
        appName: [
          { required: true, message: '请输入策略名称', trigger: 'blur' },
          { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        description: [
          { min: 0, max: 180, message: '不超过180个字符', trigger: 'blur' }
        ],
        modifyAppName: [
          { required: true, message: '请输入策略名称', trigger: 'blur' },
          { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        modifyDescription: [
          { min: 0, max: 180, message: '不超过180个字符', trigger: 'blur' }
        ]
      }

    }
  },
  created() {
    this.created();
  },
  methods: {
    //页面初始化
    created() {
      axios({
        method: 'post',
        url: '/app/index',
        data: {
          appName: this.searchGroupName == '' ? null : this.searchGroupName,
          pageSize: 10,
          pageNum: this.pageNum
        }
      }).then(res => {
        this.applyData = res.data.data.elements;
        this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //分页bar
    handleCurrentChange(val) {
      this.pageNum = val;
      this.created();
    },
    //关闭弹框的事件
    closeCreate() {
      this.ruleForm.appName = '';
      this.ruleForm.description = '';
    },
    //创建app应用
    creatApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/add',
            data: {
              appName: this.ruleForm.appName == '' ? null : this.ruleForm.appName,
              description: this.ruleForm.description == '' ? null : this.ruleForm.description
            }
          }).then(res => {
            this.creatGroup = false;
            this.created();
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //打开修改框
    openModify(val) {
      this.modify = val;
      this.ruleForm.modifyAppName = val.appName;
      this.ruleForm.modifyDescription = val.description;
      this.modifyBasicAppName = val.appName;
      this.amendApp = true;
    },
    //修改app
    modifyApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/update',
            data: {
              id: this.modify.id,
              appName: this.ruleForm.modifyAppName == '' ? null : this.ruleForm.modifyAppName,
              description: this.ruleForm.modifyDescription == '' ? null : this.ruleForm.modifyDescription
            }
          }).then(res => {
            this.amendApp = false;
            this.created();
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //关闭弹框的事件
    closeModify() {
      this.ruleForm.modifyAppName = this.modify.appName;
      this.ruleForm.modifyDescription = this.modify.description;
    },
    //删除App
    removeApp(id) {
      this.$confirm('此操作将删除该app, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/delete',
          data: {
            id: id
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.created();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.createLabel {
  text-align: right;
}

.must {
  color: red;
  display: none;
}
</style>



