<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">

    <el-button class="createApi_btn addOne" v-if="addSBtn" type="primary" @click="createBillingRule=true">创建规则</el-button>
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input v-model="searchForm.name" placeholder="规则名称" ></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model="searchForm.billingMode" placeholder="请选择">
          <el-option
            v-for="item in billingModeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>
    </el-form>

    <el-table ref="multipleTable" :data="tableList" tooltip-effect="dark" style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
      <el-table-column prop="id" label="ID" v-if="false"></el-table-column>
      <el-table-column prop="" type="index" label="序号" width="80"></el-table-column>
      <el-table-column prop="name" label="规则名称"></el-table-column>
      <el-table-column label="计费类型">
        <template slot-scope="scope">
          <span v-if="scope.row.billingType === 1">时间计费</span>
          <span v-if="scope.row.billingType === 2">按条计费</span>
        </template>
      </el-table-column>
      <el-table-column label="计费周期">
        <template slot-scope="scope">
          <span v-if="scope.row.billingCycle === 1">年</span>
          <span v-if="scope.row.billingCycle === 2">季度</span>
          <span v-if="scope.row.billingCycle === 3">月</span>
        </template>
      </el-table-column>
      <el-table-column prop="billingCycleLimit" label="次数" :formatter="billingRuleLimitFormat"></el-table-column>
      <el-table-column label="计费模式" >
        <template slot-scope="scope">
          <span v-if="scope.row.billingMode === 1">{{billingModeOptions[1].label}}</span>
          <span v-if="scope.row.billingMode === 2">{{billingModeOptions[2].label}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="updateTime" label="更新时间" :formatter="dateFormat"></el-table-column>
      <el-table-column label="操作"  width="120">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn amendColor" v-if="amendSBtn" size="small" @click="editBillingRule(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn delColor" v-if="delSBtn" size="small" @click="deleteBillingRule(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>
    <div class="page" style="padding-bottom:30px;" v-if="this.tableList.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="pageNum"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="pageSize" layout=" sizes, prev, pager, next, jumper"
                     :total="totalCount">
      </el-pagination>
    </div>

    <!-- 创建流控弹框 start -->
    <el-dialog title="创建规则" :visible.sync="createBillingRule" size="tiny" @close='closeDialog'>
      <el-form :model="createForm" :rules="rules" ref="createForm" label-width="100px" class="demo-ruleForm">
<!--        <el-form-item label="规则名称：" :label-width="formLabelWidth" prop='name'>
          <el-input class="w9000" v-model="createForm.name"></el-input>
          <p style="line-height:20px;">支持汉字、英文字母、数字、英文格式和下划线，必须以英文字母或汉字开头，4~15个字符</p>
        </el-form-item>-->
        <el-form-item label="计费类型：" :label-width="formLabelWidth" prop='billingType' >
          <el-select v-model="createForm.billingType" placeholder="请选择计费类型" class="w9000">
            <el-option :key="1" label="按时计费" :value="1"></el-option>
            <el-option :key="2" label="按条计费" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="计费周期：" :label-width="formLabelWidth" prop='billingCycle'>
          <el-select v-model="createForm.billingCycle" placeholder="请选择周期单位" class="w9000" :disabled="createForm.billingType == 2? true: false">
            <el-option :key="1" label="年" :value="1"></el-option>
            <el-option :key="2" label="季度" :value="2"></el-option>
            <el-option :key="3" label="月" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="周期额度：" :label-width="formLabelWidth" prop='billingCycleLimit'>
          <el-input class="w9000" v-model="createForm.billingCycleLimit" :disabled="createForm.billingType == 2? true: false"></el-input>
        </el-form-item>
        <el-form-item label="计费模式：" :label-width="formLabelWidth" prop='billingMode'>
          <el-select v-model="createForm.billingMode" placeholder="请选择模式" class="w9000">
            <el-option :key="1" label="查询计费" :value="1"></el-option>
            <el-option :key="2" label="查得计费" :value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="createBillingRule  = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addBillingRule('createForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 创建流控弹框 end -->

    <!-- 修改策略 start -->
    <el-dialog title="修改规则" :visible.sync="editBillingRuleFlag" size="tiny" @close='closeDialog'>
      <el-form :model="createForm" :rules="rules" ref="updateForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="规则名称：" :label-width="formLabelWidth" prop='name'>
          <el-input class="w9000" v-model="createForm.name" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="计费类型：" :label-width="formLabelWidth" prop='billingType' >
          <el-select v-model="createForm.billingType" placeholder="请选择计费类型" class="w9000">
            <el-option :key="1" label="按时计费" :value="1"></el-option>
            <el-option :key="2" label="按条计费" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="计费周期：" :label-width="formLabelWidth" prop='billingCycle'>
          <el-select v-model="createForm.billingCycle" placeholder="请选择周期单位" class="w9000" :disabled="createForm.billingType == 2? true: false">
            <el-option :key="1" label="年" :value="1"></el-option>
            <el-option :key="2" label="季度" :value="2"></el-option>
            <el-option :key="3" label="月" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="周期额度：" :label-width="formLabelWidth" prop='billingCycleLimit'>
          <el-input class="w9000" v-model="createForm.billingCycleLimit" :disabled="createForm.billingType == 2? true: false"></el-input>
        </el-form-item>
        <el-form-item label="计费模式：" :label-width="formLabelWidth" prop='billingMode'>
          <el-select v-model="createForm.billingMode" placeholder="请选择模式" class="w9000">
            <el-option :key="1" label="查询计费" :value="1"></el-option>
            <el-option :key="2" label="查得计费" :value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="editBillingRuleFlag  = false">取 消</el-button>
        <el-button type="primary" class="addOne" @click="editBillingRuleCommit('updateForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 修改策略 end -->


  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "billingRule",
  components: {},
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let sNameRule1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须以英文字母或汉字开头"));
      } else {
        callback();
      }
    };
    //------------------必须是汉字、英文字母、数字、英文格式或下划线的正则------------------//
    let sNameRule2 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("格式错误"));
      } else {
        callback();
      }
    };
    //------------------API流量限制自定义验证------------------//
    let sNo = (rule, value, callback) => {
      let regExp = /^[0-9]*[1-9][0-9]*$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须是正整数"));
      } else {
        callback();
      }
    };
    //------------------API流量最大为10000000次------------------//
    let sNum = (rule, value, callback) => {
      if (value > 10000000) {
        callback(new Error("最大为10000000(一千万)次"));
      } else {
        callback();
      }
    };

    // 策略唯一性
    let soleFlow = (rule, value, callback) => {
        var reg = /\s+/;
        if ((value = this.rowFlowData.name)) {
          const name = this.rowFlowData.name;
          axios({
            method: 'post',
            url: '/gateway/charge/billingRules/checkStrategyUnique',
            data: {
              strategyName: this.flowAmendData.name
            }
          }).then(res => {
            if (res.data.data === true){
              callback(new Error('策略名称重复'));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          }).catch(error => {
            alert('网络错误，不能访问');
          });

        } else {
          callback();
        }
      };
    let soleFlow2 = (rule, value, callback) => {
      var reg = /\s+/;
      if ((value !== this.rowFlowData.name)) {
        axios({
          method: 'post',
          url: '/gateway/charge/billingRules/checkStrategyUnique',
          data: {
            strategyName: this.flowAmendData.name
          }
        }).then(res => {
          if (res.data.data === true){
            callback(new Error('策略名称重复'));
          } else if (reg.test(value)) {
            callback(new Error('不能输入空格'));
          } else {
            callback();
          }
        }).catch(error => {
          alert('网络错误，不能访问');
        });

      } else {
        callback();
      }
    };

    return {
      jurisdiction:{
        principal: {},
        loginUrl: '',
        logoutUrl: '',
        roles: [],
        perms: []
      },
      addSBtn:false,
      amendSBtn:false,
      delSBtn:false,

      pageNum: 1,
      pageSize: 10,
      totalCount: 1,

      pageNumA: 1,
      pageSizeA: 5,
      totalA: 5,
      amendStrategyDialog: false,
      appList: [],
      oderr: "",
      tableList: [],
      flwDatalin:'',
      tableData3: [],
      flowAmendData: {
        name: "",
        no: "",
        unit: 1
      },
      group: "",
      value: "",
      searchForm: {
        name:'',
        billingMode: ''
      },
      formLabelWidth: "120px",
      description: "",
      currentPage4: 4,
      createBillingRule: false, // 创建流控弹框
      editBillingRuleFlag: false, // 编辑流控弹框
      createForm: {
        id: '',
        name: '',
        billingType: '',
        billingCycle: '',
        billingCycleLimit: '',
        billingMode: ''
      },
      delData: "",
      billingModeOptions: [
        {
          value: '',
          label: '全部'
        },
        {
          value: '1',
          label: '查询计费'
        },
        {
          value: '2',
          label: '查得计费'
        }
      ],
      multipleSelection: [],
      strategyInfo: {},
      flowArr: {},
      rowFlowData:{},
      newPage:'',

      unitFormat(row, column) {
        const unit = row[column.property];
        if (unit == undefined) {
          return "";
        }
        return unit == 1 ? "分钟" : unit == 2 ? "小时" : "天";
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      billingRuleLimitFormat(row, column) {
        const limit = row[column.property];
        if (limit === -1) {
          return "无限";
        }
        return limit;
      },
      rules: {
        billingType: [
          { required: true, message: "请选择计费类型"}
        ],
        billingCycle: [
          { required: false, message: "请选择计费周期"}
        ],
        billingCycleLimit: [
          { required: false, message: "请输入计费时间"}
        ],
        billingMode: [
          { required: true, message: "请选择计费模式"}
        ]
      }
    };
  },

  created() {
//    this.utils.getUserPermissions(this);
    this.getUserPermissions();
    this.initData();
  },

  methods: {
    //权限设置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;

        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化列表
    initData() {
      axios({
        method: "post",
        url: "/gateway/charge/billingRules/list",
        data: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,

          name: this.searchForm.name,
          billingMode: this.searchForm.billingMode
        }
      }).then(res => {
        if (res.data.code === 2000) {
          this.tableList = res.data.data.list;
          this.pageNum = res.data.data.pageNum;
          this.pageSize = res.data.data.pageSize;
          this.totalCount = res.data.data.totalCount;
        } else if(res.data.code === 403){
          location.href = res.data.data;
        }
        if(!this.jurisdiction.roles.includes('admin')){
          this.amendSBtn = false;
          this.delSBtn = false;
          this.addSBtn = false;
        }else{
          this.amendSBtn = true;
          this.delSBtn = true;
          this.addSBtn = true;
        }
      }).catch(error => {
        console.log("网络错误，不能访问" + error);
      });
    },
    //创建规则
    addBillingRule(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/gateway/charge/billingRules/add",
            data: {
              billingType: this.createForm.billingType,
              billingCycle: this.createForm.billingCycle,
              billingCycleLimit: this.createForm.billingCycleLimit,
              billingMode: this.createForm.billingMode
            }
          }).then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: 'warning',
                message: res.data.message
              });
            } else {
              this.initData();
              this.$message({
                type: 'success',
                message: res.data.message
              });
            }
            this.createBillingRule = false;
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //修改策略
    editBillingRule(row) {
      this.editBillingRuleFlag = true;

      this.createForm.id = row.id;
      this.createForm.name = row.name;
      this.createForm.billingType = row.billingType;
      this.createForm.billingCycle = row.billingCycle;
      this.createForm.billingCycleLimit= row.billingCycleLimit;
      this.createForm.billingMode = row.billingMode;
    },

    editBillingRuleCommit(formName) {
      if (this.createForm.billingType === 2) {
        this.createForm.billingCycle = '';
        this.createForm.billingCycleLimit = '';
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/gateway/charge/billingRules/update",
            data: {
              id: this.createForm.id,
              name: this.createForm.name,
              billingType: this.createForm.billingType,
              billingCycle: this.createForm.billingCycle,
              billingCycleLimit: this.createForm.billingCycleLimit,
              billingMode: this.createForm.billingMode
            }
          }).then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: 'warning',
                message: res.data.message
              });
            } else {
              this.editBillingRuleFlag = false;

              this.initData();
              this.$message({
                type: 'success',
                message: res.data.message
              });
            }
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    // 创建分组
    addPort: function() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hour = date.getHours();
      var minute = date.getMinutes();
      var second = date.getSeconds();

      this.tableData.push({
        group: this.group,
        description: this.description,
        data:
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          hour +
          ":" +
          minute +
          ":" +
          second
      }),
        (this.group = "");
      this.description = "";
      if (this.group == "") {
        $(".group").show();
      } else {
        $(".group").hide();
        this.tableData.push({
          group: this.name,
          description: this.descript
        });
      }
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    },
    handleSizeChange(val){
      this.pageSize = val;
      this.initData();
    },

    //删除策略显示弹框
    deleteBillingRule(row) {
      for(let i = 0; i<this.jurisdiction.perms.length; i++){
        if(this.jurisdiction.perms[i] === 'dde36ab7732e4f87b46c65fe39589aec'){
          this.delData = row;
          this.$confirm(`是否删除规则 "${this.delData.name}" ,如删除则不可恢复!`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            this.deleteBillingRuleCommit();
          }).catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
        }
      }
    },
    //删除策略确认
    deleteBillingRuleCommit() {
      axios({
        method: "post",
        url: "/gateway/charge/billingRules/delete",
        data: {
          id: this.delData.id
        }
      }).then(res => {
          if(res.data.code === 2000){
            this.initData();
            this.$message({
              type: 'success',
              message: res.data.message
            });
          }else{
            this.$message({
              type: 'warning',
              message: res.data.message
            });
          }
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    //appList分页查询
    handleCurrentChangeA(val) {
      this.pageNumA = val;
      // this.getAppList();
    },
    //复选框初始化时候是否可勾选
    checkboxInit(row, index) {
      const uuid = row.limitStrategyuuid;
      if (uuid != null && uuid != "") return 0;
      else return 1;
    },
    //复选框状态改变
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //关闭弹框的事件
    closeDialog() {
      this.createForm.id='';
      this.createForm.name='';
      this.createForm.billingType='';
      this.createForm.billingCycle= '';
      this.createForm.billingCycleLimit= '';
      this.createForm.billingMode='';
    }

  }
};
</script>

<style scoped>
.accreditLine {
  width: 100%;
  height: 1px;
  background: #ebebeb;
  position: relative;
  top: -15px;
}

.active {
  color: white;
  background: #546478;
}

