  min-width: 1200px;
}

.flBox {
  width: 73%;
}

.addBtn {
  width: 60px;
  height: 28px;
  color: white;
  background: #20a0ff;
  display: inline-block;
  text-align: center;
  outline: none;
}

.addedBtn {
  width: 60px;
  height: 28px;
  color: #bcbcbc;
  background: #f7f7f7;
  display: inline-block;
  text-align: center;
  outline: none;
  border: 1px solid #e4e4e4;
}
</style>



