<template>
  <el-dialog :title="title" :visible="value" :before-close="beforeClose" :modal="modal">
      <slot :close="beforeClose" v-if="visible" :params="params"/>
  </el-dialog>
</template>
<script>
export default {
  name: 'self-dialog',

  props: {
      value: {
          type: Boolean,
          required: true,
          default: false
      },
      modal: {
          type: Boolean,
          required: false,
          default: false
      },
       title: {
          type: String,
          required: true,
          default: false
      }
  },
  data() {
      return {
          visible: false,
          params: {}
      }
  },

  methods: {
      beforeClose(...args) {
          if (args.length == 1 && args[0] instanceof Event) args.shift()
          this.$emit('input', false)
          this.$emit('close', args.length != 0, ...args)
      },
      setParams(params = {}) {
          this.params = {...this.params, ...params}
      }
  },

  watch: {
      value(newVal) {
          if (!newVal) return
          if (!this.visible) {
              this.visible = true;
              return
          }

          this.visible = false
          this.$nextTick(() => this.visible = true)
      }
  }


}
</script>



