<script>
import axios from "axios";
import moment from "moment";
import PageHeader from '@/components/common/pageHeader.vue'

export default {
  name: "datasourceCharge",
  components: {PageHeader},
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let sNameRule1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须以英文字母或汉字开头"));
      } else {
        callback();
      }
    };
    //------------------必须是汉字、英文字母、数字、英文格式或下划线的正则------------------//
    let sNameRule2 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error("格式错误"));
      } else {
        callback();
      }
    };
    //------------------API流量限制自定义验证------------------//
    let currency = (rule, value, callback) => {
      let regExp = /^[0-9]+(.[0-9]{1,4})?$/;
      if (regExp.test(value) === false) {
        callback(new Error("必须是正整数"));
      } else {
        callback();
      }
    };
    //------------------API流量最大为10000000次------------------//
    let sNum = (rule, value, callback) => {
      if (value > 10000000) {
        callback(new Error("最大为10000000(一千万)次"));
      } else {
        callback();
      }
    };

    // 策略唯一性
    let soleFlow = (rule, value, callback) => {
        var reg = /\s+/;
        if ((value = this.rowFlowData.name)) {
          const name = this.rowFlowData.name;
          axios({
            method: 'post',
            url: '/gateway/charge/billingRules/checkStrategyUnique',
            data: {
              strategyName: this.flowAmendData.name
            }
          }).then(res => {
            if (res.data.data === true){
              callback(new Error('策略名称重复'));
            } else if (reg.test(value)) {
              callback(new Error('不能输入空格'));
            } else {
              callback();
            }
          }).catch(error => {
            alert('网络错误，不能访问');
          });

        } else {
          callback();
        }
      };
    let soleFlow2 = (rule, value, callback) => {
      var reg = /\s+/;
      if ((value != this.rowFlowData.name)) {
        axios({
          method: 'post',
          url: '/gateway/charge/billingRules/checkStrategyUnique',
          data: {
            strategyName: this.flowAmendData.name
          }
        }).then(res => {
          if (res.data.data === true){
            callback(new Error('策略名称重复'));
          } else if (reg.test(value)) {
            callback(new Error('不能输入空格'));
          } else {
            callback();
          }
        }).catch(error => {
          alert('网络错误，不能访问');
        });

      } else {
        callback();
      }
    };

    return {
      jurisdiction:{
        principal: {},
        loginUrl: '',
        logoutUrl: '',
        roles: [],
        perms: []
      },
      addSBtn:false,
      amendSBtn:false,
      delSBtn:false,

      pageNum: 1,
      pageSize: 10,
      totalCount: 1,
      checkUpdate: false,

      groupAry: [],
      apiAry: [],
      billingRules: [],

      titleText: '',
      pageNumA: 1,
      pageSizeA: 5,
      totalA: 5,
      amendStrategyDialog: false,
      appList: [],
      oderr: "",
      tableList: [],
      flwDatalin:'',
      tableData3: [],
      flowAmendData: {
        name: "",
        no: "",
        unit: 1
      },
      group: "",
      value: "",
      searchForm: {
        groupId:'',
        apiId: ''
      },
      formLabelWidth: "120px",
      description: "",
      currentPage4: 4,
      createTag: false, // 创建流控弹框
      editTag: false, // 编辑流控弹框
      createForm: {
        groupId: '',
        apiId: '',
        price: '',
        stock: '',
        billingRulesUuid: ''
      },
      delData: "",
      rowFlowData:{},
      addType: '',


      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },

      rules: {
        price: [
          { required: true, message: "请输入单价"}
        ],
        billingCycle: [
          { required: false, message: "请选择计费周期"}
        ],
        billingCycleLimit: [
          { required: false, message: "请输入计费时间"}
        ],
        billingMode: [
          { required: true, message: "请选择计费模式"}
        ]
      }
    };
  },

  created() {
    this.getUserPermissions();
    this.initData();
    this.utils.getGroupAry(this);
    this.utils.getBillingRules(this);
  },

  methods: {
    getUserPermissions() {
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;

        if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
          this.$confirm('权限不足，请联系管理员', '提示', {
            confirmButtonText: '退出',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            location.href = res.data.logoutUrl;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '权限不足，请联系管理员'
            });
          });
        }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    getApiAry(groupId) {
      this.utils.getApiAry(this, groupId)
      this.$set(this.searchForm, 'apiId', null)
    },

    getApiAryCreate(groupId) {
      this.utils.getApiAry(this, groupId)
      if (this.addType === 'more') {
        this.$set(this.createForm, 'apiIds', [])
      } else {
        this.$set(this.createForm, 'apiId', null)
      }
      this.selectChange()
    },

    //初始化列表
    initData() {
      this.pageNum = 1;
      this.initDataHandle()
    },
    initDataHandle() {
      axios({
        method: "post",
        url: "/gateway/charge/datasourceCharge/findForPage",
        data: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          groupId: this.searchForm.groupId,
          apiId: this.searchForm.apiId
        }
      }).then(res => {
        if (res.data.code === 2000) {
          this.tableList = res.data.data.list;
          this.totalCount = res.data.data.total;
        } else if(res.data.code === 403){
          location.href = res.data.data;
        }

        if(!this.jurisdiction.roles.includes('admin')){
          this.amendSBtn = false;
          this.delSBtn = false;
          this.addSBtn = false;
        }else{
          this.amendSBtn = true;
          this.delSBtn = true;
          this.addSBtn = true;
        }
      }).catch(error => {
        console.log("网络错误，不能访问" + error);
      });
    },
    //创建规则
    addData(formName) {
      this.$refs[formName].validate(valid => {
        const httpData = JSON.parse(JSON.stringify(this.createForm))
        console.log(httpData.endTime)
        httpData.endTime = httpData.endTime ? moment(httpData.endTime).format('x') : ''
        httpData.startTime = httpData.startTime ? moment(httpData.startTime).format('x') : ''
        let url = ''
        if (this.addType === 'more') {
          url = '/gateway/charge/datasourceCharge/listSave'
          delete httpData.apiId
        } else {
          url = '/gateway/charge/datasourceCharge/save'
          delete httpData.apiIds
        }
        if (valid) {
          axios({
            method: "post",
            url: url,
            data: httpData
          }).then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: 'warning',
                message: res.data.message
              });
            } else {
              this.initData();
              this.$message({
                type: 'success',
                message: res.data.message
              });
            }
           this.closeDialogCreate()
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //修改策略
    editData(row) {
      this.editTag = true;
      this.createForm = row;
    },

    //更新
    editDataCommit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/gateway/charge/datasourceCharge/check",
            data: this.createForm
          }).then(res => {
            if (res.data.code === 2000) {
              this.updateHandle()
            } else if (res.data.code === 1018) {
              if (Object.keys(res.data.data).length <1) {return}
              const messgeAry = Object.keys(res.data.data)
              const h = this.$createElement;
              this.$msgbox({
                title: '警告',
                message: h('ul', {style: {
                  textAlign: 'left',
                  paddingLeft: '10px',
                  listStyle: 'decimal'
                }}, [
                  messgeAry.map((item, index) => {
                   return h('li', {
                     fontSize: '14px'
                   }, item + res.data.data[item])
                  })
                ]),
                showCancelButton: true,
                confirmButtonText: '确定',
                cancelButtonText: '取消'
              }).then(() => {
                this.updateHandle()
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消'
                });
              });
            } else {
              this.$message({
                type: 'warning',
                message: res.data.message
              });
            }
          }).catch(error => {
            console.dir(error)
            console.log("网络错误，不能访问");
          });

        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    updateHandle() {
      axios({
        method: "post",
        url: "/gateway/charge/datasourceCharge/update",
        data: this.createForm
      }).then(res => {
        if (res.data.code !== 2000) {
          this.$message({
            type: 'warning',
            message: res.data.message
          });
        } else {
          this.initData();
          this.$message({
            type: 'success',
            message: res.data.message
          });
          this.closeDialog()
//              this.$refs[formName].resetFields()
        }
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    //翻页事件
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initDataHandle();
    },

    //改变分页大小事件
    handleSizeChange(val){
      this.pageSize = val;
      this.initData();
    },

    //删除策略显示弹框
    deleteData(row) {
      for(var i = 0; i<this.jurisdiction.perms.length; i++){
        if(this.jurisdiction.perms[i] == 'dde36ab7732e4f87b46c65fe39589aec'){
          this.delData = row;
          this.$confirm(`是否删除接口 "${this.delData.apiName}" ,如删除则不可恢复!`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            this.deleteBillingRuleCommit();
          }).catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
        }
      }
    },

    //关闭弹框的事件
    closeDialog() {
      this.createForm.id='';
      this.$refs.updateFormRef.resetFields()
      this.editTag = false
    },

    closeDialogCreate() {
      this.$refs.createFormRef.resetFields()
      this.createTag  = false
    },

    deleteBillingRuleCommit() {
      axios({
        method: "post",
        url: "/gateway/charge/datasourceCharge/delete",
        data: {
          id: this.delData.id
        }
      }).then(res => {
        if(res.data.code === 2000){
          this.initData();
          this.$message({
            type: 'success',
            message: res.data.message
          });
        }else{
          this.$message({
            type: 'warning',
            message: res.data.message
          });
        }
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    createBtn(type) {
      this.addType = type
      this.createTag = true
      this.createForm.groupId = null
      this.createForm.billingRulesUuid = ''
      this.createForm.endTime = ''
      this.createForm.price = ''
      this.createForm.startTime = ''
      this.createForm.stock = ''
    },
    selectChange() {
      this.$forceUpdate()
    }
  }
};
</script>

<style scoped>
.accreditLine {
  width: 100%;
  height: 1px;
  background: #ebebeb;
  position: relative;
  top: -15px;
}

.active {
  color: white;
  background: #546478;
}

#accreditBox {
  min-width: 1200px;
}

.flBox {
  width: 73%;
}

.addBtn {
  width: 60px;
  height: 28px;
  color: white;
  background: #20a0ff;
  display: inline-block;
  text-align: center;
  outline: none;
}

.addedBtn {
  width: 60px;
  height: 28px;
  color: #bcbcbc;
  background: #f7f7f7;
  display: inline-block;
  text-align: center;
  outline: none;
  border: 1px solid #e4e4e4;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected{
  color: #fff;
}
</style>



