<template>
  <div :style="{fontFamily: 'PingFangSC-Regular', height: '100%', overflow: 'hidden', position: 'relative'}">
    <nav style="height:60px;">
      <topNav></topNav>
    </nav>

    <aside :style="{float:'left', width: isCollapse ? '65px': '180px'}">
      <!--<Lmenus></Lmenus>-->
      <Menu></Menu>
    </aside>

    <content class="main_con" :style="{width: bodyWidth + 'px', position: 'absolute'}">
      <bredcrumb></bredcrumb>
      <div :style="{height: bodyHeight - 41 + 'px', overflow: 'auto'}">
        <router-view></router-view>
      </div>
    </content>
  </div>
</template>

<script>
import topNav from '../common/topNav'
import Lmenus from '../common/Lmenus'
import Menu from '../common/menu.vue'
import Bredcrumb from '../common/breadcrumb.vue'

export default {
  name: 'indexPage',
  components: {
    topNav,
    Lmenus,
    Menu,
    Bredcrumb
  },
  data() {
    return {

    }
  },
  computed: {
    isCollapse() {
      return this.$store.getters.isCollapse
    },
    bodyHeight() {
      return this.$store.getters.bodyHeight
    },
    bodyWidth() {
      return this.$store.getters.bodyWidth
    }
  },
  created() {

  },
  methods: {

  },
}
</script>

<style scoped>
.main_con {
  float: right;
  height: calc(100vh - 60px);
  padding: 0;
  overflow: auto;
  /* width:77%; */
}
</style>



