<template>
  <div class="TestApi" style="background:#ffffff;height:100%;">
    <div style="display: flex;padding-right: 15px">
      <el-form :inline="true" class="search_form" style="flex: 1;vertical-align: top">

          <el-form-item label="数据源" prop="apiName">
            <el-select class="w9000" clearable filterable v-model="searchParams.sourceName" placeholder="请选择">
              <el-option
                v-for="item in groupAry"
                :key="item.id"
                :label="item.groupName"
                :value="item.groupName">
              </el-option>
            </el-select>
          </el-form-item>
        <el-form-item>
          <el-date-picker class="w200" v-model="rangeDate" @change="dateChange" rangeSeparator="/" type="datetimerange"
                          placeholder="选择时间范围">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="info" class="searchOne" @click="dataSearch()">搜 索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary addOne" class="fr" @click="exportBill()">下载</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="TestApi" style="background:#ffffff;height:100%;">
      <div style="position: relative">
        <template v-if="showType === 'table'">
          <el-table :data="tableData" style="width: 100%;border: 1px solid #e6e6e6;"
                    :row-class-name="tableRowClassName">
            <el-table-column prop="id" label="ID" v-if="false"></el-table-column>
            <el-table-column prop="" type="index" label="序号" width="80"></el-table-column>
            <el-table-column prop="groupName" label="数据源名称"></el-table-column>
            <el-table-column prop="called" label="调用量"></el-table-column>
            <!--<el-table-column prop="stock" label="剩余量"></el-table-column>-->
            <el-table-column prop="amount" label="消费额"></el-table-column>
            <!--<el-table-column prop="balance" label="余额" ></el-table-column>-->
            <el-table-column prop="createTime" label="日期" :formatter="dateFormat"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="searchDetailInt(scope.row)" size="mini" type="primary">查看详情</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="page" style="padding-bottom:30px;" v-if="this.tableData.length > 0">
            <el-pagination style="text-align:right;margin-top:80px;" @size-change="pageSizeChange"
                           @current-change="pageNumChange" :current-page="searchParams.pageNum"
                           :page-sizes="[10, 20, 30, 40]" :page-size="searchParams.pageSize"
                           layout=" sizes, prev, pager, next, jumper" :total="totalCount">
            </el-pagination>
          </div>
          <div class="teCen mt60" v-if="this.tableData.length < 1">
            <p><img src="../../assets/Shape.png"/></p>
            <span>暂无数据</span>
          </div>
        </template>
        <el-dialog title="详情信息" :visible.sync="dialogTableVisible" size="large">
          <el-table :data="detailsTable" v-loading="listDetailLoading" :max-height="600"
                    style="width: 100%;border:1px solid #e6e6e6" :row-class-name="tableRowClassName">
            <el-table-column prop="groupName" sortable label="数据源名称"></el-table-column>
            <el-table-column prop="apiId" label="接口ID"></el-table-column>
            <el-table-column prop="price" label="单价"></el-table-column>
            <el-table-column prop="count" label="调用量"></el-table-column>
            <el-table-column prop="amount" label="消费额"></el-table-column>
            <el-table-column prop="strategyName" label="计费规则"></el-table-column>
            <el-table-column prop="createTime" label="日期" :formatter="dateFormat"></el-table-column>
          </el-table>
          <div class="page" style="padding-bottom:10px;">
            <el-pagination style="text-align:right;margin-top:10px;" @size-change="detailHandleSizeChange"
                           @current-change="detailHandleCurrentChange" :current-page="searchDetails.pageNum"
                           :page-sizes="[10, 20, 30, 40]" :page-size="searchDetails.pageSize"
                           layout=" sizes, prev, pager, next, jumper" :total="totalDetails">
            </el-pagination>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'

  export default {
    name: 'datasourceBillDay',
    components: {},
    data() {
      return {
        dialogTableVisible: false,
        activeUrl: '/gateway/charge/datasourceBill/listBillByDay',
        showType: 'table',
        jurisdiction: {
          principal: {},
          loginUrl: '',
          logoutUrl: '',
          roles: [],
          perms: []
        },
        listDetailLoading: false,
        searchParams: {
          startTime: Number(moment(moment(new Date()).format('YYYY-MM-DD')).format('x')),
          endTime: Number(moment(new Date()).format('x')),
          pageNum: 1,
          pageSize: 10,
          sourceName: ""
        },
        searchDetails: {
          startTime: '',
          endTime: '',
          pageNum: 1,
          pageSize: 10,
          sourceName: ""
        },
        rangeDate: [moment(moment(new Date()).format('YYYY-MM-DD')), moment(new Date())],
        groupAry: [],
        tableData: [],
        totalCount: 0,
        detailsTable: [],
        totalDetails: 0,

        dateFormat(row, column) {
          const date = row[column.property];
          if (date === undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD");
        }
      }
    },

    created() {
      this.$nextTick(() => {
        this.utils.getUserPermissions(this)
        this.utils.getGroupAry(this)
      })
    },
    mounted() {

    },
    methods: {
      // 日期改变
      dateChange(date) {
        let newDate = date.split("/")
        this.searchParams.startTime = Number(moment(newDate[0]).format('x'))
        this.searchParams.endTime = Number(moment(newDate[1]).format('x'))
      },
      //重置搜索条件
      dataSearch() {
        this.searchParams.pageNum = 1;
        this.initData();
      },
      //初始化与搜索框查询
      initData() {
        axios({
          method: 'post',
          url: this.activeUrl,
          data: this.searchParams
        }).then(res => {
          if (res.data.code !== 2000) {
            this.$message.error(res.data.message);
          } else {
            this.tableData = res.data.data.list;
            this.totalCount = res.data.data.totalCount;
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      tableRowClassName(row, index) {
        if (index % 2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      pageNumChange(val) {
        this.searchParams.pageNum = val;
        this.initData();
      },
      pageSizeChange(val) {
        this.searchParams.pageSize = val;
        this.dataSearch();
      },
      searchDetailInt(row) {
        this.dialogTableVisible = true;
        this.searchDetails.pageNum = 1;
        this.searchDetails.sourceName = row.groupName;
        this.searchDetails.dateType = 'day';
        this.searchDetails.startTime = row.createTime;
        this.searchDetails.endTime = '';
        this.searchDetailsHandle()
      },
      searchDetailsHandle() {
        this.listDetailLoading = true;
        axios({
          method: 'post',
          url: '/gateway/charge/datasourceBill/listBillDetail',
          data: this.searchDetails
        }).then(res => {
          this.listDetailLoading = false;
          if (res.data.code !== 2000) {
            this.$message.error(res.data.message);
          } else {
            this.detailsTable = res.data.data.list;
            this.totalDetails = res.data.data.totalCount;
          }
        }).catch(error => {
          this.listDetailLoading = false;
          console.log('网络错误，不能访问');
        });
      },

      exportBill(){
        axios({
          method: 'post',
          url: '/gateway/charge/bill/getServerURL',
          data: {}
        }).then(res => {
          console.log(res.data);
          let params = ''
          if (this.searchParams.sourceName !== '') {
            params = 'sourceName=' + this.searchParams.sourceName
          } else {
            params = 'sourceName='
          }
          if (this.searchParams.startTime !== '') {
            params = params + '&startTime=' + this.searchParams.startTime
          }
          if (this.searchParams.startTime !== '') {
            params = params + '&endTime=' + this.searchParams.endTime
          }
          location.href=res.data.data+"/datasourceBill/billExport?" + params;
        }).catch(error => {
          console.log('网络错误');
        });
      },

      detailHandleSizeChange(size) {
        this.searchDetails.pageSize = size
        this.searchDetailInt(this.searchDetails.sourceName)
      },
      detailHandleCurrentChange(page) {
        this.searchDetails.pageNum = page
        this.searchDetailsHandle()
      }
    }

  }
</script>

<style scoped>
  .card_statistical {
    padding: 5px 15px;
  }

  .grid-content {
    border: 1px solid #eaeefb;
    background: #fff;
    border-radius: 4px;
    min-height: 100px;
    display: flex;
    font-size: 0;
  }

  .grid-content:hover {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  }

  .grid-content > div {
    font-size: 18px;
    vertical-align: middle;
  }

  .canvasBox {
    text-align: right;
    flex: 0 0 50px;
  }

  .grid-content > div.canvasBoxTotal {
    flex: 1;
    padding: 0;
    margin-top: 10px;
  }

  .canvasBoxText {
    flex: 0 0 102px;
  }

  .grid-content > div.text {
    flex: 1;
    text-align: left;
    padding: 20px 10px;
    font-weight: bolder;
  }

  .text > p:first-child {
    font-weight: normal;
  }
</style>



