// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import './style/element-#ffbb1e/index.css'
import ElementUI from 'element-ui'
import $ from 'jquery'
// import '/static/bootstrap/js/bootstrap.min.js'
// import '/static/bootstrap/css/bootstrap.min.css'
import App from './App'
import router from './router'
import store from './store'
import axios from 'axios'
import Es6Promise from 'es6-promise'
import SelfUI from '@/components/common/ui'
import Common from './assets/common'
import {NO_LOGIN_ROUTERS} from './config/enviroment'
import {menu} from './store/menu'
import {activeColumn} from './utils/utils'
import * as types from './store/mutation-types'
import ZkTable from 'vue-table-with-tree-grid'

Es6Promise.polyfill()

Vue.use(ElementUI)
Vue.use(SelfUI)
Vue.use(ZkTable)
Vue.prototype.utils = Common

const $http = Vue.prototype.$http = axios.create()
$http.interceptors.response.use(
  rsp => {
    console.log('ssssss', rsp)
    return {success: true, ...rsp}
  },
  error => {
    console.log(error)
    return {success: false}
  }
)

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  //增加基础信息处理函数  这里需要加载 权限信息、用户信息、栏目信息
  // （可以通过设置noLoginRouter参数设置不需要加载的路由信息）
  if (NO_LOGIN_ROUTERS.indexOf(to.path) === -1) {
    const columnItems = menu()
    store.commit(types.COMMON_CACHE_DATA_UPDATE, {
      name: 'columnItems',
      value: columnItems
    })
    next()
  } else {
    next()
  }
})

router.afterEach(route => {
  const active = activeColumn(store.getters.commonCacheData('columnItems'), route.path)
  store.commit(types.COMMON_CACHE_DATA_UPDATE, {
    name: 'activeColumn',
    value: active
  })
  console.log(active)
  console.log(store.getters.commonCacheData('columnItems'))
  if (active) {
    // 设置面包屑 预置默认导航
    const breadcrumb = []
    if (active.beginNode) {
      breadcrumb.push({
        urls: active.beginNode.urls,
        name: active.beginNode.name
      })
    }
    breadcrumb.push({
      urls: active.urls,
      name: active.name
    })
    store.commit(types.COMMON_CACHE_DATA_UPDATE, {
      name: 'breadcrumb',
      value: breadcrumb
    })
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});







