<template>
  <div>
    <el-form :model="addGroupDataDialog" ref="addGroupDataDialog" class="demo-ruleForm" :rules="rules">
      <el-form-item label="名称：" prop="groupApiName" :label-width="formLabelWidth">
        <el-input class="w9000" v-model="addGroupDataDialog.groupApiName"></el-input>
      </el-form-item>

      <el-form-item label="描述：" prop="descriptionApi" :label-width="formLabelWidth">
        <el-input type="textarea" class="w9000" v-model="addGroupDataDialog.descriptionApi" :rows="4" placeholder="不超过180个字符"></el-input>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancleCreate">取 消</el-button>
      <el-button type="primary" @click="addGroup">确 定</el-button>
    </div>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'creatGroup',
  props: [],

  data() {

    let groupNamesValidate = (rule, value, callback) => {
      let regExp = /^[A-Za-z|\u4e00-\u9fa5]{1}[a-zA-Z0-9_\u4e00-\u9fa5]{3,49}$/;
      if (regExp.test(value) === false) {
        callback(new Error('必须以英文字母或汉字开头'));
      } else {
        callback();
      }
    };

    //效验分组唯一性
    let groupNames = (rule, value, callback) => {
      if (value = this.addGroupDataDialog.groupApiName) {
        const groupName = this.addGroupDataDialog.groupApiName;
        axios({
          method: 'post',
          url: '/apiGroup/checkUnique',
          data: {
            groupName: groupName
          }
        }).then(res => {
          if (res.data.data === false) {
            callback(new Error('分组名称重复'));
          } else {
            callback();
          }
        }).catch(error => {
          alert('网络错误，不能访问');
        });
      } else {
        callback();
      }
    };

    return {

      formLabelWidth: '125px',

      addGroupDataDialog: { descriptionApi: '', groupApiName: '' },

      //新建分组验证
      rules: {
        groupApiName: [
          { required: true, message: '名称不能为空' },
          { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
          { validator: groupNames, trigger: 'blur' },
          { validator: groupNamesValidate, trigger: 'blur' }
        ],
        descriptionApi: [
          { required: true, message: '描述不能为空' },
          { min: 1, max: 180, message: '长度在 1 到 180 个字符', trigger: 'blur' },
        ]
      },

    }
  },
  created() {

  },
  methods: {

    cancleCreate() {
      this.$emit('cancleCreate', "");
    },

    //创建分组
    addGroup() {
      this.$refs["addGroupDataDialog"].validate((valid) => {
        if (valid) {
          const groupApiName = this.addGroupDataDialog.groupApiName;
          const descriptionApi = this.addGroupDataDialog.descriptionApi;

          axios({
            method: 'post',
            url: '/apiGroup/save',
            data: {
              groupName: groupApiName,
              groupDescription: descriptionApi
            }
          }).then(res => {
            this.addGroupDataDialog.groupApiName = '';
            this.addGroupDataDialog.descriptionApi = '';
            this.$message({
              type: 'success',
              message: `新建分组 ${groupApiName} 成功`
            });

            this.$emit('addGroupOk', '');
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },
  }
}
</script>


<style scoped>

</style>



