<template>
  <div class="breadcrumb-box">
    <a @click="changeCollapse" class="toggleMenu_a">
      <!--<i v-if="!isCollapse" class="el-icon-s-fold" style="font-size: 18px"></i>-->
      <span v-if="isCollapse" class="glyphicon glyphicon-log-in" aria-hidden="true"></span>
      <span v-if="!isCollapse" class="glyphicon glyphicon-log-out" aria-hidden="true"></span>
      <!--<i v-if="isCollapse" class="el-icon-s-unfold" style="font-size: 18px"></i>-->
    </a>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="inline">
      <el-breadcrumb-item v-for="(item, index) in breadcrumbData"
                          :key="item.urls"
                          :to="{path: (item.urls && index!==breadcrumbData.length -1 )?item.urls:''}"
      >{{item.name}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>
<script>
  import * as types from '../../store/mutation-types'
    export default {
        data() {
            return {}
        },
      computed: {
        breadcrumbData() {
          console.log(this.$store.getters.commonCacheData('breadcrumb'))
          return this.$store.getters.commonCacheData('breadcrumb')
        },
        isCollapse() {
          return this.$store.getters.isCollapse
        },
      },
        components: {},
        mounted() {
        },
        methods: {
          changeCollapse() {
            if (this.isCollapse) {
              this.$store.commit(types.SET_COLLAPSE, false)
            } else {
              this.$store.commit(types.SET_COLLAPSE, true)
            }
            this.$store.commit(types.WINDOW_XY_CHANGE)
          }
        }
    }
</script>
<style scoped>
  .breadcrumb-box{
    padding: 10px;
    border-bottom: 1px solid #DCDFE6;
    text-align: left;
  }
  .inline {
    display: inline-block;
  }
  .toggleMenu_a{
    color: rgb(106, 101, 72);
    margin-right: 8px;
    font-size: 14px;
    cursor: pointer;
  }
</style>



