<template>
  <div class="requestParamsList">
    <div class="box">
      <!-- 入参定义 -->
      <div class="panel panel-default">
        <div class="console-panel-header">
          <span class="panel-header">入参定义</span>
          <table class="table table-hover table-bordered">
            <thead>
            <tr>
              <th>参数名</th>
              <th>参数位置</th>
              <th>类型</th>
              <th>必填</th>
              <th>默认值</th>
              <th>示例</th>
              <th>描述</th>
              <th width="15%">操作</th>
            </tr>
            </thead>
            <tbody v-if="requestParamsList.length <= 0">
            <tr>
              <td colspan="8" style="text-align:center">
                <span>暂无数据</span>
              </td>
            </tr>
            </tbody>
            <tbody v-if="requestParamsList.length > 0">
            <tr v-for="(item, index) in requestParamsList">
              <td>
                <el-input class="inputValidate" v-model="item.paramName" @change="paraNameVal(item,index)"></el-input>
                <p class="textRed din jqueryValidate">请输入正确格式的参数,不能为空！</p>
                <p class="textRed din jqueryValidateSame">存在相同的参数名，请正确填写参数</p>
              </td>
              <td>
                <el-select v-model="item.paramsLocation">
                  <el-option v-for="item in paramsLocationOptions" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled"></el-option>
                </el-select>
              </td>
              <td>
                <el-select v-model="item.paramsType"  @change="parTypeSix(item.paramsType)">
                  <el-option v-for="item in paramsTypeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>

              </td>
              <td>
                <el-checkbox v-model="item.paramsMust == 1 ? true : false" @change="paramsMusts(item.paramsMust,index)"></el-checkbox>
              </td>
              <td>
                <el-input v-if="item.paramsType == 1" :disabled="item.paramsMust==1 ? true : false" v-model="item.paramsDefaultValue"></el-input>

                <el-input v-if="item.paramsType == 2 || item.paramsType == 3 || item.paramsType == 4 || item.paramsType == 5" :disabled="item.paramsMust==1 ? true : false" v-model="item.paramsDefaultValue" @change="IntegerNumVal(item.paramsDefaultValue,index)"></el-input>

                <el-select v-model="item.paramsDefaultValue" placeholder="请选择" :disabled="item.paramsMust==1 ? true : false" v-if="item.paramsType == 6">
                  <el-option v-for="item in optionsBool" :key="item.label" :label="item.label" :value="item.label"></el-option>
                </el-select>
                <p class="textRed din IntegerNum" >请输入正确格式的参数！</p>
              </td>
              <td>
                <input type="text" class="form-control" maxlength="255" v-model="item.paramsExample">
              </td>
              <td>
                <input type="text" class="form-control" maxlength="255" v-model="item.paramsDescription">
              </td>
              <td>
                <el-button type="text" class="radiusBtn" size="small" @click="amendApiMore(index)">编辑</el-button>
                <el-button type="text" class="radiusBtn" size="small" @click="delTable(index)">移除</el-button>
              </td>
            </tr>
            </tbody>
          </table>
          <el-button type="primary" size="mini" style="margin-top:10px;padding:10px 15px;width:97px;" class="mt20" @click="addTable">
            <i class="el-icon-plus" style="margin-right:5px"></i>增加一条</el-button>
        </div>
      </div>

      <!-- 编辑更多弹框 start -->
      <el-dialog title="编辑更多" :visible.sync="amendApi" size="small">
        <el-form v-if="requestParamsList.length > 0">
          <el-form-item label="最大长度" :label-width="formLabelWidth" v-if="requestParamsList[amendApiIndexEdit].paramsType == 1">
            <el-input v-model="requestParamsList[amendApiIndexEdit].maxLength" type="number" class="w2000" @change="maxLine(requestParamsList[amendApiIndexEdit].maxLength)"></el-input>
            <p class="textRed din maxDate">最大长度不能小于最小长度</p>
          </el-form-item>
          <el-form-item label="最小长度" :label-width="formLabelWidth" v-if="requestParamsList[amendApiIndexEdit].paramsType == 1">
            <el-input v-model="requestParamsList[amendApiIndexEdit].minLength" class="w2000" type="number" @change="minLine(requestParamsList[amendApiIndexEdit].minLength)"></el-input>
          </el-form-item>
          <el-form-item label="参数验证" :label-width="formLabelWidth">
            <el-input type="textarea" class="w350" v-model="requestParamsList[amendApiIndexEdit].regularExpress" :rows="4" placeholder="输入正则表达式"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="amendApi  = false">取 消</el-button>
          <el-button type="primary" @click="lineValue">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 编辑更多弹框 end -->




      <div>
        <div v-for="(item,index) in newBackParams">
          <h4>{{item.interfaceName}}</h4>
          <!-- 后端服务参数 -->
          <div class="panel panel-default" >
            <div class="console-panel-header">
              <span class="panel-header">后端服务参数</span>
              <table class="table table-hover table-bordered">
                <thead>
                <tr>
                  <th>后端参数名称</th>
                  <th>后端参数位置</th>
                  <th>对应入参名称</th>
                  <th>对应入参位置</th>
                  <th>对应入参类型</th>
                </tr>
                </thead>
                <tbody v-if="item.backInParams.length <= 0">
                <tr>
                  <td colspan="5" style="text-align:center">
                    <span>暂无数据</span>
                  </td>
                </tr>
                </tbody>
                <tbody v-if="item.backInParams.length > 0">
                <tr v-for="(paramData,index) in item.backInParams">
                  <td v-if="paramData.paramsType == 1">
                    <el-input v-model="paramData.paramName" @change="paraNameVal2(paramData,index, item.backInParams)"></el-input>
                    <p class="textRed din jqueryValidate2">请输入正确格式的参数，不能为空！</p>
                    <!--<p class="textRed din jqueryValidateSame2">存在相同的后端参数名，请正确填写参数</p>-->
                  </td>
                  <td v-if="paramData.paramsType == 1">
                    <el-select v-model="paramData.paramsLocation">
                      <el-option v-for="paramData in paramsLocationOptions" :key="paramData.value" :label="paramData.label" :value="paramData.value"></el-option>
                    </el-select>
                  </td>
                  <td v-if="paramData.paramsType == 1">
                    <el-input type="text" disabled v-model="requestParamsList[index].paramName"></el-input>
                  </td>
                  <td v-if="paramData.paramsType == 1">
                    <el-select v-model="requestParamsList[index].paramsLocation" disabled>
                      <el-option v-for="paramData in paramsLocationOptions" :key="paramData.value" :label="paramData.label" :value="paramData.value"></el-option>
                    </el-select>
                  </td>
                  <td v-if="paramData.paramsType == 1">
                    <el-select disabled v-model="requestParamsList[index].paramsType">
                      <el-option v-for="paramData in paramsTypeOptions" :key="paramData.value" :label="paramData.label" :value="paramData.value"></el-option>
                    </el-select>
                  </td>

                </tr>
                </tbody>
              </table>

              <el-tabs type="border-card"  @tab-click="(data)=>handleClick(index,data)" v-model="activeName[index]">
                <el-tab-pane label="常量参数"  name="res">
                  <table class="table table-hover table-bordered" >
                    <thead>
                    <tr>
                      <th>后端参数名称</th>
                      <th>参数值</th>
                      <th>参数位置</th>
                      <th>描述</th>
                      <th>操作</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(paramDataRes,indexRes) in item.backValParams">
                      <td v-if="paramDataRes.paramsType == 2">
                        <el-input class="inputValidate4" v-model="paramDataRes.paramName" @change="paraNameVal4(paramDataRes,indexRes)"></el-input>
                        <p class="textRed din jqueryValidate4">请输入正确格式的参数，不能为空！</p>
                      </td>
                      <td v-if="paramDataRes.paramsType == 2">
                        <el-input class="inputValidate5" v-model="paramDataRes.paramValue" @change="paraNameVal5(paramDataRes,paramDataRes.paramValue,indexRes)"></el-input>
                        <p class="textRed din jqueryValidate5">请输入正确格式的参数值，不能为空！</p>
                      </td>
                      <td v-if="paramDataRes.paramsType == 2">
                        <el-select v-model="paramDataRes.paramsLocation">
                          <el-option v-for="paramDataRes in paramsLocationOptions" :key="paramDataRes.value" :label="paramDataRes.label" :value="paramDataRes.value"></el-option>
                        </el-select>
                      </td>
                      <td v-if="paramDataRes.paramsType == 2">
                        <input type="text" class="form-control" v-model="paramDataRes.paramDescription">
                      </td>
                      <td v-if="paramDataRes.paramsType == 2">
                        <el-button type="text" class="radiusBtn pubColor" size="small" @click="delNumData(index,indexRes,paramDataRes)">移除</el-button>
                      </td>
                    </tr>

                    </tbody>
                  </table>
                  <el-button type="primary" style="margin-top:10px;padding:10px 15px;width:97px;" size="mini" @click="addNumData(index)">
                    <i class="el-icon-plus" style="margin-right:5px;"></i>增加一条</el-button>
                </el-tab-pane>
                <el-tab-pane label="JSON" name="json">
                  <tr>
                    <el-input type="textarea" style="width:800px" v-model="item.backJson.paramValue" :rows="6"></el-input>
                    <span @click="jsonClcik();" class="jsonWritePlaceDialog jsonXmlCom" style="top:17px;left:17px;width:47%">{{item.backJson.paramValue}}</span>
                    <el-button @click="formatVal(index)" style="width:100px;" type="primary" class="ml35">转换成JSON</el-button>
                  </tr>
                </el-tab-pane>
                <el-tab-pane label="XML" name="xml">
                  <tr>
                    <el-input type="textarea" style="width:800px" v-model="item.backXml.paramValue" :rows="6"></el-input>
                  </tr>
                </el-tab-pane>
              </el-tabs>
            </div>
          </div>
        </div>
      </div>

    </div>
    <div slot="footer" class="dialog-footer teCen">
      <router-link :to="{path:'/portManage/portApiManage',query:{id:this.apiId}}">
        <el-button class="cancelBtn">取消</el-button>
      </router-link>
      <el-button type="primary" class="addOne" @click="saveNewValue()">保存</el-button>
    </div>
  </div>
  </div>
</template>

<script>
  import { formatJson } from "@/assets/json";
  import axios from 'axios'
  export default {
    name:'requestParamsList',
    props:['apiId'],
    data(){
      return{
        paramsLocationOptions: [],
        paramsTypeOptions: [],
        optionsBool:[
          {value:1,label:"true"},
          {value:2,label:"false"}
        ],
        amendApiIndexEdit: 0,
        amendApi:false,
        formLabelWidth: "120px",
        requestParamsList:[],
        backEndParam:[],
        numData:[],
        numDataJson:[],
        numDataXml:[],
        dataObj:[],
        resNum:[],
        boxData:[],
        newBackParams:[],
        backArr:[],
        activeName:[],
        disidH:'',
        disidC:'',
        disidP:'',
        disidBack:'',
        flag:true,
      }
    },
    created(){
      this.slider();
      this.addAmendValue();
    },
    methods:{
      //增减参数（多上游）
      addAmendValue(){
        axios({
          method:"post",
          url:"/api/getApiParams",
          data:{
            id:this.apiId,
          }
        }).then(res =>{
          this.dataObj.push(res.data.data);
          this.requestParamsList = res.data.data.requestParamsList;
          this.boxData = res.data.data.backendParamsList;
          this.boxData.map((element,ind) => {
            this.resNum = element.paramList;
            let el={};
            el.interfaceName=element.interfaceName;
            let backInParams = [];
            let backValParams = [];
            let backJson = {};
            let backXml = {};
            element.paramList.map((p,index) => {
              if(p.paramsType==1){
                backInParams.push(p)
              }else if(p.paramsType==2){
                backValParams.push(p)
                this.activeName[ind]='res'
              }else if(p.paramsType==3){
                backJson=p
                this.activeName[ind]='json'
              }else if(p.paramsType==4){
                backXml=p
                this.activeName[ind]='xml'
              }
            })
            el.disId=this.boxData[ind].disId;
            el.backInParams=backInParams;
            el.backValParams=backValParams;
            el.backJson=backJson;
            el.backXml=backXml;
            this.newBackParams.push(el);
          });
        }).catch(error =>{
          console.log('网络错误，不能访问');
        })
      },

      // 保存
      saveNewValue(){
        this.backArr = []
        var flag = true;
        this.newBackParams.map((data,indexB) =>{
          if(data.backInParams.length > 0){
            data.backInParams.map((backData,index) =>{
              let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
              let reg = /\s+/;
              if(backData.paramName == '' || regExp.test(backData.paramName) == false || reg.test(backData.paramName)){
                $(".jqueryValidate2").eq(index).show();
//							this.$message.error('请输入正确格式的后端服务参数，不能为空！');
                flag = false;
              } else {
//              try {
//                if(this.judgeSame(backData, index, data.backInParams, '2') === 'same') {
//                  throw 'error'
//                }
//              }catch (err){
//                $(".jqueryValidateSame2").eq(index).show();
//                flag = false;
//              }
                $(".jqueryValidate2").eq(index).hide();
                this.backArr.push(backData);//装入back数组（后端参数1）
              }
            });
          }
          if(data.backValParams.length > 0){
            data.backValParams.map((p,index) =>{
              let regExp = /^[a-zA-Z0-9_\u4e00-\u9fa5]{1,200}$/;
              if(p.paramName == '' || regExp.test(p.paramName) == false){
//							this.$message.error('请输入正确格式的参数，不能为空！');
                $(".jqueryValidate4").eq(index).show();
                flag = false;
              } else if(p.paramValue == ''){
                $(".jqueryValidate5").eq(index).show();
//							this.$message.error('请输入正确格式的参数值，不能为空！');
                flag = false;
              } else {
                $(".jqueryValidate4").eq(index).hide();
                $(".jqueryValidate5").eq(index).hide();
                this.backArr.push(p);//装入常量数组（2）
              }
            })
          }else if(JSON.stringify(data.backJson) !== '{}'){
            data.backJson.paramsType = 3;
            data.backJson.paramsLocation = 3;
            data.backJson.disId = this.newBackParams[indexB].disId;
            this.backArr.push(data.backJson);//装入json对象 3

          }else if(JSON.stringify(data.backXml) !== '{}'){
            data.backXml.paramsType = 4;
            data.backXml.paramsLocation = 3;
            data.backXml.disId = this.newBackParams[indexB].disId;
            this.backArr.push(data.backXml);//装入xml 4
          }
        });

        this.requestParamsList.map((data,index) =>{
          let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
          let reg = /\s+/;
          if(data.paramName == '' || regExp.test(data.paramName) == false || reg.test(data.paramName)){
            $(".jqueryValidate").eq(index).show();
          }else{
            $(".jqueryValidate").eq(index).hide();
          }
          try {
            if(this.judgeSame(data, index, this.requestParamsList, '') === 'same') {
              throw 'error'
            }
          }catch (err){
            $(".jqueryValidateSame").eq(index).show();
            flag = false;
          }
        })
        console.log(this.backArr,'改变之后back');

        if (flag) {
          axios({
            method:"post",
            url:"/api/updateApiParams",
            data:{
              apiInfo:{id:this.apiId},
              backendParamsList: this.backArr,
              requestParamsList: this.requestParamsList
            }
          }).then(res =>{
            if(flag){
              this.$message.success('保存成功');
              window.location.hash = '/portManage/portApiManage' + '?id=' + this.apiId;
            }else{
              this.backArr = [];
            }
          }).catch(error =>{
            console.log('网络错误，不能访问');
          });
        }
      },

      slider() {
        this.paramsLocationOptions.push(
          { value: 1, label: "header" },
          { value: 2, label: "query" },
          { value: 3, label: "body" }
        );
        this.paramsTypeOptions.push(
          { value: 1, label: "String" },
          { value: 2, label: "Integer" },
          { value: 3, label: "Long" },
          { value: 4, label: "Float" },
          { value: 5, label: "Double" },
          { value: 6, label: "Boolean" }
        );
      },

      amendBck(){
        this.resNum.map((data,index) => {
          if (data.paramsType == 2) {
            this.activeName = "res";
          } else if (data.paramsType == 3) {
            this.activeName = "json";
          }else if (data.paramsType == 4) {
            this.activeName = "xml";
          } else {
            this.activeName = "";
          }
        });
      },

      //必填
      paramsMusts(value, index) {
        if (this.requestParamsList[index].paramsMust == 2) {
          $(".IntegerNum").eq(index).hide();
          this.requestParamsList[index].paramsDefaultValue = "";
          this.requestParamsList[index].paramsMust = 1;
        } else this.requestParamsList[index].paramsMust = 2;
      },
      amendApiMore(index) {
        this.amendApiIndexEdit = index;
        this.amendApi = true;
      },
      maxLine() {
        for (var i = 0; i < this.requestParamsList.length; i++) {
          if (parseInt(this.requestParamsList[i].maxLength) < parseInt(this.requestParamsList[i].minLength)) {
            $(".maxDate").show();
          } else {
            $(".maxDate").hide();
          }
        }
      },
      minLine(){
        for (var i = 0; i < this.requestParamsList.length; i++) {
          if (parseInt(this.requestParamsList[i].maxLength) < parseInt(this.requestParamsList[i].minLength)) {
            $(".maxDate").show();
          } else {
            $(".maxDate").hide();
          }
        }
      },
      lineValue() {
        var flag = true;
        for (var i = 0; i < this.requestParamsList.length; i++) {
          if (parseInt(this.requestParamsList[i].maxLength) < parseInt(this.requestParamsList[i].minLength)) {
            $(".maxDate").show();
            var flag = false;
          }
        }
        if (flag) {
          $(".maxDate").hide();
          this.amendApi = false;
        }
      },

      addTable() {
        this.requestParamsList.push({
          maxLength: "50",
          minLength: "1",
          paramName: "",
          paramsLocation: 3,
          paramsType: 1,
          paramsMust: 1,
          paramsDefaultValue: "",
          paramsExample: "",
          paramsDescription: "",
          regularExpress: "" //参数验证
        });

        this.newBackParams.map((data,index) => {
          data.backInParams.push({
            disId:this.newBackParams[index].disId,
            paramName: "",
            paramValue: "",
            paramsLocation: 3,
            paramDescription: "",
            paramsType: 1
          });
        })
      },
      //常量增加一条
      addNumData(index){
        this.newBackParams[index].backValParams.push({
          disId:this.newBackParams[index].disId,
          paramName: "",
          paramValue: "",
          paramsLocation: 3,
          paramDescription: "",
          paramsType: 2
        });
      },

      //常量移除一条
      delNumData(index,indexRes,paramDataRes) {
        if(paramDataRes.disId){
          this.newBackParams[index].backValParams.splice(indexRes, 1);
        }else{
          this.newBackParams[index].backValParams.splice(indexRes, 1);
        }
      },

      //入参后端移除一条
      delTable(index) {
        this.requestParamsList.splice(index, 1);
        this.newBackParams.map(data => {
          data.backInParams.splice(index, 1);
        })
        $(".jqueryValidateSame").hide();
      },

      handleClick(index,data) {
        this.activeName[index]=data.name;
        if(data.index == 0){
          this.newBackParams[index].backJson = {};
          this.newBackParams[index].backXml = {};
        }else if(data.index == 1){
          this.newBackParams[index].backValParams = [];
          this.newBackParams[index].backXml = {};
        }else if(data.index == 2){
          this.newBackParams[index].backValParams = [];
          this.newBackParams[index].backJson = {};
        }
      },

      jsonClcik() {
        $(".jsonWritePlaceDialog").hide();
      },
      formatVal(index) {
        var resJsonAmend = formatJson(this.newBackParams[index].backJson.paramValue);
        $(".jsonWritePlaceDialog").html("<pre>" + resJsonAmend + "<pre/>");
        $(".jsonWritePlaceDialog").show();

      },
      //入参定义数组验证
      paraNameVal(obj, index) {
        $(".jqueryValidateSame").hide();
        let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
        let reg = /\s+/;
        if (obj.paramName === "" || regExp.test(obj.paramName) == false || reg.test(obj.paramName)) {
          $(".jqueryValidate").eq(index).show();
          $(".jqueryValidateSame").eq(index).hide();
        } else {
          $(".jqueryValidate").eq(index).hide();
          if(this.judgeSame(obj, index, this.requestParamsList, '') === 'same') {
            $(".jqueryValidateSame").eq(index).show();
          } else {
            $(".jqueryValidateSame").eq(index).hide();
          }
        }
      },
      //后端服务参数数组验证
      paraNameVal2(obj, index, ary) {
//      $(".jqueryValidateSame2").hide();
        let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
        let reg = /\s+/;
        if (obj.paramName === "" || regExp.test(obj.paramName) == false || reg.test(obj.paramName)) {
          $(".jqueryValidate2").eq(index).show();
//          $(".jqueryValidateSame2").eq(index).hide();
//				this.$message.error('请输入正确格式的后端服务参数，不能为空！');
        } else {
          $(".jqueryValidate2").eq(index).hide();
//        if(this.judgeSame(obj, index, ary, '2') === 'same') {
//          $(".jqueryValidateSame2").eq(index).show();
//        } else {
//          $(".jqueryValidateSame2").eq(index).hide();
//        }
        }
      },
      //常量参数数组验证
      paraNameVal4(obj, indexRes, ary) {
        console.log(indexRes);
        let regExp = /^[a-zA-Z0-9_\u4e00-\u9fa5]{1,200}$/;
        if (obj.paramName === "" || regExp.test(obj.paramName) == false) {
          $(".jqueryValidate4").eq(indexRes).show();
//				this.$message.error('请输入正确格式的参数，不能为空！');
        } else {
          $(".jqueryValidate4").eq(indexRes).hide();
        }
      },
      paraNameVal5(paramDataRes,value, indexRes) {
        if (value === "") {
          $(".jqueryValidate5").eq(indexRes).show();
//				this.$message.error('请输入正确格式的参数值，不能为空！');
        } else {
          $(".jqueryValidate5").eq(indexRes).hide();
        }
      },
      judgeSame(obj, $index, ary, num) {
        let str = ''
        ary.forEach((item, index) => {
          try {
            if ($index !== index && obj.paramName === item.paramName) {
//            $(".jqueryValidateSame" + num).eq(index).show();
              throw ('same')
            } else {
//            $(".jqueryValidateSame" + num).eq(index).hide();
            }
          } catch (err) {
            str = err
          }
        })
        return str
      }
    }
  }
</script>
<style scoped>
  .requestParamsList{
    background: #fff;
    margin-bottom: 30px;
  }
</style>



