<template>
  <div class="portApiManage" style="background:white;">

    <div class="detailBtn" style="padding:8px 10px 0 0">
      <div class="fl">
        <span class="detailNav">发现API</span>
        <router-link to="/findApi">
          <el-button size="small" @click="amendGroupDetail = true;">返回发现API</el-button>
        </router-link>
      </div>
      <p style="clear:both"></p>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">名称及描述</span>
        <table class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>分组：
                <span v-text='apiInfoVersion.groupName'>bbb222</span>
              </td>
              <td>名称：
                <span v-text='apiInfoVersion.apiName'>阿里云APP</span>
              </td>
            </tr>

            <tr>
              <td colspan="2">环境：
                <span v-if='apiInfoVersion.env == 1'>线上</span>
                <span v-else-if='apiInfoVersion.env == 2'>预发</span>
              </td>
            </tr>

            <tr>
              <td colspan="2">描述：
                <span v-text='apiInfoVersion.apiDescription'>hhh</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">服务信息</span>
        <table class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>服务类型：
                <span>HTTP</span>
              </td>
              <td>Method：
                <span v-if='apiInfoVersion.httpMethod == 1'>GET</span>
                <span v-else-if='apiInfoVersion.httpMethod == 2'>POST</span>
              </td>
            </tr>

            <tr>
              <td>超时时间：
                <span v-text='apiInfoVersion.backEndTimeout'>8000</span> ms</td>
              <td>Path：
                <span v-text='apiInfoVersion.httpPath'>/verifybankcard3/query</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default" style="position:relative">
      <div class="console-panel-header">
        <span class="panel-header">请求入参</span>
        <el-table :data="requestParamsVersionsList" style="width: 100%" v-loading="listLoading" :row-class-name="tableRowClassName">
          <el-table-column prop="paramName" label="参数名称"></el-table-column>
          <el-table-column prop="paramsLocation" label="参数位置" :formatter="paramLocationFomatter"></el-table-column>
          <el-table-column prop="paramsType" label="类型" :formatter="paramTypeFomatter"></el-table-column>
          <el-table-column prop="paramsMust" label="是否必填" :formatter="paramMustFomatter"></el-table-column>
          <el-table-column prop="paramsDefaultValue" label="默认值"></el-table-column>
          <el-table-column prop="paramsExample" label="示例"></el-table-column>
          <el-table-column prop="paramsDescription" label="描述"></el-table-column>
        </el-table>
      </div>

      <!-- 更多 start -->
      <el-form class="moreMes" style="width:400px;">
        <el-form-item label="最大长度" :label-width="formLabelWidth">
          <span>14</span>
        </el-form-item>

        <el-form-item label="枚举" :label-width="formLabelWidth">
          <span>无</span>
        </el-form-item>

        <el-form-item label="文档可见" :label-width="formLabelWidth">
          <span>是</span>
        </el-form-item>

        <el-form-item label="文档中顺序" :label-width="formLabelWidth">
          <span>默认</span>
        </el-form-item>

        <el-form-item label="参数验证" :label-width="formLabelWidth">
          <el-input type="textarea" class="w250" :rows="3"></el-input>
        </el-form-item>

        <el-form-item label="JSON验证" :label-width="formLabelWidth">
          <el-input type="textarea" class="w250" :rows="3"></el-input>
        </el-form-item>
      </el-form>
      <!-- 更多 end -->

    </div>
    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">返回结果</span>
        <table class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td colspan="2">返回类型：
                <span>JSON</span>
              </td>
            </tr>
            <tr>
              <td>返回结果示例：
                <span class="successWritePlaceDialog">{{apiInfoVersion.callBackSuccessExample}}</span>
              </td>
              <td>失败返回结果示例：
                <span class="errorWritePlaceDialog">{{apiInfoVersion.callBackFailExample}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</template>
<script>
import axios from 'axios'
import { formatJson } from '../../assets/json'
export default {
  name: 'findApiDetail',
  components: {
  },
  data() {
    return {
      formLabelWidth: "120px",
      listLoading: false,
      apiInfoVersion: '',
      backendVersionsList: [],
      requestParamsVersionsList: [],
      // callBackFailExample:''
      // 参数位置转换
      paramLocationFomatter(row, column) {
        const local = row[column.property];
        if (local == undefined) {
          return "";
        }
        return local == 1 ? "header" : (local == 2 ? "query" : "body");
      },
      //参数类型转换
      paramTypeFomatter(row, column) {
        const type = row[column.property];
        if (type == undefined) {
          return "";
        }
        var res = null;
        switch (type) {
          case 1: res = 'String'; break;
          case 2: res = 'Integer'; break;
          case 3: res = 'Long'; break;
          case 4: res = 'Float'; break;
          case 5: res = 'Double'; break;
          case 6: res = 'Boolean'; break;
        }
        return res;
      },
      // 参数是否必填转换
      paramMustFomatter(row, column) {
        const must = row[column.property];
        if (must == undefined) {
          return "";
        }
        return must == 1 ? "是" : "否";
      }
    }
  },

  created() {
    this.created();
  },

  methods: {
    moreStart() {
      $(".moreMes").show();
    },
    moreEnd() {
      $(".moreMes").hide()
    },
    //页面策略信息初始化
    created() {
      let id = this.$route.query.id;
      let versionId = this.$route.query.versionId;
      let apiGroupId = this.$route.query.apiGroupId;
      axios({
        method: 'post',
        url: '/api/findApiInfoVersion',
        data: {
          id: id,
          versionId: versionId,
          apiGroupId: apiGroupId
        }
      }).then(res => {
        this.apiInfoVersion = res.data.data.apiInfoVersion;
        this.requestParamsVersionsList = res.data.data.requestParamsVersionsList;
        var resultJson = formatJson(this.apiInfoVersion.callBackSuccessExample);
        $(".successWritePlaceDialog").html('<pre>' + resultJson + '<pre/>');
        var resultErrorJson = formatJson(this.apiInfoVersion.callBackFailExample);
        $(".errorWritePlaceDialog").html('<pre>' + resultErrorJson + '<pre/>');
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
}
</script>

<style scoped>
.moreMes {
  display: none;
  position: absolute;
  background: #fff;
  right: 250px;
  border: 1px solid #ddd;
  top: -120px;
  z-index: 999;
}
</style>



