<template>
  <div>
    <div class="panel panel-default">
      <el-button type="success"   size="mini" @click="publish()" v-if="showPublishBtn" style="position: absolute; right: 10px; top: 15px">发布</el-button>
      <el-dialog title="发布API：" :visible.sync="issue" size="tiny" style="top:-5%">
          <div>
              <el-form :model="groupHerfId" ref="groupHerfId" class="demo-ruleForm">
                  <span style="font-weight:700;">您将要发布以下API：</span>
                  <div class="issueAPI">{{groupHerfId.apiName}}</div>

                  <!--<p style="font-weight:700;">请选择要发布的环境：</p>-->

                  <!--<el-radio-group v-model="env">-->
                    <!--<el-radio-button label=1>线上</el-radio-button>-->
                    <!--<el-radio-button label=2>预发</el-radio-button>-->
                  <!--</el-radio-group>-->

                  <el-form-item prop="pubDescription" label="请填写变更备注：" :rules="[{required:true,message:'备注不能为空'},{ min:1, max:180, message:'长度在 1 到 180 个字符',trigger:'blur'}]">
                    <el-input type="textarea" v-model="groupHerfId.pubDescription" :rows="4" placeholder="必填，不超过180个字符" auto-complete="off"></el-input>
                  </el-form-item>
                  <p class="textRed">如果您设置了使用Mock，实际请求则不会调用到后端服务， 请仔细确认</p>
                  <p>该操作将导致线上环境的该API被覆盖，请仔细确认</p>
                </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button class="cancelBtn" @click="issue = false">取 消</el-button>
                <el-button type="primary" class="addOne" @click="publishApi('groupHerfId')">发布</el-button>
              </div>
            </div>
      </el-dialog>


      <div class="console-panel-header">
        <span class="panel-header">名称及描述</span>
        <table v-model="apiList2" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>分组：
                <span>{{apiList2.groupName == null ? '（无）' : apiList2.groupName}}</span>
              </td>
              <td>名称：
                <span>{{apiList2.apiName}}</span>
              </td>
            </tr>

            <tr>
              <td>是否存储：
                <span>{{apiList2.saveMongoDB ==1?'是':'否'}}</span>
              </td>
              <td>是否发送MQ：
                <span>{{apiList2.saveMQ ==1?'是':'否'}}</span>
              </td>
            </tr>


            <tr v-if="apiList2.saveMongoDB == 1">
              <td>Mongodb地址：
                <span>{{apiList2.mongodbURI}}</span>
              </td>
              <td>Mongodb库名:
                <span>{{apiList2.mongodbDBName}}</span>
              </td>
            </tr>

            <tr v-if="apiList2.saveMongoDB == 1">
              <td colspan="3">Mongodb表名：
                <span>{{apiList2.mongodbCollectionName}}</span>
              </td>
            </tr>

            <tr v-if="apiList2.saveMQ ==1">
              <td>MQ类型：
                <span>{{apiList2.mqType ==1 ?'rabbbitmq':'kafka'}}</span>
              </td>
              <td>MQ地址：
                <span>{{apiList2.mqAddress}}</span>
              </td>
            </tr>
            <tr v-if="apiList2.saveMQ ==1">
              <td colspan="3">队列名称：
                <span>{{apiList2.mqTopicName}}</span>
              </td>
            </tr>

            <tr>
              <td>是否缓存：
                <span>{{apiList2.cacheUnit !== null?'是':'否'}}</span>
              </td>
              <td>缓存时间：
                <span>{{apiList2.cacheNo}}</span>
                <span v-if="apiList2.cacheUnit ==1">小时</span>
                <span v-if="apiList2.cacheUnit ==2">天</span>
                <span v-if="apiList2.cacheUnit ==3">不限</span>
              </td>
            </tr>

            <tr>
              <td colspan="3">描述：
                <span>{{apiList2.apiDescription}}</span>
              </td>
            </tr>

          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">请求基础定义</span>
        <table v-model="apiList2" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>Path：
                <span>{{apiList2.httpPath}}</span>
              </td>
              <td>协议：
                <span>HTTP</span>
              </td>
            </tr>

            <tr>
              <td colspan="3">HTTP Method：
                <span>{{apiList2.httpMethod ==1?'GET':'POST'}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default" style="position:relative;">
      <div class="console-panel-header">
        <span class="panel-header">请求入参信息</span>
        <table class="table table-hover table-bordered">
          <thead>
            <tr>
              <th>参数名</th>
              <th>参数位置</th>
              <th>类型</th>
              <th>必填</th>
              <th>默认值</th>
              <th>示例</th>
              <th>描述</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody v-if="requestParamsList2.length <= 0">
            <tr>
              <td colspan="8" style="text-align:center">
                <span>暂无数据</span>
              </td>
            </tr>
          </tbody>
          <tbody v-if="requestParamsList2.length > 0">
            <tr v-for="(item,index) in requestParamsList2">
              <td>
                <span>{{item.paramName}}</span>
              </td>
              <td>
                <span>{{item.paramsLocation ==1 ?'header':(item.paramsLocation ==2 ?'query':'body')}}</span>
              </td>
              <td>
                <span v-if="item.paramsType == 1">String</span>
                <span v-else-if="item.paramsType == 2">Integer</span>
                <span v-else-if="item.paramsType == 3">Long</span>
                <span v-else-if="item.paramsType == 4">Float</span>
                <span v-else-if="item.paramsType == 5">Double</span>
                <span v-else-if="item.paramsType == 6">Boolean</span>
              </td>
              <td>
                <span>{{item.paramsMust == 1 ?'是':'否'}}</span>
              </td>
              <td style="width:300px;word-wrap:break-word;word-break:break-all;">
                <span>{{item.paramsDefaultValue}}</span>
              </td>
              <td style="width:300px;word-wrap:break-word;word-break:break-all;">
                <span>{{item.paramsExample}}</span>
              </td>
              <td style="width:300px;word-wrap:break-word;word-break:break-all;">
                <span>{{item.paramsDescription}}</span>
              </td>
              <td style="width:100px">
                <a href="javascript:void(0)" style="padding:0 8px;color:#1C72EE" class="radiusBtn" @mouseover="moreStart(index)" @mouseleave="moreEnd(index)">更多</a>

                <el-form class="moreMesTwo" style="width:400px;">
                  <el-form-item label="最大长度" :label-width="formLabelWidth" v-if="requestParamsList2[amendApiIndexEdit].maxLength !==null">
                    <span>{{requestParamsList2[amendApiIndexEdit].maxLength}}</span>
                  </el-form-item>

                  <el-form-item label="最小长度" :label-width="formLabelWidth" v-if="requestParamsList2[amendApiIndexEdit].minLength !==null">
                    <span>{{requestParamsList2[amendApiIndexEdit].minLength}}</span>
                  </el-form-item>

                  <el-form-item label="参数验证" :label-width="formLabelWidth">
                    <span>{{requestParamsList2[amendApiIndexEdit].regularExpress}}</span>
                  </el-form-item>
                </el-form>

              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div :data="subApiList2">
      <div style="width: 100%;height: 40px;line-height: 40px;font-size: 14px;background: #eef1f6;border: 1px solid #dfe6ec">
        <span style="display:inline-block;width: 50%;padding-left: 20px">后端服务名称</span>
        <span style="display:inline-block;width: 49%;padding-left: 20px">权重</span>
      </div>
      <ul>
        <li v-for="(item,index) in subApiList2">
          <a class="backServiceName" style="cursor: pointer" @click=checkItems(index,item)><span style="display: inline-block;width: 50%;padding-left: 20px">{{item.interfaceName}}</span><span style="display: inline-block;width: 50%;padding-left: 20px;color: black">{{item.weight}}</span></a>
          <div class="faceMes mt20 backServiceBox" style="box-shadow: 0 0 15px #FEBD49;display: none">
            <div class="panel panel-default">
              <div class="console-panel-header">
                <span class="panel-header">后端服务信息</span>
                <table v-model="childApiInfo" class="table table-hover table-bordered">
                  <tbody>
                  <tr>
                    <td  colspan="2">HTTP Method：
                      <span>{{childApiInfo.backEndHttpMethod ==1 ?'GET':'POST'}}</span>
                    </td>
                  </tr>
                  <tr>
                      <td width="50%">A中心地址：
                          <span>{{childApiInfo.backEndAddress}}{{childApiInfo.backEndPath}}</span>
                        </td>
                      <td width="50%">B中心地址：
                        <span>{{childApiInfo.backEndAddressB}}{{childApiInfo.backEndPath}}</span>
                      </td>
                  </tr>
                  <!-- <tr>
                    <td>报文头：
                      <span>{{tcpHeaderIdTable == null ? '':tcpHeaderIdTable}}</span>
                    </td>
                    <td>报文体长度：
                      <span>{{childApiInfo.tcpBodyLenBytes}}</span>
                    </td>
                    <td>读取字节：
                      <span>{{childApiInfo.tcpReadBytes}}</span>
                    </td>
                  </tr> -->

                  <tr>
                    <td>是否计费：
                      <span>{{childApiInfo.charge == 1?'是':'否'}}</span>
                    </td>
                    <td colspan="2">后端超时：
                      <span>{{childApiInfo.backEndTimeout}} ms</span>
                    </td>
                  </tr>

                  <tr>
                    <td>Mock：
                      <span>{{childApiInfo.isMock == 1?'是':'否'}}</span>
                    </td>
                    <td colspan="2">Mock返回结果：
                      <span>{{childApiInfo.mockData}}</span>
                    </td>
                  </tr>

                  </tbody>
                </table>
              </div>
            </div>

            <div class="panel panel-default">
              <div class="console-panel-header">
                <span class="panel-header"></span>
                <el-tabs type="border-card" @tab-click="" v-model="activeBack">
                  <el-tab-pane label="后端服务参数" name="backValTab">
                    <table class="table table-hover table-bordered">
                          <thead>
                          <tr>
                            <th>后端参数名称</th>
                            <th>后端参数位置</th>
                            <th>对应入参名称</th>
                            <th>对应入参位置</th>
                            <th>对应入参类型</th>
                          </tr>
                          </thead>
                          <tbody v-if="childRes1.length <= 0">
                          <tr>
                            <td colspan="5" style="text-align:center">
                              <span>暂无数据</span>
                            </td>
                          </tr>
                          </tbody>
                          <tbody v-if="childRes1.length > 0">
                          <tr v-for="(item, index) in childRes1">
                            <td>
                              <span>{{item.paramName}}</span>
                            </td>
                            <td>
                              <span v-if="item.paramsLocation == 1">header</span>
                              <span v-else-if="item.paramsLocation == 2">query</span>
                              <span v-else-if="item.paramsLocation == 3">body</span>
                            </td>
                            <td>
                              <span>{{requestParamsList2[index].paramName}}</span>
                            </td>
                            <td>
                              <span v-if="requestParamsList2[index].paramsLocation == 1">header</span>
                              <span v-else-if="requestParamsList2[index].paramsLocation == 2">query</span>
                              <span v-else-if="requestParamsList2[index].paramsLocation == 3">body</span>
                            </td>
                            <td>
                              <span v-if="requestParamsList2[index].paramsType == 1">String</span>
                              <span v-else-if="requestParamsList2[index].paramsType == 2">Integer</span>
                              <span v-else-if="requestParamsList2[index].paramsType == 3">Long</span>
                              <span v-else-if="requestParamsList2[index].paramsType == 4">Float</span>
                              <span v-else-if="requestParamsList2[index].paramsType == 5">Double</span>
                              <span v-else-if="requestParamsList2[index].paramsType == 6">Boolean</span>
                            </td>
                          </tr>
                          </tbody>
                        </table>
                  </el-tab-pane>
                  <el-tab-pane label="jsonConfig" name="jsonConfigTab" v-model="childApiInfo">
                    <span><pre>{{childApiInfo.jsonConfig}}</pre></span>
                  </el-tab-pane>
                </el-tabs>

              </div>
            </div>

            <div class="panel panel-default" style="border:1px solid blue;position:relative">
              <div class="console-panel-header">
                <el-tabs type="border-card" :value="activeName">
                  <el-tab-pane label="常量参数" name="res" disabled="disabled">
                    <el-table :data="childRes2" style="width: 100%">
                      <el-table-column prop="paramName" label="后端参数名称"></el-table-column>
                      <el-table-column prop="paramValue" label="参数值"></el-table-column>
                      <el-table-column prop="paramsLocation" label="参数位置" :formatter="paramLocationFomatter"></el-table-column>
                      <el-table-column prop="paramDescription" label="描述"></el-table-column>
                    </el-table>
                  </el-tab-pane>
                  <el-tab-pane label="JSON" name="json" disabled="disabled">
                    <el-table :data="childRes3" style="width: 100%;">
                      <el-table-column label="JSON" >
                        <template slot-scope="scope">
                          <span style="display:block;height:200px;overflow:auto;"><pre>{{scope.row.paramValue}}</pre></span>
                        </template>
                      </el-table-column>
                    </el-table>
                  </el-tab-pane>
                  <el-tab-pane label="XML" name="xml" disabled="disabled">
                    <el-table :data="childRes4" style="width: 100%">
                      <el-table-column label="XML">
                        <template slot-scope="scope">
                          <span><pre>{{scope.row.paramValue}}</pre></span>
                        </template>
                      </el-table-column>
                    </el-table>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </div>

            <div class="panel panel-default">
              <div class="console-panel-header">
                <span class="panel-header">返回结果</span>
                <table v-model="childApiInfo" class="table table-hover table-bordered">
                  <tbody>
                  <tr>
                    <td colspan="2">返回类型：
                      <span>{{childApiInfo.callBackType ==1?'JSON':''}}</span>
                    </td>
                  </tr>
                  <tr>
                    <td style="float:left;width:50%;">返回结果示例：
                      <span class="writePlace" style="width:100%;display:block;">{{childApiInfo.callBackSuccessExample}}</span>
                    </td>
                    <td style="float:right;width:50%;">失败返回结果示例：
                      <span class="errorWritePlace" style="width:100%;display:block;">{{childApiInfo.callBackFailExample}}</span>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>

          </div>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
import { formatJson } from "@/assets/json";
import axios from "axios";
export default {
  name: "amendApiData",
  props: {
    apiList2: {
      type: Object
    },
    requestParamsList2: {
      type: Array
    },
    subApiList2: {
      type: Array
    },
    numData: {
      type: Array
    },
    numDataXml: {
      type: Array
    },
    numDataJson: {
      type: Array
    },
    activeName: {
      type: String,
      default: 'res'
    },
    backendList: {
      type: Array
    }
  },
  data() {
    return {
      issue: false, //发布API
      env: 1,
      groupHerfId:{
        apiName:'',
        pubDescription:'',
      },

      formLabelWidth: "120px",
      amendApiIndexEdit: 0,
      faceNameIndex: 0,
      childApiInfo: {},
      childApiInfores: {},
      childRes: [],
      childRes1: [],
      childRes2: [],
      childRes3: [],
      childRes4: [],
      subId:'',
      jsonTabVal:'',
      activeBack:'backValTab',
      tcpHeaderIdTable:'',
      showPublishBtn: false
    };
  },
  created() {
    this.childCallRes();
  },
  methods: {
    publish(){
      this.issue = true;
      this.groupHerfId.apiName = this.apiList2.apiName;
    },
    publishApi(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const id = this.$route.query.id;
          const pubDescription = this.groupHerfId.pubDescription;

          axios({
            method: 'post',
            url: '/api/publish',
            data: {
              id,
              env: this.env,
              pubDescription: pubDescription,
            }
          }).then(res => {
            this.$emit('issueOK', '')
            this.$message({
              type: 'success',
              message: '发布成功'
            });
            this.issue = false;
            this.env = 1;
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },
    issueOK() {
      this.issue = false;
    },
    //判断报文头id
    // tcpList(){
    //   axios({
    //     method: 'post',
    //     url: '/api/queryTcpHeader',
    //     data: {}
    //   }).then(res => {
    //     let list = res.data.data;
    //     list.map(data =>{
    //       console.log(data);
    //       if(data.id == this.childApiInfo.tcpHeaderId){
    //         this.tcpHeaderIdTable = data.headerName;
    //       }
    //     });
    //     console.log(this.childApiInfo);
    //   }).catch(error => {
    //     console.log('网络错误，不能访问');
    //   });
    // },



    checkItems(index,row){
      if($(".backServiceName").eq(index).attr("id")=="open"){
        $(".backServiceName").eq(index).removeAttr("id").siblings(".backServiceBox").slideUp();
      }else {
        $(".backServiceName").eq(index).parent().siblings().children().removeAttr("id");
        $(".backServiceName").eq(index).attr("id","open").next().slideDown().parent().siblings().children('.backServiceBox').slideUp();
      };

      if(row.id == null){
        axios({
          method: "post",
          url: "/api/findApiInfo",
          data: {
            id: row.apiId
          }
        }).then(res => {
          var resg = res.data.data;
          this.childRes = [];
          this.childRes1 = [];
          this.childRes2 = [];
          this.childRes3 = [];
          this.childRes4 = [];
          this.childApiInfo = [];
          this.childApiInfo = res.data.data.apiInfo;
          // this.tcpList();
          this.childRes = this.backendList;
          this.amendBck();
          this.paramsTypeFn();
          var resultJson = formatJson(this.childApiInfo.callBackSuccessExample);
          $(".writePlace").html("<pre>" + resultJson + "<pre/>");
          var resultErrorJson = formatJson(this.childApiInfo.callBackFailExample);
          $(".errorWritePlace").html("<pre>" + resultErrorJson + "<pre/>");
        }).catch(error => {
          console.log("网络错误");
        });
      }else{
        axios({
          method: "post",
          url: "/api/back/sub/get",
          data: {
            apiId:row.apiId,
            id:row.id
          }
        }).then(res => {
          var resg2 = res.data.data;
          this.childRes = [];
          this.childRes1 = [];
          this.childRes2 = [];
          this.childRes3 = [];
          this.childRes4 = [];
          this.childApiInfo = [];
          this.childApiInfo = res.data.data.subThirdApiInfo;
          // this.tcpList();
          this.childApiInfo.callBackSuccessExample = resg2.apiInfo.callBackSuccessExample;
          this.childApiInfo.callBackFailExample = resg2.apiInfo.callBackFailExample;
          this.childRes = resg2.subThirdRequestInfo;

          this.amendBck();


          this.paramsTypeFn();
          var resultJson = formatJson(this.childApiInfo.callBackSuccessExample);
          $(".writePlace").html("<pre>" + resultJson + "<pre/>");
          var resultErrorJson = formatJson(this.childApiInfo.callBackFailExample);
          $(".errorWritePlace").html("<pre>" + resultErrorJson + "<pre/>");
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      }

    },
    amendBck() {
      this.childRes.map(data => {
        if (data.paramsType == 2) {
          this.$emit('activeChange', 'res')
        } else if (data.paramsType == 3) {
          this.$emit('activeChange', 'json')
        } else if (data.paramsType == 4) {
          this.$emit('activeChange', 'xml')
        } else {
          this.$emit('activeChange', '')
        }
      });
    },



    //判断子接口有无ID
    childCallRes(){
      this.subApiList2.map(data =>{
        this.subId = data.id;
      });
    },

    // 参数位置转换
    paramLocationFomatter(row, column) {
      const local = row[column.property];
      if (local == undefined) {
        return "";
      }
      return local == 1 ? "header" : local == 2 ? "query" : "body";
    },

    moreStart(index) {
      $(".moreMesTwo").show();
      this.amendApiIndexEdit = index;
    },
    moreEnd(index) {
      $(".moreMesTwo").hide();
      this.amendApiIndexEdit = index;
    },


    paramsTypeFn() {
      this.childRes2 = [];
      this.childRes.forEach((obj, index) => {
        if (obj.paramsType == 1) {
          this.childRes1.push(obj);
        } else if (obj.paramsType == 2) {
          this.childRes2.push(obj);
        } else if (obj.paramsType == 3) {
          this.childRes3 = [];
          this.childRes3.push(obj);
          $(".valCommon").show();
        } else if (obj.paramsType == 4) {
          this.childRes4.push(obj);
        }
      });
    },
//    判断是否为管理员
    setRole(role) {
      if (role && role.includes('admin')) {
        this.showPublishBtn = true
      } else {
        this.showPublishBtn = false
      }
    }
  }
};
</script>
<style>
.moreMesTwo {
  position: absolute;
  top: 0;
  right: 240px;
  display: none;
  width: 500px;
  background: #fff;
  border: 1px solid #ddd;
}

.valCommon {
  position: absolute;
  background: #fff;
  z-index: 999;
  top: 101px;
  left: 20px;
  width: 95%;
  height: 119px;
  overflow: auto;
  display: none;
}
.aa td {
  height: 120px;
  overflow: auto;
}
.writePlace {
  display: block;
  width: 100%;
  height: 300px;
  overflow: auto;
}
.errorWritePlace{
  display: block;
  width: 100%;
  height: 300px;
  overflow: auto;
}
.backServiceName{
  display: inline-block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #dfe6ec;
}
.backServiceName:hover{
  background: #eef1f6;
}
</style>



