<template>
  <div class="TestApi" style="background:#ffffff;height:100%;">
    <div class="card_statistical">
      <el-row :gutter="20">
        <el-col :span="6"><div class="grid-content bg-purple">
          <div class="text"><p>总调用量</p>
               <p>{{totalCount}}</p>
          </div>
          <div class="canvasBoxTotal">
            <div id="totalCountId" style="width: 100%; height: 70px"></div>
          </div>
        </div></el-col>
        <el-col :span="6"><div class="grid-content bg-purple-light">
          <div class="text">
            <p>成功</p>
            <p>{{totalSuccess}}</p>
          </div>
          <div class="canvasBox">
            <canvas-bar ref="canvasRef" :width="50" :height="50" id="successBar" :totle="totalCount" :value="totalSuccess" color="green"></canvas-bar>
          </div>
        </div></el-col>
        <el-col :span="6"><div class="grid-content bg-purple">
          <div class="text">
            <p>失败量</p>
            <p>{{totalFail}}</p>
            </div>
          <div  class="canvasBox">
            <canvas-bar ref="canvasFailRef" :width="50" :height="50" id="failBar" :totle="totalCount" :value="totalFail" color="red"></canvas-bar>
          </div>
        </div></el-col>
        <el-col :span="6"><div class="grid-content bg-purple-light">
          <div class="text">
            <p>异常量</p>
            <p>{{totalException}}</p>
          </div>
          <div  class="canvasBox">
            <canvas-bar ref="canvasExceptionRef" :width="50" :height="50" id="exceptionBar" :totle="totalCount" :value="totalException" color="#FEBD49"></canvas-bar>
          </div>
        </div></el-col>
      </el-row>
    </div>
    <div style="display: flex;padding-right: 15px">
      <el-form :inline="true" class="search_form" style="flex: 1;vertical-align: top">
        <el-form-item>
          <el-select v-model="searchParams.sourceName" clearable filterable class="w200">
            <!-- <el-tree style="border:none;" default-expand-all :data="groupOptions" :props="defaultProps" accordion @node-click="handleNodeClick"></el-tree> -->
            <el-option v-for="item in groupOptions" :key="item.id" :label="item.groupName" :value="item.groupName"></el-option>
          </el-select>
          <el-input placeholder="请输入APIID进行查询" class="w200" v-model='searchParams.apiId'></el-input>
          <el-date-picker
            class="w200"
            v-model="rangeDate"
            @change="dateChange"
            rangeSeparator="/"
            type="datetimerange"
            placeholder="选择时间范围">
          </el-date-picker>
          <el-select v-model="searchParams.status" placeholder="请选择" style="width: 80px">
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="info" class="searchOne" @click="defouteSearch()">搜 索</el-button>
        </el-form-item>
      </el-form>
      <div style="flex: 0 0 92px; vertical-align: top;margin-top: 10px">
        <el-radio-group v-model="showType" @change="showTypeChange">
          <el-radio-button label="table"><i class="el-icon-menu"></i></el-radio-button>
          <el-radio-button label="echarts"><i class="el-icon-picture"></i></el-radio-button>
        </el-radio-group>
      </div>
    </div>
    <div style="position: relative">
      <div id="groupCountListId"  :style="{position: 'absolute', top: 0,height: '400px',width:'100%', zIndex: (showType === 'echarts' && groupCountList.length > 0) ? 2 : -1}"></div>
      <template v-if="showType === 'table'" >
        <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
          <el-table-column prop="apiName" label="接口名称"></el-table-column>
          <el-table-column prop="count" label="调用总量"></el-table-column>
          <el-table-column prop="avgSuccessResponseTime" label="	平均响应时间/ms"></el-table-column>
          <el-table-column prop="countSuccess" label="成功" ></el-table-column>
          <el-table-column prop="countFail" label="	失败" ></el-table-column>
          <el-table-column prop="countExp" label="	异常" ></el-table-column>
          <el-table-column label="成功率">
            <template slot-scope="scope">
              <div @click="elPregrossClick(scope.row)" style="cursor: pointer">
                <el-progress :text-inside="true" :stroke-width="18" :percentage="scope.row.count === 0 ? 0 : Math.round(scope.row.countSuccess/scope.row.count * 100)" :status="scope.row.count === 0 ? 'exception' : scope.row.countSuccess/scope.row.count === 1 ? 'success' : 'exception' "></el-progress>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="page"  style="padding-bottom:30px;" v-if="this.tablegroup.length > 0">
          <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="searchParams.pageNum"
                         :page-sizes="[10, 20, 30, 40]" :page-size="searchParams.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
          </el-pagination>
        </div>
        <div class="teCen mt60" v-if="this.tablegroup.length < 1">
          <p><img src="../../assets/Shape.png" /></p>
          <span>暂无数据</span>
        </div>
      </template>
      <div class="teCen mt60" v-if="showType === 'echarts' && this.groupCountList.length < 1">
        <p><img src="../../assets/Shape.png" /></p>
        <span>暂无数据</span>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  import echarts from 'echarts'
  import CanvasBar from '../../components/common/ui/canvasPar/index.vue'

  export default {
    name: 'apiStatistical',
    components: {
      CanvasBar
    },
    data() {
      return {
        showType: 'table',
        jurisdiction:{},
        searchParams: {
          startTime: Number(moment(moment(new Date()).format('YYYY-MM-DD')).format('x')),
          endTime: Number(moment(new Date()).format('x')),
          sourceName: null,
          apiId: null,
          pageNum: 1,
          pageSize: 10,
          status: 'S'
        },
        total: 0,
        rangeDate: [moment(moment(new Date()).format('YYYY-MM-DD')), moment(new Date())],
        listLoading: false,
        tablegroup: [],
        groupOptions: [],
        totalCount: 0,
        totalException: 0,
        totalFail: 0,
        totalSuccess: 0,
        groupCountList: [],
        statusOptions: [
          {
            value: 'S',
            label: '全部'
          },
          {
            value: 'F',
            label: '失败'
          },
          {
            value: 'E',
            label: '异常'
          }
        ],
        groupCountListOption: {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          toolbox: {
            feature: {
              dataView: {show: true, readOnly: false},
              magicType: {show: true, type: ['line', 'bar']},
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          legend: {
            data:['调用量','响应时间']
          },
          xAxis: [
            {
              type: 'category',
              data: [],
              axisPointer: {
                type: 'shadow'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '调用量',
              axisLabel: {
                formatter: '{value} 次数'
              }
            },
            {
              type: 'value',
              name: '响应时间',
              axisLabel: {
                formatter: '{value} ms'
              }
            }
          ],
          series: [
          ]
        },
        groupCountListEchatrs: null,
        totalCountIdEachatrs: null,
        totalCountIdOption: {
          grid: {
            show: false
          },
          xAxis: {
            type: 'category',
            splitLine: {     //网格线
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
            axisLine: {
              symbol: ['none', 'arrow'],
              symbolSize: [6, 10]
            },
            data: []
          },
          yAxis: {
            type: 'value',
            splitLine: {     //网格线
              show: false
            },
            minInterval:100,
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false
            },
            axisLine: {
              symbol: ['none', 'arrow'],
              symbolSize: [6, 10]
            }
          },
          series: [{
            data: [],
            showSymbol: false,
            type: 'line',
            lineStyle: {
              normal: {
                width: 2,
                type: 'solid',
                color: '#3FA7DC'
              }
            }
          }]
        }
      }
    },

    created() {
      this.$nextTick(() => {
        this.getUserPermissions();
        this.loadGroups();
        this.groupCountListEchatrs = echarts.init(document.querySelector('#groupCountListId'));
        this.groupCountListEchatrs.setOption(this.groupCountListOption, true)
        this.resizeCharts(this.groupCountListEchatrs, 'groupCountListId')
        this.totalCountIdEachatrs = echarts.init(document.querySelector('#totalCountId'));
        this.totalCountIdEachatrs.setOption(this.totalCountIdOption, true)
        this.resizeCharts(this.totalCountIdEachatrs, 'totalCountId')
      })
    },
    mounted() {

    },
    methods: {

      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          } else {
            this.defouteSearch()
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //加载分组信息
      loadGroups() {
        axios({
          method: "post",
          url: "/apiGroup/listOfNoLowerLevelGroup",
          data: {}
        }).then(res => {
          this.groupOptions = res.data.data;
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },

      // 日期改变
      dateChange(date) {
        let newDate = date.split("/")
        this.searchParams.startTime = Number(moment(newDate[0]).format('x'))
        this.searchParams.endTime = Number(moment(newDate[1]).format('x'))
      },
      //重置搜索条件
      defouteSearch() {
        this.searchParams.pageNum = 1
        this.initData()
      },
      //初始化与搜索框查询
      initData() {
        this.listLoading = true
        axios({
          method: 'post',
          url: ' /count/index',
          data: this.searchParams
        }).then(res => {
          this.listLoading = false
          if (res.data.code !== 2000) {
            this.$message.error(res.data.message);
          } else {
            this.total = res.data.data.total
            this.tablegroup = res.data.data.apiCountList
            this.totalException = res.data.data.totalException
            this.totalFail = res.data.data.totalFail
            this.totalSuccess = res.data.data.totalSuccess
            this.totalCount = res.data.data.totalCount
            this.groupCountList = res.data.data.groupCountList
            this.$nextTick(() => {
              this.$refs.canvasRef.createdPage()
              this.$refs.canvasFailRef.createdPage()
              this.$refs.canvasExceptionRef.createdPage()
            })
            let group =[], use = [], time = []
            this.groupCountList.forEach((item) => {
              group.push(item.sourceName)
              use.push(item.countBySourceName)
              time.push(item.avgSuccessResponseTime)
            })
            this.groupCountListOption.xAxis[0].data = group
            let ary = [
              {
                name: '调用量',
                type: 'bar',
                barMaxWidth: 40,
                data: use
              },
              {
                name: '响应时间',
                type:'line',
                yAxisIndex: 1,
                barMaxWidth: 40,
                data: time
              }
            ]
            this.groupCountListOption.series = ary
            this.groupCountListEchatrs.setOption(this.groupCountListOption, true)
            this.resizeCharts(this.groupCountListEchatrs, 'groupCountListId')

            this.totalCountIdOption.xAxis.data = res.data.data.group.time
            this.totalCountIdOption.series[0].data = res.data.data.group.count
            this.totalCountIdEachatrs.setOption(this.totalCountIdOption, true)
          }
        }).catch(error => {
          this.listLoading = false
          console.log('网络错误，不能访问');
        });
      },
      //查看
      lookJson(row){
        this.jsonDialog = true;
        this.callBack =  JSON.parse(row.testResult);
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      handleCurrentChange(val) {
        this.searchParams.pageNum = val;
        this.initData();


      },
      handleSizeChange(val) {
        this.searchParams.pageSize = val;
        this.defouteSearch();
      },
      showTypeChange(str) {
//        if (str === 'echarts') {
//          this.resizeCharts()
//        } else {
//          this.groupCountListEchatrs.resize({width: 0, height: 0});
//        }
      },
      resizeCharts(echatrsObj, id) {
        const height = document.getElementById(id).offsetHeight;
        const width = document.getElementById(id).offsetWidth;
        echatrsObj.resize({width: width, height: height});
      },
      elPregrossClick(row) {
        let routerData = this.$router.resolve({
          path: '/apiStatistical/logList',
          query: {
            apiId: row.apiId,
            startTime: this.searchParams.startTime,
            endTime: this.searchParams.endTime
          }
        })
        window.open(routerData.href, '_blank');
      }
    }

  }
</script>

<style scoped>
  .card_statistical{
    padding: 5px 15px;
  }
.grid-content{
  border: 1px solid #eaeefb;
  background: #fff;
  border-radius: 4px;
  min-height: 100px;
  display: flex;
  font-size: 0;
}
  .grid-content:hover{
    box-shadow: 0 0  2px rgba(0, 0, 0, 0.2);
  }
  .grid-content>div{
     font-size: 18px;
    vertical-align: middle;
   }
  .canvasBox{
    text-align: right;
    flex: 0 0 50px;
  }
  .grid-content>div.canvasBoxTotal{
    flex: 1;
    padding: 0;
    margin-top: 10px;
  }
  .canvasBoxText{
    flex: 0 0 102px;
  }
  .grid-content>div.text{
    flex: 1;
    text-align: left;
    padding: 20px 10px;
    font-weight: bolder;
  }
  .text>p:first-child{
    font-weight: normal;
  }
</style>



