<template>
  <div class="codeMap" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入API名称进行查询" class="w350" v-model="searchStrategy"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData">搜 索</el-button>
      </el-form-item>
    </el-form>

    <table class="tableBox" :data="codeData" style="border-top:8px solid #e6e6e6">
      <tr class="tableHead">
        <td class="tabTitle">API名称</td>
        <td class="tabTitle">后端服务名称</td>
        <td class="tabTitle">抽取值Key</td>
        <td class="tabTitle">key映射</td>
        <td class="tabTitle">抽取value</td>
        <td class="tabTitle">value映射</td>
        <td class="actionTitle">操作</td>
      </tr>

      <tr  style="width: 100%;" v-for="item0 in codeData">
        <td class="apiNameList">{{item0.apiName}}</td>
        <td class="interfaceNameBox" colspan="5">
          <table style="width: 100%;">
            <!--for="subApis"-->
            <tr v-for="item1 in item0.subApis" style="">
              <td class="interfaceNameList">
                {{item1.interfaceName}}
              </td>
              <td class="mappingBox">
                <table style="width: 100%;">
                  <!--for="mapping"-->
                  <tr v-for="item2 in ((item1.mapping && item1.mapping.length > 0 ) ? item1.mapping : 1)">
                    <td class="mappingList">
                      <div  style="display: inline-block;width: 100%;"><span>{{ item2.key ? item2.key : "（无）" }}</span></div>
                    </td>
                    <td class="mappingList">
                      <div  style="display: inline-block;width: 100%;"><span>{{item2.transKey ? item2.transKey : "（无）" }}</span></div>
                    </td>
                    <td class="optionBox">
                      <table style="width: 100%;">
                        <!--for="option"-->
                        <tr v-for="item3 in (item2.option ? item2.option : 1)">
                          <td class="optionList">
                            <div style="width: 100%;height: 50px;line-height: 50px;padding-left: 20px;border-top: 1px solid #dfe6ec;border-left: 1px solid #dfe6ec;"><span style="">{{item3.orign ? item3.orign : "（无）"}}</span></div>
                          </td>
                          <td class="optionList">
                            <div style="width: 100%;height: 50px;line-height: 50px;padding-left: 20px;border-top: 1px solid #dfe6ec;border-left: 1px solid #dfe6ec;"><span style="">{{item3.target ? item3.target : "（无）"}}</span></div>
                          </td>
                        </tr>
                      </table>
                    </td>
                  </tr>
                  <tr></tr>
                </table>
              </td>
            </tr>
          </table>
        </td>
        <td style="width: 10%;border-top: 1px solid #dfe6ec;border-left:1px solid #dfe6ec;padding-left: 20px">
          <span class="lookBtn bindColor" @click="lookEvent(item0)">查看</span>

          <el-upload action="/api/247/excelUpload" class="avatar-uploader" :show-file-list="false" :http-request="filterFile">
            <el-button type="text" class="radiusBtn pubColor" v-if="exportBtn" size="small" @click="codeLeadTwoBtn(item0)">导入</el-button>
          </el-upload>

        </td>
      </tr>
    </table>
    <div class="teCen mt60" v-if="this.codeData.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>


    <el-dialog title="查看" :visible.sync="lookData" size="tiny" style="top:-5%">
      <pre><span class="writePlaces">{{callBack}}</span></pre>
      <span slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="lookData = false">关闭</el-button>
      </span>
    </el-dialog>

    <div class="page" style="padding-bottom:30px;">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { formatJson } from "@/assets/json";
import axios from "axios";

export default {
  name: "codeMap",
  data() {
    return {

      jurisdiction:{},
      exportBtn:false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      codeData: [],
      searchStrategy: "",
      formLabelWidth: "120px",
      subApisArr: [],
      mappingArr: [],
      subApisArr1: [],
      lookData: false,
      callBack: "",
      optionTar:'',
      newPage:'',
      actionUrl:'',
      codeApiId:'',
      codeUrl:'',
    };
  },

  created() {
    this.initData();
    this.utils.getUserPermissions(this);
  },

  methods: {
    filterFile({file}) {
      setTimeout(() => this.submitFile(file), 0)
      return false
    },
    async submitFile(file){
      let data = new window.FormData();
      data.append('file',file);
      const rsp = await this.$http.post("/api/" + this.codeApiId + "/uploadExcel", data, { headers:{'Content-Type':'multipart/form-data'}})
      .then(res =>{
        if(res.data.code == 2000){
          this.$message({
            type: 'success',
            message: '导入成功'
          });
          this.initData();
        }else{
          this.$message({
            type: 'error',
            message: res.data.message
          });
        }
      }).catch(error =>{
        console.log("网络错误，不能访问");
      });
    },

    //码值导入
    codeLeadTwoBtn(val){
      this.codeApiId = val.id;
      // axios({
      //   method: "post",
      //   url: "/api/getServerURL",
      //   data: {}
      // }).then(res => {
      //   this.codeApiId = val.id;
      //   this.codeUrl = res.data;
      //   this.actionUrl = "http://173.32.11.112:8081/api/"+val.id+"/uploadExcel";
      // }).catch(error => {
      //   console.log("网络错误，不能访问");
      // });
    },

    beforeAvatarUpload(file){
      var testmsg = file.name.substring(file.name.lastIndexOf('.')+1);
      const extension = testmsg === 'xls';
      const extension2 = testmsg === 'xlsx';
      const isLt2M = file.size / 1024 / 1024 < 10;

      if(!extension && !extension2){
        this.$message({
          message:'上传文件只能是 xls xlsx格式！',
          type:'warning'
        });
      }
      if(!isLt2M){
        this.$message({
          message:'上传文件大小不能超过10MB！',
          type:'warning'
        });
      }
      return extension || extension2 && isLt2M
    },



    menuData(){
      $(".el-menu-item").eq(5).removeClass('is-active');
      $(".el-menu-item").eq(4).removeClass('is-active');
      $(".el-menu-item").eq(3).addClass('is-active');
    },

    //初始化列表
    initData() {
      axios({
        method: "post",
        url: "/dict/query/config",
        data: {
          apiName: this.searchStrategy,
          pageSize: this.newPage =='' ? 10 :this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 403){
          location.href = res.data.data;
        }
        if(!this.jurisdiction.roles.includes('admin')){
          this.exportBtn = false;
        }else{
          this.exportBtn = true;
        }
        this.codeData = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;

        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    },
    handleSizeChange(val){
      this.newPage = val;
      this.initData();
    },
    lookDia(row) {
      var resultJson = formatJson(row.callBackSuccessExample);
      $(".writePlaces").html("<pre>" + resultJson + "<pre/>");
      this.callBack = resultJson;
      this.lookData = true;
    },
    lookEvent(row){
      var resultJson = formatJson(row.callBackSuccessExample);
      $(".writePlaces").html("<pre>" + resultJson + "<pre/>");
      this.callBack = resultJson;
      this.lookData = true;
    }
    /*extractKeyFormat(row, column,cellValue){
        let date = row[column.property];
        console.log(row,"[[[]]]")
        if (date == undefined||date == ""||date == null) {
            return "";
        }else {
            return "放着"
        }
    }*/
  },

};
</script>

<style scoped>
  .tableBox{
    width: 100%;margin: 0 auto;border-top: 1px solid #dfe6ec;border-bottom: 1px solid #dfe6ec;background: #fff;
  }
  .tableHead{
    width: 100%;height: 50px
  }
.tabTitle{
  width: 15%;padding-left: 20px;background: #EDEEF1;font-size: 12px;
    font-weight:bold; color:#8792A3;
}
.actionTitle{
  width: 10%;padding-left: 20px;background: #EDEEF1;font-size: 12px;
    font-weight: bold;color:#8792A3;
}
.apiNameList{
  width: 15%;height: 50px;padding-left: 20px;
  border-top: 1px solid #dfe6ec;border-left: 1px solid #dfe6ec;
}
.interfaceNameBox{
  width: 75%;
}
.interfaceNameList{
  width: 20%;height: 50px;padding-left: 20px;border-top: 1px solid #dfe6ec;border-left: 1px solid #dfe6ec;word-wrap:break-word;word-break:break-all;
}
.mappingBox{
  width: 80%;
}
.mappingList{
  width: 25%;height: 50px;padding-left: 20px;border-top: 1px solid #dfe6ec;border-left: 1px solid #dfe6ec;
}
.optionBox{
  width: 50%;
}
.optionList{
  width: 50%;;height: 50px;
}
.lookBtn{
  cursor: pointer;
  display: block;
  font-size: 12px;
  margin-left: 2px;
}
</style>



