<template>
  <div>
    <el-form v-model="groupHerfId">
      <span style="font-weight:700;">您将要下线以下API：</span>
      <div class="issueAPI">{{groupHerfId.apiName}}</div>

      <!--<p style="font-weight:700;">请选择要下线的环境：</p>-->

      <!--<el-radio-group v-model="env">-->
        <!--<el-radio-button label=1>线上</el-radio-button>-->
        <!--<el-radio-button label=2>预发</el-radio-button>-->
      <!--</el-radio-group>-->

      <p style="font-weight:700;">请选输入下线说明：</p>
      <el-form-item :label-width="120" :rules="[{required:true, message: '下线说明不能为空' ,trigger: 'blur' },
          { min: 1, max: 180, message: '下线说明不能为空', trigger: 'blur' },{ validator: descriptionValidate, trigger: 'blur'}]">
        <el-input placeholder="请输入下线说明" class="w350" v-model='remarks'></el-input>
      </el-form-item>

      <p style="color: red">该操作将导致此API在指定的环境无法被访问，可能会影响相当一部分应用和用户。请确保已经告知用户，或者确认需要强制下线</p>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button class="cancelBtn" @click="$emit('cancle', '')">取 消</el-button>
      <el-button type="primary" class="addOne" @click="overLineApi">下线</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'overLineApiCom',
  props: ["groupHerfId"],

  data() {

    return {
      env: 1,
      remarks: ''
    }
  },
  created() {

  },
  methods: {

    overLineApi() {
      const apiGroupId = this.groupHerfId.apiGroupId;
      const id = this.groupHerfId.id;
      if (this.remarks.trim() === '') {
        this.$message({
          message: '请输入备注说明!',
          type: 'warning'
        });
        return;
      }

      axios({
        method: 'post',
        url: '/api/offline',
        data: {
          apiGroupId: apiGroupId,
          id,
          env: this.env,
          remarks: this.remarks
        }
      }).then(res => {
        if (res.data.code !== 2000) {
          this.$message({
            message: '请选择已发布的运行环境,如果暂无,请先进行发布！',
            type: 'warning'
          });
        } else {
          this.$emit('offLineOK', '')
          this.$message({
            type: 'success',
            message: '下线成功'
          });
        }
      }).catch(error => {
        console.log(error.message);
      });
    },
  }
}
</script>


<style scoped>

</style>



