<template>
  <div>
    <el-table :data="tableList" border style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="uid" label="流水号" show-overflow-tooltip></el-table-column>
      <el-table-column prop="sourceResponseCode" show-overflow-tooltip width="100px" label="码值"></el-table-column>
      <el-table-column prop="sourceSpendTime" show-overflow-tooltip width="100px" label="耗时/ms"></el-table-column>
      <el-table-column prop="sourceCreateTime" show-overflow-tooltip label="时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="sourceResult" label="结果" minWidth="150px">
        <template slot-scope="scope">
          <div style="display: flex">
            <div class="flex_right_result">{{scope.row.sourceResult}} </div>
            <a class="flex_right_a" @click="showResult(scope.row.sourceResult)">查看更多</a>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="详情日志详情" width="230px">
        <template slot-scope="scope">
          <el-button-group>
            <el-button type="primary" @click="showDetail(scope.row, 'zuul')">zuul</el-button>
            <el-button type="primary" @click="showDetail(scope.row, 'client')">client</el-button>
            <el-button type="primary" @click="showDetail(scope.row, 'exit')">exit</el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <div class="page"  style="padding-bottom:30px;" v-if="this.tableList.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="searchData.pageNum"
                     :page-sizes="[10, 20, 30, 40]" :page-size="searchData.pageSize" layout=" sizes, prev, pager, next, jumper" :total="totalCount">
      </el-pagination>
    </div>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>
    <el-dialog title="详情" :visible.sync="dialogResultVisible">
      <div>
        <json-viewer :value="setJson(sourceResult)"></json-viewer>
      </div>
    </el-dialog>
    <el-dialog :title="dialogDetail.title"
               :visible.sync="dialogDetail.show"
               size="large"
               @close="detailClose">
      <div :style="{lineHeight: '24px', wordBreak: 'break-all'}">
        <p v-for="item in dialogDetailText">{{item}}</p>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import axios from 'axios'
  import { COMMON_CACHE_DATA_UPDATE } from '../../store/mutation-types'
  import moment from "moment";
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
  import JsonViewer from 'vue-json-viewer'
  import 'vue-json-viewer/style.css'
  export default {
    data() {
      return {
        dialogDetail: {
          title: '',
          show: false
        },
        dialogDetailText: '',
        sourceResult: '',
        dialogResultVisible: false,
        searchData: {
          apiId: null,
          endTime: 0,
          pageNum: 1,
          pageSize: 10,
          startTime: 0
        },
        tableList: [],
        listLoading: false,
        totalCount: 0
      }
    },
    components: {ElButton, JsonViewer},
    computed: {
      bodyHeight() {
        return this.$store.getters.bodyHeight
      },
      bodyWidth() {
        return this.$store.getters.bodyWidth
      }
    },
    mounted() {
      this.setBreadcrumb()
      this.requestParame()
    },
    methods: {
      setBreadcrumb() {
        const breadcrumb = JSON.parse(JSON.stringify(this.$store.getters.commonCacheData('breadcrumb')))
        breadcrumb.push({
          urls: this.$route.path,
          name: '日志列表'
        })
        this.$store.commit(COMMON_CACHE_DATA_UPDATE, {
          name: 'breadcrumb',
          value: breadcrumb
        })
      },
      requestParame() {
        let query = this.$route.query
        if (query && query.apiId && query.endTime && query.startTime) {
          this.searchData.apiId = query.apiId
          this.searchData.endTime = query.endTime
          this.searchData.startTime = query.startTime
          this.intData()
        } else {
          this.back()
        }
      },
      back() {
        this.$router.push({path: '/apiStatistical'})
      },
      intData() {
        this.listLoading = true
        axios({
          method: "post",
          url: "/count/errorListPage",
          data: this.searchData
        }).then(res => {
          this.listLoading = false
          if (res.data.code === 2000) {
            this.tableList = res.data.data.list;
            this.totalCount = res.data.data.totalCount;
          } else if(res.data.code === 403){
            location.href = res.data.data;
          }
        }).catch(error => {
          this.listLoading = false
          console.log("网络错误，不能访问" + error);
        });
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },
      //分页查询
      handleCurrentChange(val) {
        this.searchData.pageNum = val;
        this.intData();
      },
      handleSizeChange(val) {
        this.searchData.pageSize = val;
        this.handleCurrentChange(1);
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      showDetail(row, type) {
        this.dialogDetail.show = true
        this.dialogDetail.title = type + '日志详情'
        let httpData = {
          sourceCreateTime: row.sourceCreateTime,
          uid: row.uid,
          app: ''
        }
        if (type === 'zuul') {
          httpData.app = 'zuul-proxy'
        }
        else if (type === 'client') {
          httpData.app = 'inmgr'
        }
        else if (type === 'exit') {
          httpData.app = 'gateway-exit'
        }
        axios({
          method: "post",
          url: "/count/queryLogByUid",
          data: httpData
        }).then(res => {
          this.listLoading = false
          if (res.data.code === 2000) {
            this.dialogDetailText = res.data.data
          } else if(res.data.code === 403){
            location.href = res.data.data;
          } else {
            this.dialogDetail.show = false
            this.$message.error('请求错误')
          }
        }).catch(error => {
          console.log("网络错误，不能访问" + error);
        });
      },
      detailClose() {
        this.dialogDetailText = ''
      },
      setJson(data) {
        if (data === '') return {}
        return JSON.parse(data)
      },
      showResult(data) {
        this.sourceResult = data
        this.dialogResultVisible = true
      }
    }
  }
</script>
<style scoped>
  .flex_right_result{
    flex: 1;
    width: calc(100% - 70px);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
  }
  .flex_right_a{
    flex: 0 0 60px;
    margin-left: 10px;
    cursor: pointer;
  }
  .flex_right_a:hover{
    color: #1C72EE;
  }
</style>



