          if (res.test(value) === false) {
            callback(new Error("输入值不能为空和包含其他非法字符"));
          } else if (reg.test(value)) {
            callback(new Error("不能输入空格"));
          } else {
            callback();
          }
        },

        //后端超时
        backTimeCheckUnEmpty: (rule, value, callback) => {
          var res = /^(?!0)(?:[0-9]{1,5}|60000)$/;
          if (value === '') {
            callback(new Error('请输入请求后端超时，必须为数字'));
          } else if (res.test(value) === false) {
            callback(new Error('后端超时必须大于等于1ms，小于等于60000ms，并且必须为数字'));
          } else if(value >60000){
            callback(new Error('后端超时必须大于等于1ms，小于等于60000ms，并且必须为数字'));
          }else{
            callback();
          }
        },
        //权重验证
        weightVal: (rule, value, callback) => {
          var res = /^([0-9]{1,2}|10)$/;
          if (value === '') {
            callback(new Error('请输入请求后端超时，必须为数字'));
          }else if (res.test(value) === false) {
            callback(new Error('权重必须为0-10之间的整数'));
          }else if(value >10){
            callback(new Error('权重必须为0-10之间的整数'));
          }else{
            callback();
          }
