          var reg = /^[^\u4e00-\u9fa5]{0,}$/;
          var res = /^(\/[A-Za-z0-9_.])+./;
          if (value === "") {
            callback(new Error("请输入请求Path"));
          }else if(reg.test(value) === false){
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else if (res.test(value) === false) {
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else {
            callback();
          }
        },
        //后端Path验证
        checkReqPathBackend: (rule, value, callback) => {
          var reg = /^[^\u4e00-\u9fa5]{0,}$/;
          var res = /^(\/[A-Za-z0-9_.])*./;
          if (!value) {
            callback(new Error('请输入请求Path'))
          }else if(reg.test(value) === false){
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else if (res.test(value) === false) {
            callback(new Error("请输入正确的格式,例如/getUserInfo/[userId]"));
          } else {
            callback();
          }
        },

