            }
          }
        ];
        if (tab.index == 1) {
          $(".jsonHtml").html("");
          this.parseJSONObject(jsonStr, 0);
        }
      },

      parseJSONObject(jsonObject, level) {
        for (var key in jsonObject) {
          var trStr = "",
            type,
            isObject = false;
          trStr += '<tr class="example">';
          trStr += "<td><span>" + level + "</span></td>";
          trStr += "<td>" + key + "</td>";
          if (typeof jsonObject[key] == "number") {
            type = "number";
          } else if (typeof jsonObject[key] == "string") {
            type = "string";
          } else if (typeof jsonObject[key] == "object") {
            type = "object";
            isObject = true;
          }

          trStr +=
            '<td><select class="form-control" disabled="disabled"><option>' +
            type +
            "</option></select></td>";
          trStr += '<td><input class="form-control" /></td>';
          trStr +=
            '<td><select class="form-control" disabled="disabled"><option value="1">是</option><option value="2">否</option></select></td>';
          var codeMapBtn = $(
            '<button type="text" class="radiusBtn" size="small">码值映射</button>'
          ).click(this.codeMapFunc);
          var delBtn = $(
            '<button type="text" class="radiusBtn" size="small">删除</button>'
          ).click(this.delBtnFunc);
          var addBtn = $(
            '<button type="text" class="radiusBtn" size="small">添加</button>'
          ).click(this.addBtnFunc);
          var $tr = $("<td></td>").append(codeMapBtn, delBtn, addBtn);
          trStr += "</tr > ";
          var $tr = $(trStr).append($tr);
          $(".jsonHtml").append($tr);
          if (isObject) {
            this.parseJSONObject(jsonObject[key], ++level);
          }
        }
      },
      codeMapFunc() {
