      //深度克隆 1
      deepClone(obj) {
        var result,
          oClass = this.isClass(obj);
        //确定result的类型
        if (oClass === "Object") {
          result = {};
        } else if (oClass === "Array") {
          result = [];
        } else {
          return obj;
        }
        for (var key in obj) {
          var copy = obj[key];
          if (this.isClass(copy) == "Object" || "Array") {
            result[key] = this.deepClone(copy); //递归调用
          } else {
            result[key] = obj[key];
          }
        }
        return result;
      },
      //返回传递给他的任意对象的类
      isClass(o) {
        if (o === null) return "Null";
        if (o === undefined) return "Undefined";
        return Object.prototype.toString.call(o).slice(8, -1);
      },

      //深度克隆 2
      cloneObj(obj) {
        var str,
          newObj = obj.constructor === Array ? [] : {};
        if (typeof obj !== "object") {
          return;
        } else if (window.JSON) {
          str = JSON.stringify(obj);
          newObj = JSON.parse(str);
        } else {
          for (var i in obj) {
            newObj[i] =
              typeof obj[i] === "object" ? this.cloneObj(obj[i]) : obj[i];
          }
        }
        return newObj;
      },
