                $(".jqueryValidate4").eq(index).show();
                $(".jqueryValidate7").eq(index).hide();
                flag = false;
              } else if (data.paramValue === "" || reg.test(data.paramValue)) {
                $(".jqueryValidate5").eq(index).show();
                flag = false;
              }
//              try {
//                if(this.judgeSame(data, index, this.numData, '3') === 'same') {
//                  throw 'error'
//                }
//              }catch (err){
//                $(".jqueryValidateSame3").eq(index).show();
//                flag = false;
//              }
            });

            this.requestParamsList.map((data, index) => {
              if (regExp.test(data.paramName) == false || reg.test(data.paramName)){
                $(".jqueryValidate").eq(index).show();
                flag = false;
              }
              try {
                if(this.judgeSame(data, index, this.requestParamsList, '') === 'same') {
                  throw 'error'
                }
              }catch (err){
                $(".jqueryValidateSame").eq(index).show();
                flag = false;
              }
              if(data.paramsType ==2 || data.paramsType ==3 || data.paramsType ==4 ||data.paramsType ==5){
                if(data.paramsDefaultValue ===''){
                  $(".IntegerNum").eq(index).hide();
                }else if(regExp2.test(data.paramsDefaultValue) == false || reg.test(data.paramsDefaultValue)){
                  $(".IntegerNum").eq(index).show();
                  flag = false;
                }else{
                  $(".IntegerNum").eq(index).hide();
                }
              }
            });
            if(this.apiList.backendProtocolType ==3){
              if(regBank.test(this.apiList.backEndAddress) == false){
                flag = false;
              }
            }
            if (flag) {
              axios({
                method: "post",
                url: "/api/update",
                data: {
                  apiInfo: this.apiList,
                  backendList: this.backendList,
                  requestParamsList: this.requestParamsList,
                  resultSettingsList: this.resultSettingsList
                }
              }).then(res => {
                if (res.data.code !== 2000) {
                  this.$message.error(res.data.message);
                } else {
                  this.$message({
                    type: "success",
                    message: "修改成功"
                  });
                  if(this.$route.query.num){
                    var name,value;
                    var str=location.href; //取得整个地址栏
                    var num=str.indexOf("?")
                    str=str.substr(num+1);
                    var arr=str.split("&"); //各个参数放到数组里
                    arr[0] = 'amend=2';
                    location.hash = '#/portManage/portApiManage?'+arr[0]+'&id='+this.$route.query.id+'&apiGroupId='+this.$route.query.apiGroupId+'&productEnvVersion=' + this.$route.query.productEnvVersion;
                    console.log(location.hash);
                    this.$router.back();
                  }else{
                    var name,value;
                    var str=location.href; //取得整个地址栏
                    var num=str.indexOf("?")
                    str=str.substr(num+1);
                    var arr=str.split("&"); //各个参数放到数组里
                    arr[0] = 'amend=2';
                    location.hash = '#/portManage/portApiManage?'+arr[0]+'&id='+this.$route.query.id+'&apiGroupId='+this.$route.query.apiGroupId+'&productEnvVersion=' + this.$route.query.productEnvVersion;
                    console.log(location.hash);
                    $(".el-form-item__error").css({"color":"#fff"})
                    location.reload();
                    _this.numData = [];
                    this.apidata();
                    this.firstDialog = false;
                    this.secondDialog = false;
                    this.thirdDialog = false;
                    this.fourDialog = false;
                  }
                }
              }).catch(error => {
                console.log("参数错误");
              });
            }
          }
        }
      },

      saveData(formName) {
        let flag = true;
        if (flag) {
          this.$refs[formName].validate(valid => {
            if (valid) {
              this.fourDialog = false;
            } else {
              return false;
            }
          });
        }
      },
      //====================================      修改API    end    ===================================
      amendApiMore(index) {
        this.amendApiIndexEdit = index;
        this.amendApi = true;
      },

      //验证
      submitForm1(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.secondDialog = true;
            this.firstDialog = false;
            this.httpMethodChange();
            this.httpMethodChange2();
          } else {
            return false;
          }
        });
      },

      submitForm3(formName) {
        let flag = true;
        let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
        let reg = /\s+/;
        this.backEndParam.map((data, index) => {
          if ( data.paramName === "" || regExp.test(data.paramName) == false || reg.test(data.paramName)
          ) {
            $(".jqueryValidate2").eq(index).show();
            $(".jqueryValidate8").eq(index).hide();
            flag = false;
          }
//          try {
//            if(this.judgeSame(data, index, this.backEndParam, '2') === 'same') {
//              throw 'error'
//            }
//          }catch (err){
//            $(".jqueryValidateSame2").eq(index).show();
//            flag = false;
//          }
        });

        this.numData.map((data, index) => {
          if (
            data.paramName === "" ||
            regExp.test(data.paramName) == false ||
            reg.test(data.paramName)
          ) {
            $(".jqueryValidate4").eq(index).show();
            $(".jqueryValidate7").eq(index).hide();
            flag = false;
          } else if (data.paramValue === "" || reg.test(data.paramValue)) {
            $(".jqueryValidate5").eq(index).show();
            flag = false;
          } else {
//            try {
//              if(this.judgeSame(data, index, this.numData, '3') === 'same') {
//                throw 'error'
//              }
//            }catch (err){
//              $(".jqueryValidateSame3").eq(index).show();
//              flag = false;
//            }
          }
        });

        if (flag) {
          this.$refs[formName].validate(valid => {
            if (valid) {
              this.fourDialog = true;
              this.thirdDialog = false;
              $(".commonVaL").show();
            } else {
              console.log("error submit!!");
              return false;
            }
          });
        }
      },
      addTable() {
        this.requestParamsList.push({
          maxLength: "50",
          minLength: "1",
          paramName: "",
          paramsLocation: 1,
          paramsType: 1,
          paramsMust: 1,
          paramsDefaultValue: "",
          paramsExample: "",
          paramsDescription: "",
          regularExpress: "" //参数验证
        });
        this.backEndParam.push({
          paramName: "",
          paramValue: "",
          paramsLocation: 1,
          paramDescription: "",
          paramsType: 1
        });
      },
      delTable(index) {
        this.requestParamsList.splice(index, 1);
        this.backEndParam.splice(index, 1);
        this.amendApiIndexEdit = 0;
      },
      addNumData() {
        this.numData.push({
          paramName: "",
          paramValue: "",
          paramsLocation: 1,
          paramDescription: "",
          paramsType: 2
        });
      },
      delNumData(index) {
        this.numData.splice(index, 1);
      },

      goThirdDialog(formName) {
        let flag = true;
        let regExp = /^[^\u4e00-\u9fa5]{1,200}$/;
        let regExp2 = /^[0-9]+([.]{1,200}[0-9]+){0,1}$/;
        let reg = /\s+/;
        this.requestParamsList.map((data, index) => {
          if (regExp.test(data.paramName) == false || reg.test(data.paramName)){
            $(".jqueryValidate").eq(index).show();
            flag = false;
          }
          try {
            if(this.judgeSame(data, index, this.requestParamsList, '') === 'same') {
              throw 'error'
            }
          }catch (err){
            $(".jqueryValidateSame").eq(index).show();
            flag = false;
          }
          if(data.paramsType ==2 || data.paramsType ==3 || data.paramsType ==4 ||data.paramsType ==5 ){
            if(data.paramsDefaultValue ===''){
              $(".IntegerNum").eq(index).hide();
            }else if(regExp2.test(data.paramsDefaultValue) == false || reg.test(data.paramsDefaultValue)){
              $(".IntegerNum").eq(index).show();
              flag = false;
            }else{
              $(".IntegerNum").eq(index).hide();
            }
          }
        });

        if (flag) {
          let len = this.requestParamsList.length;
          let len2 = this.backEndParam.length;
          this.$refs[formName].validate(valid => {
            if (valid) {
              this.secondDialog = false;
              this.thirdDialog = true;
              if (len != len2) {
                for (let i = 0; i < len - len2; i++) {
                  this.backEndParam.push({
                    paramName: "",
                    paramValue: "",
                    paramsLocation: "",
                    paramDescription: "",
                    paramsType: 1
                  });
                }
              }
            } else {
              console.log("error submit!!");
              return false;
            }
          });
        }
      },
      //必填
      paramsMusts(value, index) {
        if (this.requestParamsList[index].paramsMust == 2) {
          $(".IntegerNum").eq(index).hide();
          this.requestParamsList[index].paramsDefaultValue = "";
          this.requestParamsList[index].paramsMust = 1;
        } else this.requestParamsList[index].paramsMust = 2;
      },

      loadGroups() {
        this.groupOptions = [];
        axios({
          method: "post",
          url: "/apiGroup/listOfNoLowerLevelGroup",
          data: {}
        }).then(res => {
          res.data.data.map(data => {
            this.groupOptions.push({ label: data.groupName, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },

      judgeSame(obj, $index, ary, num) {
        let str = ''
        ary.forEach((item, index) => {
          try {
            if ($index !== index && obj.paramName === item.paramName) {
              throw ('same')
            } else {
//              $(".jqueryValidateSame" + num).eq(index).hide();
            }
          } catch (err) {
            str = err
          }
        })
        return str
      },

      activeChangeHandle(str) {
        this.activeName = str
      }
    }
  };
</script>
