<template>
  <div>
    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">名称及描述</span>
        <table v-model="apiDefineDetail" class="table table-hover table-bordered">
          <tbody style="margin-bottom:10px;position:relative">
            <p style="position:absolute;top:20px;left:115px">
              <b>（版本：{{detialVersionFlag}}</b>）</p>
            <tr>
              <td>分组：
                <span>{{apiDefineDetail.groupName}}</span>
              </td>
              <td>名称：
                <span>{{apiDefineDetail.apiName}}</span>
              </td>
            </tr>

            <tr>
              <td>是否存储：
                <span>{{apiDefineDetail.saveMongoDB ==1 ?'是':'否'}}</span>
              </td>
              <td>是否发送MQ：
                <span>{{apiDefineDetail.saveMQ ==1 ?'是':'否'}}</span>
              </td>
            </tr>

            <tr>
              <td>Mongodb地址：
                <span>{{apiDefineDetail.mongodbURI}}</span>
              </td>
              <td>Mongodb库名:
                <span>{{apiDefineDetail.mongodbDBName}}</span>
              </td>
            </tr>

            <tr>
              <td>Mongodb表名：
                <span>{{apiDefineDetail.mongodbCollectionName}}</span>
              </td>
              <td>描述：
                <span>{{apiDefineDetail.apiDescription}}</span>
              </td>
            </tr>

            <tr>
              <td>MQ类型：
                <span>{{apiDefineDetail.mqType ==1 ?'rabbbitmq':'kafka'}}</span>
              </td>
              <td>MQ地址：
                <span>{{apiDefineDetail.mqAddress}}</span>
              </td>
            </tr>

            <tr>
              <td>用户名：
                <span>{{apiDefineDetail.mqUserName}}</span>
              </td>
              <td>密码：
                <span>{{apiDefineDetail.mqPasswd}}</span>
              </td>
            </tr>

            <tr>
              <td>队列名称：
                <span>{{apiDefineDetail.mqTopicName}}</span>
              </td>
              <td>
                <span></span>
              </td>
            </tr>

          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">后端服务信息</span>
        <table v-model="apiDefineDetail" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td colspan="2">A中心地址：
                <span>{{apiDefineDetail.backEndAddress}}</span>
              </td>
            </tr>
            <tr>
                <td colspan="2">B中心地址：
                  <span>{{apiDefineDetail.backEndAddressB}}</span>
                </td>
              </tr>

            <tr>
              <td>Mock：
                <span>{{apiDefineDetail.isMock == 1?'是':'否'}}</span>
              </td>
              <td>后端超时：
                <span>{{apiDefineDetail.backEndTimeout}}</span> ms
              </td>
            </tr>

            <tr>
              <td colspan="2">Mock返回结果：
                <span>{{apiDefineDetail.mockData}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">请求基础定义</span>
        <table v-model="apiDefineDetail" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>Path：
                <span>{{apiDefineDetail.httpPath}}</span>
              </td>
              <td>协议：
                <span>HTTP</span>
              </td>
            </tr>

            <tr>
              <td colspan="2">HTTP Method：
                <span>{{apiDefineDetail.httpMethod == 1?'GET':'POST'}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default" style="position:relative;">
      <div class="console-panel-header">
        <span class="panel-header">请求入参信息</span>
        <table class="table table-hover table-bordered">
          <thead>
            <tr>
              <th>参数名</th>
              <th>类型</th>
              <th>必填</th>
              <th>默认值</th>
              <th>示例</th>
              <th>描述</th>
              <th>后端参数名称</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody v-if="requestParamsVersionsList.length <= 0">
            <tr>
              <td colspan="8" style="text-align:center">
                <span>暂无数据</span>
              </td>
            </tr>
          </tbody>
          <tbody v-if="requestParamsVersionsList.length > 0">
            <tr v-for="(item,index) in requestParamsVersionsList">
              <td>{{item.paramName}}</td>
              <td>
                <span v-if="item.paramsType == 1">String</span>
                <span v-else-if="item.paramsType == 2">Integer</span>
                <span v-else-if="item.paramsType == 3">Long</span>
                <span v-else-if="item.paramsType == 4">Float</span>
                <span v-else-if="item.paramsType == 5">Double</span>
                <span v-else-if="item.paramsType == 6">Boolean</span>
              </td>
              <td>
                <span>{{item.paramsMust == 1 ?'是':'否'}}</span>
              </td>
              <td>{{item.paramsDefaultValue}}</td>
              <td>{{item.paramsExample}}</td>
              <td>{{item.paramsDescription}}</td>
              <td>{{item.paramName}}</td>
              <td>
                <a href="javascript:void(0)" style="padding:0 8px;color:#1C72EE" class="radiusBtn" @mouseover="moreStart(index)" @mouseleave="moreEnd(index)">更多</a>
                <el-form v-show="moreMes" style="width:400px;" class="moreMesStyle">
                  <el-form-item label="最大长度" :label-width="formLabelWidth">
                    <span>{{requestParamsVersionsList[amendApiIndex2].maxLength}}</span>
                  </el-form-item>

                  <el-form-item label="最小长度" :label-width="formLabelWidth">
                    <span>{{requestParamsVersionsList[amendApiIndex2].minLength}}</span>
                  </el-form-item>

                  <el-form-item label="参数验证" :label-width="formLabelWidth">
                    <span>{{requestParamsVersionsList[amendApiIndex2].regularExpress}}</span>
                  </el-form-item>
                </el-form>

              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">后端服务参数</span>
        <table class="table table-hover table-bordered">
          <thead>
            <tr>
              <th>后端参数名称</th>
              <th>后端参数位置</th>
              <th>对应入参名称</th>
              <th>对应入参位置</th>
              <th>对应入参类型</th>
            </tr>
          </thead>
          <tbody v-if="backEndParam2.length <= 0">
            <tr>
              <td colspan="5" style="text-align:center">
                <span>暂无数据</span>
              </td>
            </tr>
          </tbody>
          <tbody v-if="backEndParam2.length > 0">
            <tr v-for="(item,index) in backEndParam2">
              <td>{{item.paramName}}</td>
              <td>
                <span v-if="item.paramsType == 1">header</span>
                <span v-else-if="item.paramsType == 2">query</span>
                <span v-else-if="item.paramsType == 3">body</span>
              </td>
              <td>
                <span>{{requestParamsVersionsList[index].paramName}}</span>
              </td>
              <td>
                <span v-if="requestParamsVersionsList[index].paramsLocation == 1">header</span>
                <span v-else-if="requestParamsVersionsList[index].paramsLocation == 2">query</span>
                <span v-else-if="requestParamsVersionsList[index].paramsLocation == 3">body</span>
              </td>
              <td>
                <span v-if="requestParamsVersionsList[index].paramsType == 1">String</span>
                <span v-else-if="requestParamsVersionsList[index].paramsType == 2">Integer</span>
                <span v-else-if="requestParamsVersionsList[index].paramsType == 3">Long</span>
                <span v-else-if="requestParamsVersionsList[index].paramsType == 4">Float</span>
                <span v-else-if="requestParamsVersionsList[index].paramsType == 5">Double</span>
                <span v-else-if="requestParamsVersionsList[index].paramsType == 6">Boolean</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">常量参数</span>
        <table class="table table-hover table-bordered">
          <thead>
            <tr>
              <th>后端参数名称</th>
              <th>参数值</th>
              <th>参数位置</th>
              <th>描述</th>
            </tr>
          </thead>
          <tbody v-if="numData2.length <= 0">
            <tr>
              <td colspan="4" style="text-align:center">
                <span>暂无数据</span>
              </td>
            </tr>
          </tbody>
          <tbody v-if="numData2.length > 0">
            <tr v-for="item in numData2">
              <td>
                <span>{{item.paramName}}</span>
              </td>
              <td>
                <span>{{item.paramValue}}</span>
              </td>
              <td>
                <span>{{item.paramsLocation == 1?'header':(item.paramsLocation == 2?'query':'body')}}</span>
              </td>
              <td>
                <span>{{item.paramDescription}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">返回结果</span>
        <table v-model="groupHerfId" class="table table-hover table-bordered">
          <tbody>
          <tr>
            <td colspan="2" >返回类型：
              <span>{{groupHerfId.callBackType ==1?'JSON':''}}</span>
            </td>
          </tr>
            <tr>
              <td style="float:left;width:50%;height:200px;">返回结果示例：
                <span class="successWritePlaceDialog" style="width:313px;height:160px;display:block;overflow:auto;">{{groupHerfId.callBackSuccessExample}}</span>
              </td>
              <td style="float:right;width:50%;height:200px;">失败返回结果示例：
                <span class="errorWritePlaceDialog" style="width:313px;height:160px;display:block;overflow:auto;">{{groupHerfId.callBackFailExample}}</span>
              </td>
            </tr>

          </tbody>
        </table>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="$emit('closeLook', '')">关 闭</el-button>
    </div>
  </div>
</template>

<script>
import { formatJson } from '../../assets/json'
export default {
  name: 'queryApiDetail',
  props: ["apiDefineDetail", "requestParamsVersionsList", "backEndParam2", "detialVersionFlag", "numData2", "groupHerfId"],

  data() {

    return {
      formLabelWidth: '125px',
      amendApiIndex2: 0,
      moreMes: false,
    }
  },
  created() {
  },
  methods: {
    moreStart(index) {
      this.moreMes = true;
      this.amendApiIndex2 = index;
    },
    moreEnd(index) {
      this.moreMes = false;
      this.amendApiIndex2 = index;
    },

  }
}
</script>


<style scoped>
/* 编辑更多*/

.moreMesStyle {
  position: absolute;
  background: #fff;
  right: 130px;
  border: 1px solid #ddd;
  top: -160px;
  z-index: 999;
}
pre{
  height:160px!important;
}
</style>



