<template>
  <div class="page-header">
    <div class="page-header-content">
      <div>
        <div class="page-header-content-box">
          <span class="title"><i class="el-icon-search"></i>筛选条件</span>
          <div class="page-header-content-right f-r">
            <slot name="searchContent"></slot>
          </div>
        </div>
      </div>
    </div>
    <div class="page-header-body" :style="{'height': '48px'}">
      <slot name="searchBody"></slot>
    </div>
  </div>
</template>
<script>
    export default {
        data() {
            return {}
        },
        components: {},
        mounted() {
        },
        methods: {}
    }
</script>
<style>
  .page-header{
    width: 100%;
    margin: 0;
    background: #f8f8f8;
    border-bottom: 1px solid #DCDFE6;
  }
  .page-header .page-header-content{
    height: 30px;
    line-height: 30px;
    padding: 2px 15px;
    background: #f2f2f2;
  }
  .page-header .page-header-content-box{
    text-align: left;
  }
  .page-header .page-header-content-box .title i {
    margin-right: 5px;
  }
  .page-header .page-header-body{
    padding: 10px 15px;
    text-align: left;
    box-sizing: border-box;
    transition: all .5s ;
    -webkit-transition: all .5s ;
    overflow: hidden;
  }
  .page-header .page-header-body .search-body-item{
    margin: 0 0 5px 5px;
    display: inline-block;
    font-size: 12px;
    color: #606266;
  }
  .page-header .f-r{
    float: right;
  }
</style>



