<template>
  <div class="TestApi" style="background:#ffffff;height:100%;">
    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="instanceId" label="instanceId"></el-table-column>
      <el-table-column prop="hostName" label="hostName"></el-table-column>
      <el-table-column prop="appName" label="appName"></el-table-column>
      <el-table-column prop="status" label="status" align="center" header-align="center">
        <template slot-scope="scope">
          <span class="status_span" :style="{background:scope.row.status}"></span>
        </template>
      </el-table-column>
      <el-table-column prop="healthCheckUrl" label="healthCheckUrl" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text"  v-if="scope.row.status === 'gray'" size="small" @click="joinHealth(scope.row)">加入</el-button>
          <el-button type="text"  v-if="scope.row.status !== 'gray'" size="small" @click="outHealth(scope.row)">退出</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'

  export default {
    name: 'healthCheck',
    components: {

    },
    data() {
      return {
        jurisdiction:{},
        tablegroup: [],
        listLoading: false
      }
    },

    created() {
      this.getUserPermissions();
      this.initData();
    },

    methods: {

      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //初始化与搜索框查询
      initData() {
        this.listLoading = true
        axios({
          method: 'post',
          url: '/health/getApps',
          data: {}
        }).then(res => {
          console.log(res)
          this.tablegroup = res.data.data;
          this.listLoading = false
        }).catch(error => {
          this.listLoading = false
          console.log('网络错误，不能访问');
        });
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },
      joinHealth(row) {
        axios({
          method: 'post',
          url: '/health/addHealthCheck',
          data: {instanceId: row.instanceId}
        }).then(res => {
          if (res.data.code === 2000) {
              this.initData()
            this.$message({
              type: 'success',
              message: '加入成功'
            });
          } else {
            this.$message({
              type: 'error',
              message: res.data.message
            });
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      outHealth(row) {
        axios({
          method: 'post',
          url: '/health/delHealthCheck',
          data: {instanceId: row.instanceId}
        }).then(res => {
          console.log(res)
          if (res.data.code === 2000) {
            this.initData()
            this.$message({
              type: 'success',
              message: '退出成功'
            });
          } else {
            this.$message({
              type: 'error',
              message: res.data.message
            });
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }
    }
  }
</script>

<style scoped>
.status_span{
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 16px;
}
.el-button:focus, .el-button:hover{
  color: #20a0ff;
}
</style>



