<template>
  <div class="portManage" style="background:#ffffff;position:relative;height:100%;">
    <div style="padding: 20px">
      <el-breadcrumb separator=">">
        <el-breadcrumb-item :to="{ path: '/combinationApi' }">
          API组合列表
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-button class="createApi_btn" type="primary addOne" v-if="createBtN" @click="addApi">创建API</el-button>
    </div>
    <el-table :data="tablegroup"  @filter-change="tableChange" :row-class-name="tableRowClassName">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="apiName" label="API名称" width="250px"></el-table-column>
      <el-table-column prop="httpPath" label="路径"></el-table-column>
      <el-table-column prop="weight" label="权重"></el-table-column>
      <el-table-column prop="score" label="分值"></el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <span>{{scope.row.status == 1 ? '正常' : scope.row.status == 3 ? '下线' : ''}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="最后修改" :formatter="dateFormat" width="170px"></el-table-column>
      <el-table-column prop="" label="操作" width="160">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn mr10 pubColor" v-if="amendBtn" size="small" @click="updataApiBtn(scope.row)">编辑</el-button>
          <el-button type="text" class="radiusBtn delColor" size="small" v-if="deleteBtn" @click="delApiBtn(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="titleTab" :visible.sync="creataut" size="tiny" @close=''>
      <el-form :model="addPortForm" :rules="addPortFormRules" ref="addPortForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="分组：" :label-width="formLabelWidth" prop='apiGroupId'>
          <el-select :disabled="titleTab =='编辑' ? true : false" v-model="addPortForm.apiGroupId" filterable placeholder="请选择分组" class="w9000" @visible-change="slideListGroup" @change="slideLabelGroup">
            <el-option v-for="item in groupNameOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item  prop='apiId' label="接口名称：" :label-width="formLabelWidth">
          <el-select :disabled="titleTab =='编辑' ? true : false" v-model="addPortForm.apiId" filterable placeholder="请选择接口名称" class="w9000" @visible-change="slideListPort" @change="slideLabel">
            <el-option v-for="item in portOptions" :key="item.id" :label="item.apiName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="路径：" :label-width="formLabelWidth" prop='httpPath'>
          <el-input :disabled="titleTab =='编辑' ? true : false" :readonly="true" class="w9000"   v-model="addPortForm.httpPath"></el-input>
        </el-form-item>
        <el-form-item label="权重：" :label-width="formLabelWidth" prop='weight'>
          <el-input v-model.number="addPortForm.weight" class="w9000" ></el-input>
        </el-form-item>
        <el-form-item label="分值：" :label-width="formLabelWidth" prop='score'>
          <el-input  v-model.number="addPortForm.score" class="w9000" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('addPortForm')">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addaut('addPortForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import axios from "axios";
  import moment from 'moment'
  export default {
    data() {
      const numberRange = (rule, value, callback) => {
        let regExp = /^([0-9]|10)$/;
          if (value === '') {
            callback(new Error('权重不能为空'));
          } else if (regExp.test(value) === false) {
            callback(new Error('权重为0-10的整数!'));
          } else {
            callback();
          }
        }
      return {
        jurisdiction: null,
        combinationId: '',
        tablegroup:[],
        amendBtn: false,
        deleteBtn: false,
        createBtN: false,
        creataut: false,
        titleTab: '',
        addPortForm:{},
        formLabelWidth: "120px",
        groupNameOptions: [],
        portOptions: [],
        addPortFormRules: {
          apiGroupId: [{ required:true,type: 'number', message: '路径不能为空', trigger: 'change'}],
          apiId: [{ required:true, type: 'number', message: '路径不能为空', trigger: 'change'}],
          httpPath: [{ required:true, trigger: 'blur', message: '路径不能为空',}],
          weight: [{ required:true, type: 'number', trigger: 'blur', message: '权值不能为空'}, { validator: numberRange, trigger: 'blur'}],
          score: [{ required:true, type: 'number', message: '分值不能为空', trigger: 'blur'}]
        },
        delApiData: {}, //删除API保存对象
      }
    },
    components: {},
    mounted() {
      if (this.$route.query.combinationId) {
        this.combinationId = this.$route.query.combinationId
        this.initData()
      }
    },
    created() {
      this.getUserPermissions();
    },
    methods: {
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      initData() {
        axios({
          method: 'post',
          url: '/combination/query',
          data: {
            combinationId: this.combinationId
          }
        }).then(res => {
          if(res.data.code == 2000){
            if(!this.jurisdiction.roles.includes('admin')){
              this.amendBtn = false
              this.deleteBtn = false
              this.createBtN = false
            }else{
              this.amendBtn = true
              this.deleteBtn = true
              this.createBtN = true
              this.tablegroup =  res.data.data;
            }
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },
      //筛选
      tableChange(filters) {
        if (filters.hasOwnProperty("runTimeEnv")) {
          this.env = filters["runTimeEnv"][0];
          this.initData()
        }
      },
      addApi() {
        this.creataut = true
        this.addPortForm = {}
        this.titleTab = '创建'
      },
      updataApiBtn(row) {
        this.creataut = true
        this.titleTab = '编辑'
        this.addPortForm = row
        this.slideListPort()
      },
      slideListGroup(){
        this.groupNameOptions = [];
        axios({
          method: "post",
          url: "/apiGroup/listOfNoLowerLevelGroup",
          data: {}
        }).then(res => {
          res.data.data.map(data => {
            this.groupNameOptions.push({ label: data.groupName, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      slideListPort(){
        this.portOptions = [];
        axios({
          method: "post",
          url: "/api/findApiByGroupId",
          data: {
            id: this.addPortForm.apiGroupId,
          }
        }).then(res => {
//          res.data.data.map(data => {
//            this.portOptions.push({ label: data.apiName, id: data.id });
//          });
          this.portOptions = res.data.data
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      slideLabelGroup(vid){
        if(this.groupNameOptions.length >0){
          let obj = {};
          obj = this.groupNameOptions.find((item) =>{
            return item.id === vid
          });
          if(obj !==undefined){
            this.addPortForm.groupName = obj.label;
            this.groupTabSlide = true;
            if(this.addPortForm.apiName){
              this.addPortForm.apiName = '';
            }
          }
        }
      },
      slideLabel(vid){
        if(this.portOptions.length > 0){
          var objPort = {};
          objPort = this.portOptions.find((item) =>{
            return item.id === vid;
          });

          if(objPort !== undefined){
            console.log(objPort)
            this.addPortForm.apiName = objPort.apiName;
            this.addPortForm.httpPath = objPort.httpPath
            this.portTab = true;
          }
        }
      },
      callOf(formName){
        this.creataut  = false;
        this.addPortForm = {};
        this.$refs[formName].resetFields();
//        this.initData();
      },
      addaut(formName){
        this.$refs[formName].validate(valid => {
          let apiName = this.portOptions.filter((item) => {
            return item.id === this.addPortForm.apiId
          })
          console.log(apiName)
          if (valid) {
            axios({
              method: 'post',
              url: this.titleTab == '创建'? '/combination/add':'/combination/update',
              data: {
                combinationId:this.combinationId,
                apiGroupId: this.addPortForm.apiGroupId,
                apiId: this.addPortForm.apiId,
                httpPath:this.addPortForm.httpPath,
                weight:this.addPortForm.weight,
                score:this.addPortForm.score,
                apiName:apiName[0].apiName,
                id:this.titleTab == '编辑' ? this.addPortForm.id : '',
              }
            }).then(res => {
              if(res.data.code == 2000){
                if(this.titleTab == '创建'){
                  this.$message({type: "success",message: '新建成功'});
                }else{
                  this.$message({type: "success",message: '编辑成功'});
                }
                this.creataut = false;
                this.initData();
                this.$refs[formName].resetFields();
              }else{
                this.$message({type: "info",message: res.data.message});
              }
            }).catch(error => {
              console.log('网络错误，不能访问');
              this.addPortForm = {};
            });
          }else {
            console.log("error submit!!");
            return false;
          }
        });
      },
      delApiBtn(index, row){
        this.delApiData = row;
        this.delApiData.index = index;

        this.$confirm(`是否删除API "${this.delApiData.apiName}",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.delApiMes(index);
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },
      delApiMes(index) {
        const delData = this.delApiData;
        axios({
          method: "post",
          url: "/combination/delete",
          data: {
            id: delData.id
          }
        })
          .then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: "error",
                message: res.data.message
              });
            } else {
              this.tablegroup.splice(index, 1);
              this.$message({
                type: "success",
                message: "删除API成功"
              });
            }
          }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //时间戳
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
    }
  }
</script>



