<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入IP进行查询" class="w350" v-model='searchParams.ipAddress'></el-input>
      </el-form-item>
      <el-form-item>
        <!--<el-input placeholder="请输入appKeyId进行查询" class="w350" v-model='appKeyId'></el-input>-->
        <el-select class="w350" filterable clearable v-model="searchParams.appKeyId" placeholder="请选择">
          <el-option
            v-for="item in appKeyAry"
            :key="item.id"
            :label="item.appKey"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="handleCurrentChange(1)">搜 索</el-button>
        <el-button type="primary addOne" class="searchOne" @click="openDialog()">新 建</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="" type="index" label="序号" width="80"></el-table-column>
      <el-table-column prop="appKey" label="appKey"></el-table-column>
      <el-table-column prop="ipAddress" label="IP"></el-table-column>
      <el-table-column prop="description" label="说明"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
      <el-table-column prop="updateTime" label="更新时间" :formatter="dateFormat"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="amendIp(scope.row)">编辑</el-button>
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="delIp(scope.row)">删除</el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="searchParams.currPage"
                     :page-sizes="[10, 20, 30, 40]" :page-size="searchParams.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 新建IP弹框 start -->
    <el-dialog :title="titleText"
               :visible.sync="creatIp"
               @close="closeDialog"
               size="tiny" style="top:-5%">
      <el-form :model="addGroupData" ref="addGroupDataRef" class="demo-ruleForm">
        <el-form-item label="IP地址：" prop="ipAddress" :label-width="formLabelWidth" :rules="[{required:true, message: 'IP地址不能为空'}]">
          <el-input class="w9000" v-model="addGroupData.ipAddress"></el-input>
        </el-form-item>
        <el-form-item label="appKey：" prop="appKeyId" :label-width="formLabelWidth" :rules="[{required:true, message: 'appKey不能为空'}]">
          <el-select class="w9000" filterable clearable v-model="appKeyId"
                     @change="appKeyChange" placeholder="请选择">
            <el-option
              v-for="item in appKeyAry"
              :key="item.id"
              :label="item.appKey"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="说明：" prop="description" :label-width="formLabelWidth">
          <el-input class="w9000" v-model="addGroupData.description"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="closeDialog">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addIp('addGroupDataRef')">确 定</el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'

  export default {
    name: 'findApi',
    components: {

    },
    data() {
      return {
        jurisdiction:{},
        addGroupData:{},
        amendObj:{},
        index:0,
        titleText:'',
        creatIp:false,
        total: 0,
        page: 1,
        searchParams: {
          ipAddress: '',
          currPage: 1,
          pageSize: 10
        },
        appKeyAry: [],
        listLoading: false,
        tablegroup: [],
        newPage:'',
        formLabelWidth: "120px",
        companyId:'',
        appKeyId: null,
        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      }
    },

    created() {
      // this.getUserPermissions();
      this.handleCurrentChange(1);
      this.getAppKeyAry()
    },

    methods: {
      getAppKeyAry() {
        axios({
          method: 'post',
          url: '/app/findApp',
          data: {}
        }).then(res => {
          if(res.data.code === 2000){
            this.appKeyAry = res.data.data
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },

      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //初始化
      initData() {
        axios({
          method: 'post',
          url: '/ipWhite/search',
          data: this.searchParams
        }).then(res => {
          this.tablegroup = res.data.data.list;
          this.currPage = res.data.data.currPage;
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.totalCount;
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //搜索框查询
//      searchData() {
//        axios({
//          method: 'post',
//          url: '/ipWhite/search',
//          data: {
//            ipAddress:this.searchIp,
//            appKeyId: this.appKeyId,
//            pageSize: this.newPage == '' ? 10 : this.newPage,
//            currPage: this.currPage
//          }
//        }).then(res => {
//          this.tablegroup = res.data.data.list;
//          this.currPage = res.data.data.currPage;
//          this.pageSize = res.data.data.pageSize;
//          this.total = res.data.data.totalCount;
//        }).catch(error => {
//          console.log('网络错误，不能访问');
//        });
//      },
      //新建
      addIp(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            axios({
              method: 'post',
              url: this.titleText == '新建IP' ? '/ipWhite/save' :'/ipWhite/edit',
              data: {
                description:this.addGroupData.description,
                ipAddress:this.addGroupData.ipAddress,
                appKeyId: this.appKeyId,
                id:this.amendObj.id
              }

            }).then(res => {
              if(res.data.code == 2000){
                this.closeDialog()
                this.handleCurrentChange(1);
                this.amendObj = {};
                this.addGroupData = {};
                this.titleText = '';
              }else{
                this.$message({type: "info",message: res.data.message});
              }
            }).catch(error => {
              console.log('网络错误，不能访问');
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      openDialog() {
        this.creatIp = true;
        this.titleText = '新建IP';
        this.amendObj = {};
        this.creatIp = true;
        this.addGroupData.ipAddress = '';
        this.appKeyId = null
        this.addGroupData.appkey = null
        this.addGroupData.description = '';
      },
      //编辑
      amendIp(row){
        this.amendObj = row;
        this.creatIp = true;
        this.titleText = '编辑IP';
        this.addGroupData.ipAddress = row.ipAddress;
        this.appKeyId = row.appKeyId
        this.addGroupData.appKeyId = row.appKeyId
        this.addGroupData.description = row.description;
      },
      appKeyChange(data) {
        this.$set(this, 'appKeyId', data)
        this.$set(this.addGroupData, 'appKeyId', data)
      },
      //删除
      delIp(row){
        this.delIpData = row;
        this.$confirm(`是否删除IP "${this.delIpData.ipAddress}",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.delIPList();
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },

      delIPList(){
        axios({
          method: 'post',
          url: '/ipWhite/delete',
          data: {
            id:this.delIpData.id
          }

        }).then(res => {
          if(res.data.code == 2000){
            this.$message({type: "success",message: '删除IP成功'});
            this.handleCurrentChange(1);
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },


      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      handleCurrentChange(val) {
        this.searchParams.currPage = val;
        this.initData();
      },
      handleSizeChange(val) {
        this.searchParams.pageSize = val;
        this.handleCurrentChange(1);
      },
      closeDialog() {
        this.$refs.addGroupDataRef.resetFields()
        this.creatIp = false
      }


    }

  }
</script>

<style scoped>

</style>



