<template>
  <div class="apiText" style="background:#fff;padding:8px;">

    <div class="w1660fr" v-if="this.$route.query.productEnvVersion !==null">
      <form :model="apiListData">
        <div class="apiTextNav">
          <span class="f20">URL</span>
          <p>{{apiListData.url}}</p>
        </div>

        <div class="apiTextNav">
          <span class="f20">HTTP请求方式</span>
          <p>{{apiListData.httpMethod ==1?'GET':'POST'}}</p>
        </div>

        <div class="apiTextNav">
          <span class="f20">支持格式</span>
          <p>JSON</p>
        </div>

        <div class="apiTextNav">
          <span class="f20">请求参数</span>
          <div class="panel panel-default" style="position:relative">
            <div class="console-panel-header">
              <table class="table table-hover table-bordered">
                <thead>
                  <tr>
                    <th></th>
                    <th>必填</th>
                    <th>类型</th>
                    <th>说明</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in requestParamsList">
                    <td>
                      <span>{{item.paramName}}</span>
                    </td>
                    <td>
                      <span>{{item.paramsMust==1?'是':'否'}}</span>
                    </td>
                    <td>
                      <span v-if="item.paramsType == 1">String</span>
                      <span v-else-if="item.paramsType == 2">Integer</span>
                      <span v-else-if="item.paramsType == 3">Long</span>
                      <span v-else-if="item.paramsType == 4">Float</span>
                      <span v-else-if="item.paramsType == 5">Double</span>
                      <span v-else-if="item.paramsType == 6">Boolean</span>
                    </td>
                    <td>
                      <span>{{item.paramsDescription}}</span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <div class="apiTextNav">
          <span class="f20">结果返回示例</span>
          <p class="successWritePlaceDialog">{{apiListData.callBackSuccessExample}}</p>
        </div>

        <!-- <div class="apiTextNav">
          <span class="f20">错误代码对照表</span>
          <div class="panel panel-default" style="position:relative">
            <div class="console-panel-header">
              <table class="table table-hover table-bordered">
                <thead>
                  <tr>
                    <th>错误码</th>
                    <th>错误信息</th>
                    <th>寻值</th>
                    <th>描述</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in resultSettingsList">
                    <td>
                      <span>{{item.errorCode}}</span>
                    </td>
                    <td>
                      <span>{{item.errorMsg}}</span>
                    </td>
                    <td>
                      <span>{{item.lookupInfo }}</span>
                    </td>
                    <td>
                      <span>{{item.errorDescription}}</span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <p>关于错误返回值与错误代码，参见
          <router-link :to="{path:'errorText',query:{id:this.$route.query.id,apiGroupId:this.$route.query.apiGroupId,productEnvVersion:this.$route.query.productEnvVersion}}">错误代码说明</router-link>
        </p> -->
      </form>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { formatJson } from '../../assets/json'
import menuListDetail from '@/components/common/menuListDetail'
export default {
  name: 'apiText',
  components: {
    menuListDetail
  },
  data() {
    return {
      apiListData: {},         //API文档
      requestParamsList: [],
      resultSettingsList: [],
      jurisdiction:{},
    }
  },
  created() {
    this.apiTextList();
    // this.getUserPermissions();
  },
  methods: {
    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
      }).catch(error => {
        console.log("网络错误");
      });
    },
    apiTextList() {
      let versionId = this.$route.query.productEnvVersion;
      let apiGroupId = this.$route.query.apiGroupId;
      axios({
        method: 'post',
        url: '/api/findApiInfoVersion',
        data: {
          versionId: versionId
        }
      }).then(res => {
        for(var i = 0; i<this.jurisdiction.perms.length; i++){
          if(this.jurisdiction.perms[i] == '4dfce3d848aa4495845d77372872a8e2'){
            this.apiListData = res.data.data.apiInfoVersion;
            this.requestParamsList = res.data.data.requestParamsVersionsList;
            this.resultSettingsList = res.data.data.resultSettingsVersionsList;
            var resultJson = formatJson(this.apiListData.callBackSuccessExample);
            $(".successWritePlaceDialog").html('<pre>' + resultJson + '<pre/>');
          }
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    }
  }
}
</script>

<style scoped>
.apiTextNav {
  width: 100%;
  margin-bottom: 40px;
}

.apiTextNav p {
  border-top: 1px solid #e5e5e5;
  padding-top: 5px;
  margin-top: 5px;
}
</style>



