import * as types from '../mutation-types'
const headerHeight = 60
const state = {
  columnItems: [],
  leftColumnItems: [],
  activeColumn: {},
  breadcrumb: [],
  bodyHeight: 0,
  bodyWidth: 0,
  isCollapse: false
}

// getters
const getters = {
  bodyHeight: (state) => state.bodyHeight,
  bodyWidth: (state) => state.bodyWidth,
  isCollapse: (state) => state.isCollapse,
  commonCacheData: state => (cacheName) => {
    return state[cacheName]
  }
}

// mutations
const mutations = {
    [types.WINDOW_XY_CHANGE](state)  {
    // const hide = store.getters['permission/isCollapse']
    const widthWh = document.getElementsByTagName('html')[0].offsetWidth
    state.bodyHeight = document.getElementsByTagName('html')[0].clientHeight - headerHeight
    state.bodyWidth = state.isCollapse ? widthWh - 65 : widthWh - 180
    state.bodyWidth = state.bodyWidth < 768 ? 768 : state.bodyWidth
  },
  [types.COMMON_CACHE_DATA_UPDATE](state, cache) {
    state[cache.name] = cache.value
  },
  [types.SET_COLLAPSE]: (state, value) => {
    state.isCollapse = value
  },
}

export default {
  state,
  getters,
  mutations
}



