<template>
  <div class="topNav">
    <div class="rule_engine_nav">
      <div class="lemon_logo_box">
        <div class="lemon_logo"></div>
      </div>
      <div class="header-item">
        <template>
          <el-menu
            :default-active="activeName"
            class="menu-header"
            mode="horizontal"
            @select="handleSelect">
            <el-menu-item  v-for="item in columnItems" :index="item.id + ''" :key="item.id">
              <i v-if="item.iconCode" :class="item.iconCode"></i>
              {{item.name}}
            </el-menu-item>
          </el-menu>
        </template>
      </div>
      <div class="nav_box">
        <ul>
          <li class="loginImg" @mouseover="loginTextState=true" @mouseout="loginTextState=false">你好，<span>{{logoName}}</span>&nbsp;
          <!-- <div class="userMag dinb" style="width:104px;height:60px">
            <i class="iconfont icon-ai-edit commonIcon"></i>
            <span class=""><a :href='overUrl' style="color:#fff">修改密码</a></span>
          </div> -->
          <div class="userMag dinb" @click="loginOut" style="width:80px;color:#fff;height:60px">
            <!-- <i class="iconfont icon-tuichu1 commonIcon"></i> -->
            <img src="../../assets/logout.png" alt="">
            <span>退出</span>
          </div>
          </li>
        </ul>
      </div>
    </div>

    <el-dialog title="修改密码" size="tiny" :visible.sync="amendPassword" @close='closeDia'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="原始密码" :label-width="formLabelWidth" prop='oldPassword'>
          <el-input v-model="ruleForm.oldPassword" type='password' class="w9000"></el-input>
        </el-form-item>
        <el-form-item label="新密码" :label-width="formLabelWidth" prop='newPassword'>
          <el-input v-model="ruleForm.newPassword" type='password' class="w9000"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" :label-width="formLabelWidth" prop='confirmNewPassword'>
          <el-input v-model="ruleForm.confirmNewPassword" type='password' class="w9000"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendPassword = false">取 消</el-button>
        <el-button type="primary" @click="modifyPassword('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import axios from 'axios'
import * as types from '../../store/mutation-types'

export default {
  name: 'topNav',
  data() {
    //------------------检查原始密码是否正确------------------//
    let ValidPass = (rule, value, callback) => {
      axios({
        method: 'post',
        url: '/checkPass',
        data: {
          id: this.userInfo.id,
          oldPassword: value
        }
      }).then(res => {
        if (res.data.data != true) {
          callback(new Error('原始密码错误'));
        } else {
          callback();
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    };
    //------------------确认新密码是否相等------------------//
    let confirmPass = (rule, value, callback) => {
      if (value != this.ruleForm.newPassword) {
        callback(new Error('两次输入不一致'));
      } else {
        callback();
      }
    };
    //------------------必须含有字母和数字和特殊字符的正则------------------//
    let valiPWD = (rule, value, callback) => {
      let regExp = /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$%^&*]+$)(?![a-zA-z\d]+$)(?![a-zA-z!@#$%^&*]+$)(?![\d!@#$%^&*]+$)[a-zA-Z\d!@#$%^&*]+$/;
      if (regExp.test(value) === false) {
        callback(new Error('密码必须包含字母,数字,特殊符号'));
      } else {
        callback();
      }
    };
    return {
      overUrl:'',
      jurisdiction:{},
      logoName:'admin',
      outUrl:'',
      loginTextState: false,
      amendPassword: false,            //修改密码
      activeName: 'second',
      dialogTableVisible: false,
      dialogFormVisible: false,
      userInfo: '',
      formLabelWidth: '120px',

      ruleForm: {
        oldPassword: '',
        newPassword: '',
        confirmNewPassword: ''
      },

      rules: {
        oldPassword: [
          { required: true, message: '请输入原始密码', trigger: 'blur' },
          { validator: ValidPass, trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, max: 14, message: '长度在 6 到 14 个字符', trigger: 'blur' },
          { validator: valiPWD, trigger: 'blur' }
        ],
        confirmNewPassword: [
          { required: true, message: '请确认新密码', trigger: 'blur' },
          { validator: confirmPass, trigger: 'blur' }
        ]
      }


    }
  },
  computed: {
    columnItems() {
      return this.$store.getters.commonCacheData('columnItems')
    },
    activeColumn() {
      let column = this.$store.getters.commonCacheData('activeColumn')
      console.log(column)
      if (column && column.beginNode) {
        this.activeName = column.beginNode.id + ''
      } else {
        this.activeName = null;
      }
      this.setLeftMenu(this.activeName)
      if (!column) column = {}
      return column
    },
  },
  created() {
    // this.createds();
    this.getUserPermissions();
  },
  methods: {
    defaultSelect(column, callback) {
      if (column.children && column.children.length > 0) {
        for (const value of column.children) {
          this.defaultSelect(value, callback)
        }
      }
    },
    setLeftSelect(column) {
      if (column.children && column.children.length > 0) {
        return this.setLeftSelect(column.children[0])
      } else {
        return column
      }
    },
    handleSelect(name) {
      if (!name) return
      for (let i = 0; i < this.columnItems.length; i++) {
        const value = this.columnItems[i];
        if (value.id + '' === name) {
          this.$store.commit(types.COMMON_CACHE_DATA_UPDATE, {
            name: "leftColumnItems",
            value: value.children
          })
          //设置默认选中 如果默认选中 未设置，设置第一个节点为默认选中
          let activeNode;
          if (value.children && value.children.length > 0) {
            for (let n = 0; n < value.children.length; n++) {
              this.defaultSelect(value.children[n], function (column) {
                activeNode = column;
              })
            }
            if (!activeNode) { //未设置默认
              activeNode = this.setLeftSelect(value.children[0])
//                activeNode = value.children[0];
//                if (activeNode.children && activeNode.children.length > 0) {
//                  activeNode = activeNode.children[0]
//                  if (activeNode.children && activeNode.children.length > 0) {
//                    activeNode = activeNode.children[0]
//                  }
//                  console.log(activeNode)
//                }
            }
//              //路由跳转
            this.$router.push(activeNode.urls);
          }
          break;
        }
      }
    },
    setLeftMenu(name) {
      if (!name) return;
      for (let i = 0; i < this.columnItems.length; i++) {
        const value = this.columnItems[i];
        if (value.id === name) {
          this.$store.commit(types.COMMON_CACHE_DATA_UPDATE, {
            name: "leftColumnItems",
            value: value.children
          })
          break;
        }
      }
    },
    createds() {
      axios({
        method: 'post',
        url: '/getUserInfo',
        data: {}
      }).then(res => {
        this.userInfo = res.data;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //退出弹框
    loginOut(){
      this.$confirm('确定退出接口管理平台吗？', '提示', {
        confirmButtonText: '退出',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        location.href = this.jurisdiction.logoutUrl;
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出！'
        });
      });
    },
    //权限设置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        this.outUrl = this.jurisdiction.logoutUrl;
        this.logoName = res.data.principal.name;
        this.overUrl = 'http://172.16.16.70:8180/index.html#/resetUserPwdForm?name='+this.logoName;
        const column = this.activeColumn
        if (column && column.beginNode) {
          this.activeName = column.beginNode.id + ''
          this.setLeftMenu(column.beginNode.id)
        } else {
          this.activeName = '';
        }
      }).catch(error => {
        console.log("错误");
      });
    },

    modifyPassword(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/modifyPassword',
            data: {
              id: this.userInfo.id,
              oldPassword: this.ruleForm.oldPassword,
              newPassword: this.ruleForm.newPassword,
              confirmNewPassword: this.ruleForm.confirmNewPassword
            }
          }).then(res => {
            this.amendPassword = false;
            if (res.data.data == true) {
              this.$message({
                message: '修改成功，3秒后请重新登陆',
                type: 'success'
              });
            }
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    closeDia() {
      this.ruleForm.oldPassword = '',
        this.ruleForm.newPassword = '',
        this.ruleForm.confirmNewPassword = ''
    }
  }
}
</script>


<style scoped>
/* .createLabel {
  text-align: right;
}

.w350 {
  width: 350px;
}

.header {
  background: #005ED9 url(../../assets/logo2.png)no-repeat;
  width: 100%;
  height: 70px;
  color: #000;
  line-height: 50px;
  position: fixed;
  z-index: 999;
}

.loginName{
  color:#fff;height:70px;line-height:70px;margin-right:40px;
}
.loginName img{
  margin-left:20px;
}*/
.loginText {
  position: absolute;
  width: 200px;
  height: 100px;
  text-align: center;
  right: 5px;
  z-index: 9999;
  background: #fff;
  box-shadow: 0 0 15px #ddd;
  line-height: 50px;
  color: #000;cursor: pointer;border:1px solid #ddd ;
  margin-top: 60px;
}

.loginText ul li {
  width: 100%;
  height: 50px;
  color: #444;
  transition: all 0.3s ease-out;
}

.loginText ul li:hover {
  color: #1C72EE;
  box-shadow: 0 0 15px #1C72EE;
}
.rule_engine_nav{
  height: 60px;
  width: 100%;
  color: white;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  background: #393D40;
  display: flex;
}
.lemon_logo_box{
  /*width: 324px;*/
  height: 60px;
  flex: 0 0 200px;
  color: white;
}
.header-item{
  flex: 1;
}
.menu-header{
  background: #393D40;
  color: white;
}
.el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item.is-active, .el-menu-item.is-active{
  /*color: #FEBD49 !important;*/
  border-bottom: 2px solid #febd49 !important;
}
.el-menu--horizontal>.el-menu-item:hover, .el-menu--horizontal>.el-submenu.is-active .el-submenu__title, .el-menu--horizontal>.el-submenu:hover .el-submenu__title{
  background: #323232;
  color: #fff;
}
.el-menu--horizontal .el-menu-item{
  color: white;
  border-bottom: none;
}
.lemon_logo{
  width: 200px;
  height: 60px;
  background: url('../../assets/logo.png') no-repeat;
  background-position:left center;
}
.nav_box{
  flex: 0 0 200px;
  text-align: right;
  height: 60px;
}
.nav_box ul{
  height: 60px;
  line-height: 60px;
}
.nav_box ul li:not(:last-child){
  list-style-type: none;
  width: 10%;
  min-width: 80px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  padding: 0 15px;
  float: left;
}
.nav_active{
  background: #0c53b5;
}
.nav_box ul li:not(:last-child):hover{
  cursor: pointer;
  background: #0560d2;
}
@media screen and (max-width:1299px){
  .nav_box ul li:not(:last-child){
    font-size: 12px;
  }
}
@media screen and (min-width:1300px) and (max-width:1679px){
  .nav_box ul li:not(:last-child){
    font-size: 14px;
  }
}
@media screen and (min-width:1680px){
  .nav_box ul li:not(:last-child){
    font-size: 16px;
  }
}
.nav_box ul li:last-child{
  /* width: 18%; */
  float: right;
  text-align: right;
  /* padding-right: 25px; */
  font-size: 12px;
  color: hsla(0,0%,100%,.7);
}
.nav_box ul li:last-child a{
  color: white;
}
.nav_box ul li:last-child a:hover{
  color: #2d8cf0;
}
.nav_box ul li:last-child a i{
  position: relative;
  top: 2px;
}
.loginImg{
  cursor: pointer;
}
.navA{
  color:#fff;
}
.userMag{
  text-align: center;
}
.userMag img{
  width: 12px;
}
.userMag:hover{
  background: #FEBD49;
}
.commonIcon{
  color: #fff;
  font-size: 12px;
}
</style>



