<template>
  <div class="authorityMsg" style="background:#ffffff;position:relative;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入appKey进行查询" class="w250" v-model="searchAppKey"></el-input>
      </el-form-item>
      <el-select v-model="portAlt" clearable placeholder="请选择是否源接口">
          <el-option v-for="item in portAltOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
      <el-form-item>
        <el-input placeholder="请输入分组名称进行查询" class="w250" v-model="searchGroupName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="请输入接口名称进行查询" class="w250" v-model="port"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" v-if="searchBtn" class="searchOne" @click="initData">搜 索</el-button>
        <el-button type="primary addOne"class="fr" v-if="addBtn" style="margin-right:100px;" @click="addName">创建</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" :row-class-name="tableRowClassName">
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="appKey" label="appKey"></el-table-column>
        <el-table-column prop="apiGroupName" label="分组名称"></el-table-column>
        <el-table-column label="是否源接口">
          <template slot-scope="scope">
            <span v-if="scope.row.sourceFlag == 0">是</span>
            <span v-if="scope.row.sourceFlag == 1">否</span>
          </template>
        </el-table-column>
        <el-table-column prop="apiName" label="接口名称"></el-table-column>
        <el-table-column prop="dataConfig" label="账号信息"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <a href="javascript:void(0)" class="exportBtn">
              <span class="lookBtn bindColor" @click="amendport(scope.row)">编辑</span>
              <span class="lookBtn bindColor" @click="delport(scope.$index,scope.row)">删除</span>
            </a>
          </template>
        </el-table-column>
      </el-table>

      <div class="teCen mt60" v-if="this.tablegroup.length < 1">
        <p><img src="../../assets/Shape.png" /></p>
        <span>暂无数据</span>
      </div>

      <div class="page" style="padding-bottom:30px;" >
        <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>

      <el-dialog :title="titleTab" :visible.sync="creataut" size="tiny" @close=''>
          <el-form :model="addPortForm" ref="addPortForm" label-width="100px" class="demo-ruleForm">

            <el-form-item label="是否源接口：" :label-width="formLabelWidth" prop='sourceFlag' :rules="[{ required:true, message: '源接口不能为空'}]">
              <el-select :disabled="titleTab =='编辑' ? true : false" v-model="addPortForm.sourceFlag" placeholder="请选择显示值" class="w9000">
                <el-option :key="0" label="是" :value="0"></el-option>
                <el-option :key="1" label="否" :value="1"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="分组：" :label-width="formLabelWidth" prop='apiGroupName' :rules="[{ required:true, message: '分组不能为空'}]">
              <el-select :disabled="titleTab =='编辑' ? true : false" v-model="addPortForm.apiGroupName" filterable placeholder="请选择显示值" class="w9000" @visible-change="slideListGroup" @change="slideLabelGroup">
                  <el-option v-for="item in groupNameOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item v-if="addPortForm.sourceFlag == 1" prop='apiName' label="接口名称：" :label-width="formLabelWidth" :rules="[{ required:true, message: '接口名称不能为空'}]">
              <el-select :disabled="titleTab =='编辑' ? true : false" v-model="addPortForm.apiName" filterable placeholder="请选择接口名称" class="w9000" @visible-change="slideListPort" @change="slideLabel">
                  <el-option v-for="item in portOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="appKey：" :label-width="formLabelWidth" prop='appKey' :rules="[{ required:true, message: 'appKey不能为空'}]">
              <el-input :disabled="titleTab =='编辑' ? true : false" class="w9000" v-model="addPortForm.appKey"></el-input>
            </el-form-item>

            <el-form-item label="账号信息：" :label-width="formLabelWidth" prop='dataConfig' :rules="[{ required:true, message: '账号信息不能为空'}]">
              <el-input type="textarea" v-model="addPortForm.dataConfig" class="w9000" :rows="4" auto-complete="off"></el-input>
              <!-- <span class="errorWritePlaceDialog w550 commonVaL">{{addPortForm.dataConfig}}</span> -->
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button class="cancelBtn" @click="callOf('addPortForm')">取 消</el-button>
            <el-button type="primary" class="addOne" @click="addaut('addPortForm')">确 定</el-button>
          </div>
        </el-dialog>

    </div>
</template>
<script>
import axios from "axios";
import { formatJson } from '../../assets/json'

export default {
  name: "authorityMsg",
  data() {
    return {
      searchAppKey:'',
      searchGroupName:'',
      port:'',
      tablegroup:[],
      portAlt: null,
      portAltOptions:[
        {label:'是',id:0},
        {label:'否',id:1}
      ],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      newPage:'',
      creataut:false,
      formLabelWidth: "120px",
      addPortForm:{},
      groupNameOptions:[],
      portOptions:[],
      titleTab:'创建',
      delData:{},
      groupTab:false,
      groupTabSlide:false,
      portTab:false,
      searchBtn:false,
      addBtn:false,
      jurisdiction:{},
    };
  },

  created() {
      this.initData();
      this.utils.getUserPermissions(this);
  },

  methods: {
    initData() {
      axios({
        method: 'post',
        url: '/agency/findAgencyInfoList',
        data: {
          apiGroupName:this.searchGroupName,
          apiName:this.port,
          appKey:this.searchAppKey,
          sourceFlag:this.portAlt,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 2000){
          if(!this.jurisdiction.roles.includes('admin')){
            this.searchBtn = false;
            this.addBtn = false;
          }else{
            this.searchBtn = true;
            this.addBtn = true;
            this.tablegroup = res.data.data.elements;
            this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
            this.pageSize = res.data.data.pageSize;
            this.total = res.data.data.count;
          }
        }else{
          this.$message({type: "info",message: res.data.message});
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    slideListGroup(){
      this.groupNameOptions = [];
      axios({
        method: "post",
        url: "/apiGroup/listOfNoLowerLevelGroup",
        data: {}
      }).then(res => {
        res.data.data.map(data => {
          this.groupNameOptions.push({ label: data.groupName, id: data.id });
        });
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },
    slideLabelGroup(vid){
      if(this.groupNameOptions.length >0){
        let obj = {};
        obj = this.groupNameOptions.find((item) =>{
        return item.id === vid
        });
        if(obj !==undefined){
          this.addPortForm.groupName = obj.label;
          this.groupTabSlide = true;
          if(this.addPortForm.apiName){
            this.addPortForm.apiName = '';
          }
        }
      }
    },
    slideListPort(){
      this.portOptions = [];
      axios({
        method: "post",
        url: "/api/findApiByGroupId",
        data: {
          id:this.groupTabSlide ? this.addPortForm.apiGroupName : this.addPortForm.apiGroupId,
        }
      }).then(res => {
          res.data.data.map(data => {
            this.portOptions.push({ label: data.apiName, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    slideLabel(vid){
      if(this.portOptions.length > 0){
        var objPort = {};
        objPort = this.portOptions.find((item) =>{
          return item.id === vid;
        });

        if(objPort !== undefined){
          this.addPortForm.portName = objPort.label;
          this.portTab = true;
        }
      }
    },

    //添加
    addName(){
      this.titleTab = '创建';
      this.creataut = true;
      this.groupTabSlide = false;
      this.portTab = false;
      this.addPortForm = {};
      this.groupNameOptions = [];
      this.portOptions = [];
    },
    addaut(formName){
      if(this.addPortForm.portName == undefined || this.addPortForm.portName == ''){
        this.addPortForm.portName = '';
      }

      if(this.addPortForm.sourceFlag == 0){
        this.portTab = false;
        this.addPortForm.apiId = '';
        this.addPortForm.apiName = '';
      }

      this.$refs[formName].validate(valid => {
      if (valid) {
        axios({
          method: 'post',
          url: this.titleTab == '创建'? '/agency/add':'/agency/update',
          data: {
            apiId:this.portTab ? this.addPortForm.apiName : this.addPortForm.apiId,
            apiName:this.portTab ? this.addPortForm.portName : this.addPortForm.apiName,
            apiGroupId:this.titleTab == '创建'? this.addPortForm.apiGroupName : this.addPortForm.apiGroupId,
            apiGroupName:this.groupTabSlide ? this.addPortForm.groupName : this.addPortForm.apiGroupName,
            appKey:this.addPortForm.appKey,
            dataConfig:this.addPortForm.dataConfig,
            sourceFlag:this.addPortForm.sourceFlag,
            id:this.titleTab == '编辑' ? this.addPortForm.id : '',
          }
        }).then(res => {
          if(res.data.code == 2000){
            if(this.titleTab == '创建'){
              this.$message({type: "success",message: '新建成功'});
            }else{
              this.$message({type: "success",message: '编辑成功'});
            }
            this.creataut = false;
            this.initData();
            this.$refs[formName].resetFields();
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
          this.addPortForm = {};
        });
       }else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    amendport(row){
      this.titleTab = '编辑';
      this.addPortForm = {};
      this.addPortForm = row;
      this.creataut = true;
      this.groupTabSlide = false;
      this.portTab = false;
      console.log(row);
      // var resultErrorJson = formatJson(this.addPortForm.dataConfig);
      // $(".errorWritePlaceDialog").html('<pre>' + resultErrorJson + '<pre/>');
      // $(".errorWritePlaceDialog").show();

    },
    callOf(formName){
      this.creataut  = false;
      this.addPortForm = {};
      this.$refs[formName].resetFields();
      this.initData();
    },
    delport(index, row){
      this.delData = row;
      this.$confirm(`是否删除 "${row.apiName}",如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          this.delMsg();
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },
    delMsg(){
      axios({
        method: 'post',
        url: '/agency/delete',
        data: {
          id:this.delData.id,
        }
      }).then(res => {
        if(res.data.code == 2000){
          this.initData();
          this.$message({
            type: "success",
            message: "删除成功"
          });
        }else{
          this.$message({
            type: "info",
            message: "删除失败"
          });
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
    });
   },


    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    },
    handleSizeChange(val) {
      this.newPage = val;
      this.initData();
    },

  },

};
</script>

<style scoped>

</style>



