<template>
  <div class="appListDetail" style="background:white;">

    <div class="detailBtn" style="height:50px;">
      <div class="fl">
        <span class="detailNav">应用授权</span>
        <router-link to="/appList">
          <el-button size="small" style="width:auto">返回应用授权列表</el-button>
        </router-link>
      </div>
      <el-button type='primary' size="small" class="fr" @click="amendApp = true;">修改</el-button>
      <p style="clear:both"></p>
    </div>

    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">基本信息</span>
        <table v-model="applyDataDetail" class="table table-hover table-bordered">
          <tbody>
            <tr>
              <td>应用名称：
                <span>{{applyDataDetail.appName}}</span>
              </td>
              <td>应用ID：
                <span>{{applyDataDetail.id}}</span>
              </td>
            </tr>

            <tr>
              <td>appKey：
                <span>{{applyDataDetail.appKey}}</span>
              </td>
              <td>appSecret：
                <span>{{applyDataDetail.appSecret}}</span>
              </td>
            </tr>

            <tr>
              <td>授权策略：
                <span>{{applyDataDetail.limitStrategyuuid}}</span>
              </td>
              <td>描述：
                <span>{{applyDataDetail.description}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <el-tabs type="border-card">
      <el-tab-pane label="已授权的API">
        <el-button type='primary' size="small" class="fr" @click="authApiOpen()">添加</el-button>
        <p style="clear:both"></p>
        <el-table :data="granted" border style="width: 100%">
          <el-table-column prop="apiName" label="API名称"></el-table-column>
          <el-table-column prop="apiGroupName" label="分组"></el-table-column>
          <el-table-column prop="apiDescription" label="描述"></el-table-column>
          <el-table-column prop="env" label="环境" :formatter='envFormat'></el-table-column>
          <el-table-column prop="updateTime" label="最后修改" :formatter="dateFormat"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="deleteApi(scope.row)">解绑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="page">
          <div class="block fr" style="margin-top:20px;">
            <el-pagination @current-change="handleCurrentChange" :current-page="pageNum" :page-size="this.pageSize" layout="total, prev, pager, next" :total="total"></el-pagination>
          </div>
        </div>
      </el-tab-pane>

      <el-tab-pane label="已授权策略">
        <el-button type='primary' id='addOver' size="small" class="fr" @click="addStrategy">添加</el-button>
        <p style="clear:both"></p>
        <el-table :data='strategyData' border style="width: 100%">
          <el-table-column prop='name' label="策略名称"></el-table-column>
          <el-table-column prop="unit" label="单位" :formatter='unitFormat'></el-table-column>
          <el-table-column prop="no" label="流量限制"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="removeStrategy()">解绑</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>

    <!-- 绑定策略弹框 start -->
    <el-dialog title="为应用绑定策略" :visible.sync="bindStrategy" size="small" id="accreditBox" @close='closeDialog'>
      <div class="accreditLine"></div>
      <el-form>
        <div>
          <span style="font-weight:700;">选择要绑定的策略</span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px;width: 100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="请输入策略名称" v-model='strategyName'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getStrategyList()">搜索</el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="allStrategy" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto">

              <el-table-column label="" width="40">
                <template slot-scope="scope">
                  <el-radio v-model="radio" @change.native="getCurrentRow(scope.$index)"></el-radio>
                </template>
              </el-table-column>

              <el-table-column label="策略名称">
                <template slot-scope="scope">{{ scope.row.name}}</template>
              </el-table-column>
              <el-table-column prop="unit" label="单位" :formatter='unitFormat'></el-table-column>
              <el-table-column prop="no" label="流量限制"></el-table-column>
              <el-table-column prop="createTime" label="创建时间" :formatter='dateFormat'></el-table-column>
            </el-table>
          </div>
        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bindStrategy = false">取 消</el-button>
        <el-button type="primary" @click="setAuth()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 绑定策略弹框 end -->

    <!-- 授权弹框 start -->
    <el-dialog title="授权API" :visible.sync="authApi" size="small" id="accreditBox" @close='closeAuthApi'>
      <div class="accreditLine"></div>
      <el-form>

        <span style="font-weight:700;">选择授权环境</span><br><br>
        <template>
          <el-radio-group v-model="radioEnv" @change='radioChange'>
            <el-radio-button label="线上"></el-radio-button>
            <el-radio-button label="预发"></el-radio-button>
          </el-radio-group>
        </template><br>

        <div>
          <span style="font-weight:700;">选择要授权的API</span>
          <p></p>
          <div class="fl flBox" style="margin-bottom:20px; width:100%">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item id="accreditInput">
                <el-input placeholder="请输入API名称" v-model='searchApiName'></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click='initApiList()'>搜索</el-button>
              </el-form-item>
            </el-form>

            <el-table ref="multipleTable" :data="apiList" border tooltip-effect="dark" style="width:100%;height:380px;overflow:auto" @selection-change="handleSelectionChangeApi">
              <el-table-column type="selection" width="55" class="selection" prop='limitStrategyuuid' @selection-change="handleSelectionChangeApi">
              </el-table-column>

              <el-table-column prop="apiName" label="api名称"></el-table-column>
              <el-table-column prop="apiGroupName" label="分组"></el-table-column>
              <el-table-column prop="createTime" label="创建时间" :formatter='dateFormat'></el-table-column>
            </el-table>

          </div>
        </div>
      </el-form>
      <p style="clear:both"></p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="authApi = false">取 消</el-button>
        <el-button type="primary" @click="confirmAuthApi()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 授权API弹框 end -->

    <!-- 修改应用 begin -->
    <el-dialog title="修改应用" :visible.sync="amendApp" size="tiny" @close='closeModify'>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="应用名称：" :label-width="formLabelWidth" prop='modifyAppName'>
          <el-input v-model='ruleForm.modifyAppName'></el-input>
          <p style="line-height:20px;">全局唯一，支持汉字、英文字母、数字、英文格式的下划线，且必须以字母或汉字开始，4~15个字符</p>
        </el-form-item>
        <el-form-item label="描述：" :label-width="formLabelWidth" prop='modifyDescription'>
          <el-input v-model='ruleForm.modifyDescription' type="textarea" :rows="4" placeholder="不超过180个字符"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="amendApp  = false">取 消</el-button>
        <el-button type="primary" @click="modifyApp('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 修改应用 end -->

  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'appListDetail',
  components: {
  },
  data() {
    //------------------必须以英文与汉字开头的正则------------------//
    let appName1 = (rule, value, callback) => {
      let regExp = /^[\u4e00-\u9fa5a-zA-Z][\u4e00-\u9fa5a-zA-Z\d](?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/;
      if (regExp.test(value) === false) {
        callback(new Error('格式错误'));
      } else {
        callback();
      }
    };
    //------------------检查appName是否重复------------------//
    let appName2 = (rule, value, callback) => {
      if (value != this.applyDataDetail.appName) {
        axios({
          method: 'post',
          url: '/app/checkName',
          data: {
            appName: value
          }
        }).then(res => {
          if (res.data.data === false) {
            callback(new Error('应用名称重复'));
          } else {
            callback();
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      } else {
        callback();
      }
    };
    return {
      pageNum: 1,
      pageSize: 8,
      total: 2,
      value: '',
      applyDataDetail: {},                  //APP展示
      createAccredit: false,                //添加授权API
      bindStrategy: false,
      granted: [],  //已授权API
      amendApp: false,                  // 修改应用
      delGroupData: {},                 //删除保存对象
      strategyData: [],
      // modifyAppName:'',
      // modifyDescription:'',
      allStrategy: [],
      strategyName: '',
      radio: '',
      strategyUuid: '',
      formLabelWidth: '120px',
      authApi: false,
      searchApiName: '',
      env: 1,
      multipleSelection: [],
      apiList: [],
      radioEnv: '线上',
      envFormat(row, column) {
        const env = row[column.property];
        if (env == undefined) {
          return "";
        }
        return env == 1 ? "线上" : "预发";
      },
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      unitFormat(row, column) {
        const unit = row[column.property];
        if (unit == undefined) {
          return "";
        }
        return unit == 1 ? "分钟" : (unit == 2 ? "小时" : "天");
      },
      ruleForm: {
        modifyAppName: '',
        modifyDescription: ''
      },
      rules: {
        modifyAppName: [
          { required: true, message: '请输入策略名称', trigger: 'blur' },
          { min: 4, max: 15, message: '长度在 4 到 15 个字符', trigger: 'blur' },
          { validator: appName1, trigger: 'blur' },
          { validator: appName2, trigger: 'blur' }
        ],
        modifyDescription: [
          { min: 0, max: 180, message: '不超过180个字符', trigger: 'blur' }
        ]
      }

    }
  },

  created() {
    this.appDetails();
    this.findApis();
  },

  methods: {
    //===========================================    应用授权基本信息 展示页 List   start=========================================
    appDetails() {
      let id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/app/details',
        data: {
          id: id
        }
      }).then(res => {
        this.applyDataDetail = res.data.data;
        this.ruleForm.modifyAppName = res.data.data.appName;
        this.ruleForm.modifyDescription = res.data.data.description;
        if (res.data.data.strategy != null) {
          this.strategyData.push(res.data.data.strategy);
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //===========================================    应用授权基本信息 展示页 List   end===========================================

    //===========================================         已授权API展示         start  ===========================================
    findApis() {
      let appId = this.$route.query.id;
      axios({
        method: 'post',
        url: '/app/findApis',
        data: {
          appId: appId,
          pageNum: this.pageNum,
          pageSize: 8
        }
      }).then(res => {
        this.granted = res.data.data.elements;
        this.pageNum = res.data.data.pageNum == 0 ? 1 : res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.count;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //分页bar
    handleCurrentChange(val) {
      this.pageNum = val;
      this.findApis();
    },
    //===========================================         已授权API展示          end   ===========================================
    //解除api
    deleteApi(val) {
      this.$confirm('此操作将解绑该API授权, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/unAccredit',
          data: {
            apiId: val.id,
            appCertificationId: this.applyDataDetail.id,
            env: val.env
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '解绑成功!'
          });
          this.strategyData = [];
          this.appDetails();
          this.findApis();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消解绑'
        });
      });
    },
    //解绑限流策略
    removeStrategy() {
      this.$confirm('此操作将解绑该策略, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios({
          method: 'post',
          url: '/app/updateUuid',
          data: {
            id: this.applyDataDetail.id,
            limitStrategyuuid: null
          }
        }).then(res => {
          this.$message({
            type: 'success',
            message: '解绑成功!'
          });
          this.strategyData = [];
          this.appDetails();
          this.findApis();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消解绑'
        });
      });
    },
    //关闭弹框的事件
    closeModify() {
      this.ruleForm.modifyAppName = this.applyDataDetail.appName;
      this.ruleForm.modifyDescription = this.applyDataDetail.description;
    },
    //修改app
    modifyApp(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: '/app/update',
            data: {
              id: this.applyDataDetail.id,
              appName: this.ruleForm.modifyAppName == '' ? null : this.ruleForm.modifyAppName,
              description: this.ruleForm.modifyDescription == '' ? null : this.ruleForm.modifyDescription
            }
          }).then(res => {
            this.strategyData = [];
            this.amendApp = false;
            this.appDetails();
            this.findApis();
          }).catch(error => {
            console.log('网络错误，不能访问');
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //查询
    getAllStrategy(val) {
      axios({
        method: 'post',
        url: '/strategy/getAllStrategy',
        data: {
          strategyName: val
        }
      }).then(res => {
        this.allStrategy = res.data.data;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //添加策略
    addStrategy() {
      if (this.strategyData.length != 0) {
        this.$message({
          message: '已绑定策略，如需重新绑定，请先解绑！',
          type: 'warning'
        });
      } else {
        this.bindStrategy = true;
        this.getAllStrategy(null);
      }
    },
    //点击搜索策略
    getStrategyList() {
      this.getAllStrategy(this.strategyName == '' ? null : this.strategyName);
    },
    //关闭弹框的事件
    closeDialog() {
      this.strategyName = '';
    },
    //改变的时候
    getCurrentRow(index) {
      this.strategyUuid = this.allStrategy[index].uuid;
    },
    //设置策略
    setAuth() {
      axios({
        method: 'post',
        url: '/app/updateUuid',
        data: {
          id: this.applyDataDetail.id,
          limitStrategyuuid: this.strategyUuid
        }
      }).then(res => {
        this.bindStrategy = false;
        this.strategyData = [];
        this.findApis();
        this.appDetails();
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //授权API复选框状态改变
    handleSelectionChangeApi(val) {
      this.multipleSelection = val;
    },
    //关闭弹框的事件
    closeAuthApi() {
      this.searchApiName = '';
    },
    //打开添加授权API弹框
    authApiOpen() {
      this.authApi = true;
      this.initApiList();
    },
    initApiList() {
      axios({
        method: 'post',
        url: '/app/findNoAccreditApis',
        data: {
          appId: this.applyDataDetail.id,
          apiName: this.searchApiName == '' ? null : this.searchApiName,
          env: this.env
        }
      }).then(res => {
        this.apiList = res.data.data;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //线上/预发单选按钮改变事件
    radioChange(val) {
      if (val == '线上') {
        this.env = 1;
        this.multipleSelection = [];
        this.initApiList();
      } else if (val == '预发') {
        this.env = 2;
        this.multipleSelection = [];
        this.initApiList();
      }
    },
    //确认提交绑定api
    confirmAuthApi() {
      var flag = 0;
      var apiIds = '';
      if (this.multipleSelection.length != 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            apiIds += row.id;
          } else {
            apiIds += (',' + row.id);
          }
          flag++;
        });
        axios({
          method: 'post',
          url: '/app/appToApi',
          data: {
            appId: this.applyDataDetail.id,
            apiIds: apiIds,
            env: this.env
          }
        }).then(res => {
          this.authApi = false;
          this.findApis();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }
    }
  }
}
</script>
<style scope>
.el-table__header-wrapper .el-table__header {
  width: 100%!important;
}

.el-table__body-wrapper table {
  width: 100%!important;
}
</style>



