<template>
  <div class="TestConfig" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入APIID进行查询" class="w200" v-model.number='searchApiId'></el-input>
        <el-input placeholder="请输入groupId进行查询" class="w200" v-model.number='searchGroupId'></el-input>

        <el-select v-model="autoTest" placeholder="请选择">
          <el-option v-for="item in autoTestOptions" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
        <el-button type="primary addOne" v-if="testBtns" class="fr" style="margin-right:100px;" @click="testBtn()">一键测试</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="apiId" label="apiId"></el-table-column>
      <el-table-column prop="apiName" label="apiName"></el-table-column>
      <el-table-column prop="apiGroupId" label="分组"></el-table-column>
      <el-table-column prop="autoTest" label="状态"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <span class="lookBtn bindColor" v-if="configTestBtn" @click="configTest(scope.row)">配置测试</span>
            <span class="lookBtn bindColor" v-if="cancleTestBtn" @click="cancleTest(scope.row)">取消测试</span>
            <!-- <el-button type="text" class="radiusBtn addWcolor" v-if="configTestBtn" size="small" @click="configTest(scope.row)">配置测试</el-button>
            <el-button type="text" class="radiusBtn addWcolor" v-if="cancleTest" size="small" @click="cancleTest(scope.row)">取消测试</el-button> -->
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 发布API弹框 start -->
    <el-dialog title="查看：" :visible.sync="jsonDialog" size="tiny" style="top:-5%">
        <pre><span class="writePlaces">{{callBack}}</span></pre>
        <span slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="jsonDialog = false">关闭</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'findApi',
  components: {

  },
  data() {
    return {
      jurisdiction:{},
      outObj:{},
      jsonDialog:false,
      callBack:{},
      pageNum: 1,
      pageSize: 10,
      total: 0,
      env: 1,
      page: 1,
      searchApiId: '',
      searchGroupId:'',
      listLoading: false,
      configTestBtn:false,
      cancleTestBtn:false,
      testBtns:false,
      tablegroup: [],
      newPage:'',
      autoTest:'',
      autoTestOptions:[
      { label:'全部',id:''},
      { label:'是' ,id:'Y'},
      { label:'否' ,id:'N'},
      ],

      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  },

  created() {
    this.getUserPermissions();
    this.initData();
  },

  methods: {

    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        if(!this.jurisdiction.roles.includes('admin')){
          this.testBtns = false;
          this.configTestBtn = false;
          this.cancleTestBtn = false;
        }else{
          this.testBtns = true;
          this.configTestBtn = true;
          this.cancleTestBtn = true;
        }
        // if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
        //   this.$confirm('权限不足，请联系管理员', '提示', {
        //     confirmButtonText: '退出',
        //     cancelButtonText: '取消',
        //     type: 'warning'
        //   }).then(() => {
        //     location.href = res.data.logoutUrl;
        //   }).catch(() => {
        //     this.$message({
        //       type: 'info',
        //       message: '权限不足，请联系管理员'
        //     });
        //   });
        // }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化与搜索框查询
    initData() {
      axios({
        method: 'post',
        url: '/api/page/testResult',
        data: {
          apiId: this.searchApiId == '' ? '' : this.searchApiId,
          apiGroupId:this.searchGroupId,
          autoTest:this.autoTest,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }
      }).then(res => {
        if(res.data.code == 2000){
          this.tablegroup = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
        }else{
          this.$message({type: "info",message: res.data.message});
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //一键测试
    testBtn(){
      axios({
        method: "post",
        url: "/api/autotest",
        data: {}
        }).then(res => {
          if(res.data.data !== 2000){
            this.$message({type: "info",message: res.data.message});
          }else{
            this.$message({type: "success",message:'一键测试成功' });
          }
        }).catch(error => {
            console.log(error.message);
      });
    },
    //配置测试
    configTest(row){
      axios({
        method: 'post',
        url: '/api/autotest/'+row.apiId+'/on',
        data: {}
      }).then(res => {
        if(res.data.code == 2000){
          this.initData();
          this.$message({type: "success",message: '配置测试成功'});
        }
      }).catch(error => {
        console.log('网络错误');
      });
    },
   //取消测试
   cancleTest(row) {
      axios({
        method: 'post',
        url: '/api/autotest/'+row.apiId+'/off',
        data: {}
      }).then(res => {
        if(res.data.code == 2000){
          this.initData();
          this.$message({type: "success",message: '取消测试成功'});
        }
      }).catch(error => {
        console.log('网络错误');
      });
    },
    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();


    },
    handleSizeChange(val) {
      this.newPage = val;
      this.initData();
    },
  }

}
</script>

<style scoped>

</style>



