<template>
  <div class="jsonData">
    <el-tabs type="border-card" v-model="activeNameJSon" @tab-click="jsonTable">
      <el-tab-pane label="JSON导入" name="first">
        <el-input type="textarea" :rows="10" :cols="10" placeholder="请输入内容" v-if="this.$route.path=='/portManage'"  v-model="addApiData.apiInfo.responseConfigJson"></el-input>
        <el-input type="textarea" :rows="10" :cols="10" placeholder="请输入内容" v-if="this.$route.path=='/portManage/portApiManage'" v-model="apiList.responseConfigJson"></el-input>
        <el-button type="primary" class="mt20 addOne" @click="leadJson">导入</el-button>
      </el-tab-pane>
      <el-tab-pane label="编辑导入" name="second">
        <el-button type="text" class="radiusBtn fr pubColor" size="small" @click="addJson" style="position:absolute;right:5%;z-index:999;top:27px;">添加</el-button>
        <el-table :data="jsonArr" stripe style="width: 100%">
          <el-table-column prop="deep" label="层级" width="80"></el-table-column>
          <el-table-column label="key名称">
            <template slot-scope="scope">
              <el-input v-model="scope.row.key" @change="removeFous"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="类型">
            <template slot-scope="scope">
              <el-select v-model="scope.row.type" @change="removeFous">
                <el-option v-for="item in typeOptions" :key="item.label" :label="item.label" :value="item.label"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="transKey" label="key转换">
            <template slot-scope="scope">
              <el-select v-model="scope.row.transKey" filterable placeholder="请选择" @visible-change="keyData()" @change="removeFous">
                <el-option v-for="item in keyOptions" :key="item.label" :label="item.label" :value="item.label"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="" label="是否抽取">
            <template slot-scope="scope">
              <el-select v-model="scope.row.isData" placeholder="请选择" @change="removeFous">
                <el-option v-for="item in isDataOptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" class="radiusBtn pubColor" size="small" @click="codeMapFunc(scope.$index,scope.row)">码值映射</el-button>
              <el-button type="text" class="radiusBtn pubColor" size="small" @click="delBtnFunc(scope.$index)">删除</el-button>
              <el-button type="text" class="radiusBtn pubColor" size="small" @click="addBtnFunc(scope.$index,scope.row)">添加</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- JSON导入 end -->
      </el-tab-pane>
    </el-tabs>

    <!-- 码值映射 start -->
    <el-dialog title="码值映射" :modal=false :visible.sync="codeMap">
      <el-form>
        <el-form-item label="选择转换的key" :label-width="formLabelWidth">
          <el-select v-model="valueKey" filterable placeholder="请选择" @change="slideListVal">
            <el-option v-for="item in optionsCode" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <el-table :data="optionMap" style="width: 100%">
        <el-table-column label="value">
          <template slot-scope="scope">
            <el-input v-model="scope.row.orign" placeholder="请输入内容"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="value映射">
          <template slot-scope="scope">
            <el-select v-model="scope.row.target" filterable placeholder="请选择" class="targetDin"  @visible-change="slideListVal">
              <el-option v-for="item in optionsKey" :key="item.label" :label="item.label + ',' + item.value" :value="item.label + ',' + item.value">
                <span style="float: left">{{ item.label }}</span>
                <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button type="text" class="radiusBtn" size="small" @click="delCodeMap(scope.$index)">移除</el-button>
          </template>
        </el-table-column>
    </el-table>
    <el-button type="primary" size="mini" style="margin-top:10px;padding:10px 15px" class="mt20" @click="addCodeMap">
    <i class="el-icon-plus" style="margin-right:5px"></i>增加一条</el-button>
    <el-button type="primary" size="mini" style="margin-top:10px;padding:10px 15px" class="mt20" @click="CodeMapDel">全部清空</el-button>
    <div slot="footer" class="dialog-footer fr mb20">
      <el-button class="cancelBtn" @click="codeMap = false">取 消</el-button>
      <el-button type="primary" class="addOne" @click="addCodeMes()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 码值映射  end -->

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "jsonData",
  props: ["addApiData", "apiList"],

  data() {
    return {
    	reqArr:[],
    	codeId:0,
    	optionArr:[],
    	/************************/
      jsonArr: null,
      activeNameJSon: "first",
      value: "",
      dictItemNameOptions: [],
      keyOptions: [],
      formLabelWidth: "120px",
      codeMap: false,
      isData: "",
      transKey: "",
      optionMap: [
        {
          orign: null,
          target: null
        }
      ],
      isDataOptions: [
        { label: "是", value: "true" },
        { label: "否", value: "false" }
      ],
      response: [],
      dictionaryId: "",
      optionsKey:[],
      optionsCode:[],
      amendOPtion:[],
      codeIndex:0,
      valueKey:"",
      amendJsonIndex:0,
      valueKeyList:[],
      rowData:"",
      amendMapKey:[],
      amendMapCode:[],
      typeOptions:[],
      reqArrCopy:[],
    };
  },
  created() {
    // this.keyData();
    this.codeOptions();
  },
  mounted(){
  },
  methods: {
    //同步
    removeFous(){
      axios({
        method: "post",
        url: "/api/"+ this.$route.query.id +"/pretty",
        data: {
          responseTransParam:JSON.stringify(this.jsonArr)
        }
      }).then(res => {
        // this.apiList.responseConfigJson = JSON.stringify(this.jsonArr);
        this.apiList.responseConfigJson = res.data.data;
        this.apiList.responseTransParam = res.data.data;
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    //key转换刷新
    slideListVal(){
        if(this.valueKey == undefined){
          this.optionsKey = [];
          this.optionMap.map(data =>{
            data.target = '';
          });
        }else{
          axios({
            method: "post",
            url: "/dict/query/item",
            data: {
              dictionaryId:this.valueKey,
              pageSize:100
            }
          }).then(res => {
              var list = res.data.data.list;
              this.optionsKey = [];

              list.map(data =>{
                this.optionsKey.push({label:data.dictItemValue,id:data.dictionaryId,value:data.dictItemName});
              });
            }).catch(error => {
              console.log("网络错误，不能访问");
            });
        }
    },
    //json解析====================================================================================start
    jsonTable(tab, index) {
      if (tab.index == 1) {
        this.typeOptions = [];
        this.typeOptions.push(
          {value:1,label:'number'},
          {value:2,label:'string'},
          {value:3,label:'Boolean'},
          {value:4,label:'object'},
          {value:5,label:'Array'}
        );
        var url = window.location.hash;
        if(url == "#/portManage"){
        }else{
          this.jsonArr = JSON.parse(this.apiList.responseTransParam);
        }
      }
    },
    jsonAnalData() {
      axios({
        method: "post",
        url: "/dict/query/item",
        data: {
          dictItemValue: this.orign,
          dictionaryId:this.valueKey,
        }
      }).then(res => {
        var list = res.data.data.list;
      }).catch(error => {
        console.log("网络错误，不能访问");
      });

        var url = window.location.hash;
        if (url == "#/portManage" || this.$route.query.num == 2) {
          this.jsonAnal01();
        } else {
          this.jsonAnal02();
        }
    },
    //去重
    codeOptions(){
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {pageSize: 6000}
      }).then(res => {
        var list = res.data.data.list;
        this.optionsCode = [];
        this.optionsCode.push({ label:'全部', id: '' });
        list.map(data =>{
          this.optionsCode.push({ label:data.dictValue, id: data.id });
        })
      }).catch(error => {
        console.log("网络错误，不能访问");
      });
    },

    jsonAnal01() {
      if(!this.addApiData.apiInfo.responseConfigJson){
        this.jsonArr = null;
      }else{
        var jsonStr = JSON.parse(this.addApiData.apiInfo.responseConfigJson);
        var jsonArr = [];
        this.parseJSONObject(jsonStr, jsonArr, -1);
        this.jsonArr = jsonArr;
      }
    },

    jsonAnal02() {
      if(!this.apiList.responseConfigJson){
        this.jsonArr = null;
      }else{
        var jsonStr = JSON.parse(this.apiList.responseConfigJson);
        var jsonArr = [];
        this.parseJSONObject(jsonStr, jsonArr, -1);
        this.jsonArr = jsonArr;
        }
      },


    leadJson() {
      this.activeNameJSon = "second";
      this.typeOptions = [];
      this.typeOptions.push(
        {value:1,label:'number'},
        {value:2,label:'string'},
        {value:3,label:'Boolean'},
        {value:4,label:'object'},
        {value:5,label:'Array'}
      );

      var url = window.location.hash;
      if(url == "#/portManage" || this.$route.query.num == 2){
        this.jsonAnalData();
        this.keyData();
      }else{
        if(this.apiList.responseConfigJson !==''){
        console.log(Object.prototype.toString.call(JSON.parse(this.apiList.responseConfigJson)));
        var jsonType = Object.prototype.toString.call(JSON.parse(this.apiList.responseConfigJson));
        if(jsonType == '[object Object]'){
          console.log('原始导入方法');
          this.jsonAnalData();
          this.keyData();
          this.optionMap = [{orign: null,target: null}];
        }else if(jsonType == '[object Array]'){
          console.log('新接口导入方法');
          var url = window.location.hash;
            axios({
              method: "post",
              url: "/api/"+ this.$route.query.id +"/pretty",
              data: {
                responseTransParam:this.apiList.responseConfigJson
              }
            }).then(res => {
              this.jsonArr = JSON.parse(res.data.data);
              console.log(this.apiList.responseTransParam,'改变前');
              this.apiList.responseTransParam = JSON.stringify(this.jsonArr);
              console.log(this.apiList.responseTransParam,'改变后');
            }).catch(error => {
              console.log("网络错误，不能访问");
            });
          }
        }else{
          this.jsonArr = null;
          this.apiList.responseTransParam = null;
        }
        this.reqArrCopy = this.jsonArr;
      }
    },

    parseJSONObject(jsonObject, jsonArr, level) {
      level++;
      for (var key in jsonObject) {
        var type, arr = [],
        isObject = false;

        if (Object.prototype.toString.call(jsonObject[key]) == "[object Number]") {
          type = "number";
        } else if (Object.prototype.toString.call(jsonObject[key]) == "[object String]") {
          type = "string";
        } else if(Object.prototype.toString.call(jsonObject[key]) == "[object Boolean]"){
          type = "Boolean";
        } else if (Object.prototype.toString.call(jsonObject[key]) == "[object Object]") {
          type = "object";
          isObject = true;
        } else if (Object.prototype.toString.call(jsonObject[key]) == "[object Array]") {
          type = "array";
          arr = jsonObject[key];
        }
        var singleObj = {
          deep: level,
          key: key,
          transKey: "",
          type: type,
          isData: "",
          remark:"",
          option: null
        };
        jsonArr.push(singleObj);

        if (isObject) {
          this.parseJSONObject(jsonObject[key], jsonArr, level);
        }
        if (arr.length > 0) {
          var _this = this;
          for (var i = 0; i < arr.length; i++) {
            if(Object.prototype.toString.call(arr[0]) !== "[object Object]"){
              break;
            }
            _this.parseJSONObject(arr[i], jsonArr, level);
          }
        }
      }
    },

    //码值映射Btn
    codeMapFunc(index,row) {
      this.rowData = row;
      this.codeId = index;
      this.codeMap = true;
      var url = window.location.hash;
      if (url == "#/portManage") {
        if(this.reqArr){
          var option = [{
            orign: null,
            target: null
          }];
          var valueKey = "";
          var cacheOpt = this.optionArr[index];
          var keyOpt = this.valueKeyList[index];

          this.reqArr[index] = {id:index,selKey:(keyOpt?keyOpt:valueKey),option:(cacheOpt?cacheOpt:option)};
          this.optionMap = this.reqArr[index].option;
          this.valueKey = this.reqArr[index].selKey;
        }
      } else {
        var option = [{
          orign: null,
          target: null
        }];
        var valueKey = "";
        var cacheOpt = this.optionArr[index];
        var keyOpt = this.valueKeyList[index];

        this.reqArr[index] = {id:index,selKey:(keyOpt?keyOpt:valueKey),option:(cacheOpt?cacheOpt:option)};
        this.optionMap = this.reqArr[index].option;

        this.reqArr[index].option.map(data =>{
          if(data.target || data.orign){
            this.optionMap = this.reqArr[index].option;
          }else{
            var regsOption = JSON.parse(this.apiList.responseTransParam)[index].option;
            if(regsOption){
              this.optionMap = regsOption;
            }else {
              this.optionMap = [
                {
                  orign: null,
                  target: null
                }
              ];
            }
          }
        });

        if(this.rowData.remark){
          this.valueKey = this.rowData.remark;
        }else{
          var resparams =  JSON.parse(this.apiList.responseTransParam)[index].remark;
          if(resparams == ''){
            this.valueKey = '';
          }else{
            this.valueKey = resparams;
          }
        }
      }
    },

    delBtnFunc(index) {
      this.jsonArr.splice(index, 1);
      this.optionArr.splice(index,1);
      this.valueKeyList.splice(index,1);
      this.reqArr.splice(index,1);
      if(this.jsonArr.length <1){
        this.apiList.responseTransParam = null;
      }
      this.removeFous();

    },
    addBtnFunc(index,row) {
      this.jsonArr.push({
          deep: row.deep + 1,
          key: "",
          transKey: "",
          type: "",
          isData: "",
          option: null
      });
    },
    addJson(){
      if(this.jsonArr !==null){
        this.jsonArr.push({
          deep: 0,
          key: "",
          transKey: "",
          type: "",
          isData: "",
          option: null
        });
      }
    },

    //增加码值
    addCodeMap() {
      this.optionMap.push({
        orign: null,
        target: null
      });
    },
    CodeMapDel(){
      this.optionMap.map(data =>{
        data.orign = null;
        data.target = null;
      })
    },
    //移除码值
    delCodeMap(index) {
      this.optionMap.splice(index, 1);
    },

    anal() {
      var url = window.location.hash;
        if (url == "#/portManage" || this.$route.query.num == 2) {
          if(this.jsonArr){
            this.jsonArr.map(data =>{
              if(data.transKey == "(空)"){
                data.transKey = "";
              }
            });
          }

          if(this.valueKey == "(空)"){
            this.valueKey = "";
          };
          if (this.reqArr.length > 0) {
            this.reqArr.forEach((obj, index) => {
              var temp = [];
              if (obj !== undefined) {
                obj.option.map((data, index) => {
                  if (data.orign || data.target) {
                    temp.push(data);
                  } else {
                    temp = temp.length > 0 ? temp : null;
                  }
                });
              }
              this.jsonArr[obj.id].option = temp;
            });
          }
          this.addApiData.apiInfo.responseTransParam = JSON.stringify(this.jsonArr);
          if(this.addApiData.apiInfo.responseTransParam == 'null'){
            this.addApiData.apiInfo.responseTransParam = null;
          }
          return this.jsonArr;
        }
        else{
          if(this.jsonArr){
            this.jsonArr.map(data =>{
              if(data.transKey == "(空)"){
                data.transKey = "";
              }
            });
          }
          if(this.valueKey == "(空)"){
            this.valueKey = "";
          };
          if (this.reqArr.length > 0) {
            this.reqArr.forEach((obj, index) => {
              var temp = [];
              if (obj !== undefined) {
                obj.option.map((data, index) => {
                  if (data.orign || data.target) {
                    temp.push(data);
                  } else {
                    temp = temp.length > 0 ? temp : null;
                  }
                });
              }
              this.jsonArr[obj.id].option = temp;
            });
            console.log(this.jsonArr,'save');
          }

          if(this.apiList.responseConfigJson == null || this.apiList.responseConfigJson == ''){
            this.jsonArr = null;
            this.apiList.responseTransParam = null;
            console.log(1);
          }else if(this.apiList.responseTransParam == 'null' || this.apiList.responseTransParam == []){
            this.apiList.responseTransParam = null;
            console.log(2);
          }else if(this.apiList.responseTransParam !== this.apiList.responseConfigJson){
            console.log(this.apiList.responseTransParam,'yy');
            this.apiList.responseTransParam = this.apiList.responseTransParam;
            console.log(4);
          }else{
            this.apiList.responseTransParam = this.apiList.responseTransParam;
            console.log(5);
          }
          console.log(this.apiList.responseTransParam);
          console.log(this.apiList.responseConfigJson);
        }
    },
    //key展示
    keyData() {
      axios({
        method: "post",
        url: "/dict/query/dictionary",
        data: {pageSize:1000}
      }).then(res => {
          var list = res.data.data.list;
          this.keyOptions = [];
          this.keyOptions.push({ label:'(空)'});
          list.map(data => {
            this.keyOptions.push({ label: data.dictValue, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
    },
    //添加码值确定按钮
    addCodeMes() {
      var flag = true;
      this.rowData.remark = this.valueKey;

      this.optionArr[this.codeId] = this.optionMap;
      this.reqArr[this.codeId].option = this.optionArr[this.codeId];

      this.reqArr[this.codeId].option.map(data =>{
        if(data.orign == null || data.orign == ''){
          this.$message.info('value不能为空');
          this.codeMap = true;
          flag = false;
        }else if(data.target == null || data.target == ''){
          this.$message.info('value映射不能为空');
          this.codeMap = true;
          flag = false;
        }
      });

      if(this.reqArr.length > 0){
        this.reqArr.forEach((obj, index) => {
          var temp = [];
          if (obj !== undefined) {
            obj.option.map(data => {
              if (data.orign || data.target) {
                temp.push(data);
              } else {
                temp = temp.length > 0 ? temp : null;
              }
            });
          }
          this.jsonArr[obj.id].option = temp;
        });
      }

      if(flag){
        this.codeMap = false;
        this.valueKeyList[this.codeId] = this.rowData.remark;
        this.reqArr[this.codeId].selKey = this.valueKeyList[this.codeId];
        this.removeFous();
        console.log(this.reqArr,'reqArr');
      }
    },

  }
};
</script>
<style scoped></style>






