<template>
  <div class="findApi" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请以'535 535-590'格式输入apiId进行查询" class="w350" v-model='searchApiId'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
      </el-form-item>
      <el-button type="primary addOne" v-if="outOfflineBtn" class="fr" style="margin-right:40px;" @click="outOffBtn()">一键下线</el-button>
      <el-button type="primary addOne" v-if="outBtn" class="fr" style="margin-right:10px;" @click="publishBtn()">一键发布</el-button>
      <el-button type="primary addOne" v-if="contractBtn" class="fr" @click="contractBtnhandle()">一键合约</el-button>
      <el-button type="primary addOne" v-if="exportBtn"  class="fr" @click="exportBtnClick()">一键导出</el-button>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="id" label="apiId" width="80"></el-table-column>
      <el-table-column prop="apiName" label="API名称">
        <template slot-scope="scope">
          <span>{{scope.row.apiName}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="groupName" label="分组"></el-table-column>
      <el-table-column prop="apiDescription" label="描述" width="350"></el-table-column>
      <el-table-column prop="updateTime" label="最后修改" :formatter="dateFormat"></el-table-column>
      <el-table-column class="upEnv" prop="productEnvVersion" label="运行环境" column-key="runTimeEnv">
        <template slot-scope="scope">
          <env :rowData="scope.row" v-on:envLookData="lookData"></env>
        </template>
      </el-table-column>
      <el-table-column label="操作"  width="200">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <el-button type="info" v-if="exlBtn" class="radiusBtn pubColor" size="small" disabled @click="delApi(scope.$index,scope.row)" v-show="removeIdList.includes(scope.row.id)">取消</el-button>
            <el-button type="text" v-if="exlBtn"  class="radiusBtn pubColor" size="small" @click="delApi(scope.$index,scope.row)" v-show="!removeIdList.includes(scope.row.id)">取消</el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                     :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 发布API弹框 start -->
    <el-dialog :title="titleTab" :visible.sync="issue" size="tiny" style="top:-5%">
      <div>
        <el-form :model="outObj" ref="outObj" class="demo-ruleForm">
          <p style="font-weight:700;">请选择要<span>{{offText}}</span>的环境：</p>

          <!--<el-radio-group v-model="env">-->
            <!--<el-radio-button label=1>线上</el-radio-button>-->
            <!--<el-radio-button label=2>预发</el-radio-button>-->
          <!--</el-radio-group>-->

          <el-form-item v-if="titleTab == '发布API：'" prop="pubDescription" label="请填写变更备注：" :rules="[{required:true,message:'备注不能为空'},{ min:1, max:180, message:'长度在 1 到 180 个字符',trigger:'blur'}]">
            <el-input type="textarea" v-model="outObj.pubDescription" :rows="4" placeholder="必填，不超过180个字符" auto-complete="off"></el-input>
          </el-form-item>
          <p class="textRed">如果您设置了使用Mock，实际请求则不会调用到后端服务， 请仔细确认</p>
          <p>该操作将导致线上环境的该API被覆盖，请仔细确认</p>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="issue = false">取 消</el-button>
          <el-button type="primary" class="addOne" @click="outApi('outObj')">{{offText}}</el-button>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="创建" :visible.sync="contractDialog" size="tiny" style="top:-5%">
      <el-form :model="addPortForm" :rules="addPortFormRules" ref="addPortForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="appKey：" :label-width="formLabelWidth" prop='name'>
          <el-input class="w9000"   v-model="addPortForm.appKey"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button class="cancelBtn" @click="callOf('addPortForm')">取 消</el-button>
        <el-button type="primary" class="addOne" @click="addaut('addPortForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 查看API定义详情 start -->
    <el-dialog title="API定义详情" :visible.sync="look" style="overflow:auto;top:-5%">
      <queryApiDetail :apiDefineDetail="apiDefineDetail" :requestParamsVersionsList="requestParamsVersionsList" :backEndParam2="backEndParam2" :resultSettingsVersionsList="resultSettingsVersionsList" :detialVersionFlag="detialVersionFlag" :numData2="numData2" :groupHerfId="groupHerfId" @closeLook="look=false"></queryApiDetail>
    </el-dialog>

  </div>
</template>

<script>
  import axios from 'axios'
  import moment from 'moment'
  import env from "@/components/common/env";
  import queryApiDetail from "@/components/portManage/queryApiDetail";
  import { formatJson } from "@/assets/json";

  export default {
    name: 'findApi',
    components: {
      env,
      queryApiDetail
    },
    data() {
      return {
        look: false, //运行中查看API详情
        jurisdiction:{},
        outObj:{},
        contractDialog:false,
        issue:false,
        exlBtn:true,
        outBtn:true,
        addPortForm: {},
        addPortFormRules: {
          appKey: [{ required:true, trigger: 'blur', message: 'appKey不能为空',}],
        },
        contractBtn: true,
        formLabelWidth: "120px",
        outOfflineBtn:true,
        exportBtn: true,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        env: 1,
        page: 1,
        searchApiId: '',
        listLoading: false,
        tablegroup: [],
        apiIdListLId:[],
        removeIdList:[],
        search: '',
        groupName: '',                 //创建分组名称v-model
        description: '',
        newPage:'',
        rowId:'',
        titleTab:'',
        offText:'',
        backEndParam2: [],
        numData2: [],
        requestParamsVersionsList: [],
        resultSettingsVersionsList: [],
        apiDefineDetail: [],
        detialVersionFlag: null,
        groupHerfId: { pubDescription: "" }, //分组管理传递id
        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      }
    },

    created() {

    },

    methods: {
      //权限配置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(!this.jurisdiction.roles.includes('admin')){
            this.outBtn = false;
            this.contractBtn = false
            this.outOfflineBtn = false;
            this.exlBtn = false;
            this.exportBtn = false
          }else{
            this.outBtn = true;
            this.contractBtn = true
            this.outOfflineBtn = true;
            this.exlBtn = true;
            this.exportBtn = true
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //初始化与搜索框查询
      initData() {
        axios({
          method: 'post',
          url: '/api/publishListForFindAPI',
          data: {
            apiIdList: this.searchApiId == '' ? this.$message({type: "info",message: "请输入APIId进行查询"}) : this.searchApiId,
            pageSize: this.newPage == '' ? 10 : this.newPage,
            pageNum: this.pageNum
          }

        }).then(res => {
          if(res.data.code ==2000){
            this.removeIdList = [];
            this.tablegroup = res.data.data.apiList.list;
            this.apiIdListLId = res.data.data.apiIdList;
            this.pageNum = res.data.data.apiList.pageNum;
            this.pageSize = res.data.data.apiList.pageSize;
            this.total = res.data.data.apiList.total;
          }else{
            this.$message({type: "info",message: res.data.message});
          }

          this.getUserPermissions();
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //一键发布
      outApi(){
        let pubDescription = this.outObj.pubDescription;
        axios({
          method: "post",
          url: this.titleTab == '发布API：' ?"/api/publishByApiIdList" : "/api/offlineByApiIdList",
          data: {
            apiIdList: this.apiIdListLId,
            removeIdList:this.removeIdList,
            env: this.env,
            pubDescription: this.titleTab == '发布API：' ? pubDescription : '',
          }
        }).then(res => {
          if(res.data.code ==2000){
            if(this.titleTab == '发布API：'){
              this.$message({type: "success",message: "发布成功"});
            }else{
              this.$message({type: "success",message: "下线成功"});
            }
            this.issue = false;
            // this.outObj = {};
          }else{
            if(this.titleTab == '发布API：'){
              this.$message({type: "info",message: "发布失败"});
            } else {
              this.$message({type: "info",message: res.data.message || '下线失败'});
            }
          }
        }).catch(error => {
          console.log(error.message);
        });
      },
      publishBtn(){
        this.titleTab = '发布API：';
        this.offText = '发布';
        this.issue = true;
      },
      contractBtnhandle() {
        this.contractDialog  = true;
        this.addPortForm = {};
      },
      exportBtnClick() {
        axios({
          method: 'post',
          url: '/api/getServerURL',
          data: {
            apiIdList: this.apiIdListLId,
            removeIdList:this.removeIdList
          }
        }).then(res => {
          if (res.data.code === 2000) {
            location.href = res.data.data + "/api/apiExport?apiIdList=" + this.apiIdListLId + "&removeIdList=" + this.removeIdList
          } else {
            this.$message({
              type: 'error',
              message: res.data.message
            });
          }
        }).catch(error => {
          console.log('网络错误');
        });
      },
      callOf(formName){
        this.contractDialog  = false;
        this.addPortForm = {};
        this.$refs[formName].resetFields();
      },
      addaut(formName){
        this.$refs[formName].validate(valid => {
          if (valid) {
            axios({
              method: 'post',
              url: '/api/publicProContract',
              data: {
                appKey:this.addPortForm.appKey,
                apiIdList: this.apiIdListLId,
                removeIdList:this.removeIdList
              }
            }).then(res => {
              if(res.data.code == 2000){
                this.$message({type: "success",message: '创建成功'});
                this.contractDialog = false;
                this.initData();
                this.$refs[formName].resetFields();
              }else{
                this.$message({type: "info",message: res.data.message});
              }
            }).catch(error => {
              console.log('网络错误，不能访问');
              this.addPortForm = {};
            });
          }else {
            console.log("error submit!!");
            return false;
          }
        });
      },
      outOffBtn(){
        this.titleTab = '下线API：';
        this.offText = '下线';
        this.issue = true;
      },

      delApi(index,row){
        this.rowId = row.id;
        this.removeIdList.push(this.rowId);
      },

      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //分页查询
      handleCurrentChange(val) {
        // this.pageNum = val;
        // this.initData();
        this.pageData(val);


      },
      handleSizeChange(val) {
        // this.newPage = val;
        // this.initData();
        this.pageData(val);
      },
      //分页复用
      pageData(val){
        axios({
          method: 'post',
          url: '/api/publishListForFindAPI',
          data: {
            apiIdList: this.searchApiId == '' ? this.$message({type: "info",message: "请输入APIId进行查询"}) : this.searchApiId,
            pageSize: this.newPage == '' ? 10 : this.newPage,
            pageNum: val
          }
        }).then(res => {
          this.tablegroup = res.data.data.apiList.list;
          this.pageNum = res.data.data.apiList.pageNum;
          this.pageSize = res.data.data.apiList.pageSize;
          this.total = res.data.data.apiList.total;
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      },
      //======================================  运行中查看 API定义详情 start ===================================
      lookData(row, versionFlag) {
        this.apiDefineDetail = {};
        this.backendVersionsList = [];
        this.requestParamsVersionsList = [];
        this.resultSettingsVersionsList = [];
        this.backEndParam2 = [];
        this.numData2 = [];
        this.detialVersionFlag = versionFlag;
        this.groupHerfId = row;
        const apiGroupId = this.groupHerfId.apiGroupId;
        const groupHerf = this.groupHerfId.id;
        this.look = true;

        axios({
          method: "post",
          url: "/api/findApiInfoVersion",
          data: {
            apiGroupId: apiGroupId,
            id: groupHerf,
            versionId: versionFlag
          }
        }).then(res => {
          var resultJson = formatJson(this.groupHerfId.callBackSuccessExample);
          $(".successWritePlaceDialog").html("<pre>" + resultJson + "<pre/>");
          var resultErrorJson = formatJson(
            this.groupHerfId.callBackFailExample
          );
          $(".errorWritePlaceDialog").html(
            "<pre>" + resultErrorJson + "<pre/>"
          );
          this.apiDefineDetail = res.data.data.apiInfoVersion;
          this.backendVersionsList = res.data.data.backendVersionsList;
          res.data.data.backendVersionsList.forEach((obj, index) => {
            if (obj.paramsType == 1) {
              this.backEndParam2.push(obj);
            } else if (obj.paramsType == 2) {
              this.numData2.push(obj);
            }
          });
          this.requestParamsVersionsList = res.data.data.requestParamsVersionsList;
          this.resultSettingsVersionsList = res.data.data.resultSettingsVersionsList;
        }).catch(error => {
          console.log("网络错误");
        });
      },
    }

  }
</script>

<style scoped>
  .createLabel {
    text-align: right;
  }

  .must {
    color: red;
    display: none;
  }
</style>



