<template>
  <div class="portExcel" style="background:white;">

    <div class="detailBtn" style="padding:8px 10px 0 0">
      <div class="fl">
        <span class="detailNav">接口文档</span>
      </div>
      <el-button type="primary addOne" size="mini" class="fr" @click="down()">下载</el-button>
      <p style="clear:both"></p>

      <div class="panel panel-default">
        <div class="console-panel-header">
            <span class="panel-header">名称及描述</span>
            <table v-model="detail" class="table table-hover table-bordered">
            <tbody>
                <tr>
                    <td>api名称：
                        <span>{{detail.apiName}}</span>
                    </td>
                    <td>正式环境：
                        <span>http://inmgr.tfb.cn:5560</span>
                    </td>
                    <td>测试环境：
                        <span>http://172.32.3.175:5560</span>
                    </td>
                </tr>
                <tr colspan="3">
                    <td>url路径：
                        <span>{{detail.uri}}</span>
                    </td>
                    <td>HTTP请求方式：
                        <span>{{detail.method}}</span>
                    </td>
                </tr>

            </tbody>
            </table>
        </div>
        </div>
    </div>


    <div class="panel panel-default" style="position:relative">
      <div class="console-panel-header">
        <span class="panel-header">请求参数</span>
        <el-table :data="portList" style="width: 100%"  :row-class-name="tableRowClassName">
          <el-table-column prop="paramName" label="参数名称"></el-table-column>
          <el-table-column prop="paramMust" label="是否必填"></el-table-column>
          <el-table-column prop="type" label="类型"></el-table-column>
          <el-table-column prop="paramsLocation" label="位置" ></el-table-column>
          <el-table-column prop="paramsDescription" label="描述"></el-table-column>
        </el-table>
      </div>

      <!-- :filters="[{text:'家',value:'家'},{text,'公司',value:'公司'}]" :filter-method:"filterTag"
          filter-placement="bottom-end" -->
      <div class="console-panel-header">
        <span class="panel-header">返回参数</span>
        <!--<el-table  v-if="this.responseList.length > 0" :data="responseList" style="width: 100%" :row-class-name="tableRowClassName">
          <el-table-column prop="paramName" label="参数名称"></el-table-column>
          <el-table-column prop="paramType" label="类型" ></el-table-column>
          <el-table-column prop="description" label="中文名称"></el-table-column>
          <el-table-column prop="remark" label="描述"></el-table-column>
          <el-table-column prop="parentId" label="父节点" >
           <template slot-scope="scope">
               <el-tag :type="scope.row.parentId === ''? 'paimary':'success'" disable-transitions>
                <span v-if=scope.row.parentId>{{scope.row.parentId}}</span>
               </el-tag>
           </template>
        </el-table-column>
        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button type="text" class="radiusBtn pubColor" size="small" @click="amend(scope.row)">修改</el-button>
                <el-button type="text" class="radiusBtn pubColor" size="small" @click="del(scope.$index,scope.row)">删除</el-button>
            </template>
            </el-table-column>
        </el-table>-->

        <zk-table
          ref="table"
          sum-text="sum"
          index-text="#"
          :data="responseList"
          :columns="columns"
          :stripe="props.stripe"
          :border="props.border"
          :show-header="props.showHeader"
          :show-summary="props.showSummary"
          :show-row-hover="props.showRowHover"
          :show-index="props.showIndex"
          :tree-type="props.treeType"
          :is-fold="props.isFold"
          :expand-type="props.expandType"
          :selection-type="props.selectionType">
          <template slot="handle" slot-scope="scope">
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="amend(scope.row)">修改</el-button>
            <el-button type="text" class="radiusBtn pubColor" size="small" @click="del(scope.rowIndex,scope.row)">删除</el-button>
            <el-button v-if="scope.row.paramType === 'String'" type="text" class="radiusBtn pubColor" size="small" @click="addResultCode(scope.rowIndex,scope.row)">编辑码值</el-button>
            <el-button v-if="scope.row.paramType === 'JSONObject' || scope.row.paramType === 'JSONArray'" type="text" class="radiusBtn pubColor" size="small" @click="addNode(scope.rowIndex,scope.row)">新增子节点</el-button>
            <el-button v-if="scope.row.parentId === 0" type="text" class="radiusBtn pubColor" size="small" @click="addParentNode(scope.rowIndex,scope.row)">新增顶级节点</el-button>
          </template>
        </zk-table>

        <div class="teCen  mt60" v-if="this.responseList.length < 1" >
            <p><img src="../../assets/Shape.png" /></p>
            <span>暂无数据</span>
        </div>

        <div class="teCen mt20" style="width:100%;height:50px;" >
            <el-button v-if="this.responseList.length < 1" type="primary addOne" class="" @click="addNumDetail = true;">添加</el-button>
            <el-button v-if="this.responseList.length >= 1" type="primary addOne" class="" @click="moreDelBtn()">全部删除</el-button>
        </div>


      </div>


      <el-dialog  title="添加参数" :visible.sync="addNumDetail" size="tiny" style="top:-5%">
        <el-form :model="amnedData" ref="amnedData" class="demo-ruleForm">
            <el-form-item label="参数json：" prop="remarkJson" :label-width="formLabelWidth" :rules="[{required:true, message: '参数json不能为空' ,trigger: 'blur' }]">
            <el-input type="textarea" class="w9000" :rows="4" v-model="amnedData.remarkJson" ></el-input>
            </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
            <el-button class="cancelBtn" @click="addNumDetail  = false">取 消</el-button>
            <el-button type="primary" class="addOne" @click="addData('amnedData')">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 修改参数弹框 start-->
    <el-dialog title="修改参数" :visible.sync="amendNumDetail" size="tiny" style="top:-5%">
        <el-form :model="amnedData" ref="amnedData" class="demo-ruleForm">
            <el-form-item label="参数名称：" prop="paramName" :label-width="formLabelWidth" :rules="[{ min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }]">
            <el-input class="w9000" v-model="amnedData.paramName"></el-input>
            </el-form-item>
            <el-form-item label="中文名称：" prop="description" :label-width="formLabelWidth">
            <el-input class="w9000" v-model="amnedData.description"></el-input>
            </el-form-item>

            <el-form-item label="描述：" prop="remark" :label-width="formLabelWidth">
            <el-input type="textarea" class="w9000" :rows="4" v-model="amnedData.remark"></el-input>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button class="cancelBtn" @click="amendNumDetail  = false">取 消</el-button>
            <el-button type="primary" class="addOne" @click="amendData('amnedData')">确 定</el-button>
        </div>
    </el-dialog>
        <!-- 修改参数弹框  end-->

      <!-- 添加参数弹框 start-->
    <el-dialog title="修改参数" :visible.sync="addNodeFlag" size="tiny" style="top:-5%">
        <el-form :model="addNodeData" ref="amnedData" class="demo-ruleForm">
            <el-form-item label="参数名称：" prop="paramName" :label-width="formLabelWidth" :rules="[{ min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }]">
            <el-input class="w9000" v-model="addNodeData.paramName"></el-input>
            </el-form-item>

          <el-form-item label="类型：" :label-width="formLabelWidth">
            <el-select v-model="addNodeData.paramType" >
              <el-option :key="1" label="String" value="String"></el-option>
              <el-option :key="2" label="JSONObject" value="JSONObject"></el-option>
              <el-option :key="3" label="JSONArray" value="JSONArray"></el-option>
            </el-select>
          </el-form-item>

            <el-form-item label="中文名称：" prop="description" :label-width="formLabelWidth">
            <el-input class="w9000" v-model="addNodeData.description"></el-input>
            </el-form-item>

            <el-form-item label="描述：" prop="remark" :label-width="formLabelWidth">
            <el-input type="textarea" class="w9000" :rows="4" v-model="addNodeData.remark"></el-input>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button class="cancelBtn" @click="addNodeFlag  = false">取 消</el-button>
            <el-button type="primary" class="addOne" @click="addNodeHandler('addNodeData')">确 定</el-button>
        </div>
    </el-dialog>
        <!-- 添加参数弹框  end-->

      <!-- 绑定码值 start-->
    <el-dialog title="绑定码值" :visible.sync="addResultCodeFlag" size="tiny" style="top:-5%">
        <el-table  v-if="this.resultCodeList.length > 0" :data="resultCodeList" style="width: 100%" :row-class-name="tableRowClassName">
          <el-table-column prop="code" label="码" align="left"></el-table-column>
          <el-table-column prop="value" label="值" align="left"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" class="radiusBtn pubColor" size="small" @click="delApiResultCode(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-form :inline="true" :model="addResultCodeData">
            <el-form-item label="码：" prop="paramName"  :rules="[{ min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }]">
              <el-input  v-model="addResultCodeData.code"></el-input>
            </el-form-item>
            <el-form-item label="值：" prop="remark" >
              <el-input  v-model="addResultCodeData.value"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" >
            <el-button type="primary" class="addOne" @click="addResultCodeHandler('addNodeData')">添 加</el-button>
        </div>

    </el-dialog>
        <!-- 绑定码值  end-->

      <div class="console-panel-header">
        <span class="panel-header">errorCode对应表</span>
        <el-table :data="errorCodeNum" style="width: 100%">
          <el-table-column prop="num" label="状态码" ></el-table-column>
          <el-table-column prop="des" label="说明"></el-table-column>
        </el-table>
      </div>

      <div class="apiTextNav" v-model="detail">
        <span class="f20">返回结果示例</span>
        <p class="writePlace">{{detail.example}}</p>
      </div>
    </div>

  </div>
</template>
<script>
import axios from 'axios'
import { formatJson } from "@/assets/json";
export default {
  name: 'findApiDetail',
  components: {
  },
  data() {
    return {
        portList:[],
        responseList:[],
        amnedArr:[],
        saveList:[],
        resultCodeList:[],
        parentId:'',
        htmlData:'',
        detail:{},
        delId:"",
        amendNumDetail:false,
        addNumDetail:false,
        amnedData:{},
        formLabelWidth: "120px",
        key:'',
      addNodeFlag: false,
      addResultCodeFlag: false,
      addResultCodeData: {},
      addArrayItemFlag: false,
      addNodeData: {},
      addArrayItemData: {},

        formatter(row,column){
            return row.address
        },
        filterTag(value,row){
            return row.parentId === value;
        },

      errorCodeNum:[
          {
            num:'200',
            des:'请求成功'
          },
          {
            num:'202',
            des:'请求处理中'
          },
          {
            num:'500',
            des:'请求失败'
          },
      ],

      props: {
        stripe: false,
        border: false,
        showHeader: true,
        showSummary: false,
        showRowHover: true,
        showIndex: false,
        treeType: true,
        isFold: false,
        expandType: false,
        selectionType: false,
      },
      columns: [
        {
          label: '参数名称',
          prop: 'paramName',
          width: '400px',
        },
        {
          label: '类型',
          prop: 'paramType',
          minWidth: '50px',
        },
        {
          label: '中文名称',
          prop: 'description',
        },
        {
          label: '描述',
          prop: 'remark',
        },
        {
          label: '操作',
          prop: 'handle',
          type: 'template',
          template: 'handle'
        }
      ],

    }
  },

  created() {
    this.initData();
  },

  methods: {

    //页面信息初始化
    initData() {
      let id = this.$route.query.id;
      let versionId = this.$route.query.productEnvVersion;
      axios({
        method: 'post',
        url: '/api/showDocx',
        data: {
          apiId: id,
          versionId: versionId
        }
      }).then(res => {
        this.detail = res.data.data;
        this.portList = res.data.data.requestList;

        for(var i in res.data.data.responseList){
            this.responseList = this.responseList.concat(res.data.data.responseList[i]);
            this.responseList = this.responseList.reverse();
        }

        for(var j=0;j<this.responseList.length;j++){
            this.parentId  = this.responseList[j].parentId;
        }
        var set = new Set();
        var setSize = set.size;
        set.add(this.parentId);
        if(set.size > setSize){
            this.htmlData = this.parentId + '字段包含'
        }
        setSize = set.size;

        if(this.detail.example !== null){
            var resultJson = formatJson(this.detail.example);
            $(".writePlace").html("<pre>" + resultJson + "<pre/>");
        }

      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //添加
    addData(){
        let id = this.$route.query.id;
        let versionId = this.$route.query.productEnvVersion;

        axios({
            method: "post",
            url: "/api/parseJson",
            data: {
                apiId: id,
                json:this.amnedData.remarkJson
            }
            }).then(res => {
                if(res.data.code == 2000){
                    this.addNumDetail = false;
                    this.saveList = res.data.data.responseList;
                    // this.save();
                    this.initData();
                }
            }).catch(error => {
                console.log(error.message);
        });
    },
    //保存
    save(){
        let id = this.$route.query.id;
        axios({
            method: "post",
            url: "/api/saveDocx",
            data: {
                apiId: id,
                responseList:this.saveList
            }
            }).then(res => {
                if(res.data.code == 2000){
                    this.addNumDetail = false;
                }
            }).catch(error => {
                console.log(error.message);
        });
    },

    //新增节点
    addNode(index, row){
        this.addNodeFlag = true;
        this.addNodeData.apiId = row.apiId;
        this.addNodeData.parentId = row.id;
        this.addNodeData.position = parseInt(row.position) + 1;
    },
    //新增节点
    addParentNode(index, row){
        this.addNodeFlag = true;
        this.addNodeData.apiId = row.apiId;
        this.addNodeData.parentId = 0;
        this.addNodeData.position = 1;
    },
    //提交新增节点
    addNodeHandler(){
        // let id = this.$route.query.id;
        axios({
            method: "post",
            url: "/apiDoc/addNode",
            data: this.addNodeData
            }).then(res => {
                if(res.data.code ===2000){
                    this.addNodeFlag = false;
                    this.addNodeData = {};
                    this.responseList = [];
                    this.initData();
                }
            }).catch(error => {
                console.log(error.message);
        });
    },
    //新增码值
    addResultCode(index, row){
        this.addResultCodeFlag = true;
        this.addResultCodeData.apiId = row.apiId;
        this.addResultCodeData.paramId = row.id;
      axios({
        method: "post",
        url: "/apiDoc/resultCodeList",
        data: this.addResultCodeData
      }).then(res => {
        if(res.data.code ===2000){
          this.resultCodeList = res.data.data;
        }
      }).catch(error => {
        console.log(error.message);
      });
    },
    //提交新增节点
    addResultCodeHandler(){
        axios({
            method: "post",
            url: "/apiDoc/addResultCode",
            data: this.addResultCodeData
            }).then(res => {
                if(res.data.code ===2000){
                    this.addResultCodeFlag = false;
                    this.addResultCodeData = {};
                    this.initData();
                }
            }).catch(error => {
                console.log(error.message);
        });
    },

    //删除
    delApiResultCode(row){
      this.addResultCodeData.apiId = row.apiId;
      this.addResultCodeData.paramId = row.paramId;
      this.addResultCodeData.code = row.code;
      axios({
        method: "post",
        url: "/apiDoc/delApiResultCode",
        data: this.addResultCodeData
      }).then(res => {
        if(res.data.code ===2000){
          this.addResultCodeFlag = false;
          this.addResultCodeData = {};
          this.initData();
        }
      }).catch(error => {
        console.log(error.message);
      });
    },


    //修改
    amend(row){
        console.log(row);
        this.amendNumDetail = true;
        this.amnedData.id = row.id +'';
        this.amnedData.paramName = row.paramName;
        this.amnedData.paramType = row.paramType;
        this.amnedData.parentId = row.parentId;
        this.amnedData.position = row.position;
        this.amnedData.remark = row.remark;
        this.amnedData.description = row.description;


    },
    amendData(){
        let id = this.$route.query.id;
        axios({
            method: "post",
            url: "/api/editDocx",
            data: {
                apiId: id,
                responseList: this.amnedData,
            }
            }).then(res => {
                if(res.data.code ==2000){
                    this.amendNumDetail = false;
                    this.responseList = [];
                    this.initData();
                }
            }).catch(error => {
                console.log(error.message);
        });
    },
    //删除
    del(index, row){
        this.delGroupData = row;
        this.delGroupData.index = index;
        this.delId = row.id;
        this.$confirm(`是否删除参数 "${this.delGroupData.paramName}",如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          this.delNumBtn();
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },
    delNumBtn(){
        axios({
            method: "post",
            url: "/api/deleteDocx",
            data: {
                id: this.delId
            }
            }).then(res => {
                if(res.data.code == 2000){
                    this.responseList.splice(this.delGroupData.index,1);
                    this.responseList = [];
                    this.initData();
                }
            }).catch(error => {
                console.log(error.message);
        });
    },

    //全部删除
    moreDelBtn(){
        this.$confirm(`是否删除所有参数,如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          this.moreDel();
        //   location.reload();
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },
    moreDel(){
        let id = this.$route.query.id;
        axios({
            method: "post",
            url: "/api/deleteDocx",
            data: {
                apiId: id
            }
            }).then(res => {
                this.responseList = [];
                this.initData();
            }).catch(error => {
                console.log(error.message);
        });
    },

    down(){
        let id = this.$route.query.id;
        let versionId = this.$route.query.productEnvVersion;
        axios({
        method: 'post',
        url: '/api/getServerURL',
        data: {
          apiId:id,
          versionId:versionId
        }
      }).then(res => {
        console.log(res.data);
          location.href=res.data.data+"/api/downloadDocx?apiId="+id+"&versionId="+versionId;

      }).catch(error => {
        console.log('网络错误');
      });
    },




    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },
  }
}
</script>

<style scoped>
.apiTextNav {
  width: 100%;
  margin-bottom: 40px;
}
.apiTextNav p {
  border-top: 1px solid #e5e5e5;
  padding-top: 5px;
  margin-top: 5px;
}
</style>



