import Vue from 'vue'
import Router from 'vue-router'
import Port from '@/components/page/Port'                               //分组管理
import portManage from '@/components/page/portManage'                   //API管理（API列表）

import portApiManage from '@/components/apiDetail/portApiManage'             //API列表详情
import accreditMes from '@/components/apiDetail/accreditMes'                 //授权信息
import history from '@/components/apiDetail/history'                         //发布历史
import monitoring from '@/components/apiDetail/monitoring'                   //API监控信息
import debugApi from '@/components/apiDetail/debugApi'                            //API调试
import apiText from '@/components/apiDetail/apiText'                                  //API文档
import apiDetailNum from '@/components/apiDetail/apiDetailNum'                            //API调试
import portExcel from '@/components/apiDetail/portExcel'                            //API文档

import flowMonitoring from '@/components/page/flowMonitoring'           //流量监控
import ipList from '@/components/page/ipList'                        //ip白名单
import flowMonitoringDetail from '@/components/page/flowMonitoringDetail'    //流量控制策略详情
import findApi from '@/components/page/findApi'                          //发现Api
import TestApi from '@/components/page/TestApi'                           //测试Api
import combinationApi from '@/components/page/combinationApi'   //组合Api
import combinationIdList from '@/components/page/combinationIdList'
import TestConfig from '@/components/page/TestConfig'                          //配置测试
import networkRelationship from '@/components/page/networkRelationship'                          //网络关系
import flowRateLimit from '@/components/page/flowRateLimit'
import flowRateLimitInterface from '@/components/page/flowRateLimitInterface'
import serviceRoute from '@/components/page/serviceRoute'
import tcpHeader from '@/components/page/tcpHeader'                          //测试Api
import trafficMonitoring from '@/components/page/trafficMonitoring'          //流量监控

import findApiDetail from '@/components/page/findApiDetail'                  //发现Api
import appList from '@/components/page/appList'                              //APP应用列表
import appListDetail from '@/components/page/appListDetail'                  //APP应用列表详情页
import errorText from '@/components/page/errorText'                                  //API文档
import App from '../App'
import indexPage from '../components/page/indexPage'
import apiDetail from '../components/page/apiDetail'
import codeValue from '../components/page/codeValue'
import codeMap from '../components/page/codeMap'

import authorityMsg from '../components/page/authorityMsg'

import flowDistribution from '../components/page/flowDistribution'
import apiTree from '../components/common/apiTree'
import weight from '../components/common/weight'
import flowControl from '@/components/page/flowControl'
import newValue from '@/components/portManage/newValue'

import healthCheck from '@/components/page/healthCheck'
import billingRule from '@/components/page/billingRule'
import datasourceCharge from '@/components/page/datasourceCharge'

import customerBill from '@/components/page/customerBill'
import customerBillDay from '@/components/page/customerBillDay'
import customerBillMonth from '@/components/page/customerBillMonth'
import customerBillYear from '@/components/page/customerBillYear'
import datasourceBill from '@/components/page/datasourceBill'
import datasourceBillDay from '@/components/page/datasourceBillDay'
import datasourceBillMonth from '@/components/page/datasourceBillMonth'
import datasourceBillYear from '@/components/page/datasourceBillYear'

import apiOfflineRecord from '@/components/page/apiOfflineRecord'

import apiStatistical from '@/components/page/apiStatistical'
import logList from '@/components/page/logList'
import apiDownstream from '@/components/page/apiDownstream'
import authList from '@/components/page/authList'
import appManage from '@/components/page/appManage'

import profit from '@/components/page/profit'
import currentLimit  from '@/components/page/currentLimiting.vue'

Vue.use(Router)

export default new Router({
    //mode:'history',
    routes: [{
        path: '',
        component: indexPage,
        children: [{
            path: '',
            redirect: '/portManage', // 重定向
            name: 'portManage',
            component: portManage
        },{
            path: '/Port',
            name: 'Port',
            component: Port
        }, {
            path: '/portManage',
            name: 'portManage',
            component: portManage
        },{
            path: '/flowMonitoring',
            name: 'flowMonitoring',
            component: flowMonitoring
        }, {
            path: '/ipList',
            name: 'ipList',
            component: ipList
        },  {
            path: '/trafficMonitoring',
            name: 'trafficMonitoring',
            component: trafficMonitoring
        }, {
            path: '/flowMonitoringDetail',
            name: 'flowMonitoringDetail',
            component: flowMonitoringDetail
        }, {
            path: '/findApi',
            name: 'findApi',
            component: findApi
        }, {
            path: '/tcpHeader',
            name: 'tcpHeader',
            component: tcpHeader
        }, {
            path: '/TestApi',
            name: 'TestApi',
            component: TestApi
        }, {
          path: '/combinationApi',
          name: 'combinationApi',
          component: combinationApi
        },{
          path: '/combinationApi/combinationIdList',
          name: 'combinationIdList',
          component: combinationIdList
        },
          {
            path: '/networkRelationship',
            name: 'networkRelationship',
            component: networkRelationship
        },  {
          path: '/flowRateLimit',
            name: 'flowRateLimit',
            component: flowRateLimit
          },
          {
            path: '/flowRateLimitInterface',
            name: 'flowRateLimitInterface',
            component: flowRateLimitInterface
          },
          {
            path: '/serviceRoute',
            name: 'serviceRoute',
            component: serviceRoute
          },
          {
            path: '/TestConfig',
            name: 'TestConfig',
            component: TestConfig
        }, {
            path: '/findApiDetail',
            name: 'findApiDetail',
            component: findApiDetail
        }, {
            path: '/appList',
            name: 'appList',
            component: appList
        }, {
            path: '/appListDetail',
            name: 'appListDetail',
            component: appListDetail
        }, {
            path: '/errorText',
            name: 'errorText',
            component: errorText
        }, {
            path: '/codeValue',
            name: 'codeValue',
            component: codeValue
        }, {
            path: '/codeMap',
            name: 'codeMap',
            component: codeMap
        }, {
            path: '/authorityMsg',
            name: 'authorityMsg',
            component: authorityMsg
        },{
            path: '/flowDistribution',
            name: 'flowDistribution',
            component: flowDistribution
        }, {
            path: '/apiTree',
            name: 'apiTree',
            component: apiTree
        },{
            path: '/weight',
            name: 'weight',
            component: weight
        },{
            path: '/flowControl',
            name: 'flowControl',
            component: flowControl
        },{
            path: '/portManage/newValue',
            name: 'newValue',
            component: newValue
        },
          {
            path: '/healthCheck',
            name: 'healthCheck',
            component: healthCheck
          },
          {
            path: '/apiStatistical',
            name: 'apiStatistical',
            component: apiStatistical
          },
          {
            path: '/apiStatistical/logList',
            name: 'apiStatisticalLogList',
            component: logList
          },
          {
            path: '/apiDownstream',
            name: 'apiDownstream',
            component: apiDownstream
          },
          {
            path: '/authList',
            name: 'authList',
            component: authList
          },
          {
            path: '/appManage',
            name: 'appManage',
            component: appManage
          },
          {
              path: '',
              component: apiDetail,
              children: [
                  {
                      path: '/portManage/portApiManage',
                      name: 'portApiManage',
                      component: portApiManage
                  }, {
                      path: '/portManage/accreditMes',
                      name: 'accreditMes',
                      component: accreditMes
                  }, {
                      path: '/portManage/history',
                      name: 'history',
                      component: history
                  }, {
                      path: '/portManage/monitoring',
                      name: 'monitoring',
                      component: monitoring
                  }, {
                      path: '/portManage/debugApi',
                      name: 'debugApi',
                      component: debugApi
                  }, {
                      path: '/portManage/apiDetailNum',
                      name: 'apiDetailNum',
                      component: apiDetailNum
                  }, {
                      path: '/portManage/apiText',
                      name: 'apiText',
                      component: apiText
                  }, {
                      path: '/portManage/portExcel',
                      name: 'portExcel',
                      component: portExcel
                  }]
          },
          {
            path: '/billingRule',
            name: 'billingRule',
            component: billingRule
          },
          {
            path: '/datasourceCharge',
            name: 'datasourceCharge',
            component: datasourceCharge
          },
          {
            path: '/customerBill',
            name: 'customerBill',
            component: customerBill
          },
          {
            path: '/customerBillDay',
            name: 'customerBillDay',
            component: customerBillDay
          },
          {
            path: '/customerBillMonth',
            name: 'customerBillMonth',
            component: customerBillMonth
          },
          {
            path: '/customerBillYear',
            name: 'customerBillYear',
            component: customerBillYear
          },
          {
            path: '/datasourceBill',
            name: 'datasourceBill',
            component: datasourceBill
          },
          {
            path: '/datasourceBillDay',
            name: 'datasourceBillDay',
            component: datasourceBillDay
          },
          {
            path: '/datasourceBillMonth',
            name: 'datasourceBillMonth',
            component: datasourceBillMonth
          },
          {
            path: '/datasourceBillYear',
            name: 'datasourceBillYear',
            component: datasourceBillYear
          },
          {
            path: '/apiOfflineRecord',
            name: 'apiOfflineRecord',
            component: apiOfflineRecord
          },
          {
            path: '/profit',
            name: 'profit',
            component: profit
          },
          {
            path: '/currentLimit',
            name: 'currentLimit',
            component: currentLimit
          }

        ]
    }]
})



