<template>
  <div class="ipList" style="background:#ffffff;height:100%;">
    <el-form :inline="true" class="search_form">
      <el-form-item>
        <el-input placeholder="请输入header进行查询" class="w350" v-model='searcHeader'></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="searchData()">搜 索</el-button>
        <el-button type="primary addOne" v-if="addName" class="searchOne" @click="creatIp = true,titleText = '新建'">新 建</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tablegroup" style="width: 100%;border-top:8px solid #e6e6e6" v-loading="listLoading" :row-class-name="tableRowClassName">
      <el-table-column prop="" type="index" label="序号" width="80"></el-table-column>
      <el-table-column prop="headerName" label="名称"></el-table-column>
      <el-table-column prop="headerValue" label="值" width="400"></el-table-column>
      <el-table-column prop="headerLength" label="长度"></el-table-column>
      <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <a href="javascript:void(0)" class="exportBtn">
            <el-button type="text" v-if="amendName" class="radiusBtn pubColor" size="small" @click="amendIp(scope.row)">编辑</el-button>
            <el-button type="text" v-if="delName" class="radiusBtn pubColor" size="small" @click="delIp(scope.row)">删除</el-button>
          </a>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tablegroup.length < 1">
      <p><img src="../../assets/Shape.png" /></p>
      <span>暂无数据</span>
    </div>

    <div class="page" style="padding-bottom:30px;" >
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <!-- 新建值弹框 start -->
    <el-dialog :title="titleText" :visible.sync="creatIp" size="tiny" style="top:-5%">
        <el-form :model="addGroupData" ref="addGroupData" class="demo-ruleForm">

          <el-form-item label="名称：" prop="name" :label-width="formLabelWidth" :rules="[{required:true, message: '名称不能为空'}]">
            <el-input class="w9000" v-model="addGroupData.name"></el-input>
          </el-form-item>

          <el-form-item label="值：" prop="val" :label-width="formLabelWidth" :rules="[{required:true, message: '值不能为空'}]">
            <el-input class="w9000" v-model="addGroupData.val"></el-input>
          </el-form-item>

          <el-form-item label="长度：" prop="lenght" :label-width="formLabelWidth" :rules="[{required:true, message: '长度不能为空'},
          {type:'number', message: '请输入数字'}]">
            <el-input class="w9000" v-model.number="addGroupData.lenght"></el-input>
          </el-form-item>

          <el-form-item label="用户：" prop="user" :label-width="formLabelWidth" :rules="[{required:true, message: '用户不能为空'}]">
              <el-input class="w9000" v-if="titleText == '新建'" v-model="addGroupData.user"></el-input>
            <el-input class="w9000" v-if="titleText == '编辑'" disabled v-model="addGroupData.user"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button class="cancelBtn" @click="creatIp = false">取 消</el-button>
          <el-button type="primary" class="addOne" @click="addIp('addGroupData')">确 定</el-button>
        </div>
      </el-dialog>


  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'

export default {
  name: 'findApi',
  components: {

  },
  data() {
    return {
      jurisdiction:{},
      addGroupData:{},
      amendObj:{},
      index:0,
      titleText:'',
      creatIp:false,
      addName:true,
      amendName:true,
      delName:true,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      page: 1,
      searcHeader:'',
      listLoading: false,
      tablegroup: [],
      newPage:'',
      formLabelWidth: "120px",
      companyId:'',
      delValData:{},

      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  },

  created() {
    //this.getUserPermissions();
    this. initData();
  },

  methods: {

    //权限配置
    getUserPermissions(){
      axios({
        method: "post",
        url: "/getCasUser",
        data: {}
      }).then(res => {
        this.jurisdiction  = res.data;
        // if(this.jurisdiction.roles[0] !== 'admin'){
        //   this.addName = false;
        //   this.amendName = false;
        //   this.delName = false;
        // }else{
        //   this.addName = true;
        //   this.amendName = true;
        //   this.delName = true;
        // }
      }).catch(error => {
        console.log("网络错误");
      });
    },
    //初始化
    initData() {
      axios({
        method: 'post',
        url: '/api/tcpHeaderList',
        data: {
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }

      }).then(res => {
        this.tablegroup = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //搜索框
    searchData() {
      axios({
        method: 'post',
        url: '/api/searchTcpHeaderList',
        data: {
          header:this.searcHeader == '' ? this.initData() :this.searcHeader,
          pageSize: this.newPage == '' ? 10 : this.newPage,
          pageNum: this.pageNum
        }

      }).then(res => {
        this.tablegroup = res.data.data.list;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
        this.total = res.data.data.total;
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    //新建
    addIp(formName){
      this.$refs[formName].validate(valid => {
      if (valid) {
        axios({
          method: 'post',
          url: this.titleText == '新建' ? '/api/addTcpHeader' : '/api/updateTcpHeader',
          data: {
            headerName:this.addGroupData.name,
            headerValue:this.addGroupData.val,
            headerLength:this.addGroupData.lenght,
            creatUser:this.addGroupData.user,
            id:this.amendObj.id
          }

        }).then(res => {
          if(res.data.code == 2000){
            this.creatIp = false;
            this.initData();
            this.addGroupData = {};
            this.$refs[formName].resetFields();
          }else{
            this.$message({type: "info",message: res.data.message});
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      } else {
        console.log("error submit!!");
        return false;
      }
    });
    },
    //编辑
    amendIp(row){
      this.amendObj = row;
      this.creatIp = true;
      this.titleText = '编辑';
      this.addGroupData.name = row.headerName;
      this.addGroupData.val = row.headerValue;
      this.addGroupData.lenght = row.headerLength;
      this.addGroupData.user = row.creatUser;
    },

    //删除
    delIp(row){
      this.delValData = row;
      this.$confirm(`是否删除header "${this.delValData.headerName}",如删除则不可恢复!`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
          this.delValList();
      }).catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除"
        });
      });
    },

    delValList(){
      axios({
        method: 'post',
        url: '/api/deleteTcpHeader',
        data: {
          id:this.delValData.id
        }

      }).then(res => {
        if(res.data.code == 2000){
          this.$message({type: "success",message: '删除header成功'});
          this.initData();
        }else{
          this.$message({type: "info",message: res.data.message});
        }
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },


    tableRowClassName(row, index) {
      if (index%2 !== 0) {
        return 'info-row';
      }
      return '';
    },

    //分页查询
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();


    },
    handleSizeChange(val) {
      this.pageNum = val;
      this.initData();
    }



  }

}
</script>

<style scoped>

</style>



