<template>
  <div>
    <apiListNav :activeIndex=4></apiListNav>

    <p style="clear:both"></p>
    <div class="panel panel-default">
      <div class="console-panel-header">
        <span class="panel-header">返回结果基础定义</span>
      </div>
    </div>
    <el-form class="ml100 demo-ruleForm" :model="addApiData" ref="addApiData4">
      <el-form-item label="返回类型" :label-width="formLabelWidth">
        <select class="form-control w350">
          <option v-for="item in callBackTypeOptions" :key="item.value" :label="item.label" :value="item.value">{{addApiData.apiInfo.callBackType==1?'JSON':''}}</option>
        </select>
        <!-- <el-select v-model="addApiData">
          <el-option v-for="item in callBackTypeOptions" :key="item.value" :label="item.label" :value="item.value">{{addApiData.apiInfo.callBackType==1?'JSON':''}}</el-option>
        </el-select> -->
      </el-form-item>
      <el-form-item label="返回结果示例" v-if="this.$route.query.num !==true" style="position:relative;float:left;display:inline-block;width:40%;"
        prop="apiInfo.callBackSuccessExample" :label-width="formLabelWidth" :rules="[{required:true, message: '返回结果示例不能为空'},{validator:successVal,trigger:'blur'}]">
        <el-input type="textarea" :rows="8" placeholder="不超过2000个字符" v-model="addApiData.apiInfo.callBackSuccessExample" auto-complete="off"></el-input>
        <span @click="successClcik" class="successWritePlaceDialog w550 commonVaL">{{addApiData.apiInfo.callBackSuccessExample}}</span>
      </el-form-item>

      <el-form-item label="失败返回结果示例" v-if="this.$route.query.num !==true" style="float:right;width:40%;margin-right:15%" prop="apiInfo.callBackFailExample"
        :label-width="formLabelWidth" :rules="[{validator:errorVal,trigger:'blur'}]">
        <el-input type="textarea" :rows="8" placeholder="不超过2000个字符" v-model="addApiData.apiInfo.callBackFailExample" auto-complete="off"></el-input>
        <span @click="errorClcik" class="errorWritePlaceDialog w550 commonVaL">{{addApiData.apiInfo.callBackFailExample}}</span>
      </el-form-item>
      <p style="clear:both;"></p>

      <jsonData :addApiData="addApiData" ref="jsonDataRef"></jsonData>

      <div class="dialog-footer teCen mt20">
        <el-button type="info" class="searchOne" @click="preStep">上一步</el-button>
        <el-button type="primary" class="addOne" id="addBtnApi" @click="addApiBtn">创建</el-button>
        <el-button type="primary" class="addOne" id="addBtnApiDip" disabled @click="addApiBtn" style="display:none">创建</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import apiListNav from './apiListNav';
  import axios from 'axios'
  import { formatJson } from '../../assets/json'
  import jsonData from '@/components/common/jsonData'


  export default {
    name: 'fourthDialog',
    props: ['addApiData'],
    components: {
      apiListNav,
      jsonData
    },

    data() {
      return {
        formLabelWidth: '125px',
        callBackTypeOptions: [{ value: 1, label: 'JSON' }],
        codeMap: false,
        dictItemNameOptions: [],
        keyOptions: [],
        activeNameJSon: 'first',
        value: '',
        jsonArr:[],

        successVal: (rule, value, callback) => {
          if ((value) === '') {
            callback(new Error('输入值不能为空'));
          }else{
            var resultJson = formatJson(this.addApiData.apiInfo.callBackSuccessExample);
            $(".successWritePlaceDialog").html('<pre>' + resultJson + '<pre/>');
            $(".successWritePlaceDialog").show();
            callback();
          }
        },
        errorVal: (rule, value, callback) => {
          var resultErrorJson = formatJson(this.addApiData.apiInfo.callBackFailExample);
          $(".errorWritePlaceDialog").html('<pre>' + resultErrorJson + '<pre/>');
          $(".errorWritePlaceDialog").show();
        },
      }
    },
    created() {
    },
    methods: {
      
      successClcik() {
        $(".successWritePlaceDialog").hide();
      },
      errorClcik() {
        $(".errorWritePlaceDialog").hide();
      },
      addApiBtn: function () {
        var jsonArr = this.$refs.jsonDataRef.anal();
        this.$emit('addApiBtn')
      },

      preStep: function () {
        this.$emit('preStep', "");
      },

    }
  }

</script>


<style scoped>
</style>


