<template>
  <div>
    <div class="accreditLine"></div>
    <el-form v-model="groupHerfId">
      <span style="font-weight:700;">您将对下列API进行授权操作：</span>
      <div class="issueAPI">{{groupHerfId.apiName}}</div>

      <span style="font-weight:700;">选择要授权的环境：</span>
      <p></p>
      <template>
        <el-radio-group v-model="env">
          <el-radio-button label=1>线上</el-radio-button>
          <el-radio-button label=2>预发</el-radio-button>
        </el-radio-group>
      </template>
      <br>

      <div>
        <span style="font-weight:700;">选择要绑定的APP</span>
        <p></p>
        <div class="fl flBox" style="margin-bottom:20px;width: 100%">
          <el-form :inline="true" class="demo-form-inline">
            <el-form-item>
              <el-input placeholder="请输入应用名称" v-model="searchimpowerName" class="w250"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="publicData()">搜索</el-button>
            </el-form-item>
          </el-form>
          <el-table ref="multipleTable" border tooltip-effect="dark" style="width: 100%;height: 380px;overflow:auto" :data="appKeyData" @selection-change="handleSelectionChangeApi">
            <el-table-column type="selection" width="55" class="selection" prop='' @selection-change="handleSelectionChangeApi"></el-table-column>

            <el-table-column prop="appName" label="应用名称"></el-table-column>
            <el-table-column prop="appKey" label="appKey"></el-table-column>
            <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>
            <el-table-column prop="description" label="描述"></el-table-column>

          </el-table>
        </div>
      </div>
    </el-form>
    <p style="clear:both"></p>
    <div slot="footer" class="dialog-footer">
      <el-button @click="$emit('cancle', '')">取 消</el-button>
      <el-button type="primary" @click="NoAppDataDetailBtn">确 定</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'accreditCom',
  props: ["groupHerfId", "searchimpowerName", "appKeyData", "dateFormat"],

  data() {

    return {
      env: 1,
      multipleSelection: [],
    }
  },
  created() {

  },
  methods: {

    publicData() {
      const id = this.groupHerfId.id;
      axios({
        method: 'post',
        url: '/api/findNoAccreditApps',
        data: {
          appName: this.searchimpowerName == '' ? null : this.searchimpowerName,
          apiId: id,
          env: this.env
        }
      }).then(res => {
        this.appKeyData = res.data.data;
      }).catch(error => {
        console.log(error.data.message);
      })
    },

    //授权API复选框状态改变
    handleSelectionChangeApi(val) {
      this.multipleSelection = val;
    },

    //确认提交授权
    NoAppDataDetailBtn() {
      const groupHerf = this.groupHerfId.id;
      var flag = 0;
      var appIds = '';

      if (this.multipleSelection.length > 0) {
        this.multipleSelection.forEach(row => {
          if (flag == 0) {
            appIds += row.id;
          } else {
            appIds += (',' + row.id);
          }
          flag++;
        });

        axios({
          method: 'post',
          url: '/api/apiToApp',
          data: {
            apiId: groupHerf,
            appIds: appIds,
            env: this.env
          }
        }).then(res => {
          if (res.data.code !== 2000) {
            alert(res.data.message);
          } else {
            this.$emit('accreditOK', '');
            this.publicData();
            this.$message({
              type: 'success',
              message: '添加授权成功'
            });
          }
        }).catch(error => {
          console.log('网络错误，不能访问');
        });
      }
    },

  }
}
</script>


<style scoped>

</style>



