                  <env :rowData="scope.row" v-on:envLookData="lookData"></env>
                </template>
              </el-table-column>
              <el-table-column prop="" label="操作" width="240px">
                <template slot-scope="scope">
                  <router-link class="" :to="{path:'portManage/portApiManage',query:{amend:1,id:scope.row.id,apiGroupId:scope.row.apiGroupId,productEnvVersion:(scope.row.productEnvVersion == null ? '' : scope.row.productEnvVersion)}}">
                    <el-button type="text" class="radiusBtn mr10 pubColor" v-if="amendBtn" size="small">编辑</el-button>
                  </router-link>
                  <el-button type="text" class="radiusBtn delColor" size="small" v-if="delMagBtn" @click="delApiBtn(scope.$index, scope.row)">删除</el-button>
                  <el-button type="text" class="radiusBtn pubColor" size="small" v-if="pubMagBtn" @click="publish(scope.$index, scope.row)">发布</el-button>
                  <el-button type="text" class="radiusBtn pubColor" size="small" v-if="upMagBtn" @click="overLine(scope.$index, scope.row)">下线</el-button>
                  <el-button type="text" class="radiusBtn pubColor" size="small" v-if="copyMagBtn" @click="copyBtn(scope.$index, scope.row)">复制</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>

          <p style="clear:both"></p>
          <div class="teCen mt60" v-if="this.apiList.length < 1">
            <p><img src="../../assets/Shape.png" /></p>
            <span>暂无数据</span>
          </div>

          <div class="page" style="padding-bottom:30px;">
            <el-pagination id="pageOne" style="text-align:right;margin-top:80px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
                           :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize" layout=" sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

            <el-pagination id="pageTwo" style="text-align:right;margin-top:80px;display:none;" @size-change="handleSizeChange2" @current-change="handleCurrentChange2" :current-page="currentPage2"
                           :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize2" layout=" sizes, prev, pager, next, jumper" :total="total2">
            </el-pagination>

            <el-pagination id="pageThree" style="text-align:right;margin-top:80px;display:none;" @size-change="handleSizeChange3" @current-change="handleCurrentChange3" :current-page="currentPage3"
                           :page-sizes="[10, 20, 30, 40]" :page-size="this.pageSize3" layout=" sizes, prev, pager, next, jumper" :total="total3">
            </el-pagination>

          </div>
