    <el-dialog title="创建API：" :visible.sync="createDialog" size="full" :modal="false" style="margin:60px 0 0 188px;margin-left: 188px;" :before-close="closeDialog">

      <firstDialog v-if="dialogIndex==1" :data='addApiData.apiInfo' :cacheInput="cacheInput" :cacheInputOptions="cacheInputOptions" :groupOptions='groupOptions' @creatGroup="creatGroup=true" @nextStep="submitForm(1)" @cancle="preStep(1)"></firstDialog>

      <secondDialog v-on:amendApiMore="amendChild" v-else-if="dialogIndex==2" :addApiData='addApiData' :backEndParam='backEndParam' @amendApiMore="amendApi=true" @nextStep="submitForm(2)" @preStep="preStep(2)"></secondDialog>

      <thirdDialog v-else-if="dialogIndex==3" :addApiData='addApiData' :backEndParam='backEndParam' :numData='numData' :numDataJson="numDataJson" :numDataXml="numDataXml" @nextStep="submitForm(3)" @preStep="preStep(3)"></thirdDialog>

      <fourthDialog v-else-if="dialogIndex==4" :addApiData='addApiData' @addApiBtn="addApiBtn" @preStep="preStep(4)"></fourthDialog>
    </el-dialog>
    <!-- 创建分组弹框 start -->
    <el-dialog title="创建分组" :visible.sync="creatGroup" size="tiny">
      <creatGroup @addGroupOk="addGroupOk" @cancleCreate="creatGroup=false"></creatGroup>
    </el-dialog>
    <!-- 发布API弹框 start -->
    <el-dialog title="发布API：" :visible.sync="issue" size="tiny" style="top:-5%">
      <publishApi :groupHerfId="groupHerfId" @issueOK="issueOK" @cancle="issue=false"></publishApi>
    </el-dialog>
    <!-- 授权弹框 start -->
    <el-dialog title="授权" :visible.sync="accredit" size="small" id="accreditBox">
      <accreditCom :groupHerfId="groupHerfId" :searchimpowerName="searchimpowerName" :appKeyData="appKeyData" :dateFormat="dateFormat" @accreditOK="accreditOK" @cancle="accredit=false"></accreditCom>
    </el-dialog>
    <!-- 下线弹框 start -->
    <el-dialog title="下线API：" :visible.sync="offLine" size="tiny" style="top:-5%">
      <overLineApiCom :groupHerfId="groupHerfId" @cancle="offLine=false" @offLineOK="offLineOK"></overLineApiCom>
    </el-dialog>
    <!-- 编辑更多弹框 start -->
    <el-dialog title="编辑更多" :visible.sync="amendApi" size="small"  style="top:-5%">
      <amendApi :dataStrs="dataStrs" @amendChild="amendChild" :addApiData='addApiData' @cancleAmendApi="amendApi=false" @confirm="amendApi=false"></amendApi>
    </el-dialog>
    <!-- 查看API定义详情 start -->
    <el-dialog title="API定义详情" :visible.sync="look" style="overflow:auto;top:-5%">
      <queryApiDetail :apiDefineDetail="apiDefineDetail" :requestParamsVersionsList="requestParamsVersionsList" :backEndParam2="backEndParam2" :resultSettingsVersionsList="resultSettingsVersionsList" :detialVersionFlag="detialVersionFlag" :numData2="numData2" :groupHerfId="groupHerfId" @closeLook="look=false"></queryApiDetail>
    </el-dialog>
  </div>
</template>

<script>
  import axios from "axios";
  import moment from "moment";
  import topNav from "@/components/common/topNav";
  import page from "@/components/common/page";
  import env from "@/components/common/env";
  import firstDialog from "@/components/portManage/firstDialog";
  import secondDialog from "@/components/portManage/secondDialog";
  import thirdDialog from "@/components/portManage/thirdDialog";
  import fourthDialog from "@/components/portManage/fourthDialog";
  import creatGroup from "@/components/portManage/creatGroup";
  import amendApi from "@/components/portManage/amendApi";
  import queryApiDetail from "@/components/portManage/queryApiDetail";
  import publishApi from "@/components/portManage/publishApi";
  import accreditCom from "@/components/portManage/accreditCom";
  import overLineApiCom from "@/components/portManage/overLineApiCom";
  import { formatJson } from "@/assets/json";
  import { jurisdictDet } from "@/assets/getDetail"
  import apiTree from "@/components/common/apiTree";
  import PageHeader from '@/components/common/pageHeader.vue'

  export default {
    name: "portManage",
    components: {
      topNav,
      page,
      env,
      firstDialog,
      secondDialog,
      thirdDialog,
      fourthDialog,
      creatGroup,
      amendApi,
      queryApiDetail,
      publishApi,
      accreditCom,
      overLineApiCom,
      apiTree,
      PageHeader
    },
    data() {
      return {
        addMagBtn:false,
        amendBtn:false,
        delMagBtn:false,
        pubMagBtn:false,
        upMagBtn:false,
        copyMagBtn:false,
        jurisdiction:{},
        detailObj:[],
        detailObj2:[],
        detailObj3:[],
        detailObj4:[],
        detailObj5:[],
        detailObj6:[],
        detailObj7:[],
        detailObj8:[],
        apiDataId:'',
        dataId:'',
        dataId2:'',
        dataLabel:'',
        alertTextArr:[],
        dataStrs:'',
        dialogIndex: 1,
        value: "",
        currentPage: 1,
        currentPage2: 1,
        currentPage3: 1,
        pageSize: 10,
        pageSize2: 10,
        pageSize3: 10,
        total: 0,
        total2: 0,
        total3: 0,
        aDmsg:'',
        newPage:'',
        newPage2:'',
        newPage3:'',
        groupArr: [], //  分组（全部）
        runArr: [{ text: "线上", value: 1 }, { text: "预发", value: 2 }], //  运行环境（全部）
        creatGroup: false, //创建分组
        searchCond: {},
        apiList: [], // API表格数据
        groupOptions: [],

        cacheInput:[{value:2}],
        cacheInputOptions:[
          {value:2,label:"否"},
          {value:1,label:"是"},
        ],

        createDialog: false,
        appKeyData: [], //授权展示
        searchimpowerName: "", //授权搜索
        issue: false, //发布API

        delApiData: {}, //删除API保存对象
        accredit: false, //授权API
        offLine: false, //下线API
        amendApi: false, //编辑更多按钮
        look: false, //运行中查看API详情


        searchGroupName: "", //搜索
        searchGroupId:"",
        groupHerfId: { pubDescription: "" }, //分组管理传递id
        rowIndex: null,
        detialVersionFlag: null,
        apiDefineDetail: {},
        backendVersionsList: [],
        requestParamsVersionsList: [],
        resultSettingsVersionsList: [],

        //api添加
        addApiData: {
          apiInfo: {
            apiGroupId: "",
            apiName: "",
            apiDescription: "",
            saveMongoDB: "2", //是否存储
            mongodbURI: "",
            mongodbDBName: "",
            mongodbCollectionName: "",
            saveMQ: "2", //发送MQ
            cacheNo: null, //是否缓存
            cacheUnit: null,
            mqType: "",
            mqAddress: "",
            mqUserName: null,
            mqPasswd: "",
            mqTopicName: "", //队列
            httpPath: "",
            httpMethod: 1,
            backendProtocolType: 1,
            callBackType: 1,
            callBackSuccessExample: "",
            callBackFailExample: "",
            backEndAddress: "",
            backEndAddressB: "",
            backEndPath: "",
            backEndHttpMethod: 1,
            backEndTimeout: "",
            isMock: 2,
            charge: 2,
            mockData: "",
            interfaceName: "",
            responseTransParam: null, //json
            responseConfigJson: ""
          },
          //后端服务参数配置
          backEndRequestParamsList: [],
          //入参定义
          requestParamsList: [
            {
              maxLength: "50",
              minLength: "1",
              paramName: "",
              paramsLocation: 2,
              paramsType: 1,
              paramsMust: 1,
              paramsDefaultValue: "",
              paramsExample: "",
              paramsDescription: "",
              regularExpress: "" //参数验证
            }
          ]
        },
        backEndParam: [],
        numData: [
          {
            paramName: "",
            paramValue: "",
            paramsLocation: 2,
            paramDescription: "",
            paramsType: 2
          }
        ],
        numDataJson: [],
        numDataXml: [],

        backEndParam2: [],
        numData2: [],

        dataTree: [],
        defaultProps: {
          children: "children",
          label: "label"
        },
        addflowData: {
          subThirdApiInfo: {},
          subThirdRequestInfo: [
            {
              requestParamsId: "",
              paramsType: "",
              paramName: "",
              paramValue: "",
              paramsLocation: "",
              paramDescription: ""
            }
          ],
        },
        defaultExpanded: []
      };
    },
    computed: {
      bodyHeight() {
        return this.$store.getters.bodyHeight
      },
      bodyWidth() {
        return this.$store.getters.bodyWidth
      }
    },
    created() {
      this.getUserPermissions();
      this.initData();
      // this.menuGroup();
      this.loadGroups();
      this.addFlow();
      this.treeGroup();
    },
    methods: {
      apiIdChange(){
        var resNum = /^(-|\+)?\d+$/;
        console.log(resNum.test(this.searchGroupId))
      },
      //权限设置
      getUserPermissions(){
        axios({
          method: "post",
          url: "/getCasUser",
          data: {}
        }).then(res => {
          this.jurisdiction  = res.data;
          if(this.jurisdiction.perms.length == 0 || this.jurisdiction.perms == null){
            this.$confirm('权限不足，请联系管理员', '提示', {
              confirmButtonText: '退出',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              location.href = res.data.logoutUrl;
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '权限不足，请联系管理员'
              });
            });
          }
        }).catch(error => {
          console.log("网络错误");
        });
      },

      amendChild(dataStr){
        this.dataStrs = dataStr;
      },

      handleNodeClick(data) {
        this.searchGroupName = "";
        this.searchGroupId = "";
        this.pageSize = 10;
        this.pageSize2 = 10;
        this.pageSize3 = 10;
        this.aDmsg = data;
        $(".el-table-filter__list-item.is-active").css({"color":"#000","background-color":"#fff"});
        this.apiDataId = data.id;
        this.apiList = [];
        this.dataId = data.id;
        this.dataId2 = data.id;
        this.dataLabel = data.label;
        this.searchCond.env = '';
        if (data.label == "全部") {
          $("#pageOne").show();
          $("#pageTwo").hide();
          $("#pageThree").hide();
          this.pageSize = 10;
          this.currentPage = 1;
          this.initData();
        } else if (data.label == "（无）") {
          $("#pageThree").hide();
          $("#pageTwo").show();
          $("#pageOne").hide();
          this.pageSize2 = 10;
          this.currentPage2 = 1;
          axios({
            method: "post",
            url: "/api/apiInfoListByGroup",
            data: {
              apiGroupId: data.id,
              flagNoGroup: 1,
              env:this.searchCond.env == undefined ? '' :this.searchCond.env,
              pageSize: this.newPage2 =='' ? 10 : this.newPage2,
              pageNum: this.currentPage2
            }
          }).then(res => {
            this.$set(this, 'apiList',  res.data.data.list)
//            this.apiList = res.data.data.list;
            this.pageSize2 = res.data.data.pageSize;
            this.total2 = res.data.data.total;
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          $("#pageThree").show();
          $("#pageTwo").hide();
          $("#pageOne").hide();
          this.pageSize3 = 10;
          this.currentPage3 = 1;
          axios({
            method: "post",
            url: "/api/apiInfoListByGroup",
            data: {
              apiGroupId: data.id,
              pageSize: this.newPage3 =='' ? 10 : this.newPage3,
              pageNum: this.currentPage3
            }
          }).then(res => {
            this.apiList = res.data.data.list;
            this.pageSize3 = res.data.data.pageSize;
            this.total3 = res.data.data.total;
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        }

      },

      dataInit() {
        if (this.$route.query.num) {
          this.createDialog = true;
          this.dialogIndex = 3;
          this.backEndParam.push({
            paramName: "",
            paramValue: "",
            paramsLocation: 2,
            paramDescription: "",
            paramsType: 1
          });
        }
      },
      //关闭弹框的事件
      closeDialog(done) {
        if(this.$route.query.num){
          this.$confirm("确认关闭？")
            .then(_ => {
              done();
              this.$router.back();
            })
            .catch(_ => {});

        }else{
          var url = window.location.href;
          url = url.replace("num=2&", "");
          setTimeout(function() {
            window.location.href = url;
          }, 300);
          this.$confirm("确认关闭？")
            .then(_ => {
              done();
              location.reload();
            })
            .catch(_ => {});
        }
      },


      //========================================   api表格数据展示 start    ========================================
      //流量传过去的
      addFlow() {
        if (this.$route.query.num == 2) {
          this.dataInit();
          axios({
            method: "post",
            url: "/api/findApiInfo",
            data: {
              id: this.$route.query.id,
              apiGroupId: this.$route.query.groupId
            }
          }).then(res => {
            console.log(res.data.data, "流量跳转");
            res.data.data.apiInfo.interfaceName = "";
            res.data.data.apiInfo.backEndAddress = "";
            res.data.data.apiInfo.backEndPath = "";
            res.data.data.apiInfo.backEndTimeout = "";
            res.data.data.apiInfo.weight = "";
            res.data.data.apiInfo.mockData = "";


            this.addApiData.apiInfo = res.data.data.apiInfo;
            this.addApiData.apiInfo.responseConfigJson = "";
            this.addApiData.apiInfo.responseTransParam = "";
            this.addApiData.requestParamsList = res.data.data.requestParamsList;

            this.addApiData.backEndRequestParamsList = res.data.data.backendList;


            this.backEndParam = [];
            this.numData = [];
            this.addApiData.backEndRequestParamsList.forEach((obj, index) => {
              obj.paramName = '';
              obj.paramsLocation = 1;
              if (obj.paramsType == 1) {
                this.backEndParam.push(obj);
              }else if (obj.paramsType == 2) {
                this.numData = [];
              }
            });
            this.addApiData.backEndRequestParamsList = [];

          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        }
      },

      initData() {
        let apiGroupId = this.$route.query.apiGroupId;
        this.searchCond = {
          apiGroupId: apiGroupId,
          apiName: this.searchGroupName == "" ? null : this.searchGroupName,
          apiId:this.searchGroupId == "" ? null :this.searchGroupId,
          pageSize: this.newPage =='' ? 10: this.newPage,
          env:this.searchCond.env == undefined ? '' :this.searchCond.env,
          pageNum: this.currentPage,
        };
        this.queryData();
      },

      searchByCond() {
        var flagNum = true;
        var resNum = /^(-|\+)?\d+$/;
        if(resNum.test(this.searchGroupId) == false && this.searchGroupId !== ''){
          flagNum = false;
          this.$message.info('apiId请输入整数！');
        }
        if(flagNum){
          this.searchCond.apiName =
            this.searchGroupName == "" ? null : this.searchGroupName;
          this.searchCond.apiId =
            this.searchGroupId == "" ? null : this.searchGroupId;
          this.searchCond.flagNoGroup = this.dataLabel == "（无）" ? 1 : '';
          this.queryData();
        }
      },

      queryData() {
        axios({
          method: "post",
          url: "/api/apiInfoListByGroup",
          data: this.searchCond
        }).then(res => {
          if(res.data.code == 403){
            location.href = res.data.data;
          }

          if(this.jurisdiction.roles && this.jurisdiction.roles.includes('admin')){
            this.addMagBtn = true;
            this.amendBtn = true;
            this.delMagBtn = true;
            this.pubMagBtn = true;
            this.upMagBtn = true;
            this.copyMagBtn = true
          }else{
            this.amendBtn = false;
            this.delMagBtn = false;
            this.pubMagBtn = false;
            this.upMagBtn = false;
            this.addMagBtn = false;
            this.copyMagBtn = false
          }
          this.apiList = res.data.data.list;
          this.currentPage = res.data.data.pageNum;
          this.pageSize = res.data.data.pageSize;
          this.total = res.data.data.total;

        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //========================================   api表格数据展示  end     ========================================
      //========================================  分组（全部）分组管理接口 start ===================================
      menuGroup() {
        const groupHerf = this.groupHerfId;
        axios({
          method: "post",
          url: "/apiGroup/allList",//加载所有的分组
          data: {}
        }).then(resGroup => {
          this.jurisdiction.perms.map(data =>{
            if(data == '8e0d4a7bd5a7467ebe33dca8c4b9c22c'){
              var groupArrLen = resGroup.data.data;
              for (var i = 0; i < groupArrLen.length; i++) {
                this.groupArr[i] = {
                  text: groupArrLen[i].groupName,
                  value: groupArrLen[i].id
                };
              }
            }
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //========================================  分组（全部）分组管理接口 end =====================================
      //========================================        发布API     start    =======================================
      publish(index, row) {
        this.groupHerfId = row;
        this.rowIndex = index;
        this.issue = true;
      },

      delApiMes(index) {
        const delData = this.delApiData;
        axios({
          method: "post",
          url: "/api/delete",
          data: {
            apiGroupId: delData.apiGroupId,
            id: delData.id
          }
        })
          .then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: "error",
                message: res.data.message
              });
            } else {
              this.apiList.splice(index, 1);
              this.$message({
                type: "success",
                message: "删除API成功"
              });
            }
          }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //删除Api
      delApiBtn(index, row) {
        this.delApiData = row;
        this.delApiData.index = index;

        this.$confirm(`是否删除API "${this.delApiData.apiName}",如删除则不可恢复!`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.delApiMes(index);
        }).catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      },
      //========================   授权APP   ================
      impower(row) {
        this.accredit = true;
        this.groupHerfId = row;
      },
      //========================================        下线API      start   =======================================
      overLine(index, row) {
        this.groupHerfId = row;
        this.rowIndex = index;
        this.offLine = true;
      },

      issueOK() {
        this.issue = false;
      },

      accreditOK() {
        this.accredit = false;
        this.initData();
      },
      offLineOK() {
        this.offLine = false;
        this.initData();
      },

      //======================================  运行中查看 API定义详情 start ===================================
      lookData(row, versionFlag) {
        this.apiDefineDetail = {};
        this.backendVersionsList = [];
        this.requestParamsVersionsList = [];
        this.resultSettingsVersionsList = [];
        this.backEndParam2 = [];
        this.numData2 = [];
        this.detialVersionFlag = versionFlag;
        this.groupHerfId = row;
        const apiGroupId = this.groupHerfId.apiGroupId;
        const groupHerf = this.groupHerfId.id;
        this.look = true;

        axios({
          method: "post",
          url: "/api/findApiInfoVersion",
          data: {
            apiGroupId: apiGroupId,
            id: groupHerf,
            versionId: versionFlag
          }
        }).then(res => {
          var resultJson = formatJson(this.groupHerfId.callBackSuccessExample);
          $(".successWritePlaceDialog").html("<pre>" + resultJson + "<pre/>");
          var resultErrorJson = formatJson(
            this.groupHerfId.callBackFailExample
          );
          $(".errorWritePlaceDialog").html(
            "<pre>" + resultErrorJson + "<pre/>"
          );
          this.apiDefineDetail = res.data.data.apiInfoVersion;
          this.backendVersionsList = res.data.data.backendVersionsList;
          res.data.data.backendVersionsList.forEach((obj, index) => {
            if (obj.paramsType == 1) {
              this.backEndParam2.push(obj);
            } else if (obj.paramsType == 2) {
              this.numData2.push(obj);
            }
          });
          this.requestParamsVersionsList = res.data.data.requestParamsVersionsList;
          this.resultSettingsVersionsList = res.data.data.resultSettingsVersionsList;
        }).catch(error => {
          console.log("网络错误");
        });
      },
      //======================================  运行中查看 API定义详情 end ===============================
      //======================================      分页     start  ======================================
      handleCurrentChange(val) {
        this.currentPage = val;
        this.searchCond.pageNum = val;
        this.initData();
      },
      handleSizeChange(val){
        this.newPage = val;
        this.searchCond.pageNum = val;
        this.initData();
      },
      handleCurrentChange2(val2){
        this.apiList = [];
        this.currentPage2 = val2;
        axios({
          method: "post",
          url: "/api/apiInfoListByGroup",
          data: {
            apiGroupId: this.dataId,
            flagNoGroup: 1,
            env:this.searchCond.env == undefined ? '' :this.searchCond.env,
            pageSize: this.newPage2 =='' ? 10 : this.newPage2,
            pageNum: this.currentPage2
          }
        }).then(res => {
          this.apiList = res.data.data.list;
          this.pageSize = res.data.data.pageSize;
          this.total2 = res.data.data.total;
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      handleSizeChange2(val){
        this.newPage2 = val;
        this.handleNodeClick(this.aDmsg);
      },
      handleCurrentChange3(val3){
        this.apiList = [];
        this.currentPage3 = val3;
        axios({
          method: "post",
          url: "/api/apiInfoListByGroup",
          data: {
            apiGroupId: this.dataId2,
            pageSize: this.newPage3 =='' ? 10 :this.newPage3,
            env:this.searchCond.env == undefined ? '' :this.searchCond.env,
            pageNum: this.currentPage3 == undefined ? 1: this.currentPage3,
            flagNoGroup:this.dataLabel == "（无）" ? 1 : '',
          }
        }).then(res => {
          this.apiList = res.data.data.list;
          this.pageSize = res.data.data.pageSize;
          this.total3 = res.data.data.total;
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      handleSizeChange3(val){
        this.newPage3 = val;
        this.handleNodeClick(this.aDmsg);
      },
      //======================================      分页      end   ======================================
      //筛选
      tableChange(filters) {
        if (filters.hasOwnProperty("groups")) {
          this.searchCond.apiGroupId = filters["groups"][0];
        } else if (filters.hasOwnProperty("runTimeEnv")) {
          this.searchCond.env = filters["runTimeEnv"][0];
        }
        this.newPage = '';
        this.newPage2 = '';
        this.newPage3 = '';
        axios({
          method: "post",
          url: "/api/apiInfoListByGroup",
          data: {
            apiGroupId: this.dataLabel == "全部" ? '' : this.apiDataId,
            apiName: this.searchGroupName == "" ? null : this.searchGroupName,
            pageSize: 10,
            env:this.searchCond.env == undefined ? '' :this.searchCond.env,
            pageNum: this.dataLabel == "全部" ? this.currentPage : (this.dataLabel == "（无）") ? this.currentPage2 : this.currentPage3,
            flagNoGroup:this.dataLabel == "（无）" ? 1 : '',
          }
        }).then(res => {
          this.apiList = res.data.data.list;
          this.pageSize = res.data.data.pageSize;
          if(this.dataLabel == "（无）"){
            this.currentPage2 = 1;
            this.total2 = res.data.data.total;
          }else if(this.dataLabel == "全部"){
            this.currentPage = res.data.data.pageNum;
            this.currentPage = 1;
            this.total = res.data.data.total;
          }else if(this.dataLabel == '' && this.searchCond.env){
            this.currentPage = res.data.data.pageNum;
            this.currentPage = 1;
            this.total = res.data.data.total;
          }else{
            this.currentPage3 = res.data.data.pageNum;
            this.currentPage3 = 1;
            this.total3 = res.data.data.total;
          }

        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //创建API 初始化分组
      addApi() {
        this.createDialog = true;
        this.loadGroups();
      },
      addGroupOk() {
        this.creatGroup = false;
        this.loadGroups();
      },

      //加载分组信息
      loadGroups() {
        this.groupOptions = [];
        axios({
          method: "post",
          url: "/apiGroup/listOfNoLowerLevelGroup",
          data: {}
        }).then(res => {
          res.data.data.map(data => {
            this.groupOptions.push({ label: data.groupName, id: data.id });
          });
        }).catch(error => {
          console.log("网络错误，不能访问");
        });
      },
      //======================================创建API结果======================================
      addApiBtn() {
        if (this.$route.query.num == 2) {
          this.addflowData.subThirdApiInfo.apiId = this.addApiData.apiInfo.id;
          this.addflowData.subThirdApiInfo.interfaceName = this.addApiData.apiInfo.interfaceName;
          this.addflowData.subThirdApiInfo.backEndAddress = this.addApiData.apiInfo.backEndAddress;
          this.addflowData.subThirdApiInfo.backEndPath = this.addApiData.apiInfo.backEndPath;
          this.addflowData.subThirdApiInfo.backEndHttpMethod = this.addApiData.apiInfo.backEndHttpMethod;
          this.addflowData.subThirdApiInfo.backEndTimeout = this.addApiData.apiInfo.backEndTimeout;
          this.addflowData.subThirdApiInfo.callBackType = this.addApiData.apiInfo.callBackType;
          this.addflowData.subThirdApiInfo.weight = this.addApiData.apiInfo.weight;
          this.addflowData.subThirdApiInfo.backendProtocolType = this.addApiData.apiInfo.backendProtocolType;
          this.addflowData.subThirdApiInfo.responseConfigJson = this.addApiData.apiInfo.responseConfigJson;
          this.addflowData.subThirdApiInfo.responseTransParam = this.addApiData.apiInfo.responseTransParam;
          this.addflowData.subThirdRequestInfo = this.addApiData.backEndRequestParamsList;


          this.backEndParam.forEach((obj, index) => {
            this.addflowData.subThirdRequestInfo.push(obj);
          });

          this.numData.forEach((obj, index) => {
            this.addflowData.subThirdRequestInfo.push(obj);
          });

          this.numDataJson.forEach((obj, index) => {
            this.addflowData.subThirdRequestInfo.push(obj);
          });

          this.numDataXml.forEach((obj, index) => {
            this.addflowData.subThirdRequestInfo.push(obj);
          });

          axios({
            method: "post",
            url: "/api/back/sub/save",
            data: this.addflowData
          }).then(res => {
            console.log(res, "流量跳转保存");
            if (res.data.code !== 2000) {
              this.$message.error(res.data.message);
              this.createDialog = true;
            } else {
              this.$message.success('创建成功');
              this.createDialog = false;
              this.$router.back();
              $("#addBtnApi").css({"display":"none"});
              $("#addBtnApiDip").css({"display":"inline-block"});
            }
          }).catch(error => {
            console.log("网络错误，不能访问");
          });
        } else {
          var _this = this;
          _this.addApiData.backEndRequestParamsList = [];
          _this.backEndParam.forEach((obj, index) => {
            _this.addApiData.backEndRequestParamsList.push(obj);
          });

          if(this.addApiData.apiInfo.backendProtocolType == 3){
            this.addApiData.apiInfo.backEndPath = null;
            this.addApiData.apiInfo.backEndHttpMethod = null;
            _this.numDataXml.forEach((obj, index) => {
              _this.addApiData.backEndRequestParamsList.push(obj);
            });
          }else{
            _this.numData.forEach((obj, index) => {
              _this.addApiData.backEndRequestParamsList.push(obj);
            });

            _this.numDataJson.forEach((obj, index) => {
              _this.addApiData.backEndRequestParamsList.push(obj);
            });

            _this.numDataXml.forEach((obj, index) => {
              _this.addApiData.backEndRequestParamsList.push(obj);
            });
          }

          this.addApiData.requestParamsList.forEach((obj, index) => {
            obj.paramsMust ? (obj.paramsMust = 1) : (obj.paramsMust = 2);
            if(obj.paramsType == 2 || obj.paramsType == 3 || obj.paramsType == 4 || obj.paramsType == 5 || obj.paramsType == 6){
              obj.maxLength = '';
              obj.minLength = '';
            }
          });

          this.addApiData.apiInfo.callBackSuccessExample = this.addApiData.apiInfo.callBackSuccessExample.replace(/\s+/g,"");
          this.addApiData.apiInfo.callBackSuccessExample = this.addApiData.apiInfo.callBackSuccessExample.replace(/\s+/g,"");

          if(this.addApiData.apiInfo.cacheNo == 0){
            this.addApiData.apiInfo.cacheNo = null;
          }else if(this.addApiData.apiInfo.cacheUnit == ''){
            this.addApiData.apiInfo.cacheUnit = null;
          }

          if(this.addApiData.apiInfo.tcpHeaderId == null){
            this.addApiData.apiInfo.tcpBodyLenBytes = 0;
            this.addApiData.apiInfo.tcpReadBytes = 8;
          }



          axios({
            method: "post",
            url: "/api/save",
            data: this.addApiData
          }).then(res => {
            if (res.data.code !== 2000) {
              this.$message.error(res.data.message);
              this.createDialog = true;
            } else {
              $("#addBtnApi").css({"display":"none"});
              $("#addBtnApiDip").css({"display":"inline-block"});
              this.createDialog = false;
              this.searchCond.pageNum = 1;
              this.currentPage = 1;
              this.initData();
              location.reload();
            }
          }).catch(error => {
            this.$message.error("请求错误");
          });
        }
      },
      clearPropertiesVal(obj) {
        for (let key in obj) {
          let val = obj[key];
          if (Object.prototype.toString.call(val) == "[object Array]") {
            for (let i = 0; i < val.lenth; i++) {
              clearPropertiesVal(val);
            }
          } else {
            obj[key] = "";
          }
        }
      },
      treeGroup() {
        axios({
          method: "post",
          url: "/apiGroup/listByLevel",
          data: {}
        }).then(res => {
          this.jurisdiction.perms.map(dataPerm =>{
            if(dataPerm == '9bb84cb2b3874e48980eeff0a008fb26'){
              this.detailObj3.push('9bb84cb2b3874e48980eeff0a008fb26');
            }else if(dataPerm == '6269100027da4d0c87b2666a93df5cea'){
              this.detailObj3.push('6269100027da4d0c87b2666a93df5cea');
            }
            if(this.detailObj3.length > 1){
              this.dataTree = res.data.data;
              var data = res.data.data,
                iLen = data.length,
                results = [];
              for (var i = 0; i < iLen; i++) {
                if (data[i].parentId == null) {
                  var child = {
                    label: data[i].groupName,
                    id: data[i].id
                  };
                  child.children = this.aa(data[i].id, data);
                  results.push(child);
                }
              }
              results.splice(0,0,{label: "全部"});
              results.push({id: 0,label: "（无）"})
              this.defaultExpanded = []
              results.forEach(item => {
                if(item.id && item.id !== 0) {
                  this.defaultExpanded.push(item.id)
                }
              })
              this.alertTextArr = results;
            }
          });
        }).catch(error => {
          console.log("网络错误");
        });
      },
      aa(id, data) {
        var results = [];
        for (var i = 0, iLen = data.length; i < iLen; i++) {
          if (data[i].parentId == id) {
            var child = {
              label: data[i].groupName,
              id: data[i].id
            };
            child.children = this.aa(data[i].id, data);
            results.push(child);
          }
        }
        return results;
      },

      //===================表单验证：====================
      preStep(index) {
        if (index == 1) this.createDialog = false;
        else {
          this.dialogIndex = index - 1;
        }
      },
      submitForm(index) {
        this.dialogIndex = index + 1;
      },

      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

      //时间戳
      dateFormat(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss");
      },
      tableRowClassName(row, index) {
        if (index%2 !== 0) {
          return 'info-row';
        }
        return '';
      },

      //表格 分组（全部）筛选
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      //复制
      copyBtn(index, row) {
        axios({
          method: "post",
          url: "/api/copy",
          data: {
            id: row.id
          }
        })
          .then(res => {
            if (res.data.code !== 2000) {
              this.$message({
                type: "error",
                message: res.data.message
              });
            } else {
              this.$message({
                type: "success",
                message: "API复制成功"
              });
              this.initData()
            }
          }).catch(error => {
          console.log("网络错误，不能访问");
        });
      }
    }
  };
</script>
<style scoped>
  .treeBox {
    flex:0 0 200px;
    /*position: absolute;*/
    /*top: 0px;*/
    /*left: 0;*/
    border: 1px solid #dfe6ec;
  }

  .treeBox .treeGroup {
    width: 100%;
    height: 40px;
    line-height: 40px;
    display: inline-block;
    padding-left: 15px;
    border-bottom: 1px solid #dfe6ec;
    font-weight: bold;
    font-size:12px;
    color:#8792A3;
  }

  .el-tree-node__content {
    line-height: 40px !important;
    height: 40px !important;
    border-bottom: 1px solid #dfe6ec !important;
  }
  /* .el-input.is-disabled .el-input__inner{
    color:#eef1f6!important;
  } */
  .upEnv .cell{
    color:#1777D7 !important;
  }


  /*提示文字*/
  .box {
    width: 400px;
  }
  .top {
    text-align: center;
  }

  .left {
    float: left;
    width: 60px;
  }

  .right {
    float: right;
    width: 60px;
  }

  .bottom {
    clear: both;
    text-align: center;
  }

  .item {
    margin: 4px;
  }

  .left .el-tooltip__popper,
  .right .el-tooltip__popper {
    padding: 8px 10px;
  }

</style>



