<template>
  <div class="monitoring" style="width:100%;background:#fff;">

    <div class="detailBtn">
      <span class="detailNav">监控信息</span>
      <router-link to="/Port">
        <el-button class="w90" size="small">返回API列表</el-button>
      </router-link>
    </div>

    <div class="dataTime">
      <span class="demonstration" style="margin-left:10px;">开始时间：</span>
      <el-date-picker v-model="value0" type="datetime" @change="pickTime0" placeholder="选择日期" :picker-options="pickerOptions0" id="startDateTime"></el-date-picker>
      <span class="demonstration">结束时间：</span>
      <el-date-picker v-model="value1" type="datetime" @change="pickTime1" placeholder="选择日期" :picker-options="pickerOptions1" id="endDateTime"></el-date-picker>
      <el-button @click="search()" id="mySearch" :disabled="isDisable">查询</el-button>
      <div style="display:inline-block;float:right;margin-right:25px">
        <el-button :class="[hourCheck]" @click="hours()">近一小时</el-button>
        <el-button :class="[dayCheck]" @click="today()">今天</el-button>
        <el-button :class="[weekCheck]" @click="weeks()">近一周</el-button>
        <el-button :class="[monthCheck]" @click="months()">近一月</el-button>
      </div>
      <div class="monitoring-detail">
        <div class="monitoringMes mr20 mb20" style="width:100%;">
          <span>请求次数(单位:次数)</span>
          <div id="reqTimes" style="height:400px;width:95%"></div>
        </div>

        <div class="monitoringMes mb20" style="width:100%;">
          <span>响应时间 (单位:ms)</span>
          <div id="respTimes" style="height:400px;width:95%"></div>
        </div>

        <div class="monitoringMes" style="width:100%;">
          <span>错误分布(单位:次数)</span>
          <div id="errorDistribute" style="height:400px;width:95%"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import echarts from 'echarts'
import menuListDetail from '@/components/common/menuListDetail'
import { dateFormat } from '../../assets/time'
export default {
  name: 'monitoring',
  components: {
    menuListDetail
  },
  data() {
    return {
      isDisable: false,
      hourCheck: 'checked',
      dayCheck: 'unCheck',
      weekCheck: 'unCheck',
      monthCheck: 'unCheck',
      sTime: 0,
      eTime: 0,
      startTime: "",
      endTime: "",
      sign: 5,
      pickerOptions0: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7 - 3600 * 1000 * 24 * 30 || time.getTime() >= Date.now();
        },
      },
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7 - 3600 * 1000 * 24 * 30 || time.getTime() >= Date.now();
        },
      },
      value0: '',
      value1: '',
    }
  },

  mounted() {  //DOM加载完成
    var date = new Date();
    var seperator1 = "-";
    var seperator2 = ":";
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    var endDateTime = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
    endDateTime = new Date(Date.parse(endDateTime.replace(/-/g, "/")));
    endDateTime = endDateTime.getTime();
    var startDateTime = endDateTime - 3600 * 1000;
    $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(startDateTime, "yyyy-MM-dd hh:mm:ss");
    $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(endDateTime, "yyyy-MM-dd hh:mm:ss");
    var startTime = parseInt(startDateTime);
    var endTime = parseInt(endDateTime);
    this.startTime = startTime;
    this.endTime = endTime;
    this.sTime = startTime;
    this.eTime = endTime;
    this.sign = 1;
    this.timesAjax();
    this.respAjax();
    this.codesAjax();
  },


  methods: {
    pickTime0(val) {
      var sTime = new Date(Date.parse(val.replace(/-/g, "/")));
      var secondTime = Date.parse(new Date($(".el-date-editor--datetime")[1].lastElementChild.value));
      sTime = sTime.getTime();
      this.sTime = sTime;
      this.pickerOptions1.disabledDate = function disabledDate(time) {
        return time.getTime() <= sTime - 3600 * 24 * 1000 || time.getTime() >= Date.now();
      }
      if (parseInt(sTime) > parseInt(secondTime)) {
        $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(sTime, "yyyy-MM-dd hh:mm:ss");
      }
    },
    pickTime1(val) {
      var eTime = new Date(Date.parse(val.replace(/-/g, "/")));
      var firstTime = Date.parse(new Date($(".el-date-editor--datetime")[0].lastElementChild.value));
      eTime = eTime.getTime();
      this.eTime = eTime;
      this.pickerOptions0.disabledDate = function disabledDate(time) {
        return time.getTime() <= Date.now() - 8.64e7 - 3600 * 1000 * 24 * 30 || time.getTime() > eTime;
      }
      if (parseInt(firstTime) > parseInt(eTime)) {
        $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(eTime, "yyyy-MM-dd hh:mm:ss");
      }
    },
    timesAjax() {
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/monitor/times',
        data: {
          apiId: id,
          sign: this.sign,
          startTime: this.startTime,
          endTime: this.endTime,
        }
      }).then(res => {
        var reqTimes = echarts.init(document.getElementById('reqTimes'));
        var showTime = res.data.data.time;
        var timeArr = [], dayArr = [], dayTimeArr = [];
        for (var i = 0; i < showTime.length; i++) {
          timeArr[i] = (showTime[i].split(" ")[1]).split(":")[0] + ":" + (showTime[i].split(" ")[1]).split(":")[1];
          dayArr[i] = (showTime[i].split(" ")[0]).split("年")[1];
          dayTimeArr[i] = dayArr[i] + " " + timeArr[i]
        }
        var reqTimesOption = {
          tooltip: {
            trigger: 'axis',
            formatter: function(params, ticket, callback) {
              return showTime[params[0].dataIndex] + "</br>" + "请求次数 ：" + params[0].data
            }
          },
          legend: {
            data: ['请求次数']
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: dayTimeArr
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '请求次数',
              type: 'line',
              stack: '总量',
              data: res.data.data.number
            },
          ]
        };
        reqTimes.setOption(reqTimesOption);
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    respAjax() {
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/monitor/responses',
        data: {
          apiId: id,
          sign: this.sign,
          startTime: this.startTime,
          endTime: this.endTime,
        }
      }).then(res => {
        var respTimes = echarts.init(document.getElementById('respTimes'));
        var showTime = res.data.data.time;
        var timeArr = [], dayArr = [], dayTimeArr = [];
        for (var i = 0; i < showTime.length; i++) {
          timeArr[i] = (showTime[i].split(" ")[1]).split(":")[0] + ":" + (showTime[i].split(" ")[1]).split(":")[1];
          dayArr[i] = (showTime[i].split(" ")[0]).split("年")[1];
          dayTimeArr[i] = dayArr[i] + " " + timeArr[i]
        }
        var respTimesOption = {
          tooltip: {
            trigger: 'axis',
            formatter: function(params, ticket, callback) {
              return showTime[params[0].dataIndex] + "</br>" + "响应时间 ：" + params[0].data
            }
          },
          legend: {
            data: ['响应时间']
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: dayTimeArr,
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '响应时间',
              type: 'line',
              stack: '总量',
              data: res.data.data.response
            },
          ]
        };
        respTimes.setOption(respTimesOption);
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    codesAjax() {
      const id = this.$route.query.id;
      axios({
        method: 'post',
        url: '/monitor/codes',
        data: {
          apiId: id,
          sign: this.sign,
          startTime: this.startTime,
          endTime: this.endTime,
        }
      }).then(res => {
        var codes = {};
        codes.code = res.data.data.codes;
        codes.times = res.data.data.times;
        // var codes = {
        //     times:[
        //         "2017-08-01 10:00:15","2017-08-01 10:00:30","2017-08-01 10:00:45","2017-08-01 10:01:00","2017-08-01 10:01:15",
        //         "2017-08-01 10:01:30","2017-08-01 10:01:45","2017-08-01 10:02:00","2017-08-01 10:02:15","2017-08-01 10:02:30",
        //     ],
        //     code:{
        //         101:[20,55,62,84,50,20,55,62,84,50],
        //         1001:[65,55,31,25,31,20,55,62,84,50],
        //         102:[55,14,36,21,19,20,55,62,84,50],
        //         1022:[70,88,14,85,14,20,55,62,84,50],
        //         1555:[53,88,14,54,57,20,55,62,84,50],
        //         1666:[36,55,14,41,15,20,55,62,84,50],
        //         1777:[28,22,14,24,25,20,55,62,84,50],
        //         1888:[77,43,22,14,45,20,55,62,84,50],
        //         1999:[77,55,14,78,55,20,55,62,84,50],
        //         1000:[48,88,54,35,15,20,55,62,84,50],
        //     }
        // };
        var series = [], legend = [];
        for (var key in codes.code) {
          legend.push(key);
          series.push({
            name: key,
            type: 'line',
            // stack: '总量',
            data: codes.code[key]
          })
        }
        var errorDistribute = echarts.init(document.getElementById('errorDistribute'));
        var errorDistributeOption = {
          tooltip: {
            trigger: 'axis',
          },
          legend: {
            data: legend
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: codes.times,

            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: series
        };
        errorDistribute.setOption(errorDistributeOption);
      }).catch(error => {
        console.log('网络错误，不能访问');
      });
    },
    search() {
      var startTime = Date.parse(new Date($(".el-date-editor--datetime")[0].lastElementChild.value));
      var endTime = Date.parse(new Date($(".el-date-editor--datetime")[1].lastElementChild.value));
      startTime = parseInt(startTime);
      endTime = parseInt(endTime);
      this.startTime = startTime;
      this.endTime = endTime;
      this.sign = 5;
      this.timesAjax();
      this.respAjax();
      this.codesAjax();
    },
    hours() {
      this.hourCheck = 'checked';
      this.dayCheck = 'unCheck';
      this.weekCheck = 'unCheck';
      this.monthCheck = 'unCheck';
      this.sign = 1;
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var endDateTime = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
      endDateTime = new Date(Date.parse(endDateTime.replace(/-/g, "/")));
      endDateTime = endDateTime.getTime();
      var startDateTime = endDateTime - 3600 * 1000;
      $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(startDateTime, "yyyy-MM-dd hh:mm:ss");
      $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(endDateTime, "yyyy-MM-dd hh:mm:ss");
      var startTime = parseInt(startDateTime);
      var endTime = parseInt(endDateTime);
      this.startTime = startTime;
      this.endTime = endTime;
      this.timesAjax();
      this.respAjax();
      this.codesAjax();
    },
    today() {
      this.hourCheck = 'unCheck';
      this.dayCheck = 'checked';
      this.weekCheck = 'unCheck';
      this.monthCheck = 'unCheck';
      this.sign = 2;
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var endDateTime = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
      endDateTime = new Date(Date.parse(endDateTime.replace(/-/g, "/")));
      endDateTime = endDateTime.getTime();
      var startDateTime = endDateTime - (date.getHours() * 3600 * 1000 + date.getMinutes() * 60 * 1000 + date.getSeconds() * 1000);
      $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(startDateTime, "yyyy-MM-dd hh:mm:ss");
      $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(endDateTime, "yyyy-MM-dd hh:mm:ss");
      var startTime = parseInt(startDateTime);
      var endTime = parseInt(endDateTime);
      this.startTime = startTime;
      this.endTime = endTime;
      this.timesAjax();
      this.respAjax();
      this.codesAjax();
    },
    weeks() {
      this.hourCheck = 'unCheck';
      this.dayCheck = 'unCheck';
      this.weekCheck = 'checked';
      this.monthCheck = 'unCheck';
      this.sign = 3;
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var endDateTime = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
      endDateTime = new Date(Date.parse(endDateTime.replace(/-/g, "/")));
      endDateTime = endDateTime.getTime();
      var startDateTime = endDateTime - 3600 * 24 * 7 * 1000;
      $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(startDateTime, "yyyy-MM-dd hh:mm:ss");
      $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(endDateTime, "yyyy-MM-dd hh:mm:ss");
      var startTime = parseInt(startDateTime);
      var endTime = parseInt(endDateTime);
      this.startTime = startTime;
      this.endTime = endTime;
      this.timesAjax();
      this.respAjax();
      this.codesAjax();
    },
    months() {
      this.hourCheck = 'unCheck';
      this.dayCheck = 'unCheck';
      this.weekCheck = 'unCheck';
      this.monthCheck = 'checked';
      this.sign = 4;
      var date = new Date();
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var endDateTime = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds();
      endDateTime = new Date(Date.parse(endDateTime.replace(/-/g, "/")));
      endDateTime = endDateTime.getTime();
      var startDateTime = endDateTime - 3600 * 24 * 30 * 1000;
      $(".el-date-editor--datetime")[0].lastElementChild.value = dateFormat(startDateTime, "yyyy-MM-dd hh:mm:ss");
      $(".el-date-editor--datetime")[1].lastElementChild.value = dateFormat(endDateTime, "yyyy-MM-dd hh:mm:ss");
      var startTime = parseInt(startDateTime);
      var endTime = parseInt(endDateTime);
      this.startTime = startTime;
      this.endTime = endTime;
      this.timesAjax();
      this.respAjax();
      this.codesAjax();
    },
  }
}
</script>


<style scoped>
.monitoring {
  background: #fff;
}

.dataTime {
  width: 100%;
  height: 50px;
  border: 1px solid #E1E6EB;
  line-height: 50px;
}

.monitoring-detail {
  /* width: 100%; */
}

.monitoring-detail .monitoringMes {
  width: 49%;
  height: 450px;
  border: 1px solid #E1E6EB;
  /* float: left; */
}

.monitoring-detail .monitoringMes span {
  background: #F4F5F9;
  border: 1px solid #E1E6EB;
  padding-left: 15px;
  display: block;
  border-left: 3px solid #778;
  border-top: none;
  border-right: none;
}

.checked {
  background: #1C72EE;
  color: white;
  outline: none;
  border: none;
}

.checked:hover {
  border: none;
}

.unCheck {
  background: #EEF0F3;
  color: black;
  outline: none;
  border: none;
}

.unCheck:hover {
  border: none;
}
</style>



