<template>
  <div class="flowMonitoring" style="background:#ffffff;position:relative;height:100%;">
    <!--<div class="card_statistical">-->
      <!--<el-row :gutter="20">-->
        <!--<el-col :span="8"><div class="grid-content bg-purple">-->
          <!--<div class="text"><p>总支出</p>-->
            <!--<p>{{totalCost}}</p>-->
          <!--</div>-->
        <!--</div></el-col>-->
        <!--<el-col :span="8"><div class="grid-content bg-purple-light">-->
          <!--<div class="text">-->
            <!--<p>总收入</p>-->
            <!--<p>{{totalIncome}}</p>-->
          <!--</div>-->
        <!--</div></el-col>-->
        <!--<el-col :span="8"><div class="grid-content bg-purple">-->
          <!--<div class="text">-->
            <!--<p>总利润</p>-->
            <!--<p>{{totalProfit}}</p>-->
          <!--</div>-->
        <!--</div></el-col>-->
      <!--</el-row>-->
    <!--</div>-->
    <el-form :inline="true" class="search_form">
      <el-form-item label="分组：" prop="sourceName" label-width="55px">
        <el-select class="w9000" filterable clearable v-model="searchForm.sourceName" placeholder="请选择" @change="getApiAry">
          <el-option
            v-for="item in groupAry"
            :key="item.id"
            :label="item.groupName"
            :value="item.groupName">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item  label="时间: " label-width="55px">
        <el-date-picker class="w200" v-model="rangeDate" @change="dateChange" rangeSeparator="/" type="datetimerange"
                        placeholder="选择时间范围">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="info" class="searchOne" @click="initData()">搜 索</el-button>
        <el-button type="primary addOne" class="searchOne" @click="outExpress()">下 载</el-button>
      </el-form-item>
    </el-form>
    <div class="summary_box">
      <span>总条数：</span><span class="text">{{totalCount}}</span>
      <span style="margin-left: 10px">总收入：</span> <span class="text">{{totalIncome}}</span>
      <span style="margin-left: 10px">总支出：</span> <span class="text">{{totalCost}}</span>
      <span style="margin-left: 10px">总利润：</span> <span class="text">{{totalProfit}}</span>
    </div>
    <el-table ref="multipleTable" :data="tableList" tooltip-effect="dark"
              style="width:100%;border-top:8px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
      <el-table-column prop="groupName" label="分组名称"></el-table-column>
      <el-table-column prop="income" label="收入" :formatter="dataFixed"></el-table-column>
      <el-table-column prop="cost" label="支出" :formatter="dataFixed"></el-table-column>
      <el-table-column prop="profit" label="利润" :formatter="dataFixed"></el-table-column>
      <el-table-column prop="action" label="操作" width="80">
        <template slot-scope="scope">
          <el-button type="text" class="radiusBtn pubColor" @click="showDetail(scope.row)">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="teCen mt60" v-if="this.tableList.length < 1">
      <p><img src="../../assets/Shape.png"/></p>
      <span>暂无数据</span>
    </div>
    <div class="page" style="padding-bottom:30px;" v-if="this.tableList.length > 0">
      <el-pagination style="text-align:right;margin-top:80px;" @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="this.searchForm.pageNum"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="this.searchForm.pageSize" layout=" sizes, prev, pager, next, jumper"
                     :total="totalCount">
      </el-pagination>
    </div>
    <el-dialog title="详情" :visible.sync="showDetailDialog" size="large" style="top:-5%;" @close="dialogClose">
      <el-table ref="multipleTable" :data="tableDetailData" tooltip-effect="dark"
                style="width: 100%;border-bottom: 1px solid #e6e6e6; border-right: 1px solid #e6e6e6" :row-class-name="this.utils.tableRowClassName">
        <el-table-column prop="apiId" label="接口id"></el-table-column>
        <el-table-column prop="apiName" label="接口名称"></el-table-column>
        <el-table-column prop="income" label="收入" :formatter="dataFixed"></el-table-column>
        <el-table-column prop="cost" label="支出" :formatter="dataFixed"></el-table-column>
        <el-table-column prop="profit" label="利润" :formatter="dataFixed"></el-table-column>
      </el-table>
      <div class="page" style="padding-bottom:30px;" v-if="this.tableDetailData.length > 0">
        <el-pagination style="text-align:right;margin-top:30px;" @size-change="handleSizeDetailChange"
                       @current-change="handleCurrentDetailChange"
                       :current-page="this.detailSearch.pageNum"
                       :page-sizes="[10, 20, 30, 40]"
                       :page-size="this.detailSearch.pageSize" layout=" sizes, prev, pager, next, jumper"
                       :total="tableDetailTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import axios from "axios";
  import moment from "moment";
  import echarts from 'echarts'
  import CanvasBar from '../../components/common/ui/canvasPar/index.vue'

  export default {
    name: "apiOfflineRecord",
    components: {CanvasBar},
    data() {
      return {
        jurisdiction: {
          principal: {},
          loginUrl: '',
          logoutUrl: '',
          roles: [],
          perms: []
        },
        totalCount: 0,
        totalProfit: 0,
        totalCost: 0,
        totalIncome: 0,
        groupAry: [],
        apiAry: [],
        rangeDate: [moment().startOf('month').format('YYYY-MM-DD HH:mm:ss'), moment(new Date())],
        tableList: [],
        searchForm: {
          pageNum: 1,
          pageSize: 10,
          startTime: Number(moment(moment().startOf('month').format('YYYY-MM-DD HH:mm:ss')).format('x')),
          endTime: Number(moment(new Date()).format('x'))
        },
        description: "",

        delData: "",
        rowFlowData: {},
        showDetailDialog: false,
        tableDetailData: [],
        tableDetailTotal: 0,
        loadingDetail: false,
        detailSearch: {
          pageNum: 1,
          pageSize: 10
        },

        dateFormat(row, column) {
          const date = row[column.property];
          if (date == undefined) {
            return "";
          }
          return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }
      };
    },

    created() {
      this.initData();
      this.utils.getUserPermissions(this);
      this.utils.getGroupAry(this);
    },

    methods: {
      getApiAry(groupId) {
        this.handleCurrentChange(1);
      },
      // 日期改变
      dateChange(date) {
        let newDate = date.split("/")
        this.searchForm.startTime = Number(moment(newDate[0]).format('x'))
        this.searchForm.endTime = Number(moment(newDate[1]).format('x'))
        this.handleCurrentChange(1);
      },

      //初始化列表
      initData() {
        axios({
          method: "post",
          url: "/gateway/charge/profit/listProfit",
          data: this.searchForm
        }).then(res => {
          if (res.data.code === 2000) {
            this.tableList = res.data.data.list;
            this.totalCount = res.data.data.totalCount;
            this.totalProfit = res.data.data.totalProfit.toFixed(2)
            this.totalCost = res.data.data.totalCost.toFixed(2)
            this.totalIncome = res.data.data.totalIncome.toFixed(2)
          } else if (res.data.code === 403) {
            location.href = res.data.data;
          }
        }).catch(error => {
          console.log("网络错误，不能访问" + error);
        });
      },

      //翻页事件
      handleCurrentChange(val) {
        this.searchForm.pageNum = val;
        this.initData();
      },
      //改变分页大小事件
      handleSizeChange(val) {
        this.searchForm.pageSize = val;
        this.initData();
      },
      showDetail(row) {
        this.showDetailDialog = true
        this.detailSearch.startTime = this.searchForm.startTime
        this.detailSearch.endTime = this.searchForm.endTime
        this.detailSearch.sourceName = row.groupName
        this.detailInt()
      },

      dialogClose() {
        this.showDetailDialog = false
        this.tableDetailData = []
        this.tableDetailTotal = 0;
      },

      detailInt() {
        this.detailSearch.pageNum = 1
        this.detailSearch.pageSize = 10
        this.detailHandle()
      },

      detailHandle() {
        this.loadingDetail = true
        axios({
          method: "post",
          url: "/gateway/charge/profit/listProfitDetail",
          data: this.detailSearch
        }).then(res => {
          this.loadingDetail = false
          if (res.data.code === 2000) {
            this.tableDetailData = res.data.data.list;
            this.tableDetailTotal = res.data.data.totalCount;
          } else if (res.data.code === 403) {
            location.href = res.data.data;
          }
        }).catch(error => {
          this.loadingDetail = false
          console.log("网络错误，不能访问" + error);
        });
      },
      handleSizeDetailChange(val) {
        this.detailSearch.pageNum = val
        this.detailHandle()
      },
      handleCurrentDetailChange(val) {
        this.detailSearch.pageSize = val
        this.detailInt()
        },
      outExpress() {
        axios({
          method: "post",
          url: "/gateway/charge/bill/getServerURL",
          data: {}
        }).then(res => {
          let params = ''
          if (this.searchForm.sourceName) {
            params = 'sourceName=' + this.searchForm.sourceName
          } else {
            params = 'sourceName='
          }
          if (this.searchForm.startTime !== '') {
            params = params + '&startTime=' + this.searchForm.startTime
          }
          if (this.searchForm.startTime !== '') {
            params = params + '&endTime=' + this.searchForm.endTime
          }
          location.href=res.data.data+"/profit/profitExport?" + params;
        }).catch(error => {
          console.log("网络错误，不能访问" + error);
        });
      },
      dataFixed(row, column) {
        const date = row[column.property];
        if (date == undefined) {
          return "";
        }
        return date.toFixed(2);
      }
    }
  };
</script>

<style scoped>
  .card_statistical{
    padding: 5px 15px;
  }
  .grid-content{
    border: 1px solid #eaeefb;
    background: #fff;
    border-radius: 4px;
    min-height: 100px;
    display: flex;
    font-size: 0;
  }
  .grid-content:hover{
    box-shadow: 0 0  2px rgba(0, 0, 0, 0.2);
  }
  .grid-content>div{
    font-size: 18px;
    vertical-align: middle;
  }
  .grid-content>div.text{
    flex: 1;
    text-align: left;
    padding: 20px 10px;
    font-weight: bolder;
  }
  .grid-content>div.canvasBoxTotal{
    flex: 1;
    padding: 0;
    margin-top: 10px;
  }
  .canvasBox{
    text-align: right;
    flex: 0 0 50px;
  }
  .canvasBoxText{
    flex: 0 0 102px;
  }
  .text>p:first-child{
    font-weight: normal;
  }
  .summary_box{
    padding: 10px 15px;
    border-top: 8px solid rgb(230, 230, 230);
  }
  .summary_box .text{
    color: #FEBD49
  }
</style>



