<template>
	<div class="newValue" style="background:#ffffff;height:100%;">
		<requestParamsList :apiId="apiId"></requestParamsList>
	</div>
</template>
<script>
import requestParamsList from '@/components/portManage/requestParamsList'
export default {
	name:'newValue',
	components:{
		requestParamsList
	},
	data(){
		return{
			apiId:'',
		}
	},
	created(){
		this.initData();
	},
	methods:{
		initData(){
			this.apiId = this.$route.query.id;
		},
	}
}
</script>
<style scoped>

</style>






